import React from 'react';
import PropTypes from 'prop-types';
import numeral from 'numeral';

const SavingsDetail = ({ className, name, savings, factor }) => (
    <tr className={className}>
        <td className="text-secondary">{name}</td>
        <td>{savings !== undefined ? numeral(savings).format('0.0 ib') : null}</td>
        <td className="text-right">
            {savings === undefined && factor === undefined ? 'N/A' : null}
            {factor !== undefined ? `${numeral(factor).format('0.0')}X` : null}
        </td>
    </tr>
);

SavingsDetail.propTypes = {
    name: PropTypes.string.isRequired,
    savings: PropTypes.number,
    factor: PropTypes.number,
};

export default SavingsDetail;
