const App = require('app/Application');
const _ = require('underscore');
var Backbone = require('backbone');
var ArrayEventModel = require('app/models/ArrayEventModel');

var cache;

function getCache() {
    if (!cache) {
        cache = {};
        App.handleEvent('global', 'change:account', function () {
            cache = {};
        });
    }
    return cache;
}

var PoolEventsCollection = Backbone.Collection.extend({
    model: ArrayEventModel,

    poolUid: undefined,
    category: null,
    monthRange: 12,

    initialize: function (models, options) {
        _.defaults(this, options);
        console.assert(!options || (options && options.poolUid), 'poolUid must be provided');
    },

    url: function () {
        return this.api()
            .pod()
            .account()
            .segment('pools')
            .segment(this.poolUid)
            .segment('events')
            .addQuery({
                monthRange: this.monthRange,
                category: this.category,
            });
    },

    fetch: function (options) {
        if (!this.deferred) {
            this.deferred = Backbone.Collection.prototype.fetch.call(this, options);
        }
        return this.deferred;
    },

}, {
    get: function (poolId) {
        var cached = getCache()[poolId];
        if (!cached) {
            cached = getCache()[poolId] = new PoolEventsCollection([], {
                poolUid: poolId,
            });
        }
        return cached;
    },
});

module.exports = PoolEventsCollection;
