import React from 'react';
import { COLUMN_WIDTHS } from '@infosight/elmer/dist/components/Table';
import { ObjectDetailLink } from '@infosight/elmer/dist/infrastructure';
import {
    isEmpty,
    formatValue,
    formatDateTime,
    formatDateTimeWithTimeZone,
} from '../../../../../../util/formatter';
import {
    FORMAT_NUMBER_NO_PRECISION,
    FORMAT_BYTE_PRECISION,
    FORMAT_BYTE_10_PRECISION,
    FORMAT_MOMENT_CALENDAR_DAY,
    FORMAT_MOMENT_DATE_TIME,
    FORMAT_MOMENT_DATE_TIME_TIMEZONE_FROM_DESC,
} from '../../../../../../util/formatter/constants';
import ArraySupportStatus from './ArraySupportStatus';
import { ArrayHardwareStatus, AsupStatus, HeartbeatStatus } from './ArrayHealth';
import { OBJECTS } from '../../../../inventory';

export const DEFAULT_TAB = 'summary';
const MONITOR_SUPPORT_TAB = {
    value: 'monitor_support',
    title: 'Monitoring Services/Support',
};
/* eslint-disable react/prop-types */
export default infrastructureConfig =>
    ({
        categories: [DEFAULT_TAB, MONITOR_SUPPORT_TAB],
        columns: [
            {
                Header: 'Array',
                accessor: 'arraySearchableName',
                filterable: true,
                minWidth: COLUMN_WIDTHS.LARGE,
                Cell: ({ original: { hostName, arraySn } }) => {
                    const name = `${!isEmpty(hostName) ? `${hostName}` : ''}${isEmpty(hostName) ? arraySn : ` (${arraySn})`}`;
                    return (<ObjectDetailLink name={name} id={arraySn} includeIcon={false} objectType={OBJECTS.ARRAY} />);
                },
            },
            {
                Header: 'Group',
                accessor: 'groupName',
                minWidth: COLUMN_WIDTHS.MEDIUM,
                filterable: true,
                categories: [DEFAULT_TAB],
            },
            {
                Header: 'Pool',
                accessor: 'poolSortName',
                minWidth: COLUMN_WIDTHS.SMALL,
                filterable: true,
                Cell: ({ original: { poolName, poolUid } }) =>
                    <ObjectDetailLink name={poolName} id={poolUid} objectType={OBJECTS.POOL} config={infrastructureConfig} includeIcon={false} />,
                categories: [DEFAULT_TAB],
            },
            {
                Header: 'Model',
                accessor: 'model',
                minWidth: COLUMN_WIDTHS.XSMALL,
                filterable: true,
                categories: [DEFAULT_TAB],
            },
            {
                Header: 'S/W Version',
                accessor: 'softwareVersion',
                headerClassName: 'text-right',
                className: 'text-right',
                minWidth: COLUMN_WIDTHS.SMALL,
                Cell: ({ original: { softwareVersion } }) => softwareVersion && softwareVersion.split('-')[0],
                filterable: true,
                categories: [DEFAULT_TAB],
            },
            {
                Header: 'Shelves',
                accessor: 'shelfCount',
                minWidth: COLUMN_WIDTHS.XSMALL,
                headerClassName: 'text-right',
                className: 'text-right',
                Cell: ({ original: { shelfCount } }) => formatValue(shelfCount, FORMAT_NUMBER_NO_PRECISION),
                categories: [DEFAULT_TAB],
            },
            {
                Header: 'Hardware Status',
                accessor: 'hardwareStatusComparator',
                minWidth: COLUMN_WIDTHS.MEDIUM,
                headerClassName: 'text-center',
                className: 'text-center',
                Cell: ({ original: { latestHeartbeat, isAllFlash } }) => <ArrayHardwareStatus latestHeartbeat={latestHeartbeat} isAllFlash={isAllFlash} />,
                categories: [DEFAULT_TAB],
            },
            {
                Header: 'Raw Size',
                headerClassName: 'text-center',
                columns: [
                    {
                        Header: 'HDD',
                        accessor: 'arrayTotalRawHddBytes',
                        headerClassName: 'text-right',
                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { arrayTotalRawHddBytes } }) => formatValue(arrayTotalRawHddBytes, FORMAT_BYTE_10_PRECISION),
                    },
                    {
                        Header: 'SSD',
                        accessor: 'arrayTotalRawSsdBytes',
                        headerClassName: 'text-right',
                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { arrayTotalRawSsdBytes } }) => formatValue(arrayTotalRawSsdBytes, FORMAT_BYTE_10_PRECISION),
                    },
                ],
                categories: [DEFAULT_TAB],
            },
            {
                Header: 'Usable Space',
                headerClassName: 'text-center',
                columns: [
                    {
                        Header: 'Total',
                        accessor: 'arrayCapacityBytes',
                        headerClassName: 'text-right',
                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.XSMALL,
                        Cell: ({ original: { arrayCapacityBytes } }) => formatValue(arrayCapacityBytes, FORMAT_BYTE_PRECISION),
                    },
                    {
                        Header: 'Available',
                        accessor: 'arrayFreeBytes',
                        headerClassName: 'text-right',
                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { arrayFreeBytes } }) => formatValue(arrayFreeBytes, FORMAT_BYTE_PRECISION),
                    },
                ],
                categories: [DEFAULT_TAB],
            },
            {
                Header: 'Host Interface',
                accessor: 'networkCardInfo',
                minWidth: COLUMN_WIDTHS.MEDIUM,
                filterable: true,
                categories: [DEFAULT_TAB],
            },
            {
                Header: 'Call Home',
                headerClassName: 'text-center',
                columns: [
                    {
                        Header: 'Status',
                        accessor: 'sendingAsups',
                        headerClassName: 'text-center',
                        className: 'text-center',
                        minWidth: COLUMN_WIDTHS.XSMALL,
                        Cell: ({ original: { sendingAsups, asupReceivedTime } }) =>
                            <AsupStatus sendingAsups={sendingAsups} asupReceivedTime={asupReceivedTime} />,
                    },
                    {
                        Header: 'Last Data Sent',
                        accessor: 'asupReceivedTime',
                        headerClassName: 'text-right',
                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { asupReceivedTime, arrayTimezone } }) => (
                            <div
                                className="ellipsis-on-overflow"
                                title={formatDateTimeWithTimeZone(asupReceivedTime, FORMAT_MOMENT_DATE_TIME_TIMEZONE_FROM_DESC, arrayTimezone)}
                            >
                                {formatDateTimeWithTimeZone(asupReceivedTime, FORMAT_MOMENT_DATE_TIME_TIMEZONE_FROM_DESC, arrayTimezone)}
                            </div>),
                    },
                ],
                categories: [MONITOR_SUPPORT_TAB],
            },
            {
                Header: 'Heartbeat',
                headerClassName: 'text-center',
                columns: [
                    {
                        Header: 'Status',
                        accessor: 'isHbDelayed',
                        headerClassName: 'text-center',
                        className: 'text-center',
                        minWidth: COLUMN_WIDTHS.XSMALL,
                        Cell: ({ original: { latestHeartbeat } }) =>
                            latestHeartbeat && <HeartbeatStatus timestamp={latestHeartbeat.timestamp} />,
                    },
                    {
                        Header: 'Last Heartbeat',
                        accessor: 'latestHbSort',
                        headerClassName: 'text-right',
                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { latestHeartbeat } }) => (
                            <div
                                className="ellipsis-on-overflow"
                                title={formatDateTimeWithTimeZone(latestHeartbeat && latestHeartbeat.timestamp, FORMAT_MOMENT_DATE_TIME)}
                            >
                                {formatDateTimeWithTimeZone(latestHeartbeat && latestHeartbeat.timestamp, FORMAT_MOMENT_DATE_TIME)}
                            </div>),
                    },
                ],
                categories: [MONITOR_SUPPORT_TAB],
            },
            {
                Header: 'Support',
                headerClassName: 'text-center',
                columns: [
                    {
                        Header: 'Status',
                        accessor: 'parsedSupportStatus',
                        filterable: true,
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { parsedSupportStatus } }) => <ArraySupportStatus parsedSupportStatus={parsedSupportStatus} />,
                    },
                    {
                        Header: 'Agreement',
                        accessor: 'supportAgrmt',
                        filterable: true,
                        minWidth: COLUMN_WIDTHS.MEDIUM,
                    },
                    {
                        Header: 'End Date',
                        accessor: 'supportEndDate',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { supportEndDate } }) => formatDateTime(supportEndDate, FORMAT_MOMENT_CALENDAR_DAY, true),
                    },
                ],
                categories: [MONITOR_SUPPORT_TAB],
            },
            {
                Header: 'Timezone',
                accessor: 'arrayTimezone',
                minWidth: COLUMN_WIDTHS.LARGE,
                filterable: true,
                categories: [MONITOR_SUPPORT_TAB],
            },
        ],
    });
