import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { ReactHighcharts } from '@infosight/elmer/dist/components/ReactHighcharts';
import NoData from '@infosight/elmer/dist/components/NoData';
import ReactDimensions from 'react-dimensions';
import { isNumber } from 'underscore';
import { lightGrey04 } from '../../../../util/colors';
import chartOptions from './chartOptions';
import { SNAPSHOT_USAGE_COLOR, VOLUME_USAGE_COLOR } from '../../util';

export class FolderUsageChart extends PureComponent {
    data() {
        const { volumeUsage, snapshotUsage, unusedReserve, limit } = this.props;

        const data = [{
            name: 'Volume Usage',
            y: volumeUsage,
            color: VOLUME_USAGE_COLOR,
        }, {
            name: 'Snapshot Usage',
            y: snapshotUsage,
            color: SNAPSHOT_USAGE_COLOR,
        }];

        if (unusedReserve !== 0) {
            data.push({
                name: 'Unused Reserve',
                y: unusedReserve,
                color: 'url(#unusedReservePattern)',
            });
        }

        data.push({
            name: 'Unused',
            y: limit - (volumeUsage + snapshotUsage + unusedReserve),
            color: lightGrey04,
        });

        return data;
    }

    config() {
        const { volumeUsage, snapshotUsage, unusedReserve, containerHeight } = this.props;

        if (![volumeUsage, snapshotUsage, unusedReserve].every(isNumber)) {
            return null;
        }

        return chartOptions({ height: containerHeight, data: this.data() });
    }

    render() {
        const config = this.config();

        return (
            <NoData hasData={!!config}>
                {() => <ReactHighcharts config={config} />}
            </NoData>
        );
    }
}

FolderUsageChart.propTypes = {
    volumeUsage: PropTypes.number,
    snapshotUsage: PropTypes.number,
    unusedReserve: PropTypes.number,
    limit: PropTypes.number,
    containerHeight: PropTypes.number,
};

export default ReactDimensions({ elementResize: true })(FolderUsageChart);
