import React from 'react';
import numeral from 'numeral';
import PercentGrowth from '@infosight/elmer/dist/components/PercentGrowth';
import { COLUMN_WIDTHS } from '@infosight/elmer/dist/components/Table';
import { Progress, ProgressWithValue } from '../../../../components/Progress';
import NamedNimbleIcon from '../../../nimble/components/NamedNimbleIcon';
import SavingsBreakdownPopover from '../savings/SavingsBreakdownPopover';
import { SNAPSHOT_USAGE_COLOR, VOLUME_USAGE_COLOR } from '../../util';

/* eslint-disable react/prop-types */

const applicationsConfig = (maxTotalUsage, maxTotalPhysicalUsage) => ({
    categories: ['space'],
    columns: [
        {
            Header: 'Application',
            accessor: 'applicationCategory',
            minWidth: COLUMN_WIDTHS.LARGE,
            Cell: ({ original: { applicationCategory } }) => <NamedNimbleIcon iconName={applicationCategory} inline />,
        },
        {
            Header: 'Volumes',
            accessor: 'volumeCount',
            minWidth: COLUMN_WIDTHS.SMALL,
            Cell: ({ original: { volumeCount } }) => <span>{ volumeCount }</span>,
        },
        {
            Header: 'Volume Usage',
            accessor: 'volumeUsed',
            minWidth: COLUMN_WIDTHS.XLARGE,
            style: { marginTop: '5px' },
            Cell: ({ original: { volumeUsed } }) => (
                <ProgressWithValue leftLabel={numeral(volumeUsed).format('0.0 ib')}>
                    <Progress
                        color={VOLUME_USAGE_COLOR}
                        max={maxTotalUsage}
                        value={volumeUsed}
                    />
                </ProgressWithValue>
            ),
        },
        {
            Header: 'Snapshot Usage',
            accessor: 'snapshotUsed',
            minWidth: COLUMN_WIDTHS.XLARGE,
            style: { marginTop: '5px' },
            Cell: ({ original: { snapshotUsed } }) => (
                <ProgressWithValue leftLabel={numeral(snapshotUsed).format('0.0 ib')}>
                    <Progress
                        color={SNAPSHOT_USAGE_COLOR}
                        max={maxTotalUsage}
                        value={snapshotUsed}
                    />
                </ProgressWithValue>
            ),
        },
        {
            Header: 'Physical Usage (After Deduplication)',
            accessor: 'totalPhysicalUsed',
            minWidth: COLUMN_WIDTHS.XLARGE,
            style: { marginTop: '5px' },
            Cell: ({ original: { totalPhysicalUsed } }) => (
                <ProgressWithValue leftLabel={numeral(totalPhysicalUsed).format('0.00 ib')}>
                    <Progress
                        color={VOLUME_USAGE_COLOR}
                        max={maxTotalPhysicalUsage}
                        value={totalPhysicalUsed}
                    />
                </ProgressWithValue>
            ),
        },
        {
            Header: 'Past Growth (days)',
            headerClassName: 'text-center',
            columns: [
                {
                    Header: '90',
                    headerClassName: 'text-right',
                    accessor: 'growthPct90day',
                    filterable: false,
                    className: 'text-right',
                    minWidth: COLUMN_WIDTHS.XSMALL,
                    Cell: ({ original: { growthPct90day } }) => (growthPct90day === null ? '' : <PercentGrowth value={growthPct90day} isPercent />),
                },
                {
                    Header: '60',
                    headerClassName: 'text-right',
                    accessor: 'growthPct60day',
                    filterable: false,
                    className: 'text-right',
                    minWidth: COLUMN_WIDTHS.XSMALL,
                    Cell: ({ original: { growthPct60day } }) => (growthPct60day === null ? '' : <PercentGrowth value={growthPct60day} isPercent />),
                },
                {
                    Header: '30',
                    headerClassName: 'text-right',
                    accessor: 'growthPct30day',
                    filterable: false,
                    className: 'text-right',
                    minWidth: COLUMN_WIDTHS.XSMALL,
                    Cell: ({ original: { growthPct30day } }) => (growthPct30day === null ? '' : <PercentGrowth value={growthPct30day} isPercent />),
                },
            ],
        },
        {
            Header: 'Savings',
            accessor: 'totalPhysicalUsed',
            minWidth: COLUMN_WIDTHS.MEDIUM,
            Cell: ({ original }) => {
                const { savingsFactor, totalSavings } = original;
                let child = null;
                if (totalSavings !== undefined) {
                    const [value, unit] = numeral(totalSavings).format('0.0 ib').split(' ');
                    child = `${value} ${unit || 'B'}`;
                    if (savingsFactor !== undefined) {
                        child = `${child} | ${numeral(savingsFactor).format('0.00')}x`;
                    }
                }
                if (totalSavings <= 0) {
                    return <span>{child}</span>;
                }
                return <SavingsBreakdownPopover {...original}>{child}</SavingsBreakdownPopover>;
            },
        },
    ],
});

export { applicationsConfig as default };
