import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { compact } from 'underscore';
import Legend from '@infosight/elmer/dist/components/Legend';
import ChartProvider from './ChartProvider';
import style from './styles.scss';

const cx = classnames.bind(style);

// TODO unlikely, but I wonder how well this whole thing will work with synchronized charts

const buildLegend = (pointTracking, valueFormatter, getObjectLinkProps, defaultLegendValues) => {
    const values = pointTracking.values || defaultLegendValues;
    return compact(pointTracking.series).map((series) => {
        const value = valueFormatter(values[series.id] && values[series.id].value);
        return {
            id: series.id,
            label: series.name,
            icon: true,
            color: series.color === null ? 'transparent' : series.color,
            value,
            linkProps: getObjectLinkProps(series.highchartsSeries),
        };
    });
};

const PointTrackingChart = ({
    className,
    pointTracking,
    buildConfig,
    valueFormatter,
    getObjectLinkProps,
    timestampFormat,
    chartContainerClassName,
    defaultLegendItems,
}) => (

    <div className={classnames(className, 'row expanded collapse')}>
        <div className={classnames('column large-19 medium-16', cx('chart-column'))}>
            <ChartProvider
                className={chartContainerClassName}
                onAfterChartRendered={pointTracking.onAfterChartRendered}
                onMouseOverPoint={pointTracking.onMouseOverPoint}
                onMouseOut={pointTracking.onMouseOut}
                buildConfig={buildConfig}
            />
        </div>
        <Legend
            className="column large-5 medium-8"
            timestampFormat={timestampFormat}
            items={buildLegend(pointTracking, valueFormatter, getObjectLinkProps, defaultLegendItems)}
            timestamp={pointTracking.timestamp || null}
            reserveTimestampSpace
            valueTitle="Usage"
        />
    </div>
);

PointTrackingChart.propTypes = {
    chartContainerClassName: PropTypes.string,

    /**
     * @param Object containing these values:
     * onAfterChartRendered,
     * onMouseOverPoint,
     * onMouseOut,
     * containerWidth,
     * containerHeight
     *
     * @return {Object} Highcharts config object
     */
    buildConfig: PropTypes.func.isRequired,

    /**
     * Get the props for a elmer.QueryLink component
     * @param highchartsSeries
     * @return {Object}
     */
    getObjectLinkProps: PropTypes.func.isRequired,

    defaultLegendItems: PropTypes.object,

    /**
     * @param {*} value
     * @return {*}
     */
    valueFormatter: PropTypes.func,

    /**
     * Moment format
     */
    timestampFormat: PropTypes.string.isRequired,

    // Passthrough from providePointTracking in parent component
    pointTracking: PropTypes.shape({
        onMouseOverPoint: PropTypes.func.isRequired,
        onMouseOut: PropTypes.func.isRequired,
        onAfterChartRendered: PropTypes.func,
        chart: PropTypes.shape({
            series: PropTypes.arrayOf(PropTypes.shape({
                color: PropTypes.string,
                options: PropTypes.shape({
                    id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
                }),
                name: PropTypes.string.isRequired,
            })),
        }),

        /**
         * WHen the point is active (hovered), this should be the timestamp; otherwise null
         */
        timestamp: PropTypes.number,

        /**
         * Keys in this object should be the `chart.series.id`
         */
        values: PropTypes.objectOf(PropTypes.shape({
            id: PropTypes.string.isRequired,
            value: PropTypes.node.isRequired,
            data: PropTypes.object,
            color: PropTypes.string,
        })),
    }).isRequired,
};

PointTrackingChart.defaultProps = {
    timestampFormat: 'L',
};

export default PointTrackingChart;
