import numeral from 'numeral';

/**
 * Builds a String representing Network Card Information based on the Port Count
 * @param tenGigSfpPortCount
 * @param tenGigBaseTPortCount
 * @param gigPortCount
 * @param fcPortCount
 * @returns {string}
 */
const getNetworkCardInfo = ({ tenGigSfpPortCount, tenGigBaseTPortCount, gigPortCount, fcPortCount }) => {
    const networkString = [];
    if (tenGigSfpPortCount > 0) {
        networkString.push(numeral(tenGigSfpPortCount).divide(2).format('0,0').concat(' Dual 10Gbe SFP+'));
    }
    if (tenGigBaseTPortCount > 0) {
        networkString.push(numeral(tenGigBaseTPortCount).divide(2).format('0,0').concat(' Dual 10GbaseT'));
    }
    if (gigPortCount > 0) {
        networkString.push(numeral(gigPortCount).divide(2).format('0,0').concat(' Dual 1GbE'));
    }
    if (fcPortCount > 0) {
        networkString.push(numeral(fcPortCount).divide(2).format('0,0').concat(' Dual 16Gb FC'));
    }
    const networkData = networkString.join('; ');
    return networkData;
};

export default getNetworkCardInfo;
