import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { Caret } from '../TreeView';
import style from './style.scss';

const cx = classnames.bind(style);

const ArrayGroupNodeHeader = ({ node }) => <span className={cx('name')}>{node.name}</span>;

ArrayGroupNodeHeader.propTypes = {
    node: PropTypes.shape({
        name: PropTypes.string,
    }),
};

const ArrayGroupContainer = ({ node }) => (
    <div className={cx('node', 'disabled', 'text-muted')}>
        <Caret toggled className={cx('caret', 'invisible')} />
        <i className={cx('fa fa-server', 'icon')} />
        <ArrayGroupNodeHeader node={node} />
    </div>
);

ArrayGroupContainer.propTypes = {
    node: PropTypes.object,
};

export default ArrayGroupContainer;
