import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { noop } from 'underscore';
import style from './style.scss';

const cx = classnames.bind(style);

const ComplexToggle = ({ active, onClick, children, disabled }) => {
    const isActive = active && !disabled;
    const onclick = disabled ? noop : onClick;

    return (
        <button className={cx('btn-block', 'toggle', { active })} onClick={onclick} disabled={disabled}>
            <div className={cx('pull-left', 'align-middle')}>{children}</div>
            <div className={cx('pull-right', 'align-middle')}>
                <i className={cx('fa', 'caret', { 'fa-caret-up': isActive, 'fa-caret-down': !isActive })} />
            </div>
        </button>
    );
};

ComplexToggle.propTypes = {
    active: PropTypes.bool.isRequired,
    children: PropTypes.element,
    onClick: PropTypes.func.isRequired,
    disabled: PropTypes.bool.isRequired,
};

ComplexToggle.defaultProps = {
    disabled: false,
};

export default ComplexToggle;
