import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { withRouter } from 'react-router';
import { buildTargetLocation } from '@infosight/elmer/dist/components/Router';
import { QueryTimeRangeSelector, TimeRangeSelection, toTimeRangeSelection } from '@infosight/elmer/dist/components/TimeRangeSelector';
import Backbone from 'backbone';
import { performanceTimeRangeParam } from '../../queryParams';
import PerformanceView from '../../../../../../../app/modules/asset_details/performance/PerformanceView';
import RangeModel from '../../../../../../../app/modules/asset_details/performance/RangeModel';
import { awaitLegacyApp } from '../../../../../../containers/NimbleLegacyContainer';

function translateGenericQueryTime(time) {
    return time.lookback
        ? time
        : {
            min: time.range.start,
            max: time.range.end,
        };
}

class ArrayPerformanceContainer extends Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = { displayTimeRange: null };
        this.rangeModel = new RangeModel();
        this.updateRangeModel(this.props.timeRange);
        this.rangeModel.on('change:actualDataRange', this.setDisplayTimeRange);
    }

    componentWillReceiveProps({ timeRange }) {
        if (timeRange.equals(this.props.timeRange)) {
            return;
        }
        this.updateRangeModel(timeRange);
    }

    onDragZoom({ min, max }) {
        const { location, history } = this.props;
        const rangeSelection = toTimeRangeSelection({
            from: min,
            to: max,
        });
        history.replace(buildTargetLocation({
            location,
            to: { search: rangeSelection.serialize(performanceTimeRangeParam()) },
        }));
    }

    setDisplayTimeRange(model) {
        this.setState({ displayTimeRange: QueryTimeRangeSelector.toTimeRangeSelection(model.get('actualDataRange')) });
    }

    updateRangeModel(timeRange) {
        this.rangeModel
            .clear({ silent: true })
            .set(translateGenericQueryTime(timeRange.toGenericQueryTime().time));
    }

    handleRef(el) {
        if (el) {
            this.renderLegacyView(el);
        } else {
            this.view.destroy();
        }
    }

    renderLegacyView(el) {
        const arrayModel = new Backbone.Model({
            serialNumber: this.props.array,
        });

        this.view = new PerformanceView({
            el,
            poolUid: this.props.poolUid,
            arrayModel,
            rangeModel: this.rangeModel,
        });
        this.view.on('dragZoom', this.onDragZoom, this);
        this.view.render();
    }

    render() {
        const { timeRange } = this.props;
        return (
            <div>
                <div className="row expanded align-right">
                    <QueryTimeRangeSelector
                        parameterPrefix={performanceTimeRangeParam()}
                        value={timeRange}
                        presets={QueryTimeRangeSelector.PRESETS.VOLATILE}
                        displayValue={this.state.displayTimeRange}
                    />
                </div>
                <div ref={this.handleRef} />
            </div>
        );
    }
}

ArrayPerformanceContainer.propTypes = {
    poolUid: PropTypes.string.isRequired,
    array: PropTypes.string,
    timeRange: PropTypes.instanceOf(TimeRangeSelection).isRequired,

    // Injected by withRouter
    history: PropTypes.object,
    location: PropTypes.object,
};

export default awaitLegacyApp(withRouter(ArrayPerformanceContainer));
