import React from 'react';
import PropTypes from 'prop-types';
import NimbleIcon from '../../../nimble/components/NimbleIcon/NimbleIcon';
import { FOLDER, POOL, ALL } from '../../constants';

const Selection = ({ value, className }) => {
    if (!value) {
        return <div className={className} />;
    }

    switch (value.type) {
    case FOLDER:
        return (
            <div className={className}>
                <NimbleIcon name="Folder" /> <span>{value.folderName}</span>
            </div>
        );
    case POOL:
        return (
            <div className={className}>
                <NimbleIcon name="Pool" />
                <span> {value.groupName} <span className="subtitle">{value.poolName}</span></span>
            </div>
        );
    case ALL:
    default:
        return (<div className={className}>All Pools</div>);
    }
};

Selection.propTypes = {
    value: PropTypes.shape({
        arraySn: PropTypes.string,
        groupUID: PropTypes.string,
        groupName: PropTypes.string,
        poolUID: PropTypes.string,
        poolName: PropTypes.string,
        folderName: PropTypes.string,
    }),
};

export default Selection;
