var Marionette = require('backbone.marionette');
var numeral = require('numeral');

var ImpactScore = require('./ImpactScoreImpl').default;

module.exports = Marionette.Object.extend({

    initialize: function (options) {
        this.percentile = options.percentile / 100;
        this.score = ImpactScore.percentileToScore(this.percentile);
        this.color = ImpactScore.COLOR_SCALE(this.score).hex();
        this.classification = ImpactScore.scoreToClassification(this.score);
    },

    render: function () {
        var result = this.classification.name;
        if (!this.classification.hideScore) {
            result += ' (' + numeral(this.score).format('0') + '/10)';
        }
        return result;
    },

});
