import moment from 'moment-timezone';
import { convertPhpFormatToMomentFormat } from '@infosight/elmer/dist/utils/time';

/**
 * Generate the config used for a DateTime Axis label formatter
 *
 * Use: Spread the result into the xAxis.label object
 */
export function dateTimeAxisLabelFormatterSnippet() {
    return {
        formatter() {
            return moment(this.value).format(convertPhpFormatToMomentFormat(this.dateTimeLabelFormat));
        },
    };
}

/**
 * Generate the config used for a DateTime Axis that includes the label formatter
 *
 * Use: Spread the result into the xAxis object
 */
export function dateTimeAxisSnippet() {
    return {
        type: 'datetime',
        lineWidth: 2,
        labels: {
            ...dateTimeAxisLabelFormatterSnippet(),
        },
    };
}

export function dateTimeXAxisMixin({ min, max }) {
    return () => ({
        xAxis: {
            min,
            max,
        },
    });
}
