import React, { Component } from 'react';
import PropTypes from 'prop-types';
import numeral from 'numeral';
import { isNumber } from 'underscore';
import KeyStatistic from '@infosight/elmer/dist/components/KeyStatistic';
import PopoverTrigger from '@infosight/elmer/dist/components/PopoverTrigger';
import SavingsDetail from './SavingsDetail';

class Savings extends Component {
    renderOverlay() {
        const {
            cloneSavings,
            dedupSavings,
            compressionSavings,
            cloneSavingsFactor,
            dedupSavingsFactor,
            compressionSavingsFactor,
        } = this.props;

        return (
            <table className="text-left table-border">
                <tbody>
                    {isNumber(cloneSavings) && <SavingsDetail name="Clones" savings={cloneSavings} factor={cloneSavingsFactor} />}
                    {isNumber(dedupSavings) && <SavingsDetail name="Deduplication" savings={dedupSavings} factor={dedupSavingsFactor} />}
                    {isNumber(compressionSavings) && <SavingsDetail name="Compression" savings={compressionSavings} factor={compressionSavingsFactor} />}
                </tbody>
            </table>
        );
    }

    render() {
        const isHeadline = this.props.context === 'headline';
        const WrapperComponent = isHeadline ? KeyStatistic : 'div';
        const Small = ({ children }) => isHeadline ? <small>{ children }</small> : <span>{ children }</span>; // eslint-disable-line

        const { totalSavings, totalSavingsFactor } = this.props;
        if (!isNumber(totalSavings)) {
            return <div>N/A</div>;
        }

        const [value, unit] = numeral(totalSavings).format('0.0 ib').split(' ');

        const content = (
            <WrapperComponent>
                {value} <Small>{ unit || 'B'}</Small> | {numeral(totalSavingsFactor).format('0.0')}<Small>X</Small>
            </WrapperComponent>
        );

        if (!totalSavings) {
            return content;
        }

        return <PopoverTrigger placement="bottom" popover={this.renderOverlay()} popperProps={{ style: { zIndex: 2 } }}>{content}</PopoverTrigger>;
    }
}

Savings.propTypes = {
    context: PropTypes.oneOf(['headline', 'table']),
    totalSavings: PropTypes.number,
    totalSavingsFactor: PropTypes.number,
    cloneSavings: PropTypes.number,
    cloneSavingsFactor: PropTypes.number,
    dedupSavings: PropTypes.number,
    dedupSavingsFactor: PropTypes.number,
    compressionSavings: PropTypes.number,
    compressionSavingsFactor: PropTypes.number,
};

Savings.defaultProps = {
    context: 'headline',
};

export default Savings;
