var Backbone = require('backbone');

module.exports = Backbone.Model.extend(
    /** @lends models/ArrayEventModel.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name models/ArrayEventModel
         * @constructor
         * @augments Backbone.Model
         */


        defaults: {
            serialNumber: null,
            date: null,
            category: null,
            details: null,
            change: null,
            previous: null,
            current: null,
            notes: null,
        },

        /**
         * Helper method for getting the Highcharts configuration for
         * an entry on a flag series.
         * @param excludeColors optional parameter to specify whether the color has to be
         excluded or not. If not passed considered as include colors
         * @return {Object} Returns a flag config object.
         */
        getFlagConfig: function (excludeColors) {
            var date = this.get('date'),
                category = this.get('category'),
                change = this.get('change'),
                details = this.get('details'),
                notes = this.get('notes'),
                flagConfig = null;

            notes = (notes) ? '<br />' + notes : '';

            switch (category) {

            case 'SOFTWARE':
                flagConfig = {
                    x: date,
                    title: 'Software',
                    text: 'Software updated to: ' + change + notes,
                    color: excludeColors ? null : '#ED254E',
                };
                break;

            case 'HARDWARE':
                flagConfig = {
                    x: date,
                    title: 'Hardware',
                    text: details + ' from ' +
                        this.get('previous') + ' to ' +
                        this.get('current') +
                        notes,
                    color: excludeColors ? null : '#090446',
                };
                break;

            case 'CAPACITY':
                flagConfig = {
                    x: date,
                    title: 'Capacity',
                    text: details + '. ' + change + notes,
                    color: excludeColors ? null : '#F06449',
                };
                break;

                // New events should be added to this switch block
            default:
                console.error('Unexpected array event category: ' + category + '. Chart may not display correctly.', this.attributes);
                break;
            }

            return flagConfig;
        },
    });
