import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { getStore } from '../../../util/storeProvider';
import NimbleUserTenantHelpers from './NimbleUserTenantHelpers';
import { tenantSelector } from '../../user';

export { NimbleUserTenantHelpers };

export const buildNimbleTenantUrl = (tenantId, ...rest) => buildUrl('org', encodeURIComponent(`urn:nimble:${tenantId}`), ...rest);
export const buildUrlForCurrentTenant = (...rest) => {
    const nimbleTenant = tenantSelector(getStore().getState());
    return nimbleTenant && nimbleTenant.tenant ? buildNimbleTenantUrl(nimbleTenant.tenant.id, ...rest) : undefined;
};

/**
 * Nimble Resellers all have their own tenant, but they tend to have no Nimble Arrays.
 * Thus, they have no use for a bunch of empty infrastructure analytics pages when their tenant is selected.
 * So don't display Nimble/VMWare options.
 *
 * For other user types, we can simply use the authority
 *
 * @return {boolean|undefined}
 *  Is this a reseller AND the current tenant is the reseller's account AND has >=1 arrays? -> TRUE
 *  Is this a reseller AND the current tenant is the reseller's account AND has 0 arrays? -> FALSE
 *  else -> undefined
 */
export const nimbleResellerAuthorizationEvaluator = () => {
    if (NimbleUserTenantHelpers.isReseller() && NimbleUserTenantHelpers.ownsSelectedTenant()) {
        return NimbleUserTenantHelpers.hasArrays() && NimbleUserTenantHelpers.currentTenantIsNimbleTenant();
    }

    return undefined;
};
