import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import { GROUP_POOL_FOLDER_RELATIONS_FETCH, GROUP_POOL_FOLDER_RELATIONS_RESET } from '../constants';
import Request from '../../../services/Request';

export const fetchRelationships = () => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(GROUP_POOL_FOLDER_RELATIONS_FETCH, dispatch);

    dispatchStart();

    new Request().pod().account()
        .urlSegment('capacity/pools/overview')
        .get()
        .then(response => response.data)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const resetRelationships = () => ({
    type: GROUP_POOL_FOLDER_RELATIONS_RESET,
});
