import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import ReactDOM from 'react-dom';
import style from './flagStyle.scss';
import { formatDateTime } from '../../../../../../util/formatter';

const rootElement = document.getElementById('main');

const getFlagConfig = eventCollection => eventCollection.reduce((memo, event) => {
    const { arraySn, category, change, eventDetails, previousValue, currentValue } = event;
    switch (category) {
    case 'SOFTWARE':
        return memo.concat(`${arraySn} - Software updated to: ${change && change.split('-')[0]}`);

    case 'HARDWARE':
        return memo.concat(`${arraySn} - ${eventDetails} from ${previousValue} to ${currentValue}`);

    case 'CAPACITY':
        return memo.concat(`${arraySn} - ${eventDetails} ${change}`);

    default:
        console.error(`Unexpected array event category: ${category}. Chart may not display correctly.`);
        return memo;
    }
}, []);

const cx = classnames.bind(style);

export class EventFlag extends Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.el = document.createElement('div');
    }

    componentDidMount() {
        rootElement.appendChild(this.el);
    }

    componentWillUnmount() {
        rootElement.removeChild(this.el);
    }

    render() {
        const { eventCollection, point, chart, chartDomId } = this.props;

        if (!eventCollection || (eventCollection && eventCollection.length <= 0)) {
            return null;
        }

        const flagsData = getFlagConfig(eventCollection);
        const element = document.getElementById(chartDomId);
        const { top, left } = element.getBoundingClientRect();

        const eventFlag = (
            <div
                style={{
                    background: point.color,
                    /*
                        No. of events on day can vary, so try to set the bottom position where the flag should end
                        as that would make the event flag to scale towards the top keeping the bottom end fixed.

                        In order to achieve that, calculate the distance between the end of viewport and bottom of the chart.
                        Distance between the bottom of view port and the event chart = viewport height - top/bottom of chart
                    */
                    bottom: (document.documentElement.clientHeight - top) - chart.plotTop,

                    /**
                     * Adding window.pageXOffset to consider horizontal scrolling
                     */
                    left: -2 + point.plotX + chart.plotLeft + left + window.pageXOffset,
                }}
                className={cx('flagContainer')}
            >
                <ol className={cx('eventList')}>
                    <li className={cx('eventDate')}>{formatDateTime(point.x, 'L')}</li>
                    {flagsData.map(event => (
                        <li key={event}>
                            {event}
                        </li>
                    ))}
                </ol>
            </div>
        );

        // TODO: In some corner cases, the flag position might not be ideal. Use react-popper (IS-16219)
        return ReactDOM.createPortal(eventFlag, this.el);
    }
}

EventFlag.propTypes = {
    chartDomId: PropTypes.string,
    point: PropTypes.object,
    chart: PropTypes.object,
    eventCollection: PropTypes.arrayOf(PropTypes.shape({
        timestamp: PropTypes.oneOfType([PropTypes.object, PropTypes.number]),
        arraySn: PropTypes.string,
        category: PropTypes.string,
        verbosity: PropTypes.string,
        eventDetails: PropTypes.string,
        change: PropTypes.string,
        previousValue: PropTypes.string,
        currentValue: PropTypes.string,
    })),
};

export default EventFlag;
