import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Async from '@infosight/elmer/dist/components/Async';
import NoData from '@infosight/elmer/dist/components/NoData';
import Card from '@infosight/elmer/dist/components/Card';
import {
    QueryTimeRangeSelector,
    TimeRangeSelection,
    deserialize,
    toTimeRangeSelection,
    makeChartSelector,
    makeRangeSelector,
} from '@infosight/elmer/dist/components/TimeRangeSelector';
import { volumesCapacitySelector, scopingObjectSizeSeriesSelector } from '../../reducer';
import { getTopVolumesCapacity, getScopingObjectSizeSeries, reset } from '../../actionCreators';
import TopVolumesChart from './TopVolumesChart';

const buildParam = (type, param) => `capacity.${type}${param}`;
export const timeParam = param => buildParam('time.', param);

class CapacityContainer extends Component {
    componentDidMount() {
        this.fetchData();
    }

    componentDidUpdate({ timeRange }) {
        if (!this.props.timeRange.equals(timeRange)) {
            this.fetchData();
        }
    }

    componentWillUnmount() {
        this.props.resetVolumeCollectionStore();
    }

    fetchData() {
        const { config, scope, timeRange } = this.props;

        this.props.getVolumesCapacity({
            rankBy: 'dataUsageBytes',
            scope: config.convertToGenericQueryScope(scope),
            timeRange: timeRange.toGenericQueryTime(),
        });

        this.props.getScopingObjectSizeSeries({
            scope: config.convertToGenericQueryScope(scope),
            timeRange: timeRange.toGenericQueryTime(),
        });
    }

    render() {
        const {
            volumesCapacity,
            timeRange,
            loadingVolumesCapacity,
            config,
            scope,
            loadingScopingObjectSizeSeries,
            loadedScopingObjectSizeSeries,
            scopingObjectSizeSeries,
            history,
            location,
        } = this.props;
        const hasData = !!(volumesCapacity && volumesCapacity.data && volumesCapacity.data.length > 0) && loadedScopingObjectSizeSeries;
        let displayTimeRange;
        let chartStartTime;
        let chartEndTime;

        if (hasData) {
            displayTimeRange = TimeRangeSelection.from(volumesCapacity.meta.time);
            chartStartTime = volumesCapacity.meta.time.start;
            chartEndTime = volumesCapacity.meta.time.end;
        }

        const parameterPrefix = timeParam('');
        const onSelectRange = makeRangeSelector(location, parameterPrefix, false, history);
        const onChartSelect = makeChartSelector(onSelectRange);

        return (
            <div>
                <QueryTimeRangeSelector
                    value={timeRange}
                    presets={QueryTimeRangeSelector.PRESETS.NONVOLATILE}
                    parameterPrefix={parameterPrefix}
                    displayValue={displayTimeRange}
                    card
                />
                <Card title="Top Volumes by Data Usage">
                    <Async loading={loadingVolumesCapacity || loadingScopingObjectSizeSeries}>
                        <NoData hasData={hasData} message="No capacity history data available.">
                            {() => (<TopVolumesChart
                                className="chart-container--medium"
                                baseTimeSeries={volumesCapacity.data}
                                scopingObjectSizeSeries={scopingObjectSizeSeries.data}
                                chartRangeStart={chartStartTime}
                                chartRangeEnd={chartEndTime}
                                config={config}
                                scope={scope}
                                onChartSelect={onChartSelect}
                            />)
                            }
                        </NoData>
                    </Async>
                </Card>
            </div>
        );
    }
}

CapacityContainer.propTypes = {
    // Injected by mapDispatchToProps
    getScopingObjectSizeSeries: PropTypes.func,
    getVolumesCapacity: PropTypes.func,
    resetVolumeCollectionStore: PropTypes.func.isRequired,

    // Injected by mapStateToProps
    loadingVolumesCapacity: PropTypes.bool,
    volumesCapacity: PropTypes.shape({
        data: PropTypes.arrayOf(PropTypes.object),
        meta: PropTypes.shape({
            time: PropTypes.shape({
                start: PropTypes.number.isRequired,
                end: PropTypes.number.isRequired,
            }),
        }),
    }),
    loadingScopingObjectSizeSeries: PropTypes.bool,
    loadedScopingObjectSizeSeries: PropTypes.bool,
    scopingObjectSizeSeries: PropTypes.shape({
        data: PropTypes.shape({
            id: PropTypes.string,
            name: PropTypes.string,
            type: PropTypes.string,
            // Only when the Scoped Object is folder
            hasFolderLimit: PropTypes.bool,
            timeseries: PropTypes.arrayOf(PropTypes.shape({
                timestamp: PropTypes.number,
                sizeBytes: PropTypes.number,
            })),
        }),
        meta: PropTypes.shape({
            time: PropTypes.shape({
                start: PropTypes.number.isRequired,
                end: PropTypes.number.isRequired,
            }),
        }),
    }),
    timeRange: PropTypes.oneOfType([
        PropTypes.instanceOf(TimeRangeSelection),
        PropTypes.string,
    ]).isRequired,

    // Injected by XX_INFRASTRUCTURE_XX
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),
    config: PropTypes.object.isRequired,
    history: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    match: PropTypes.shape({ // eslint-disable-line react/no-unused-prop-types
        params: PropTypes.shape({
            // Time Range
            [timeParam('duration')]: PropTypes.string,
            [timeParam('from')]: PropTypes.string,
            [timeParam('to')]: PropTypes.string,
        }).isRequired,
    }).isRequired,
};

const mapStateToProps = (state, { match }) => ({
    ...volumesCapacitySelector(state),
    ...scopingObjectSizeSeriesSelector(state),

    // TODO this default should be set deeper in the tree, closer to the TimeRangeSelector instance
    timeRange: deserialize(match.params, timeParam('')) || toTimeRangeSelection({ duration: 'P3M' }),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    getVolumesCapacity: getTopVolumesCapacity,
    getScopingObjectSizeSeries,
    resetVolumeCollectionStore: reset,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(CapacityContainer);
