import moment from 'moment-timezone';
import { find } from 'underscore';

/**
 * Based on com.nimblestorage.infosight.dao.query.TimeAggregation
 */
const GRANULARITIES = [
    moment.duration(1, 'minute'),
    moment.duration(2, 'minutes'),
    moment.duration(5, 'minutes'),
    moment.duration(10, 'minutes'),
    moment.duration(15, 'minutes'),
    moment.duration(30, 'minutes'),
    moment.duration(1, 'hour'),
    moment.duration(2, 'hours'),
    moment.duration(4, 'hours'),
    moment.duration(8, 'hours'),
    moment.duration(12, 'hours'),
    moment.duration(1, 'day'),
];

const byIso8601Duration = GRANULARITIES.reduce((memo, g) => ({ ...memo, [g.toJSON()]: g }), {});

export function findGranularity(iso8601Duration) {
    return byIso8601Duration[iso8601Duration];
}

/**
 * Determine the granularity that would produce no more than a specific number of samples
 * @param startTime
 * @param endTime
 * @param {Number} maxSamples
 * @return {moment.duration}
 */
export function determineGranularityFromSampleCount(startTime, endTime, maxSamples) {
    if (!startTime || (startTime.isValid && !startTime.isValid())) {
        return undefined;
    }

    if (!endTime || (endTime.isValid && !endTime.isValid())) {
        return undefined;
    }

    const chartRange = moment(endTime).diff(moment(startTime));
    return find(GRANULARITIES, pointSize => chartRange / pointSize.asMilliseconds() <= maxSamples);
}

/**
 *
 * @param startTime
 * @param endTime
 * @param {Number} pointWidthPixels
 * @param {Number} plotAreaWidthPixels
 * @return {moment.duration}
 */
export function determineGranularityFromDimensions(startTime,
    endTime,
    pointWidthPixels = 1,
    plotAreaWidthPixels = 900) {
    const maxSamples = Math.floor(plotAreaWidthPixels / pointWidthPixels);
    return determineGranularityFromSampleCount(startTime, endTime, maxSamples);
}
