/**
 * This file exists because Highcharts insists on adding itself to the window object and will throw
 * [error #16](https://www.highcharts.com/errors/16) if another instance exists. In a microapp env, this obviously
 * is problematic, because we shouldn't have to force everyone to use an old version of highcharts.
 *
 * What this does is simply remove the window ref, initialize Highcharts, then replace the window reference.
 * Optimally, there would be nothing at window.Highcharts, but let's do no evil.
 */
/* eslint-disable global-require */
/* eslint-disable import/prefer-default-export */
const { initializeHighcharts } = require('@infosight/elmer/dist/charts/highcharts');
const Highstock = require('highcharts/highstock.src');

// Safely apply any plugins/addons
require('highcharts/modules/heatmap')(Highstock);
require('highcharts/modules/treemap')(Highstock);
require('highcharts-more')(Highstock);

initializeHighcharts(Highstock);
require('highcharts-pattern-fill')(Highstock);

module.exports.Highstock = Highstock;
