import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { defer } from 'underscore';
import hoistNonReactStatics from 'hoist-non-react-statics';
import getDisplayName from 'recompose/getDisplayName';
import wrapDisplayName from 'recompose/wrapDisplayName';
import { Spinner } from '@infosight/elmer/dist/components/Spinner';
import App from 'app/Application';

class NimbleLegacyContainer extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    handleRef(el) {
        if (el) {
            this.renderLegacyView(el);
        } else if (this.view) {
            // Do this async to avoid issues with React trying to destroy the child element
            defer(() => {
                this.view.destroy();
            });
        }
    }

    renderLegacyView(el) {
        this.view = this.props.createLegacyView(el);
        this.view.render();
    }

    render() {
        return <div className={this.props.className} ref={this.handleRef} />;
    }
}

NimbleLegacyContainer.propTypes = {
    createLegacyView: PropTypes.func.isRequired,
    className: PropTypes.string,
};

/**
 * App is asynchronously intialized once it is determined that the user has Nimble assets. A legacy page could be rendered
 * before App is fully initialized, so just block rendering until it is ready.
 */
export function awaitLegacyApp(WrappedComponent) {
    class LegacyAppAwaiter extends Component { // eslint-disable-line react/no-multi-comp
        constructor(props) {
            super(props);
            autobind(this);
            this.state = { ready: !!App };
        }

        componentDidMount() {
            if (!this.state.ready) {
                this.waitForLegacyApp();
            }
        }

        componentWillUnmount() {
            if (this.waiterId) {
                window.clearTimeout(this.waiterId);
            }
        }

        waitForLegacyApp() {
            if (!App) {
                this.waiterId = window.setTimeout(this.waitForLegacyApp, 500);
            } else {
                this.setState({ ready: true });
            }
        }

        render() {
            return this.state.ready ? <WrappedComponent {...this.props} /> : <Spinner />;
        }
    }

    LegacyAppAwaiter.displayName = wrapDisplayName(
        WrappedComponent,
        getDisplayName(LegacyAppAwaiter)
    );

    return hoistNonReactStatics(LegacyAppAwaiter, WrappedComponent);
}

export default awaitLegacyApp(NimbleLegacyContainer);
