import { getStore } from '../../util/storeProvider';
import { authorizationEvaluatorSelector } from './reducer';

const VCENTER = 'VMWARE_VCENTER';

export function isNimbleBoundary(boundary) {
    return !boundary || boundary.indexOf('Nimble') >= 0;
}

export function getStoreServVCenterFromBoundary(boundary, inventory) {
    if (boundary && boundary.indexOf('StoreServ.Vcenter.') === 0) {
        const uid = boundary.slice('StoreServ.Vcenter.'.length);
        if (inventory && inventory.objects && inventory.objects.index && inventory.objects.index[VCENTER] &&
            inventory.objects.index[VCENTER][uid] && inventory.objects.index[VCENTER][uid].name) {
            return inventory.objects.index[VCENTER][uid].name;
        }
    }
    return null;
}

export function getBoundaryName(boundary, inventory) {
    if (isNimbleBoundary(boundary)) {
        return 'Nimble';
    }
    if (boundary && boundary.indexOf('StoreServ.Vcenter.') === 0) {
        const vcenter = getStoreServVCenterFromBoundary(boundary, inventory);
        return vcenter || '3PAR StoreServ vCenter';
    }
    return '3PAR StoreServ';
}

export function getBoundarySystemType(boundary) {
    if (isNimbleBoundary(boundary)) {
        return 'Nimble';
    }
    return '3PAR StoreServ';
}

export const AuthorizationEvaluator = {
    evaluate(opts) {
        const evaluator = authorizationEvaluatorSelector(getStore().getState());
        if (!evaluator) {
            return false;
        }

        return evaluator.evaluate(opts);
    },

    filter(opts) {
        const evaluator = authorizationEvaluatorSelector(getStore().getState());
        if (!evaluator) {
            return opts;
        }

        return evaluator.filter(opts);
    },
};
