export default class ValueWithUnits {
    constructor(value, units, separator = ' ') {
        this.value = value;
        this.separator = separator;
        this.units = units;
    }

    toString() {
        return `${this.value}${this.separator}${this.units}`;
    }

    toValue() {
        return `${this.value}`;
    }
}
