import React from 'react';
import PropTypes from 'prop-types';
import Headline from '@infosight/elmer/dist/components/Headline';
import KeyStatistic from '@infosight/elmer/dist/components/KeyStatistic';
import StatusIndicator from '@infosight/elmer/dist/components/StatusIndicator';
import NamedNimbleIcon from '../../../../components/NamedNimbleIcon';
import { getValueAndUnit, getByteValueAndUnit } from '../../../../../../util/numeralHelpers';
import { DataProtection, DaysUntilFull } from '../../../common';
import PastGrowthList from './PastGrowthList';
import {
    NUMBER_FORMAT_PRECISION,
    NUMBER_ABBREVIATE_FORMAT,
    INFO_NOT_AVAILABLE,
    formatValue,
    isEmpty,
} from '../../../../../constants';

const numeralPerSec = (value, format) => {
    const valueAndUnit = getValueAndUnit(value, format);
    const number = valueAndUnit[0];
    const unit = valueAndUnit[1] || 'B';

    if (!number) {
        return INFO_NOT_AVAILABLE;
    }
    return (<span>{number} <small>{unit}/sec</small></span>);
};


function VolumeHeadline({
    appCategory, dataUsageBytes, snapshotBytes, allocatedBytes, protectionLevel, last30daysGrowth,
    last60daysGrowth, last90daysGrowth, daysUntilFull, avgIopsPerWeek, avgReadLatencyMs,
    avgWriteLatencyMs, avgThroughput, dedupeEnabled }) {
    const [volumeUsedValue, usedUnit] = getByteValueAndUnit(dataUsageBytes);
    const [snapshotUsedValue, snapshotUnit] = getByteValueAndUnit(snapshotBytes);
    const [volumeCapacityValue, capacityUnit] = getByteValueAndUnit(allocatedBytes);
    const daysUntilFullDataExists = daysUntilFull !== null;

    return (
        <Headline>
            <Headline.Section className="columns">
                <h6>Application</h6>
                <div>
                    <NamedNimbleIcon iconName={appCategory} className="flex-child-grow align-center" />
                </div>
            </Headline.Section>
            <Headline.Section className={daysUntilFullDataExists ? 'columns medium-11' : 'columns medium-9'}>
                <div className="row">
                    <div className={daysUntilFullDataExists ? 'columns medium-10' : 'columns medium-12'}>
                        <h6>Volume Usage</h6>
                        <KeyStatistic>
                            {volumeUsedValue} <small>{usedUnit}{volumeCapacityValue ? ' of ' : null}</small>{volumeCapacityValue} <small>{capacityUnit}</small>
                        </KeyStatistic>
                        <PastGrowthList last90daysGrowth={last90daysGrowth} last60daysGrowth={last60daysGrowth} last30daysGrowth={last30daysGrowth} />
                    </div>
                    <div className="columns">
                        <h6>Snapshot Usage</h6>
                        <KeyStatistic>
                            {snapshotUsedValue} <small>{snapshotUnit}</small>
                        </KeyStatistic>
                    </div>
                    {daysUntilFullDataExists &&
                        <div className="columns">
                            <h6>Days Until Full</h6>
                            <KeyStatistic>
                                <DaysUntilFull className="font-weight-inherit" days={daysUntilFull} />
                            </KeyStatistic>
                        </div>
                    }
                    <div className="columns">
                        <h6>Dedupe</h6>
                        <StatusIndicator status={dedupeEnabled ? 'success' : 'unavailable'} size="small" />
                        <span>{dedupeEnabled ? 'Enabled' : 'Disabled'}</span>
                    </div>
                </div>
            </Headline.Section>
            <Headline.Section className="columns medium-9">
                <h6 className="stacked-title">Performance<br /><small>(Past Week Avg)</small></h6>
                <div className="row">
                    <div className="columns">
                        <KeyStatistic className="nowrap">
                            {formatValue(avgIopsPerWeek, NUMBER_ABBREVIATE_FORMAT)} {!isEmpty(avgIopsPerWeek) ? <small>IO/sec</small> : null }
                        </KeyStatistic>
                        <h6 className="no-margin">IOPS</h6>
                    </div>
                    <div className="columns">
                        <KeyStatistic>
                            {numeralPerSec(avgThroughput, '0.0 ib')}
                        </KeyStatistic>
                        <h6 className="no-margin">Throughput</h6>
                    </div>
                    <div className="columns">
                        <KeyStatistic>
                            {formatValue(avgReadLatencyMs, NUMBER_FORMAT_PRECISION)} {!isEmpty(avgReadLatencyMs) ? <small>ms</small> : null }
                        </KeyStatistic>
                        <h6 className="no-margin">R Latency</h6>
                    </div>
                    <div className="columns end">
                        <KeyStatistic>
                            {formatValue(avgWriteLatencyMs, NUMBER_FORMAT_PRECISION)} {!isEmpty(avgWriteLatencyMs) ? <small>ms</small> : null }
                        </KeyStatistic>
                        <h6 className="no-margin">W Latency</h6>
                    </div>
                </div>
            </Headline.Section>
            <Headline.Section className="columns">
                <h6>Data Protection</h6>
                <div className="row">
                    <div className="columns">
                        <DataProtection protectionLevel={protectionLevel} />
                    </div>
                </div>
            </Headline.Section>
        </Headline>
    );
}

VolumeHeadline.propTypes = {
    appCategory: PropTypes.string,
    dataUsageBytes: PropTypes.number,
    snapshotBytes: PropTypes.number,
    allocatedBytes: PropTypes.number,
    protectionLevel: PropTypes.string,
    last30daysGrowth: PropTypes.number,
    last60daysGrowth: PropTypes.number,
    last90daysGrowth: PropTypes.number,
    daysUntilFull: PropTypes.number,
    avgIopsPerWeek: PropTypes.number,
    avgReadLatencyMs: PropTypes.number,
    avgWriteLatencyMs: PropTypes.number,
    avgThroughput: PropTypes.number,
    dedupeEnabled: PropTypes.bool,
};

export default VolumeHeadline;
