import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { isNumber } from 'underscore';
import Bar from './Bar';
import './style.scss';

const Progress = ({ className, value, min, max, color, context }) => {
    if (!isNumber(min) || !isNumber(max) || !isNumber(value)) {
        return null;
    }

    return (
        <div
            className={classnames(className, 'progress', context)}
            aria-valuemax={max}
            aria-valuemin={min}
            aria-valuenow={value}
            role="progressbar"
        >
            <Bar value={value} color={color} max={max} min={min} />
        </div>
    );
};

// Either percent | value & number are required. percent is primary
Progress.propTypes = {
    value: PropTypes.number,
    min: PropTypes.number,
    max: PropTypes.number,
    color: PropTypes.string,
    context: PropTypes.oneOf(['chart', 'thin']),
};

Progress.defaultProps = {
    value: 0,
    min: 0,
    max: 100,
    context: 'thin',
};

export default Progress;
