import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { DEFAULT_ICON_HEIGHT, DEFAULT_ICON_WIDTH } from './constants';
import SharePointIcon from './icons/nmbl-sharepoint-app-category.png';
import VirtualServerIcon from './icons/nmbl-virtual-server-app-category.png';
import VirtualDesktopIcon from './icons/nmbl-virtual-desktop-app-category.png';
import FileServerIcon from './icons/nmbl-file-server-app-category.png';
import OtherIcon from './icons/nmbl-other-app-category.png';
import UnassignedIcon from './icons/nmbl-unassigned-app-category.png';
import RemoteReplica from './icons/nmbl-remote-replica.png';
import LocalReplication from './icons/nmbl-local-replication.png';
import ArrayFan from './ArrayFan';
import ArrayPowerSupply from './ArrayPowerSupply';
import ArraySSD from './ArraySSD';
import ArrayHDD from './ArrayHDD';
import ArrayController from './ArrayController';
import ArrayHeartbeatDown from '../../../../../images/nmbl-dashboard-heartbeat-down.png';
import ArrayHeartbeatUp from '../../../../../images/nmbl-dashboard-heartbeat-up.png';
import ArrayAsupDown from '../../../../../images/nmbl-dashboard-asup-down.png';
import ArrayAsupUp from '../../../../../images/nmbl-dashboard-asup-up.png';

const ImageIcon = ({ className, srcIcon, altText, height, width }) => (
    <img
        style={{ height: height || DEFAULT_ICON_HEIGHT, width: width || DEFAULT_ICON_WIDTH }}
        src={srcIcon}
        alt={altText}
        className={className}
    />
);

ImageIcon.propTypes = {
    srcIcon: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.object,
    ]),
    height: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    altText: PropTypes.string,
};

export const APP_CATEGORY_ICONS = {
    EXCHANGE: 'Exchange',
    SHAREPOINT: 'SharePoint',
    VIRTUAL_SERVER: 'Virtual Server',
    VIRTUAL_DESKTOP: 'Virtual Desktop',
    FILE_SERVER: 'File Server',
    OTHER: 'Other',
    MULTIPLE: 'Multiple',
    NONE: 'None',
    UNASSIGNED: 'Unassigned',
    ORACLE: 'Oracle',
    DB2: 'DB2',
    SQL_SERVER: 'SQL Server',
};

export const ICONS = Object.assign({}, APP_CATEGORY_ICONS, {
    FOLDER: 'Folder',
    SPACER: 'Spacer', // Adds Empty Space instead of icon
    REMOTE_PROTECTION: 'Remote',
    LOCAL_PROTECTION: 'Local Only',
    POOL: 'Pool',
    ARRAY_FAN: 'Array Fan',
    ARRAY_POWER_SUPPLY: 'Array Power Supply',
    ARRAY_SSD: 'Array SSD',
    ARRAY_HDD: 'Array HDD',
    ARRAY_CONTROLLER: 'Array Controller',
    ARRAY_HEARTBEAT_UP: 'Array Heartbeat Up',
    ARRAY_HEARTBEAT_DOWN: 'Array Heartbeat Down',
    ARRAY_ASUP_UP: 'Array Asup Up',
    ARRAY_ASUP_DOWN: 'Array Asup Down',
});

// Source:
// https://code.eng.nimblestorage.com/browse/~br=default/gui/nmbl-gui/app/utils/icon-utils.js?r=618ee23fa4ef1024e91a96c0383b71f49368ff03
const database = ({ className }) => <span className={classnames('fa fa-database', className)} />;
const icons = {
    [ICONS.FOLDER]: ({ className }) => <span className={classnames('fa fa-folder-o', className)} />,
    [ICONS.SPACER]: ({ className }) => <span className={classnames('fa fa-fw', className)} />, // Adds empty space..useful in aligning icons in lists
    [ICONS.EXCHANGE]: ({ className }) => <span className={classnames('fa fa-envelope-o', className)} />,
    [ICONS.SHAREPOINT]: ({ className }) => <ImageIcon srcIcon={SharePointIcon} alt="sharepoint" className={className} />,
    [ICONS.VIRTUAL_SERVER]: ({ className }) => <ImageIcon srcIcon={VirtualServerIcon} alt="virtual server" className={className} />,
    [ICONS.VIRTUAL_DESKTOP]: ({ className }) => <ImageIcon srcIcon={VirtualDesktopIcon} alt="virtual desktop" className={className} />,
    [ICONS.FILE_SERVER]: ({ className }) => <ImageIcon srcIcon={FileServerIcon} alt="file server" className={className} />,
    [ICONS.OTHER]: ({ className }) => <ImageIcon srcIcon={OtherIcon} alt="other" className={className} />,
    [ICONS.MULTIPLE]: ({ className }) => <span className={classnames('fa fa-clone', className)} />,
    [ICONS.NONE]: ({ className }) => <span className={className} />,
    [ICONS.POOL]: ({ className }) => <span className={classnames('fa fa-th', className)} />,
    [ICONS.UNASSIGNED]: ({ className }) => <ImageIcon srcIcon={UnassignedIcon} alt="unassigned" className={className} />,
    [ICONS.ORACLE]: database,
    [ICONS.DB2]: database,
    [ICONS.SQL_SERVER]: database,
    [ICONS.LOCAL_PROTECTION]: ({ className }) => <ImageIcon srcIcon={LocalReplication} alt="Local Only" className={className} />,
    [ICONS.REMOTE_PROTECTION]: ({ className }) => <ImageIcon srcIcon={RemoteReplica} alt="Remote" className={className} />,
    [ICONS.ARRAY_FAN]: ({ className, ...props }) => <ArrayFan className={className} {...props} />,
    [ICONS.ARRAY_POWER_SUPPLY]: ({ className, ...props }) => <ArrayPowerSupply className={className} {...props} />,
    [ICONS.ARRAY_SSD]: ({ className, ...props }) => <ArraySSD className={className} {...props} />,
    [ICONS.ARRAY_HDD]: ({ className, ...props }) => <ArrayHDD className={className} {...props} />,
    [ICONS.ARRAY_CONTROLLER]: ({ className, ...props }) => <ArrayController className={className} {...props} />,
    [ICONS.ARRAY_HEARTBEAT_UP]: ({ className, ...props }) => <ImageIcon srcIcon={ArrayHeartbeatUp} alt="Heartbeat Up" className={className} {...props} />,
    [ICONS.ARRAY_HEARTBEAT_DOWN]: ({ className, ...props }) => <ImageIcon srcIcon={ArrayHeartbeatDown} alt="Heartbeat Down" className={className} {...props} />,
    [ICONS.ARRAY_ASUP_UP]: ({ className, ...props }) => <ImageIcon srcIcon={ArrayAsupUp} alt="Asups Recieved" className={className} {...props} />,
    [ICONS.ARRAY_ASUP_DOWN]: ({ className, ...props }) =>
        <ImageIcon srcIcon={ArrayAsupDown} alt="Asups Not Recieved or Delayed" className={className} {...props} />,
};

const NimbleIcon = ({ className, name, ...props }) => {
    const Component = icons[name];
    return Component !== undefined ? <Component className={className} {...props} /> : null;
};

NimbleIcon.propTypes = {
    name: PropTypes.string.isRequired,
    height: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

NimbleIcon.defaultProps = {
    name: 'Unassigned',
};

NimbleIcon.ICONS = ICONS;
NimbleIcon.APP_CATEGORY_ICONS = APP_CATEGORY_ICONS;

export default NimbleIcon;
