import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Route, Redirect, Switch } from 'react-router-dom';
import classnames from 'classnames/bind';
import Async from '@infosight/elmer/dist/components/Async';
import Card from '@infosight/elmer/dist/components/Card';
import NoData from '@infosight/elmer/dist/components/NoData';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import {
    getArrayDetail,
    getArrayHardwareUpgradeRecommendations,
    reset,
} from '../actionCreators';
import {
    arraySelector,
    arrayHardwareUpgradeRecommendationSelector,
} from '../reducer';
import ArrayHeadline from './ArrayHeadline';
import ArrayShelves from './ArrayShelves';
import ArrayHadrwareRecommendations from './HardwareRecommendations';
import SoftwareRecommendationsMarionetteWrapper from './SoftwareRecommendationsMarionetteWrapper';

class ArrayDetailContainer extends Component {
    constructor(...args) {
        super(...args);
        this.state = {};

        const { config, scope, location } = this.props;
        if (location.pathname.endsWith('/performance')) {
            const relations = config.getObjectTopology(scope.type, scope.id);
            const poolUidFromInventory = relations && relations.parent && relations.parent.id;
            this.state = { poolUidFromInventory, preventLoad: !!poolUidFromInventory };
        }
    }

    componentDidMount() {
        const { scope: { id }, loadingArray, loadedArray } = this.props;
        if (!this.state.preventLoad && !loadingArray && !loadedArray) {
            this.props.getArrayDetail(id);
            this.props.getArrayHardwareUpgradeRecommendations(id);
        }
    }

    componentWillUnmount() {
        this.props.reset();
    }

    render() {
        const {
            array,
            loadingArrayHardwareUpgradeRecommendations,
            match,
            scope,
            loadingArray,
        } = this.props;
        const { poolUidFromInventory } = this.state;

        return (
            <Switch>
                <Route
                    exact
                    path={buildUrl(match.path, 'performance')}
                    render={() => (
                        <Async loading={!poolUidFromInventory && !array}>
                            <Redirect
                                push={false}
                                to={`/infrastructure/storage/nimble/pools/${poolUidFromInventory
                                || (array && array.poolUid)}?tab=performance&perf.array=${scope.id}`}
                            />
                        </Async>
                    )}
                />
                <Route
                    render={() => (
                        <React.Fragment>
                            <Async loading={!array}>
                                {() => (<ArrayHeadline {...(array || {})} />)}
                            </Async>
                            <div className={classnames('row expanded')}>
                                <div className="medium-11 column medium-unstack">
                                    <Card title="Shelves">
                                        <Async loading={!array}>
                                            {() => (<ArrayShelves {...(array || {})} />)}
                                        </Async>
                                    </Card>
                                </div>
                                <div className="medium-13 column medium-unstack">
                                    <Card title="Hardware Recommendations" className="flex-container flex-dir-column">
                                        <Async loading={loadingArrayHardwareUpgradeRecommendations}>
                                            {() => (<ArrayHadrwareRecommendations {...this.props} />)}
                                        </Async>
                                    </Card>
                                    <Card title="Software Recommendations">
                                        <Async loading={loadingArray}>
                                            <NoData hasData={array} message="No array data available">
                                                {() => (<SoftwareRecommendationsMarionetteWrapper
                                                    version={array.softwareVersion}
                                                    hostname={array.hostName}
                                                    serialNumber={array.arrayUid}
                                                />)}
                                            </NoData>
                                        </Async>
                                    </Card>
                                </div>
                            </div>
                        </React.Fragment>
                    )}
                />
            </Switch>
        );
    }
}

ArrayDetailContainer.propTypes = {
    array: PropTypes.shape({
        accountId: PropTypes.string,
        arrayUid: PropTypes.string,
        arrayType: PropTypes.string,
        hostName: PropTypes.string,
        groupName: PropTypes.string,
        poolUid: PropTypes.string,
        poolName: PropTypes.string,
        arrayId: PropTypes.string,
        model: PropTypes.string,
        softwareVersion: PropTypes.string,
        supportStatus: PropTypes.string,
        sendingAsups: PropTypes.string,
        supportEndDate: PropTypes.string,
        supportAgrmt: PropTypes.string,
        arrayTimezone: PropTypes.string,
        arrayCapacityBytes: PropTypes.number,
        arrayFreeBytes: PropTypes.number,
        shelfCount: PropTypes.number,
        shelfSsdRawCapacityBytes: PropTypes.number,
        shelfHddRawCapacityBytes: PropTypes.number,
        hddDriveCount: PropTypes.number,
        isAllFlash: PropTypes.bool,
        shelfSn: PropTypes.string,
        headShelfSn: PropTypes.string,
        shelfModel: PropTypes.string,
        assetType: PropTypes.oneOf(['Array', 'Shelf']),
        latestHeartBeat: PropTypes.shape({
            serialNumber: PropTypes.string,
            timestamp: PropTypes.number,
            controllers: PropTypes.arrayOf(PropTypes.shape({
                name: PropTypes.string,
                state: PropTypes.string,
                components: PropTypes.arrayOf(PropTypes.shape({
                    name: PropTypes.oneOf(['psu', 'fan', 'temperature']),
                    status: PropTypes.string,
                })),
            })),
            ssdGoodCount: PropTypes.number,
            ssdFailedCount: PropTypes.number,
            hddGoodCount: PropTypes.number,
            hddFailedCount: PropTypes.number,
        }),
        lastAsupReceivedTime: PropTypes.number,
    }),
    loadingArray: PropTypes.bool,
    loadedArray: PropTypes.bool,
    getArrayDetail: PropTypes.func,
    arrayHardwareUpgradeRecommendations: PropTypes.shape({
        arrayType: PropTypes.string,
        currentArrayModel: PropTypes.string,
        currentController: PropTypes.string,
        currentCacheSystemTotalBytes: PropTypes.number,
        currentCacheHeadBytes: PropTypes.number,
        currentCacheTotalAfsBytes: PropTypes.number,
        currentCacheExpNonAfsBytes: PropTypes.number,
        currentHddBytes: PropTypes.number,
        currentHddExpansionShelves: PropTypes.number,
        recommendDiskUpgrade: PropTypes.bool,
        recommendCPUUpgrade: PropTypes.bool,
        recommendCacheUpgrade: PropTypes.bool,
        addAfsShelf: PropTypes.bool,
        recommendScaleOut: PropTypes.bool,
        recommendedArrayModel: PropTypes.string,
        recommendedController: PropTypes.string,
        recommendCacheSystemTotalBytes: PropTypes.number,
        recommendedCacheHeadBytes: PropTypes.number,
        recommendedCacheTotalAfsBytes: PropTypes.number,
        recommendedCacheExpNonAfsBytes: PropTypes.number,
        recommendedHddExpansionShelves: PropTypes.number,
    }),
    getArrayHardwareUpgradeRecommendations: PropTypes.func,
    loadingArrayHardwareUpgradeRecommendations: PropTypes.bool,

    // Injected by withQueryParams
    match: PropTypes.shape({
        url: PropTypes.string,
        params: PropTypes.shape({}).isRequired,
    }).isRequired,
    location: PropTypes.shape({
        pathname: PropTypes.string.isRequired,
    }).isRequired,

    // Injected by XX_INFRASTRUCTURE_XX
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),
    config: PropTypes.object.isRequired,
    reset: PropTypes.func,
};

const mapStateToProps = state => ({
    ...arraySelector(state),
    ...arrayHardwareUpgradeRecommendationSelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    reset,
    getArrayDetail,
    getArrayHardwareUpgradeRecommendations,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(ArrayDetailContainer);
