import React from 'react';
import FlashNotification from '@infosight/elmer/dist/components/FlashNotification';
import { createReducersForAction } from '@infosight/elmer/dist/utils/redux';
import rootSelector from '../rootSelector';
import {
    FETCH,
    CHANGE_ROLE,
    DELETE,
    CREATE,
    RESET,
} from './constants';

const initialState = {
    users: null,
};

function changeRoleReducer(state, { success, user, error }) {
    // This is a bit hacky to be doing "rendering" here, but all this is really doing is lightweight message formatting via JSX
    // The actual rendering is done by the Global <FlashNotification /> component in the <AppContainer> tree
    if (success) {
        FlashNotification.success(<span>Successfully changed role for <b>{user.login}</b></span>);
        return {
            ...state,
            users: state.users.map((u) => {
                if (u.login !== user.login) {
                    return u;
                }
                return user;
            }),
        };
    }
    FlashNotification.error(<span>Error changing role for <b>{user.login}</b>: {error}</span>);
    return state;
}

function deleteUserReducer(state, { success, user, error }) {
    if (success) {
        FlashNotification.success(<span>Successfully deleted <b>{user.login}</b></span>);
        return {
            ...state,
            users: state.users.filter(u => u.login !== user.login),
        };
    }
    FlashNotification.error(<span>Error deleting <b>{user.login}</b>: {error}</span>);
    return state;
}

function createUserReducer(state, { success, user, login, error }) {
    if (success) {
        FlashNotification.success(<span>Successfully created <b>{login}</b></span>);
        return {
            ...state,
            users: [
                ...state.users,
                user,
            ],
        };
    }
    FlashNotification.error(<span>Error creating <b>{login}</b>: {error}</span>);
    return state;
}

const ACTION_HANDLERS = {
    [RESET]: () => (initialState),
    [CHANGE_ROLE]: changeRoleReducer,
    [DELETE]: deleteUserReducer,
    [CREATE]: createUserReducer,
    ...createReducersForAction({ type: FETCH, stateKey: 'users' }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

export const userSelector = state => rootSelector(state).user;
