import { createReducersForAction, createSelector, getInitialState } from '@infosight/elmer/dist/utils/redux';
import rootState from '../../rootStateSelector';
import { USER_CONTEXT_CHANGED } from '../../../user/constants';

import {
    ARRAY_FETCH,
    ARRAY_HARDWARE_UPGRADE_RECOMMENDATIONS_FETCH,
    RESET,
} from './constants';

const initialState = {
    ...getInitialState('array'),
    ...getInitialState('arrayHardwareUpgradeRecommendations'),
};

const ACTION_HANDLERS = {
    [USER_CONTEXT_CHANGED]: () => (initialState),
    [RESET]: () => (initialState),
    ...createReducersForAction({ type: ARRAY_FETCH, stateKey: 'array' }),
    ...createReducersForAction({ type: ARRAY_HARDWARE_UPGRADE_RECOMMENDATIONS_FETCH, stateKey: 'arrayHardwareUpgradeRecommendations' }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

const baseSelector = state => rootState(state).arrayDetail;
export const arraySelector = createSelector(baseSelector, 'array');
export const arrayHardwareUpgradeRecommendationSelector = createSelector(baseSelector, 'arrayHardwareUpgradeRecommendations');
