import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Async from '@infosight/elmer/dist/components/Async';
import NoData from '@infosight/elmer/dist/components/NoData';
import moment from 'moment-timezone';
import {
    QueryTimeRangeSelector,
    deserialize,
    makeChartSelector,
    makeRangeSelector,
} from '@infosight/elmer/dist/components/TimeRangeSelector';
import { performanceTimeRangeParam } from '../../../collection/components/performance/queryParams';
import PerformanceChartsPage from '../../../../../charts/PerformanceChartsPage';
import { getVolumePerformance } from '../../actionCreators';
import { performanceSelector } from '../../reducer';

const timeRangePresets = [
    moment.duration(1, 'day'),
    moment.duration(1, 'week'),
    moment.duration(1, 'month'),
    moment.duration(3, 'months'),
];

class VolumePerformanceContainer extends Component {
    componentDidMount() {
        const {
            loadingVolumePerformance,
            loadedVolumePerformance,
        } = this.props;

        if (!loadingVolumePerformance && !loadedVolumePerformance) {
            this.fetchData(this.props);
        }
    }

    componentWillReceiveProps(nextProps) {
        const { volumeSn, timeRange } = nextProps;

        if (volumeSn !== this.props.volumeSn || !timeRange.equals(this.props.timeRange)) {
            this.fetchData(nextProps);
        }
    }

    fetchData(props) {
        this.props.getVolumePerformance(props.volumeSn, props.timeRange.toGenericQueryTime());
    }

    render() {
        const {
            loadingVolumePerformance,
            loadedVolumePerformance,
            timeRange,
            volumePerformance,
            history,
            location,
        } = this.props;

        let displayTimeRange;
        if (volumePerformance && volumePerformance.data && volumePerformance.data.length) {
            displayTimeRange = QueryTimeRangeSelector.toTimeRangeSelection(volumePerformance.meta.time);
        }

        const transformedVolumePerformance = ((volumePerformance && volumePerformance.data
            && volumePerformance.data.length && volumePerformance.data[0].timeseries) || []).map(obj => ({
            timestamp: obj.timestamp,
            readLatencyMs: obj.latencyRead,
            writeLatencyMs: obj.latencyWrite,
            readIops: obj.iopsRandomSequentialRead,
            writeIops: obj.iopsRandomSequentialWrite,
            readMbps: obj.mbpsRandomSequentialRead / (2 ** 20),
            writeMbps: obj.mbpsRandomSequentialWrite / (2 ** 20),
            severityScore: obj.severityScore,
        }));

        const onSelectRange = makeRangeSelector(location, performanceTimeRangeParam(), false, history);
        const onChartSelect = makeChartSelector(onSelectRange);

        return (
            <div>
                <QueryTimeRangeSelector
                    parameterPrefix={performanceTimeRangeParam()}
                    value={timeRange}
                    presets={timeRangePresets}
                    displayValue={displayTimeRange}
                    card
                />
                <Async loading={loadingVolumePerformance}>
                    <NoData hasData={!!(loadedVolumePerformance && transformedVolumePerformance.length > 0)}>
                        {() => (
                            <PerformanceChartsPage
                                timeseries={transformedVolumePerformance}
                                chartRangeStart={moment(volumePerformance.meta.time.start)}
                                chartRangeEnd={moment(volumePerformance.meta.time.end)}
                                onChartSelect={onChartSelect}
                            />
                        )}
                    </NoData>
                </Async>
            </div>
        );
    }
}

VolumePerformanceContainer.propTypes = {
    volumeSn: PropTypes.string.isRequired,
    loadingVolumePerformance: PropTypes.bool,
    loadedVolumePerformance: PropTypes.bool,
    volumePerformance: PropTypes.object,
    getVolumePerformance: PropTypes.func,
    timeRange: PropTypes.object,
    history: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
};

const mapUrlToProps = ({ match: { params } }) => ({
    timeRange: deserialize(params, performanceTimeRangeParam('')) || QueryTimeRangeSelector.DEFAULTS.VOLATILE,
});

const mapStateToProps = (state, props) => ({
    ...mapUrlToProps(props),
    ...performanceSelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    getVolumePerformance,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(VolumePerformanceContainer);
