import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Async from '@infosight/elmer/dist/components/Async';
import NoData from '@infosight/elmer/dist/components/NoData';
import { TimeRangeSelection, makeChartSelector, makeRangeSelector } from '@infosight/elmer/dist/components/TimeRangeSelector';
import CapacityChart from './CapacityChart';
import { getPoolCapacity } from '../../../actionCreators';
import { capacitySelector } from '../../../reducer';
import { capacityTimeRangeParam } from '../../../queryParams';


class CapacityChartContainer extends PureComponent {
    componentDidMount() {
        const {
            loadingPoolCapacity,
            loadedPoolCapacity,
            poolCapacity,
            onDataReceived,
        } = this.props;

        if (!loadingPoolCapacity && !loadedPoolCapacity) {
            this.fetchData(this.props);
        } else if (onDataReceived && poolCapacity && poolCapacity.meta) {
            onDataReceived({ capacityDataMetaInfo: poolCapacity.meta.time });
        }
    }

    componentWillReceiveProps(nextProps) {
        const { timeRange } = nextProps;

        if (!timeRange.equals(this.props.timeRange)) {
            this.fetchData(nextProps);
        }
    }

    componentDidUpdate({ poolCapacity: prevPoolCapacity }) {
        const { poolCapacity, onDataReceived } = this.props;
        if (onDataReceived && poolCapacity && poolCapacity.meta && poolCapacity !== prevPoolCapacity) {
            onDataReceived({ capacityDataMetaInfo: poolCapacity.meta.time });
        }
    }

    fetchData(props) {
        const { scope, config, timeRange } = props;

        const fetchOptions = {
            scope: config.convertToGenericQueryScope(scope),
            timeRange, // This is one off where we do not convert timeRange to generic query format in the Container, instead we do it in actionCreator
        };
        this.props.getPoolCapacity(fetchOptions);
    }

    render() {
        const { poolCapacity,
            loadingPoolCapacity,
            loadedPoolCapacity,
            includeSavingsSeries,
            isLineChart,
            chartRangeStart,
            chartRangeEnd,
            history,
            location } = this.props;
        const hasPoolCapacityData = loadedPoolCapacity && !!(poolCapacity && poolCapacity.data);
        const onSelectRange = makeRangeSelector(location, capacityTimeRangeParam(), false, history);
        const onChartSelect = makeChartSelector(onSelectRange);

        return (
            <Async loading={loadingPoolCapacity}>
                <NoData hasData={hasPoolCapacityData}>
                    {() => (
                        <CapacityChart
                            poolCapacity={poolCapacity}
                            chartRangeStart={chartRangeStart}
                            chartRangeEnd={chartRangeEnd}
                            includeSavingsSeries={includeSavingsSeries}
                            onChartSelect={onChartSelect}
                            isLineChart={isLineChart}
                            forecastChart
                        />
                    )}
                </NoData>
            </Async>
        );
    }
}

CapacityChartContainer.propTypes = {
    onDataReceived: PropTypes.func,
    isLineChart: PropTypes.bool,
    includeSavingsSeries: PropTypes.bool,
    loadingPoolCapacity: PropTypes.bool,
    loadedPoolCapacity: PropTypes.bool,
    getPoolCapacity: PropTypes.func,
    poolCapacity: PropTypes.shape({
        data: PropTypes.shape({
            timeseries: PropTypes.arrayOf(PropTypes.shape({
                timestamp: PropTypes.number,
                usedBytes: PropTypes.number,
                allocatedBytes: PropTypes.number,
                totalSavingsBytes: PropTypes.number,
                forecastedUsageBytes: PropTypes.number,
                forecastedLowerBoundBytes: PropTypes.number,
                forecastedUpperBoundBytes: PropTypes.number,
            })).isRequired,
        }).isRequired,
        meta: PropTypes.shape({
            time: PropTypes.shape({
                start: PropTypes.number.isRequired,
                end: PropTypes.number.isRequired,
            }).isRequired,
        }).isRequired,
    }),
    timeRange: PropTypes.instanceOf(TimeRangeSelection).isRequired,
    scope: PropTypes.shape({ // eslint-disable-line react/no-unused-prop-types
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),
    config: PropTypes.object.isRequired, // eslint-disable-line react/no-unused-prop-types
    /**
     * Starting timestamp where the chart begins
     * moment or number
     */
    chartRangeStart: PropTypes.oneOfType([PropTypes.object, PropTypes.number]),

    /**
     * Last timestamp where the chart ends
     * moment or number
     */
    chartRangeEnd: PropTypes.oneOfType([PropTypes.object, PropTypes.number]),
    history: PropTypes.object.isRequired,
    location: PropTypes.object,
};

CapacityChartContainer.defaultProps = {
    includeSavingsSeries: true,
    isLineChart: false,
};

const mapStateToProps = state => ({
    ...capacitySelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    getPoolCapacity,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(CapacityChartContainer);
