const _ = require('underscore');
var Marionette = require('backbone.marionette');
var RangeModel = require('./RangeModel');
var PerformanceChartView = require('./PerformanceChartView');
var tpl = require('raw-loader!./PerformanceView.html');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    ui: {
        moreHelpToggle: 'a[href="#coral-view-more-help"]',
    },

    className: 'performance-details',

    events: {
        'show.bs.collapse #coral-view-more-help': function () {
            this.ui.moreHelpToggle.text('Less...');
        },
        'hide.bs.collapse #coral-view-more-help': function () {
            this.ui.moreHelpToggle.text('More...');
        },
    },

    regions: {
        chart: '[data-region="chart"]',
    },

    childEvents: {
        'dragZoom': 'onDragZoom',
    },

    initialize: function () {
        this.model = this.getOption('rangeModel') || new RangeModel();
    },

    onRender: function () {
        this.renderChart();
    },

    renderChart: function () {
        this.showChildView('chart', new PerformanceChartView({
            poolUid: this.getOption('poolUid'),
            arrayModel: this.getOption('arrayModel'),
            rangeModel: this.model,
        }));
    },

    onDragZoom: function (view, range) {
        this.trigger('dragZoom', range);
    },

});
