const _ = require('underscore');
var Backbone = require('backbone');
var Marionette = require('backbone.marionette');
var ImpactScore = require('./ImpactScore');
var numeral = require('numeral');
var tpl = require('raw-loader!./IopsChart.html');
var tooltipTpl = require('raw-loader!./IopsChartTooltip.html');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    tooltipTemplate: _.template(tooltipTpl),

    templateHelpers: function () {
        return {
            impactScoreColor: new ImpactScore({ percentile: this.model.get('weightedAverageSeverityPercentile') }).color,
            barPercent: numeral(this.model.get('averageIops') / this.options.maxIops).format('0%'),
            numeral: numeral,
        };
    },

    initialize: function (options) {
        this.chartDefaults = options.chartDefaults;
        this.type = options.type;
        this.model = this.buildModel();
    },

    buildModel: function () {
        var type = this.type;
        var totals = this.collection.reduce(function (memo, point) {
            memo.iops += point.get('iops' + type);
            memo.severityPercentile += point.get('severityPercentile' + type) * point.get('iops' + type);
            return memo;
        }, { iops: 0, severityPercentile: 0 });
        return new Backbone.Model({
            type: this.type,
            averageIops: totals.iops / this.collection.length,
            weightedAverageSeverityPercentile: totals.severityPercentile / totals.iops,
        });
    },

    onRender: function () {
        var type = this.type;
        this.$('[data-chart]').highcharts('StockChart', _(this.chartDefaults({
            tooltipTemplate: this.tooltipTemplate,
            title: {
                text: type,
            },
        })).extend({
            series: [
                {
                    name: type,
                    type: 'column',
                    data: this.collection.map(function (point) {
                        var impactScore = new ImpactScore({ percentile: point.get('severityPercentile' + type) });
                        return {
                            x: point.get('timestamp'),
                            y: point.get('iops' + type),
                            color: impactScore.color,
                            impactScore: impactScore,
                            latency: point.get('latency' + type),
                            type: type,
                        };
                    }),
                },
            ],
            yAxis: _(this.chartDefaults({}).yAxis).extend({
                max: this.options.maxIops,
                title: {
                    text: 'IO/sec',
                },
            }),
        }));
    },

});
