import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { ClientControlledTable } from '@infosight/elmer/dist/components/Table';
import { getSortedValues, getMaxTotalUsage } from './gridViewOptions';
import applicationsConfig from './applicationsConfig';
import style from './style.scss';

const cx = classnames.bind(style);

function ApplicationsTable({ values, loadingCollection, userName }) {
    const data = getSortedValues(values || []);
    const maxTotalUsage = getMaxTotalUsage(data);
    const maxTotalPhysicalUsage = getMaxTotalUsage(data, 'totalPhysicalUsed');

    const { columns, categories } = applicationsConfig(maxTotalUsage, maxTotalPhysicalUsage);

    return (
        <div className={cx('applications-container')}>
            <ClientControlledTable
                tableId={`${userName}_NIMBLE_APPLICATIONS_CAPACITY`}
                data={data || []}
                columns={columns}
                categories={categories}
                loading={loadingCollection}
                syncUrl={false}
            />
        </div>
    );
}

ApplicationsTable.propTypes = {
    loadingCollection: PropTypes.bool.isRequired,
    userName: PropTypes.string,
    values: PropTypes.arrayOf(PropTypes.shape({
        applicationCategory: PropTypes.string, // Default is "default"
        volumeCount: PropTypes.number.isRequired,
        volumeUsed: PropTypes.number.isRequired,
        snapshotUsed: PropTypes.number.isRequired,
        totalUsed: PropTypes.number.isRequired,
        growthPct30day: PropTypes.number,
        growthPct60day: PropTypes.number,
        growthPct90day: PropTypes.number,
    })),
};

export default ApplicationsTable;
