import React from 'react';
import PropTypes from 'prop-types';
import { Highstock as Highcharts } from 'progeny/modules/nimble/highchartsProxy';

/**
 * Reset tooltip
 * @param {*} mouseEvent ( mouseLeave | mouseOut event)
 */
function resetTooltips(mouseEvent) {
    let chart;
    let hoveredPoint;
    let chartIndex;
    let seriesIndex;
    let event;
    let seriesCount;
    const chartCount = Highcharts.charts.length;

    for (chartIndex = 0; chartIndex < chartCount; chartIndex += 1) {
        chart = Highcharts.charts[chartIndex];
        if (chart) {
            seriesCount = chart.series.length;
            event = chart.pointer.normalize(Object.assign({}, mouseEvent)); // Find coordinates within the chart
            // Iterate over all the series of the chart
            for (seriesIndex = 0; seriesIndex < seriesCount; seriesIndex += 1) {
                hoveredPoint = chart.series[seriesIndex].searchPoint(event, true); // Get the hovered point
                if (hoveredPoint) {
                    hoveredPoint.onMouseOut();
                    if (chart.tooltip && typeof chart.tooltip.hide === 'function') {
                        chart.tooltip.hide(hoveredPoint);
                    }
                }
            }
            chart.xAxis[0].hideCrosshair();
        }
    }
}

/**
 * Draw sycnhronized cross hairs on mouse move
 * @param {*} mouseMoveEvent
 */
function syncChartCrossHairs(mouseMoveEvent) {
    let chart;
    let hoveredPoint;
    let chartIndex;
    let seriesIndex;
    let event;
    let seriesCount;
    // TODO: Instead of accessing all the charts on the page, use an attributet to filter the charts we want to sync
    const chartCount = Highcharts.charts.length;

    for (chartIndex = 0; chartIndex < chartCount; chartIndex += 1) {
        chart = Highcharts.charts[chartIndex];
        if (chart) {
            seriesCount = chart.series.length;
            event = chart.pointer.normalize(Object.assign({}, mouseMoveEvent)); // Find coordinates within the chart
            const hoverPoints = [];
            // Iterate over all the series of the chart
            for (seriesIndex = 0; seriesIndex < seriesCount; seriesIndex += 1) {
                hoveredPoint = chart.series[seriesIndex].searchPoint(event, true); // Get the hovered point
                if (hoveredPoint) {
                    hoveredPoint.onMouseOver(); // Show the hover marker
                    hoverPoints.push(hoveredPoint);
                    chart.xAxis[0].drawCrosshair(mouseMoveEvent, hoveredPoint); // Show the crosshair
                }
            }

            if (hoverPoints.length && chart.tooltip && typeof chart.tooltip.refresh === 'function') {
                chart.tooltip.refresh(hoverPoints);
            }
        }
    }
}

/**
 * Basic Highcharts crosshair and marker synchronization with no external dependencies. Just put this in the component tree
 * and all charts in the DOM will be synched.
 *
 * For more complex cases, such as Shower Tile views where the various chart elements are deconstructed and recomposed, use ChartSyncProvider
 */
const SyncChart = ({ onMouseLeave, children }) => (
    <div onMouseMove={syncChartCrossHairs} onMouseLeave={onMouseLeave || resetTooltips} onMouseOut={onMouseLeave || resetTooltips}>
        {children}
    </div>
);


SyncChart.propTypes = {
    /**
     * Consumer can override the behavior as it can be different based on the Chart
     * Ex: On some charts we reset to default Legend Values ( which have to be computed in most cases)
     *     On some we might just show empty values or reset tooltip
     * If none passed, it defaults to tooltip reset
     */
    onMouseLeave: PropTypes.func,
    children: PropTypes.arrayOf(PropTypes.oneOfType([
        PropTypes.func,
        PropTypes.element,
        PropTypes.node,
    ])),
};

export default SyncChart;
