const _ = require('underscore');
var Backbone = require('backbone');
var moment = require('moment');
var SupportContractEndDateTooFarInFutureInYears = 10;
var SupportContractExpirationWarningThresholdInDays = 90;
var HeartbeatDelayThresholdInMinutes = 25;
var HeartbeatStaleThresholdInDays = 7;

var SupportStatus = Object.freeze({
    NOT_AVAILABLE: 'NOT_AVAILABLE',
    CURRENT: 'CURRENT',
    EXPIRING: 'EXPIRING',
    EXPIRED: 'EXPIRED',
});

var parseSupportStatus = function (response) {
    if (response.supportStatus === SupportStatus.EXPIRED) {
        return SupportStatus.EXPIRED;
    }

    // pass 'true' to avoid rounding
    if (Math.abs(moment(response.supportEndDate).diff(moment(), 'years', true)) > SupportContractEndDateTooFarInFutureInYears) {
        return SupportStatus.NOT_AVAILABLE;
    }

    var expiring = Math.abs(moment(response.supportEndDate).diff(moment.utc(), 'days')) <= SupportContractExpirationWarningThresholdInDays;
    return expiring ? SupportStatus.EXPIRING : SupportStatus.CURRENT;
};

var parseLatestHeartbeat = function (latestHeartbeat) {
    if (!latestHeartbeat) {
        return;
    }
    latestHeartbeat.isDelayed = moment().diff(moment(latestHeartbeat.timestamp), 'minutes') > HeartbeatDelayThresholdInMinutes;
};

// TODO Merge this into AssetDetailsModel
module.exports = Backbone.Model.extend({
    /** @lends app/modules/assets_report/models/AssetModel.prototype */

    /**
     * @classdesc <span style="color: red;">Developer description to be added.</span>
     * @name app/modules/assets_report/models/AssetModel
     * @constructor
     * @augments Backbone.Model
     */

    idAttribute: 'serialNumber',

    defaults: {
        company: null,
        accountId: null,
        serialNumber: null,
        assetId: null,
        arrayType: null,
        model: null,
        version: null,
        hostname: null,
        group: null,
        pool: null,
        assetStreet: null,
        assetCity: null,
        assetCountry: null,
        assetZip: null,
        assetState: null,
        supportStatus: null,
        supportStartDate: null,
        supportEndDate: null,
        sendingAsups: null,
        asupReceived: null,
        asupProcessed: null,
        assetShipDate: null,
        assetShipStatus: null,
        supportAgreement: null,
        arrayTimezone: null,
        capacity: null,
        free: null,
        expansionShelves: 0,
        criticalEvents: null,
        urgentEvents: null,
        importantEvents: null,
        warningEvents: null,
        hasEvents: null,
        asupStatus: null,
        hbStatus: 'not-loaded',
        hbReceived: null,
        hbAge: null,
        charts: [],
        upgradable: null,
        allFlash: null,
    },

    parse: function (response) {
        response = {
            ...response,
            supportStatus: parseSupportStatus(response),
            hostname: response.hostname || response.hostName,
            serialNumber: response.serialNumber || response.arraySn,
            latestHeartBeat: response.latestHeartBeat ? { ...response.latestHeartBeat } : null,
            version: response.version || response.softwareVersion,
        };

        parseLatestHeartbeat(response.latestHeartbeat);

        return response;
    },

    versionSupported: function (release) {
        var version = this.get('version');

        // If there are no ASUPs for array, then we don't know the version
        if (!version) {
            return true;
        }

        // Bad data happens. If no matching release, assume it is supported. Only likely to happen in non-prod envs.
        if (!release) {
            return true;
        }

        return release.get('supported');
    },

    hasHardwareIssues: function () {
        var hb = this.get('latestHeartbeat');
        if (!hb) {
            return false;
        }
        if (moment().diff(hb.timestamp, 'days') > HeartbeatStaleThresholdInDays) {
            // Exclude hardware issues from stale heartbeats
            return false;
        }
        return !_(hb.controllers)
                .chain()
                .pluck('state')
                .difference(['active', 'standby']) // Any state values NOT 'active' or 'standby' indicate a warning
                .isEmpty()
                .value() ||
            hb.ssdFailedCount ||
            hb.hddFailedCount ||
            _(hb.controllers).some(function (controller) {
                return _(controller.components).some(function (component) {
                    return component.status != 'OK';
                });
            });
    },

    hasHeartbeatWarning: function () {
        return !this.get('latestHeartbeat') || this.get('latestHeartbeat').isDelayed;
    },

    hasAsupWarning: function () {
        return this.get('sendingAsups') != 'RECEIVED';
    },

}, {
    SupportStatus: SupportStatus,
});
