import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { ReactHighcharts } from '@infosight/elmer/dist/components/ReactHighcharts';
import ReactDimensions from 'react-dimensions';
import NoData from '@infosight/elmer/dist/components/NoData';
import { CHART_COLORS } from '../../../../../../util/chartColors';

export class PoolPerformanceSnapShotView extends PureComponent {
    constructor(props) {
        super(props);
        autobind(this);
    }

    data() {
        const { cpuCacheTimeseries, cpuThresholdPercent, cacheThresholdPercent } = this.props;
        return cpuCacheTimeseries.map(point => ({
            x: point.timestamp,
            y: (point.cpuUsagePercent > cpuThresholdPercent || point.cacheUsagePercent > cacheThresholdPercent) ? 1 : null,
        }));
    }

    config() {
        return {
            chart: {
                type: 'column',
                backgroundColor: 'transparent',
                width: this.props.containerWidth,
                height: this.props.containerHeight,
                spacingTop: 0,
                spacingBottom: 0,
                spacingLeft: 40,
                spacingRight: 0,
            },
            title: false,
            xAxis: {
                type: 'datetime',
                visible: true,
            },
            yAxis: {
                visible: false,
            },
            tooltip: false,
            plotOptions: {
                column: {
                    pointPlacement: 0.5,
                    pointPadding: 0,
                    groupPadding: 0,
                    borderWidth: 0,
                    minPointLength: 3,
                },
            },
            series: [{
                name: 'Pool Performance Impact',
                color: CHART_COLORS.redOrange,
                data: this.data(),
            }],
        };
    }

    render() {
        const config = this.config();

        return (
            <NoData hasData={!!config}>
                {() => <ReactHighcharts config={config} />}
            </NoData>
        );
    }
}

PoolPerformanceSnapShotView.propTypes = {
    cpuCacheTimeseries: PropTypes.arrayOf(PropTypes.shape({
        cpuUsagePercent: PropTypes.number,
        cacheUsagePercent: PropTypes.number,
    })),
    cpuThresholdPercent: PropTypes.number,
    cacheThresholdPercent: PropTypes.number,
    containerHeight: PropTypes.number.isRequired,
    containerWidth: PropTypes.number.isRequired,
};

PoolPerformanceSnapShotView.defaultProps = {
    containerHeight: 0,
    containerWidth: 0,
};

export default ReactDimensions({ elementResize: true })(PoolPerformanceSnapShotView);
