import React from 'react';
import PropTypes from 'prop-types';

export const daysUntilFullText = (days) => {
    if (days === null || days === undefined) {
        return null;
    }

    let dayEstimate = null;
    if (days <= 7) {
        dayEstimate = 'one week';
    } else if (days <= 15) {
        dayEstimate = 'two weeks';
    } else if (days <= 31) {
        dayEstimate = 'one month';
    } else if (days <= 60) {
        dayEstimate = 'two months';
    } else if (days <= 90) {
        dayEstimate = 'three months';
    }

    return dayEstimate;
};

const DaysUntilFull = ({ className, days }) => <span className={className}>{daysUntilFullText(days)}</span>;

DaysUntilFull.propTypes = {
    days: PropTypes.number,
};

export default DaysUntilFull;
