import { createReducersForAction, createSelector } from '@infosight/elmer/dist/utils/redux';
import { USER_CONTEXT_CHANGED } from '../../user/constants';
import { FETCH_SFDC_CONTACT } from '../actions/constants';

const initialState = {};

const ACTION_HANDLERS = {
    [USER_CONTEXT_CHANGED]: () => (initialState),
    ...createReducersForAction({ type: FETCH_SFDC_CONTACT, stateKey: 'contact' }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

const baseSelector = state => state.supportTab.core;
export const contactSelector = createSelector(baseSelector, 'contact');
