import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { isFunction } from 'underscore';
import { getPoolCapacity, resetPoolCapacity, getAggregatePoolCapacity } from '../../actionCreators/pool';
import PoolCapacity from '../layout/PoolCapacity';
import {
    poolContextSelector,
    isLoadingSelector,
    topPoolsSelector,
} from '../../reducer/poolContext/selectors';

class PoolCapacityContainer extends Component {
    componentDidMount() {
        if (!this.props.loading) {
            const { poolUid, arraySerialNumber, lookbackMonths: lookback } = this.props;
            this.getData(poolUid)({ poolUid, lookback, arraySerialNumber });
        }
    }

    componentWillReceiveProps({ poolUid, lookbackMonths, arraySerialNumber, loading }) {
        if (this.props.poolUid !== poolUid || this.props.lookbackMonths !== lookbackMonths) {
            this.getData(poolUid)({ poolUid, lookback: lookbackMonths, arraySerialNumber });
        }

        if (this.props.toggleFilters) {
            this.props.toggleFilters(!loading);
        }
    }

    componentWillUnmount() {
        if (isFunction(this.props.resetPoolCapacity)) {
            this.props.resetPoolCapacity();
        }
    }

    getData(poolUid) {
        return poolUid ? this.props.getPoolCapacity : this.props.getAggregatePoolCapacity;
    }

    render() {
        return (
            <PoolCapacity {...this.props} />
        );
    }
}

PoolCapacityContainer.propTypes = {
    loading: PropTypes.bool.isRequired,
    getAggregatePoolCapacity: PropTypes.func.isRequired,
    getPoolCapacity: PropTypes.func.isRequired,
    resetPoolCapacity: PropTypes.func,

    // These should be passed from parent component
    lookbackMonths: PropTypes.number.isRequired,
    poolUid: PropTypes.string,
    toggleFilters: PropTypes.func,
    arraySerialNumber: PropTypes.string,
    isAggregate: PropTypes.bool, // eslint-disable-line react/no-unused-prop-types
    config: PropTypes.object,
};

PoolCapacityContainer.defaultProps = {
    lookbackMonths: 3,
    loading: false,
    isAggregate: false,
};

const mapUrlToProps = (state, { match: { params } }) => ({
    sort: params.sort,
});

const mapStateToProps = (state, props) => ({
    ...mapUrlToProps(state, props),
    ...poolContextSelector(state),
    topPoolsTimeSeries: topPoolsSelector(state),
    loading: isLoadingSelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    getPoolCapacity,
    getAggregatePoolCapacity,
    resetPoolCapacity,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(PoolCapacityContainer);
