import { createSelector } from 'reselect';
import { values } from 'underscore';
import * as types from '../actions/constants';
import { USER_CONTEXT_CHANGED } from '../../user/constants';

const initialState = {
    error: false,
    loaded: false,
    raw: null,
    value: null,
};

function replaceCases(state, { payload }) {
    return {
        error: false,
        loaded: true,
        raw: payload,
    };
}

function updateCaseDetails(state, { payload: { caseData, commentsData, attachmentsData, rmaLabelsData } }) {
    return {
        error: false,
        loaded: true,
        raw: {
            ...state.raw,
            [caseData.id]: {
                ...caseData,
                comments: commentsData,
                attachments: attachmentsData,
                rmaLabels: rmaLabelsData,
            },
        },
    };
}

function addCommentToCase(state, { payload: comment }) {
    const { parentId: caseId } = comment;
    const parentCase = state.raw[caseId];
    const comments = Array.isArray(parentCase.comments) ? parentCase.comments : [];
    const newParentCase = { ...parentCase, comments: comments.concat(comment) };

    return {
        error: false,
        loaded: true,
        raw: {
            ...state.raw,
            [caseId]: newParentCase,
        },
    };
}

function addAttachmentToCase(state, { payload: attachment }) {
    const { ParentId: caseId } = attachment;
    const parentCase = state.raw[caseId];
    const attachments = Array.isArray(parentCase.attachments) ? parentCase.attachments : [];
    const newParentCase = { ...parentCase, attachments: attachments.concat(attachment) };

    return {
        error: false,
        loaded: true,
        raw: {
            ...state.raw,
            [caseId]: newParentCase,
        },
    };
}

export default function supportReducer(state = initialState, action) {
    switch (action.type) {
    case types.FETCH_CASE_HEADERS_OK:
        return replaceCases(state, action);

    case types.FETCH_CASE_DETAIL_OK:
    case types.POST_CASE_OK:
    case types.POST_CASE_RESOLUTION_OK:
        return updateCaseDetails(state, action);

    case types.POST_CASE_ATTACHMENT_OK:
        return addAttachmentToCase(state, action);

    case types.POST_CASE_COMMENT_OK:
        return addCommentToCase(state, action);

    case types.FETCH_CASE_COMMENTS_OK:
    case types.FETCH_CASE_ATTACHMENTS_OK:
        break;

    case types.FETCH_CASE_HEADERS_FAIL:
        return { error: true };

    case USER_CONTEXT_CHANGED:
    case types.FETCH_CASE_DETAIL_FAIL:
    case types.FETCH_CASE_COMMENTS_FAIL:
    case types.FETCH_CASE_ATTACHMENTS_FAIL:
        return initialState;

    default:
        return state;
    }

    return state;
}

// Selectors
const baseCasesSelector = state => state.supportTab.cases;

export const fetchCasesFailedSelector = (state) => {
    const cases = baseCasesSelector(state);
    return !!(cases && cases.error);
};

export const caseListSelector = createSelector(baseCasesSelector, fetchCasesFailedSelector, (cases) => {
    let caseList = null;
    if (cases.loaded) {
        caseList = values(cases.raw);
    }

    return {
        error: cases.error,
        loaded: cases.loaded,
        raw: cases.raw,
        value: caseList,
    };
});
