import { USER_CONTEXT_CHANGED } from '../user/constants';

import {
    ADD_GLOBAL_NOTIFICATION,
    REMOVE_GLOBAL_NOTIFICATION,
    DISMISS_GLOBAL_NOTIFICATION,
} from './actionCreators';

const initialState = {};

const ACTION_HANDLERS = {
    [ADD_GLOBAL_NOTIFICATION]: (state, { severity, content, id }) => ({
        ...state,
        [id]: { id, severity, content },
    }),
    [DISMISS_GLOBAL_NOTIFICATION]: (state, { id }) => {
        const newState = { ...state };
        delete newState[id];
        return newState;
    },
    [REMOVE_GLOBAL_NOTIFICATION]: (state, { id }) => {
        const newState = { ...state };
        delete newState[id];
        return newState;
    },
    [USER_CONTEXT_CHANGED]: () => (initialState),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

// Selectors
export const notificationsSelector = state => Object.values(state);
