import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import ReactDimensions from 'react-dimensions';
import { isNumber, findLastIndex, uniqueId, property } from 'underscore';
import numeral from 'numeral';
import NoData from '@infosight/elmer/dist/components/NoData';
import mergeSharedTooltip, { Timestamp } from '@infosight/elmer/dist/charts/mergeSharedTooltip';
import { DAY_MOMENT_FORMAT } from '@infosight/elmer/dist/utils/time';
import { getUnitPrefix, convertBytes, BINARY_BASE } from '@infosight/elmer/dist/utils/bytes';
import { moveCrosshair } from '../../../../../../modules/charts/util/synchronization';
import { xAxisDataRange } from '../../../../../charts/mixins/xAxis';
import { dateTimeAxisSnippet } from '../../../../../charts/mixins/dateTimeAxis';
import { defaultGetCorrespondingPoints, truncateTime } from './util';
import { CHART_COLORS } from '../../../../../../util/chartColors';
import { FORMAT_BYTE_PRECISION, FORMAT_NUMBER_PRECISION } from '../../../../../../util/formatter/constants';

const FORECAST_RANGE_SERIES = 'forecastRangeSeries';

const getPerformanceDegradationBytes = bytes => (isNumber(bytes) ? bytes * 0.9 : bytes);

const ForecastChart = mergeSharedTooltip({
    valueFormatter(point) {
        if (point.series.options.id === FORECAST_RANGE_SERIES) {
            return `${numeral(point.point.low).format(FORMAT_BYTE_PRECISION)} - ${numeral(point.point.high).format(FORMAT_BYTE_PRECISION)}`;
        }
        return numeral(point.y).format(FORMAT_BYTE_PRECISION);
    },
    xValueFormatter(point) {
        return Timestamp(point.x, DAY_MOMENT_FORMAT);
    },
});

class OverviewCapacityChart extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.containerElementId = uniqueId(this.constructor.name);
    }

    componentDidMount() {
        if (this.props.chartSync) {
            this.props.chartSync.monitorChart(this, { syncPoints: this.syncPoints, resetPoints: this.resetPoints });
        }
    }

    componentWillUnmount() {
        const container = document.getElementById(this.containerElementId);
        if (!container) {
            return;
        }

        container.removeEventListener('mouseleave', this.onMouseOut);
    }

    onAfterChartRendered(chart) {
        this.chart = chart;
        const container = document.getElementById(this.containerElementId);
        if (!container) {
            return;
        }

        container.addEventListener('mouseleave', this.onMouseOut);
    }

    onMouseOut(event) {
        if (this.props.chartSync) {
            this.props.chartSync.syncPoints({ event });
        } else {
            this.resetPoints();
        }
    }

    onMouseOverPoint(opts) {
        if (!opts.point) {
            return;
        }
        if (this.props.chartSync) {
            this.props.chartSync.syncPoints(opts);
        } else {
            this.syncPoints(opts.point);
        }
    }

    getYAxis() {
        const { poolCapacity } = this.props;
        const timeSeries = poolCapacity.data.timeseries;
        const maxTotalCapacity = this.getMaxTotalCapacity(timeSeries);
        const storageUnit = getUnitPrefix(maxTotalCapacity, BINARY_BASE);
        const displayUnit = storageUnit ? `${storageUnit}iB` : '';
        return {
            title: {
                enabled: false,
            },
            labels: {
                formatter() {
                    return `${numeral(convertBytes(this.value, storageUnit)).format(FORMAT_NUMBER_PRECISION)} ${displayUnit}`;
                },
            },
        };
    }

    getChartConfig() {
        const { chartRangeStart, chartRangeEnd } = this.props;

        const series = [this.allocatedCapacitySeries(),
            this.totalUsageSeries(),
            this.performanceDegradationCapacitySeries(),
            this.usageRangeForecastSeries(),
            this.usageForecastSeries(),
        ];

        return ({
            chart: {
                height: this.props.containerHeight,
                spacingTop: 20,
                zoomType: null,
            },
            title: false,
            xAxis: {
                ...dateTimeAxisSnippet(),
                ...xAxisDataRange(chartRangeStart, chartRangeEnd),
            },
            yAxis: this.getYAxis(),
            series,
            plotOptions: {
                line: {
                    marker: {
                        enabled: false,
                        symbol: 'circle',
                        states: {
                            hover: {
                                enabled: true,
                            },
                        },
                    },
                },
                series: {
                    connectNulls: true,
                },
            },
        });
    }

    getMaxTotalCapacity(timeSeries) {
        const { getTotalCapacity } = this.props;
        const sizes = timeSeries.map(getTotalCapacity);
        return Math.max.apply(null, sizes);
    }

    usageForecastSeries() {
        const thisComponent = this;
        const { poolCapacity } = this.props;
        const timeSeries = poolCapacity.data.timeseries;
        return {
            type: 'line',
            id: 'forecastedUsage',
            name: 'Forecasted Pool Usage',
            color: CHART_COLORS.blue,
            dashStyle: 'shortdash',
            data: timeSeries.map(val => ({
                x: truncateTime(val.timestamp),
                y: val.forecastedUsageBytes,
            })),
            point: {
                events: {
                    mouseOver(event) {
                        if (thisComponent.lastPointHovered !== this) {
                            thisComponent.lastPointHovered = this;
                            thisComponent.onMouseOverPoint({ point: this, event });
                        }
                    },
                },
            },
        };
    }

    usageRangeForecastSeries() {
        const thisComponent = this;
        const { poolCapacity } = this.props;
        const timeSeries = poolCapacity.data.timeseries;
        return {
            type: 'arearange',
            id: FORECAST_RANGE_SERIES,
            name: 'Forecasted Range',
            color: CHART_COLORS.blue,
            linkedTo: 'forecastedUsage',
            data: timeSeries.map(val => ({
                x: truncateTime(val.timestamp),
                low: val.forecastedLowerBoundBytes,
                high: val.forecastedUpperBoundBytes,
            })),
            lineWidth: 0,
            fillOpacity: 0.3,
            zIndex: 0,
            point: {
                events: {
                    mouseOver(event) {
                        if (thisComponent.lastPointHovered !== this) {
                            thisComponent.lastPointHovered = this;
                            thisComponent.onMouseOverPoint({ point: this, event });
                        }
                    },
                },
            },
        };
    }

    allocatedCapacitySeries() {
        const thisComponent = this;
        const { poolCapacity } = this.props;
        const timeSeries = poolCapacity.data.timeseries;
        // Reverse search the data array for the last actual data point ( data point just before the forecast starts)
        const lastActualDataPoint = timeSeries[findLastIndex(timeSeries, { dataSource: 'Actual' })];
        return {
            type: 'line',
            id: 'Capacity',
            name: 'Capacity',
            color: CHART_COLORS.redOrange,
            lineWidth: 2,
            data: timeSeries.map(val => ({
                x: truncateTime(val.timestamp),
                // Its possible that the data has only forecast data ie., there is not actual data
                y: isNumber(val.allocatedBytes) ? val.allocatedBytes : lastActualDataPoint && lastActualDataPoint.allocatedBytes,
            })),
            step: true,
            point: {
                events: {
                    mouseOver(event) {
                        if (thisComponent.lastPointHovered !== this) {
                            thisComponent.lastPointHovered = this;
                            thisComponent.onMouseOverPoint({ point: this, event });
                        }
                    },
                },
            },
        };
    }

    totalUsageSeries() {
        const thisComponent = this;
        const { poolCapacity } = this.props;
        const timeSeries = poolCapacity.data.timeseries;
        return {
            type: 'line',
            id: 'totalusage',
            name: 'Total Usage',
            color: CHART_COLORS.blue,
            fillColor: CHART_COLORS.blueLight,
            data: timeSeries.map(val => ({
                x: truncateTime(val.timestamp),
                y: val.usedBytes,
            })),
            point: {
                events: {
                    mouseOver(event) {
                        if (thisComponent.lastPointHovered !== this) {
                            thisComponent.lastPointHovered = this;
                            thisComponent.onMouseOverPoint({ point: this, event });
                        }
                    },
                },
            },
        };
    }

    performanceDegradationCapacitySeries() {
        const thisComponent = this;
        const { poolCapacity } = this.props;
        const timeSeries = poolCapacity.data.timeseries;
        // Reverse search the data array for the last actaul data point ( data point just before the forecast starts)
        const lastActualDataPoint = timeSeries[findLastIndex(timeSeries, { dataSource: 'Actual' })];
        return {
            type: 'line',
            dashStyle: 'dot',
            name: 'Performance Degradation Threshold (90%)',
            color: CHART_COLORS.redOrange,
            lineWidth: 2,
            data: timeSeries.map(val => ({
                x: truncateTime(val.timestamp),
                // Its possible that the data has only forecast data ie., there is not actual data
                y: val.allocatedBytes ? getPerformanceDegradationBytes(val.allocatedBytes) :
                    lastActualDataPoint && getPerformanceDegradationBytes(lastActualDataPoint.allocatedBytes),
            })),
            step: true,
            point: {
                events: {
                    mouseOver(event) {
                        if (thisComponent.lastPointHovered !== this) {
                            thisComponent.lastPointHovered = this;
                            thisComponent.onMouseOverPoint({ point: this, event });
                        }
                    },
                },
            },
        };
    }

    syncPoints(point) {
        const hoverPoints = this.props.getCorrespondingPoints(point, this.chart);
        if (this.chart.legend) {
            this.chart.legend.group.hide();
            this.chart.legend.display = false;
        }
        moveCrosshair(hoverPoints, this.chart);
    }

    resetPoints() {
        if (this.chart) {
            // Remove markers from any active points
            if (this.chart.hoverPoints) {
                this.chart.hoverPoints.forEach(point => point.setState());
            }

            if (this.chart.xAxis) {
                this.chart.xAxis[0].hideCrosshair();
                this.chart.tooltip.hide();
            }

            if (this.chart.legend) {
                this.chart.legend.group.show();
                this.chart.legend.display = true;
            }
        }
    }


    render() {
        const { poolCapacity } = this.props;
        const hasData = poolCapacity && poolCapacity.data
            && poolCapacity.data.timeseries && poolCapacity.data.timeseries.length > 0;
        return (
            <NoData hasData={hasData} message="No capacity history data available.">
                {() => (
                    <ForecastChart config={this.getChartConfig()} callback={this.onAfterChartRendered} domProps={{ id: this.containerElementId }} />
                )}
            </NoData>
        );
    }
}

OverviewCapacityChart.propTypes = {
    poolCapacity: PropTypes.shape({
        data: PropTypes.shape({
            timeseries: PropTypes.arrayOf(PropTypes.shape({
                timestamp: PropTypes.oneOfType([PropTypes.number, PropTypes.object]),
                usedBytes: PropTypes.number,
                allocatedBytes: PropTypes.number,
                totalSavingsBytes: PropTypes.number,
                forecastedUsageBytes: PropTypes.number,
                forecastedLowerBoundBytes: PropTypes.number,
                forecastedUpperBoundBytes: PropTypes.number,
            })).isRequired,
        }).isRequired,
        meta: PropTypes.shape({
            time: PropTypes.shape({
                start: PropTypes.number.isRequired,
                end: PropTypes.number.isRequired,
            }).isRequired,
        }).isRequired,
    }),

    /**
     * Get all the values at a timestamp that should be displayed in the legend when the user hovers over a point.
     * This function is allowed to have synchronous side effects, such as updating crosshairs.
     * @param point {Highcharts.Point}
     * @param chart {Highcharts.Chart}
     * @param event {Event}
     * @return {pointValues, hoverPoints}
     *      {Object} pointValues Each key should be the series id. The value is {color, value}
     *      {Array<Highcharts.Point>} hoverPoints
     */
    getCorrespondingPoints: PropTypes.func,

    /**
     * Wrap this element in a `<SynchronizedChart>` element to inject this prop.
     */
    chartSync: PropTypes.shape({
        monitorChart: PropTypes.func.isRequired,
        removeChart: PropTypes.func.isRequired,
        syncPoints: PropTypes.func.isRequired,
    }),

    getTotalCapacity: PropTypes.func,

    /**
     *
     * @ignore
     */
    containerHeight: PropTypes.number.isRequired,

    /**
     * Starting timestamp where the chart begins
     * moment or number
     */
    chartRangeStart: PropTypes.oneOfType([PropTypes.object, PropTypes.number]),

    /**
     * Last timestamp where the chart ends
     * moment or number
     */
    chartRangeEnd: PropTypes.oneOfType([PropTypes.object, PropTypes.number]),
};

OverviewCapacityChart.defaultProps = {
    containerHeight: 0,
    getCorrespondingPoints: defaultGetCorrespondingPoints,
    getTotalCapacity: property('allocatedBytes'),
};

export default ReactDimensions({ elementResize: true })(OverviewCapacityChart);
