import React from 'react';
import PropTypes from 'prop-types';
import { property } from 'underscore';
import moment from 'moment-timezone';
import CapacityTimeSeries, { FORECAST_RANGE_SERIES } from '../../../../../../charts/CapacityTimeSeries';
import chartOptions from './capacityChartOptions';

const CapacityChart = ({ poolCapacity, includeSavingsSeries, isLineChart, onChartSelect }) => (
    <CapacityTimeSeries
        timeSeries={poolCapacity.data.timeseries}
        chartRangeStart={moment(poolCapacity.meta.time.dataStart)}
        chartRangeEnd={moment(poolCapacity.meta.time.dataEnd)}
        getTotalCapacity={property('allocatedBytes')}
        getForecastedCapacity={property('forecastedUsageBytes')}
        chartConfig={chartOptions({
            timeseries: poolCapacity.data.timeseries,
            forecastRangeId: FORECAST_RANGE_SERIES,
            includeSavingsSeries,
            isLineChart,
        })}
        forecastChart
        onChartSelect={onChartSelect}
    />
);

CapacityChart.propTypes = {
    poolCapacity: PropTypes.shape({
        data: PropTypes.shape({
            timeseries: PropTypes.arrayOf(PropTypes.shape({
                timestamp: PropTypes.oneOfType([PropTypes.number, PropTypes.object]),
                usedBytes: PropTypes.number,
                allocatedBytes: PropTypes.number,
                totalSavingsBytes: PropTypes.number,
                forecastedUsageBytes: PropTypes.number,
                forecastedLowerBoundBytes: PropTypes.number,
                forecastedUpperBoundBytes: PropTypes.number,
            })).isRequired,
        }).isRequired,
        meta: PropTypes.shape({
            time: PropTypes.shape({
                start: PropTypes.number.isRequired,
                end: PropTypes.number.isRequired,
            }).isRequired,
        }).isRequired,
    }),
    isLineChart: PropTypes.bool,
    includeSavingsSeries: PropTypes.bool,
    onChartSelect: PropTypes.func,
};

export default CapacityChart;
