import { getStore } from '../../../util/storeProvider';
import { nimbleTotalSelector } from '../inventory/reducer';
import { nimbleTenantAdminAccess, AUTHORITIES, PRIVILEGES, PERMISSION_TARGET, nimbleTenantPrimaryAccess } from '../../user/constants';
import { permissionsSelector, tenantSelector, AuthorizationEvaluator } from '../../user';

/**
 * This is a "static class" that should encapsulate the complex conditions that define Nimble users.
 * Each function should synchronously return a value.
 * Functions should all handle any null refs, since they can be run even for users with no nimble products.
 */
const NimbleUserTenantHelpers = {
    isReseller() {
        return AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.ATTRIBUTE_NIMBLE_RESELLER } });
    },

    isExternal() {
        return AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.REALM_EXTERNAL } });
    },

    isInternal() {
        return AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.REALM_INTERNAL } });
    },

    ownsSelectedTenant() {
        return AuthorizationEvaluator.evaluate({ access: nimbleTenantAdminAccess });
    },

    isPrimaryTenant() {
        return AuthorizationEvaluator.evaluate({ access: nimbleTenantPrimaryAccess });
    },

    hasArrays() {
        try {
            return !!nimbleTotalSelector(getStore().getState());
        } catch (ex) {
            return false;
        }
    },

    getAuthorizedTenantCount() {
        try {
            const permissions = permissionsSelector(getStore().getState());
            if (!permissions) {
                return 0;
            }

            let count = 0;
            for (let i = 0; i < permissions.length; i += 1) {
                const { privilege, targetType, targetId } = permissions[i];
                if (privilege === PRIVILEGES.ACCESS && targetType === PERMISSION_TARGET.NIMBLE_TENANT) {
                    if (targetId === PERMISSION_TARGET.GLOBAL_TARGET_ID) {
                        return Infinity;
                    }

                    count += 1;
                }
            }

            return count;
        } catch (ex) {
            return 0;
        }
    },

    canAccessMultipleTenants() {
        return NimbleUserTenantHelpers.getAuthorizedTenantCount() > 1;
    },

    getCurrentTenant() {
        const tenant = tenantSelector(getStore().getState());
        return tenant ? tenant.tenant : null;
    },

    currentTenantIsNimbleTenant() {
        return !!this.getCurrentTenant();
    },

    /**
     * Useful for debugging
     */
    toJSON() {
        return Object.entries(NimbleUserTenantHelpers).reduce((json, [key, value]) => {
            if (key !== NimbleUserTenantHelpers.toJSON.name) {
                json[key] = value();
            }

            return json;
        });
    },
};

export default NimbleUserTenantHelpers;
