import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import PinnedIcon from 'react-icons/lib/fa/thumb-tack';
import CheckIcon from 'react-icons/lib/fa/check-circle';
import XIcon from 'react-icons/lib/fa/times-circle';
import EncryptedIcon from 'react-icons/lib/fa/lock';
import { IconAdaptor } from '@infosight/elmer/dist/components/IconAdaptor';
import styles from './style.scss';

const cx = classnames.bind(styles);

const leftSpace = {
    paddingLeft: '6px',
};

const VolumeStatus = ({ volumePinned, volumeEncrypted }) => (
    <div className="flex-container">
        <div className="row">
            <div
                className={cx(volumeEncrypted ? null : 'off')}
                title={volumeEncrypted ? 'encrypted' : 'not encrypted'}
            >
                <IconAdaptor size={20} type="svg"><EncryptedIcon /></IconAdaptor>
                <span className={cx('superscript', cx(volumeEncrypted ? null : 'off'))}>
                    {volumeEncrypted && <IconAdaptor size={14} type="svg"><CheckIcon /></IconAdaptor>}
                    {!volumeEncrypted && <IconAdaptor size={14} type="svg"><XIcon /></IconAdaptor>}
                </span>
            </div>
            <div
                className={cx(volumePinned ? null : 'off')}
                title={volumePinned ? 'pinned' : 'not pinned'}
                style={leftSpace}
            >
                <IconAdaptor size={20} type="svg"><PinnedIcon /></IconAdaptor>
                <span className={cx('superscript', cx(volumePinned ? null : 'off'))}>
                    {volumePinned && <IconAdaptor size={14} type="svg"><CheckIcon /></IconAdaptor>}
                    {!volumePinned && <IconAdaptor size={14} type="svg"><XIcon /></IconAdaptor>}
                </span>
            </div>
        </div>
    </div>);


VolumeStatus.propTypes = {
    volumePinned: PropTypes.bool,
    volumeEncrypted: PropTypes.bool,
};

export default VolumeStatus;
