import { createSelector } from 'reselect';
import { last, compact } from 'underscore';

export const poolContextSelector = state => state.capacityReport.poolContext;

export const isLoadingSelector = (state) => {
    const { loadingTimeSeries, loadingFolders, loadingApplications, loadingSummary } = poolContextSelector(state);
    return loadingTimeSeries || loadingApplications || loadingFolders || loadingSummary;
};

const aliasPoolName = (groupName, groupLeaderHostName, groupLeaderSerialNumber) => {
    const DEFAULT = 'default';
    if (groupName && groupName !== DEFAULT) {
        return groupName;
    } else if (groupLeaderHostName && groupLeaderHostName !== DEFAULT) {
        return groupLeaderHostName;
    }

    return groupLeaderSerialNumber;
};

const stateTopPoolsTimeSeriesSelector = state => poolContextSelector(state).topPoolsTimeSeries;
export const topPoolsSelector = createSelector(
    stateTopPoolsTimeSeriesSelector,
    (topPools) => {
        if (!topPools) {
            return undefined;
        }
        const translated = Object.values(topPools)
            .map((timeSeries) => {
                let pool;
                if (timeSeries[0]['Pool UID'] === '0') {
                    pool = {
                        'Pool Name': 'Other Pools',
                    };
                } else {
                    [pool] = timeSeries;
                }

                if (!pool) {
                    return undefined;
                }

                let poolName = pool['Pool Name'];
                if (poolName === 'default') {
                    poolName += ` in ${aliasPoolName(pool.groupName, pool.arrayHostName, pool.arraySn)}`;
                }

                return {
                    timeSeries,
                    poolName,
                    poolUid: pool['Pool UID'],
                    currentUsage: last(timeSeries).used,
                };
            });
        return compact(translated)
            .sort((a, b) => (a.currentUsage < b.currentUsage || a.poolUid === 0 ? 1 : -1));
    }
);
