var Backbone = require('backbone');

var supported = function (release) {
    return release.displayStatus !== 'End of Software Support';
};

module.exports = Backbone.Model.extend({

    parse: function (release) {
        release.shortVersion = release.version.split('-')[0];
        if (!release.displayStatus) {
            release.displayStatus = release.status.description;
        }

        release.supported = supported(release);
        return release;
    },

});
