const _ = require('underscore');
var Backbone = require('backbone');

module.exports = Backbone.Collection.extend({

    initialize: function (models, options) {
        this.options = options;
    },

    url: function () {
        let url = this.api()
            .pod().account()
            .segment('pools')
            .segment(this.options.poolUid)
            .segment('performance/coral')
            .addQuery(this.options.rangeModel.getQueryParams());
        if (this.options.arrayModel.get('serialNumber')) {
            url.addQuery('serialNumber', this.options.arrayModel.get('serialNumber'));
        }
        return url;
    },

    calculateAverages: function (metrics) {
        var length = this.length;
        return this
            .chain()
            .reduce(function (memo, point) {
                metrics.forEach(function (metric) {
                    memo[metric] += point.get(metric);
                });
                return memo;
            }, _(metrics).object(_.times(metrics.length, _.constant(0))))
            .mapObject(function (sum) {
                return sum / length;
            })
            .value();
    },

    calculateMaximums: function (metrics) {
        return this
            .reduce(function (memo, point) {
                metrics.forEach(function (metric) {
                    memo[metric] = Math.max(memo[metric], point.get(metric));
                });
                return memo;
            }, _(metrics).object(_.times(metrics.length, _.constant(0))));
    },

    getSeries: function (metric) {
        return this.map(function (point) {
            return {
                x: point.get('timestamp'),
                y: point.get(metric),
            };
        });
    },

    parse: function (points) {
        for (var i = 0; i < points.length; i++) {
            var point = points[i];
            point.iopsTotal = point.iopsRead + point.iopsWrite;
            point.latencyTotal = (point.latencyRead * point.iopsRead + point.latencyWrite * point.iopsWrite) / point.iopsTotal;
            point.mbpsTotal = point.mbpsRandomRead + point.mbpsRandomWrite + point.mbpsSequentialRead + point.mbpsSequentialWrite;
        }
        return points;
    },

});
