const App = require('app/Application');
const _ = require('underscore');
const Backbone = require('backbone');
const { API_BASE_URL } = require('progeny/config');

var URI = require('urijs');
var Marionette = require('backbone.marionette');

var ALL_ACCOUNTS_ID = '~';

var Api = _.bind(Marionette.extend, URI)({

    constructor: function Api() {
        Api.__super__.constructor.call(this, API_BASE_URL);
    },

    /**
     * Construct a URL for the account resource.
     * Will dynamically build the proper URL for an "own-account" vs. a "cross-account" access.
     *
     * @return {Api}  An Api object that can be further appended to
     */
    account: function () {
        var accountSelection = App.request('global', 'get:account:selection');
        if (accountSelection.isPrimary() || (accountSelection.isSingle() && accountSelection.getAccount().id == null)) {
            return this.segment('account');
        } else if (accountSelection.isSingle()) {
            return this.segment('accounts').segment(accountSelection.getAccount().id);
        } else {
            console.assert(accountSelection.isAll());
            return this.segment('accounts').segment(ALL_ACCOUNTS_ID);
        }
    },

    /**
     * Construct a URL for pod resource that is managed in pod app
     */
    pod: function () {
        var accountSelection = App.request('global', 'get:account:selection');
        this.addQuery({ podLocator: `Nimble.Tenant.${accountSelection.getAccount().id}` });

        return this.segment('pod');
    },

    /**
     * Construct a URL for central resource that is managed in central app
     */
    central: function () {
        return this.segment('central');
    },

    /**
     * Construct a URL for an array resource
     */
    array: function (arraySn) {
        return this
            .pod()
            .segment('arrays')
            .segment(arraySn);
    },

    user: function () {
        return this.segment('user');
    },

    /**
     * Delegates to String.replace() on the string representation of this URL
     * This allows us to return an Api object from Backbone.Collection.url method (instead of having
     * to call toString() each time) and still have Backbone URL processing work.
     */
    replace: function () {
        var urlString = this.toString();
        return urlString.replace.apply(urlString, arguments);
    },

}, {
    ALL_ACCOUNTS_ID: ALL_ACCOUNTS_ID,
});

var newApi = function () {
    var api = new Api();
    this.trigger('api:create', api);
    return api;
};

// Extend the Backbone.Model and Backbone.Collection prototypes with the newApi function
// This allows Model/Collection mixins to listen for the 'api:create' event to decorate the api.

_.extend(Backbone.Model.prototype, { api: newApi });
_.extend(Backbone.Collection.prototype, { api: newApi });
_.extend(Backbone.View.prototype, { api: newApi });

module.exports = Api;
