const _ = require('underscore');
var Backbone = require('backbone');
var Marionette = require('backbone.marionette');
var numeral = require('numeral');
var chartColors = require('./ChartColors');
var tpl = require('raw-loader!./MbpsChart.html');

var METRICS = [
    {
        id: 'mbpsSequentialRead',
        name: 'Seq Read',
        color: chartColors.read.lighten().toHexString(),
    },
    {
        id: 'mbpsRandomRead',
        name: 'Rand Read',
        color: chartColors.read.darken().toHexString(),
    },
    {
        id: 'mbpsSequentialWrite',
        name: 'Seq Write',
        color: chartColors.write.lighten().toHexString(),
    },
    {
        id: 'mbpsRandomWrite',
        name: 'Rand Write',
        color: chartColors.write.darken().toHexString(),
    },
];

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    initialize: function (options) {
        this.chartDefaults = options.chartDefaults;
        var metricKeys = _(METRICS).pluck('id');
        var maxMbps = _.max(this.collection.calculateMaximums(metricKeys));
        var averages = this.collection.calculateAverages(metricKeys);

        var metrics = ['Read', 'Write'].map(function (type) {
            var totalAverage = 0;
            var totalRatio = 0;
            var highestRatio = 0;

            var values = ['mbpsSequential', 'mbpsRandom'].map(function (metricPrefix) {
                var METRIC = _(METRICS).findWhere({ id: metricPrefix + type });
                var average = averages[METRIC.id];
                var ratio = average / maxMbps;

                totalAverage += average;
                totalRatio += ratio;

                // keep track of highest ratio because when totalRatio goes above 1
                // the bars become stacked and the label will be displayed too far to
                // the right of the bar. In this case, the percentage should be
                // based off of the highest ratio value.
                if (ratio > highestRatio) {
                    highestRatio = ratio;
                }

                if (totalRatio > 1) {
                    totalRatio = highestRatio;
                }

                return _({
                    average: numeral(average).format('0.0'),
                    percent: numeral(ratio).format('0%'),
                    type: metricPrefix.slice(4),
                }).extend(METRIC);
            });

            return {
                type: type,
                values: values,
                average: numeral(totalAverage).format('0.0'),
                percent: numeral(totalRatio).format('0%'),
            };
        });

        this.model = new Backbone.Model({ metrics: metrics });
    },

    onRender: function () {
        var collection = this.collection;
        this.$('[data-chart="mbps"]').highcharts('StockChart', _(this.chartDefaults()).extend({
            chart: _(this.chartDefaults(this).chart).extend({
                type: 'area',
            }),
            series: METRICS.map(function (metric) {
                return _(metric).extend({
                    data: collection.getSeries(metric.id),
                });
            }),
            yAxis: _(this.chartDefaults(this).yAxis).extend({
                title: {
                    text: 'MiB/sec',
                },
            }),
        }));

        this.$('[data-toggle="popover"]').popover();
    },

});
