/* eslint max-len: 0 */
import React from 'react';
import PropTypes from 'prop-types';
import { COLORS, DEFAULT_ICON_HEIGHT, DEFAULT_ICON_WIDTH } from './constants';

const ArrayController = ({ className, width = DEFAULT_ICON_WIDTH, height = DEFAULT_ICON_HEIGHT, status }) => {
    const fillColor = status ? COLORS.NORMAL : COLORS.ERROR;
    return (
        <svg viewBox="0 0 90 90" width={width} height={height} className={className}>
            <circle cx="45" cy="45" r="45" fill={fillColor} />
        </svg>
    );
};

ArrayController.propTypes = {
    /**
     * True - If the Controller is working fine
     * False - If there is any issue
     */
    status: PropTypes.bool,
    height: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

export default ArrayController;
