export const INFO_NOT_AVAILABLE = 'N/A';
export const UNKNOWN = 'Unknown';

/* List of Formats supported accross the App */
// Formats : Data
export const FORMAT_BYTE_TYPE = 'ib';
export const FORMAT_BYTE_NO_PRECISION = `0 ${FORMAT_BYTE_TYPE}`;
export const FORMAT_BYTE_PRECISION = `0.0 ${FORMAT_BYTE_TYPE}`;

// Data formatted in base 10 for Physical Hardware
export const FORMAT_BYTE_10_TYPE = 'b';
export const FORMAT_BYTE_10_NO_PRECISION = `0 ${FORMAT_BYTE_10_TYPE}`;
export const FORMAT_BYTE_10_PRECISION = `0.0 ${FORMAT_BYTE_10_TYPE}`;

// Formats : Number
export const FORMAT_NUMBER_NO_PRECISION = '0,0';
export const FORMAT_NUMBER_PRECISION = '0,0.0';
export const FORMAT_NUMBER_EXTRA_PRECISION = '0,0.00';

// Formats: Number Abbreviation ( Ex: 1.2m, 2.4k)
// TODO: Add numeral wrapper to change the abbreviations to Uppercase
export const FORMAT_NUMBER_ABBR = '0.0a';

// Formats: PERCENT
export const FORMAT_PERCENT_PRECISION = '0.0 %';

// Formats: momentjs date
export const FORMAT_MOMENT_CALENDAR_DAY = 'l';

// momentjs date time
export const FORMAT_MOMENT_DATE_TIME = 'L LTS';

// Use this if the input Timezone is described as a string ( Ex: 'America/Los_Angeles')
export const FORMAT_MOMENT_DATE_TIME_TIMEZONE_FROM_DESC = `${FORMAT_MOMENT_DATE_TIME} z`;

// Units
// Units: Latency (milliseconds)
export const UNIT_LATENCY_ABBR = 'ms';

// Units: IOPS
export const UNIT_IOPS_ABBR = 'IO/sec';
