import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { formatValue } from '../../../../../util/formatter';
import { FORMAT_BYTE_10_PRECISION } from '../../../../../util/formatter/constants';
import styles from './hardwareRecommendations.scss';

const cx = classnames.bind(styles);

const getCapacityRecommendation = (arrayHardwareUpgradeRecommendations) => {
    const {
        arrayType,
        addAfsShelf,
        recommendCacheUpgrade,
        recommendDiskUpgrade,
        currentHddExpansionShelves,
        recommendedHddExpansionShelves,
    } = arrayHardwareUpgradeRecommendations;

    if (recommendCacheUpgrade && arrayType === 'all-flash') {
        if (addAfsShelf) {
            return '+1 shelf';
        }
        return 'Populate Bank B';
    } else if (recommendDiskUpgrade) {
        const additionalShelves = recommendedHddExpansionShelves - currentHddExpansionShelves;
        return `+${additionalShelves} ${additionalShelves > 1 ? 'shelves' : 'shelf'}`;
    }

    return null;
};

// ported from legacy: clean later
const getUpgradeNotes = (arrayHardwareUpgradeRecommendations) => {
    const {
        recommendScaleOut,
        recommendCPUUpgrade,
        recommendCacheUpgrade,
        recommendDiskUpgrade,
        currentController,
        recommendedController,
    } = arrayHardwareUpgradeRecommendations;

    const recommendedComponents = [];
    let componentsString = '';

    if (recommendDiskUpgrade) {
        recommendedComponents.push('capacity');
    }

    if (recommendCPUUpgrade) {
        recommendedComponents.push('cpu');
    }

    if (recommendCacheUpgrade) {
        recommendedComponents.push('cache');
    }

    // make the recommended components string readable
    for (let i = 0; i < recommendedComponents.length; i += 1) {
        componentsString += recommendedComponents[i];

        if (i < recommendedComponents.length - 2) {
            componentsString += ', ';
        }
        if (i < recommendedComponents.length - 1) {
            componentsString += ' and ';
        }
    }

    // set the appropriate upgrade notes
    if (recommendScaleOut) {
        return `Scale-Out is recommended based on your ${componentsString} needs.`;
    } else if (!recommendCPUUpgrade && currentController !== recommendedController) {
        return `A controller upgrade is required to support the recommended ${componentsString} upgrades`;
    }

    return null;
};

const HardwareRecommendations = ({ arrayHardwareUpgradeRecommendations }) => {
    if (!arrayHardwareUpgradeRecommendations) {
        return null;
    }
    const {
        arrayType,
        currentController,
        currentCacheSystemTotalBytes,
        currentCacheHeadBytes,
        currentCacheTotalAfsBytes,
        currentCacheExpNonAfsBytes,
        currentHddBytes,
        recommendCPUUpgrade,
        recommendCacheUpgrade,
        recommendScaleOut,
        recommendedController,
        recommendCacheSystemTotalBytes,
        recommendedCacheHeadBytes,
        recommendedCacheTotalAfsBytes,
        recommendedCacheExpNonAfsBytes,
    } = arrayHardwareUpgradeRecommendations;
    const upgradeNotes = getUpgradeNotes(arrayHardwareUpgradeRecommendations);
    return (
        <div className="card card-no-border card-no-background">
            <div className="elmer-grid-view">
                <table>
                    <thead>
                        <tr>
                            <th>
                                {''}
                            </th>
                            <th>
                                Current
                            </th>
                            <th>
                                Recommended
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>
                                Controller
                            </td>
                            <td>
                                {currentController}
                            </td>
                            <td>
                                <strong>
                                    {!recommendScaleOut && recommendCPUUpgrade && recommendedController !== currentController ? recommendedController : null}
                                </strong>
                            </td>
                        </tr>
                        { arrayType !== 'all-flash' &&
                        <React.Fragment>
                            <tr className={cx('cacheRow')}>
                                <td>
                                    Cache
                                </td>
                                <td>
                                    {''}
                                </td>
                                <td>
                                    {''}
                                </td>
                            </tr>
                            <tr className={cx('cacheValueRow')}>
                                <td>
                                    &nbsp;&nbsp;&nbsp;&nbsp;Head Shelf
                                </td>
                                <td>
                                    {formatValue(currentCacheHeadBytes, FORMAT_BYTE_10_PRECISION)}
                                </td>
                                <td>
                                    <strong>
                                        {!recommendScaleOut && recommendCacheUpgrade && recommendedCacheHeadBytes ?
                                            formatValue(recommendedCacheHeadBytes, FORMAT_BYTE_10_PRECISION) : null}
                                    </strong>
                                </td>
                            </tr>
                            <tr className={cx('cacheValueRow')}>
                                <td>
                                    &nbsp;&nbsp;&nbsp;&nbsp;All-Flash Shelf
                                </td>
                                <td>
                                    {formatValue(currentCacheTotalAfsBytes, FORMAT_BYTE_10_PRECISION)}
                                </td>
                                <td>
                                    <strong>
                                        {!recommendScaleOut && recommendCacheUpgrade && recommendedCacheTotalAfsBytes ?
                                            formatValue(recommendedCacheTotalAfsBytes, FORMAT_BYTE_10_PRECISION) : null}
                                    </strong>
                                </td>
                            </tr>
                            <tr className={cx('cacheValueRow')}>
                                <td>
                                    &nbsp;&nbsp;&nbsp;&nbsp;HDD Shelf
                                </td>
                                <td>
                                    {formatValue(currentCacheExpNonAfsBytes, FORMAT_BYTE_10_PRECISION)}
                                </td>
                                <td>
                                    <strong>
                                        {!recommendScaleOut && recommendCacheUpgrade && recommendedCacheExpNonAfsBytes ?
                                            formatValue(recommendedCacheExpNonAfsBytes, FORMAT_BYTE_10_PRECISION) : null}
                                    </strong>
                                </td>
                            </tr>
                            <tr className={cx('cacheValueRow')}>
                                <td>
                                    &nbsp;&nbsp;&nbsp;&nbsp;Total
                                </td>
                                <td>
                                    {formatValue(currentCacheSystemTotalBytes, FORMAT_BYTE_10_PRECISION)}
                                </td>
                                <td>
                                    <strong>
                                        {!recommendScaleOut && recommendCacheUpgrade && recommendCacheSystemTotalBytes ?
                                            formatValue(recommendCacheSystemTotalBytes, FORMAT_BYTE_10_PRECISION) : null}
                                    </strong>
                                </td>
                            </tr>
                        </React.Fragment>
                        }
                        <tr>
                            <td>
                                Capacity
                            </td>
                            <td>
                                {arrayType !== 'all-flash' ? formatValue(currentHddBytes, FORMAT_BYTE_10_PRECISION) :
                                    formatValue(currentCacheSystemTotalBytes, FORMAT_BYTE_10_PRECISION)}
                            </td>
                            <td>
                                <strong>
                                    {!recommendScaleOut ? getCapacityRecommendation(arrayHardwareUpgradeRecommendations) : null }
                                </strong>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            {
                upgradeNotes && (
                    <div className={cx('upgradeNotesContainer')}>
                        <i className={classnames('fa fa-exclamation-circle')} aria-hidden="true" />
                        <span className={cx('upgradeNotes')}>{upgradeNotes}</span>
                    </div>)
            }
        </div>
    );
};

HardwareRecommendations.propTypes = {
    arrayHardwareUpgradeRecommendations: PropTypes.shape({
        arrayType: PropTypes.string,
        currentArrayModel: PropTypes.string,
        currentController: PropTypes.string,
        currentCacheSystemTotalBytes: PropTypes.number,
        currentCacheHeadBytes: PropTypes.number,
        currentCacheTotalAfsBytes: PropTypes.number,
        currentCacheExpNonAfsBytes: PropTypes.number,
        currentHddBytes: PropTypes.number,
        currentHddExpansionShelves: PropTypes.number,
        recommendDiskUpgrade: PropTypes.bool,
        recommendCPUUpgrade: PropTypes.bool,
        recommendCacheUpgrade: PropTypes.bool,
        addAfsShelf: PropTypes.bool,
        recommendScaleOut: PropTypes.bool,
        recommendedArrayModel: PropTypes.string,
        recommendedController: PropTypes.string,
        recommendCacheSystemTotalBytes: PropTypes.number,
        recommendedCacheHeadBytes: PropTypes.number,
        recommendedCacheTotalAfsBytes: PropTypes.number,
        recommendedCacheExpNonAfsBytes: PropTypes.number,
        recommendedHddExpansionShelves: PropTypes.number,
    }),
};

export default HardwareRecommendations;
