/* eslint-disable jsx-a11y/no-static-element-interactions */
import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { Caret } from '../TreeView';
import style from './style.scss';

const cx = classnames.bind(style);

const PoolNodeHeader = ({ node: { name, parent }, onClick }) => {
    const hasSiblings = parent.children.length > 1;
    if (hasSiblings) {
        return <span onClick={onClick} className={cx('name')}>{name}</span>;
    }

    return (
        <span onClick={onClick} className={cx('name')}>
            {parent.name} <span className={cx('sub-name')}>{name}</span>
        </span>
    );
};

PoolNodeHeader.propTypes = {
    node: PropTypes.shape({
        name: PropTypes.string,
        parent: PropTypes.shape({
            name: PropTypes.string,
        }),
    }),
    onClick: PropTypes.func,
};

const PoolNodeIcon = props => <i className={cx('fa fa-th', 'icon')} {...props} />;

const PoolContainer = ({ onClick, onClickIcon, node }) => {
    const hasChildren = node.children && node.children.length;

    return (
        <div className={cx('node')}>
            <Caret
                onClick={hasChildren ? onClickIcon : null}
                toggled={node.toggled}
                className={cx('caret', { invisible: !hasChildren })}
            />
            <PoolNodeIcon onClick={onClick} />
            <PoolNodeHeader node={node} onClick={onClick} />
        </div>
    );
};

PoolContainer.propTypes = {
    onClick: PropTypes.func,
    onClickIcon: PropTypes.func,
    node: PropTypes.object,
};
export default PoolContainer;


const SelectedPool = ({ className, node }) => (
    <div className={className}>
        <PoolNodeIcon />
        <PoolNodeHeader node={node} />
    </div>
);

SelectedPool.propTypes = {
    node: PropTypes.object,
};

export { SelectedPool };
