const _ = require('underscore');
var Marionette = require('backbone.marionette');
var IopsChart = require('./IopsChart');
var tpl = require('raw-loader!./IopsChartGroup.html');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    regions: {
        Read: '[data-region="read"]',
        Write: '[data-region="write"]',
    },

    onRender: function (options) {
        this.maxIops = _.max(this.collection.calculateMaximums(['iopsRead', 'iopsWrite']));
        ['Read', 'Write'].forEach(this.showChild.bind(this));
    },

    showChild: function (type) {
        this.showChildView(type, new IopsChart(_(this.options).extend({
            type: type,
            maxIops: this.maxIops,
        })));
    },

});
