const App = require('app/Application');
const Marionette = require('backbone.marionette');
const AccountModel = require('./AccountModel');
const NimbleUserTenantHelpers = require('../../../progeny/modules/nimble/tenantSelection/NimbleUserTenantHelpers').default;

module.exports = Marionette.Object.extend({

    account: null,
    mode: null,

    initialize: function () {
        this.setAccount(new AccountModel(NimbleUserTenantHelpers.getCurrentTenant()));
    },

    setAccount: function (account) {
        this.account = account;
    },

    getAccount: function () {
        return this.account;
    },

    isSingle: function () {
        return true;
    },

    isAll: function () {
        return false;
    },

    isPrimary: function () {
        var primaryAccount = App.request('global', 'get:account:primary');
        return this.isSingle() && primaryAccount && (this.account.id === primaryAccount.id);
    },

});
