import { createSelector } from 'reselect';
import { groupBy, omit } from 'underscore';
import { createReducersForAction } from '@infosight/elmer/dist/utils/redux';
import { USER_CONTEXT_CHANGED } from '../../user/constants';
import { GROUP_POOL_FOLDER_RELATIONS_FETCH, GROUP_POOL_FOLDER_RELATIONS_RESET, ALL, POOL, FOLDER } from '../constants';

const initialState = {
    relationships: undefined,
    loadingRelationships: false,
};

const ACTION_HANDLERS = {
    [USER_CONTEXT_CHANGED]: () => initialState,
    [GROUP_POOL_FOLDER_RELATIONS_RESET]: () => initialState,
    ...createReducersForAction({ type: GROUP_POOL_FOLDER_RELATIONS_FETCH, stateKey: 'relationships' }),
};

export default function reducer(state = initialState, { type, ...action }) {
    const handler = ACTION_HANDLERS[type];
    return handler ? handler(state, action) : state;
}

export const objectRelationshipsSelector = state => state.capacityReport.objectRelationships;

export const relationshipsByTypeSelector = createSelector(
    objectRelationshipsSelector,
    ({ relationships }) => {
        if (!relationships) {
            return null;
        }

        // No pool or folder ID
        const all = { type: ALL };

        const byPool = Object.values(groupBy(relationships, 'poolUID'))
            .map(value => ({
                ...omit(value[0], 'folderName', 'folderUID'),
                type: POOL,
            }));

        const byFolder = relationships.map(value => ({ ...value, type: FOLDER }));

        return [all, ...byPool, ...byFolder];
    }
);

export const hasFoldersSelector = createSelector(
    objectRelationshipsSelector,
    ({ relationships }) => {
        if (!relationships) {
            return false;
        }
        return !!relationships.find(relation => relation.folderName !== null);
    }
);
