import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';

const cx = classnames;

const Caret = ({ className, toggled, ...props }) => (
    <i className={cx(className, 'fa', { 'fa-caret-right': !toggled, 'fa-caret-down': toggled })} {...props} />
);

Caret.propTypes = {
    toggled: PropTypes.bool.isRequired,
};

Caret.defaultProps = {
    toggled: false,
};

export default Caret;
