import { createReducersForAction, createSelector, getInitialState } from '@infosight/elmer/dist/utils/redux';
import rootState from '../../rootStateSelector';
import { USER_CONTEXT_CHANGED } from '../../../user/constants';
import {
    LIST_FETCH,
    SUMMARY_CARDS_FETCH,
    TREND_CARDS_FETCH,
    CSV_FETCH,
    RESET,
} from './constants';

const initialState = {
    ...getInitialState('pools'),
    ...getInitialState('summaryCards'),
    ...getInitialState('trendCards'),
    ...getInitialState('poolsCsv'),
};

const ACTION_HANDLERS = {
    [USER_CONTEXT_CHANGED]: () => (initialState),
    [RESET]: () => (initialState),
    ...createReducersForAction({ type: LIST_FETCH, stateKey: 'pools' }),
    ...createReducersForAction({ type: SUMMARY_CARDS_FETCH, stateKey: 'summaryCards' }),
    ...createReducersForAction({ type: TREND_CARDS_FETCH, stateKey: 'trendCards' }),
    ...createReducersForAction({ type: CSV_FETCH, stateKey: 'poolsCsv' }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

const baseSelector = state => rootState(state).poolCollection;
export const poolsCollectionSelector = state => baseSelector(state);
export const csvSelector = createSelector(baseSelector, 'poolsCsv');
