import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';

const VolumeUsageInfo = ({ title }) => (
    <div
        className={classnames('flex-container align-middle')}
        title={
            'Capacity information at volume level reflects logical usage  before any savings.' +
        ' This is different from the SM UI which indicates usage after compression.' +
        ' SM UI will also start showing logical usage in the near future.'
        }
    >
        <span>{title}</span>
        <span
            className={classnames('fa fa-info-circle')}
            style={{ paddingLeft: '4px' }}
        />
    </div>
);

VolumeUsageInfo.propTypes = {
    title: PropTypes.string,
};

export default VolumeUsageInfo;
