import _ from 'underscore';
import { ARRAY_GROUP, FOLDER, POOL, ROOT } from '../../constants';

const createChildren = children => _(children)
    .chain()
    .values()
    .map(createTree) // eslint-disable-line no-use-before-define
    .sortBy('name')
    .value();

const createTree = ({ children, ...node }) => ({
    ...node,
    children: children ? createChildren(children) : undefined,
});

const createNode = (id, type, name, value) => ({ id, type, name, value, children: {} });

export default function createObjectGraph(relations) {
    if (!relations) {
        return undefined;
    }

    const children = relations.reduce((base, relation) => {
        const { groupUID: groupId, poolUID: poolUid, folderUID: folderId } = relation;
        const { groupName, poolName, folderName } = relation;

        if (!base[groupId]) {
            base[groupId] = createNode(groupId, ARRAY_GROUP, groupName); // eslint-disable-line no-param-reassign
        }

        if (poolUid) {
            const group = base[groupId];

            if (!group.children[poolUid]) {
                const value = { ...relation, type: POOL };
                group.children[poolUid] = createNode(poolUid, POOL, poolName, value);
            }

            if (folderId) {
                const pool = group.children[poolUid];

                if (!pool.children[folderId]) {
                    const value = { ...relation, type: FOLDER };
                    pool.children[folderId] = createNode(folderId, FOLDER, folderName, value);
                }
            }
        }

        return base;
    }, {});

    return createTree({ children, id: ROOT, type: ROOT, name: ROOT });
}
