export const PERMISSION_TARGET = {
    NIMBLE_TENANT: 'Nimble.Customer',
    NIMBLE_SYSTEM: 'Nimble.System',
    STOREFRONT_SYSTEM: 'Storefront.System',
    STOREFRONT_SYSTEM_GROUP: 'Storefront.SystemGroup',
    GLOBAL_TARGET_ID: '_global',
    ACTIVE_TARGET_ID: '_active',
};

export const nimbleTenantAdminAccess = {
    permissions: [{
        privilege: PRIVILEGES.ADMIN,
        targetType: PERMISSION_TARGET.NIMBLE_TENANT,
        targetId: PERMISSION_TARGET.ACTIVE_TARGET_ID,
    }],
};

export const nimbleTenantPrimaryAccess = {
    permissions: [{
        privilege: PRIVILEGES.PRIMARY,
        targetType: PERMISSION_TARGET.NIMBLE_TENANT,
        targetId: PERMISSION_TARGET.ACTIVE_TARGET_ID,
    }],
};

export const nimbleThirdPartyPermissionRequestAccess = {
    $or: [
        nimbleTenantAdminAccess,
        {
            authorities: AUTHORITIES.ATTRIBUTE_NIMBLE_RESELLER,
            permissions: {
                privilege: PRIVILEGES.PRIMARY,
                targetType: PERMISSION_TARGET.NIMBLE_TENANT,
                targetId: PERMISSION_TARGET.ACTIVE_TARGET_ID,
            },
        },
    ],
};
