import React from 'react';
import PropTypes from 'prop-types';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { DefaultDetailPageTitle } from '@infosight/elmer/dist/infrastructure';
import { IconAdaptor } from '@infosight/elmer/dist/components/IconAdaptor';
import FaExternalLink from 'react-icons/lib/fa/external-link';
import { OBJECTS } from '../inventory';

const ArrayDetailPageTitle = (props) => {
    const { scope: { id: serialNumber, name }, className } = props;
    const hostName = name && name.substr(0, name.indexOf('(')).trim();
    let link = buildUrl('', `/InfoSight/static/nimosLauncher.html?arraySn=${serialNumber}&hostname=${hostName}`);
    if (!hostName) {
        link = buildUrl('', `/InfoSight/static/nimosLauncher.html?arraySn=${serialNumber}`);
    }

    return (
        <span className={className}>
            <DefaultDetailPageTitle {...props} className={null} />
            &nbsp;|&nbsp;
            <a target="_blank" rel="noopener noreferrer" href={link}>
                Manage <IconAdaptor adjustment="baseline" size={20}><FaExternalLink /></IconAdaptor>
            </a>
        </span>
    );
};

ArrayDetailPageTitle.propTypes = {
    productInterface: PropTypes.shape({}).isRequired,
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
        name: PropTypes.string,
    }),
};

const DetailPageTitle = (props) => {
    if (props.scope.type === OBJECTS.ARRAY) {
        return <ArrayDetailPageTitle {...props} />;
    }

    return <DefaultDetailPageTitle {...props} />;
};

DetailPageTitle.propTypes = {
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
    }),
};

export default DetailPageTitle;
