export default function plotOptionsMixin(type) {
    const area = (type === 'area' ? {
        stacking: 'normal',
    } : null);

    return () => ({
        plotOptions: {
            area,
            column: {
                pointPlacement: 'on',
            },
            series: {
                borderWidth: 0,
                pointPadding: 0,
                groupPadding: 0,
                marker: {
                    enabled: false,
                    symbol: 'circle',
                },
                events: {
                    legendItemClick: (e) => {
                        e.preventDefault();
                    },
                },
                turboThreshold: Number.MAX_VALUE,
            },
        },
    });
}
