import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { pluck, last, isNumber } from 'underscore';
import numeral from 'numeral';
import ReactDimensions from 'react-dimensions';
import NoData from '@infosight/elmer/dist/components/NoData';
import mergeSharedTooltip, { Timestamp } from '@infosight/elmer/dist/charts/mergeSharedTooltip';
import { CHART_COLORS } from '../../../../util/chartColors';
import { red01 } from '../../../../util/colors';
import { formatBytes } from '../../util';
import { xAxisDataRange } from '../../../charts/mixins/xAxis';

const DEGRADATION = 'DEGRADATION';
const CAPACITY = 'CAPACITY';

const Chart = mergeSharedTooltip({
    getPoints(that) {
        const { points } = that;
        return [points[1], points[0]]
            .concat(points.slice(2))
            .filter(point => point.series.options.id !== DEGRADATION);
    },
    valueFormatter(point) {
        return numeral(point.y).format('0.0 ib');
    },
    xValueFormatter(point) {
        return Timestamp(point.x, 'L');
    },
});

// Exported for testing
export class PoolCapacityTimeSeries extends Component {
    /**
     * chartRangeStart and chartRangeEnd are moment objects, so referential equality checks fail,
     * resulting in unnecessary user-visible re-rendering
     */
    shouldComponentUpdate({ timeSeries, containerHeight, containerWidth, chartRangeStart, chartRangeEnd }) {
        return !(
            chartRangeStart.isSame(this.props.chartRangeStart) &&
            chartRangeEnd.isSame(this.props.chartRangeEnd) &&
            timeSeries === this.props.timeSeries &&
            containerHeight === this.props.containerHeight &&
            containerWidth === this.props.containerWidth
        );
    }

    config() {
        const maxCapacity = this.maxCapacity();
        const finalCapacity = this.finalCapacity();
        const { chartRangeStart, chartRangeEnd, timeSeries } = this.props;

        const extremes = {
            timeSeriesStart: chartRangeEnd,
            timeSeriesEnd: chartRangeStart,
        };

        const firstPoint = timeSeries.find(x => x.used !== null);
        if (firstPoint) {
            extremes.timeSeriesStart = Math.min(extremes.timeSeriesStart, firstPoint.asupLocalDate);
        }

        const lastPoint = timeSeries.concat().reverse().find(x => x.used !== null);
        if (lastPoint) {
            extremes.timeSeriesEnd = Math.max(extremes.timeSeriesEnd, lastPoint.asupLocalDate);
        }

        return {
            chart: {
                width: this.props.containerWidth,
                height: this.props.containerHeight,
                spacingBottom: 0,
                spacingTop: 20,
                marginTop: 30,
                type: 'area',
                resetZoomButton: {
                    theme: {
                        display: 'none',
                    },
                },
            },
            title: false,
            xAxis: xAxisDataRange(chartRangeStart, chartRangeEnd),
            yAxis: {
                title: {
                    text: 'USAGE (TiB)',
                },
                labels: {
                    formatter() {
                        return formatBytes(this.value, maxCapacity);
                    },
                },
                plotLines: [0.25, 0.5, 0.75].map(stop => ({
                    color: CHART_COLORS.plotBandGrey,
                    dashStyle: 'ShortDot',
                    value: this.finalCapacity() * stop,
                    width: 2,
                })),
            },
            legend: {
                enabled: true,
                symbolHeight: 10,
                symbolWidth: 10,
                labelFormatter() {
                    return this.userOptions.id === CAPACITY
                        ? `${this.name} ${numeral(finalCapacity).format('0 ib')}`
                        : this.name;
                },
            },
            series: [
                this.savingsSeries(),
                this.totalUsageSeries(),
                this.maxCapacitySeries(),
                this.performanceDegradationCapacitySeries(),
            ],
            plotOptions: {
                area: {
                    animation: false,
                    stacking: 'normal',
                    marker: {
                        enabled: false,
                    },
                },
                line: {
                    marker: {
                        enabled: false,
                        width: 0,
                        height: 0,
                        states: {
                            hover: {
                                enabled: false,
                            },
                        },
                    },
                },
            },
        };
    }

    savingsSeries() {
        return {
            color: CHART_COLORS.teal,
            fillColor: CHART_COLORS.tealLight,
            name: 'Savings',
            data: this.props.timeSeries.map(val => ({
                x: val.asupLocalDate,
                y: val.savingsTotal,
            })),
            legendIndex: 2,
            visible: false,
        };
    }

    totalUsageSeries() {
        return {
            color: CHART_COLORS.blue,
            fillColor: CHART_COLORS.blueLight,
            name: 'Total Usage',
            data: this.props.timeSeries.map(val => ({
                x: val.asupLocalDate,
                y: val.used,
            })),
            legendIndex: 1,
            events: {
                legendItemClick(event) {
                    event.preventDefault();
                },
            },
        };
    }

    maxCapacitySeries() {
        return {
            type: 'line',
            color: red01,
            lineWidth: 2,
            legendIndex: 10,
            id: CAPACITY,
            name: 'Capacity',
            data: this.props.timeSeries.map(val => ({
                x: val.asupLocalDate,
                y: val.capacity,
            })),
            step: true,
        };
    }

    performanceDegradationCapacitySeries() {
        return {
            type: 'line',
            color: red01,
            lineWidth: 2,
            dashStyle: 'dot',
            legendIndex: 11,
            id: DEGRADATION,
            name: 'Performance Degradation Threshold (90%)',
            data: this.props.timeSeries.map(val => ({
                x: val.asupLocalDate,
                y: isNumber(val.capacity) ? val.capacity * 0.9 : val.capacity,
            })),
            step: true,
            marker: {
                enabled: false,
            },
        };
    }

    maxCapacity() {
        return Math.max.apply(null, pluck(this.props.timeSeries, 'capacity'));
    }

    finalCapacity() {
        return last(this.props.timeSeries).capacity;
    }

    render() {
        const hasData = this.props.timeSeries && this.props.timeSeries.length > 0;

        return (
            <NoData hasData={hasData} message="No capacity history data available.">
                {() => <Chart config={this.config()} />}
            </NoData>
        );
    }
}

PoolCapacityTimeSeries.propTypes = {
    timeSeries: PropTypes.arrayOf(PropTypes.shape({
        asupLocalDate: PropTypes.number,
        used: PropTypes.number,
        capacity: PropTypes.number,
        savingsTotal: PropTypes.number,
    })),

    /**
     * Moment
     */
    chartRangeStart: PropTypes.object,

    /**
     * Moment
     */
    chartRangeEnd: PropTypes.object,

    // From ReactDimensions
    containerWidth: PropTypes.number,
    containerHeight: PropTypes.number.isRequired,
};

PoolCapacityTimeSeries.defaultProps = {
    containerWidth: 0,
    containerHeight: 0,
};

export default ReactDimensions({ elementResize: true })(PoolCapacityTimeSeries);
