import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { connect } from 'react-redux';
import { withQueryParams } from '@infosight/elmer/dist/components/Router';
import { bindActionCreators } from 'redux';
import Async from '@infosight/elmer/dist/components/Async';
import { param } from 'jquery';
import { identity, pick } from 'underscore';
import style from './style.scss';
import PoolFolderTreeViewDropdown from '../PoolFolderTreeViewDropdown';
import FolderCapacityContainer from './FolderCapacityContainer';
import PoolCapacityContainer from './PoolCapacityContainer';
import { FOLDER, POOL, ALL } from '../../constants';
import Selection from './Selection';
import { fetchRelationships, resetRelationships } from '../../actionCreators/objectRelationships';
import {
    relationshipsByTypeSelector,
    objectRelationshipsSelector,
    hasFoldersSelector,
} from '../../reducer/objectRelationships';
import { productInterface as infrastructureConfig } from '../../../nimble/config';

const cx = classnames.bind(style);

class CapacityContainer extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.state = { disableFilters: false };
    }

    componentWillMount() {
        if (!this.props.loadingRelationships) {
            this.props.fetchRelationships();
        }

        // Update the page style from Bootstrap to "New Look and Feel"
        const contentRegion = document.getElementById('content-region');
        if (contentRegion) {
            contentRegion.classList.add('hack-100vw');
        }
    }

    componentWillUnmount() {
        this.props.resetRelationships();

        // Revert the page style from "New Look and Feel" to Bootstrap
        const contentRegion = document.getElementById('content-region');
        if (contentRegion) {
            contentRegion.classList.remove('hack-100vw');
        }
    }

    onSelectMonthRange(event) {
        this.updateUrlParams({ monthRange: event.target.value });
    }

    updateUrlParams(mergeParams) {
        const params = pick({ ...this.props.match.params, ...mergeParams }, identity);
        this.props.history.push(`?${param(params)}`, {});
    }

    updateFragmentForResource({ poolUID, folderUID, type }) {
        if (type === ALL) {
            this.updateUrlParams({ pool: null });
        }

        if (type === POOL && poolUID) {
            this.updateUrlParams({ pool: poolUID, folderId: null });
        }

        if (type === FOLDER && folderUID) {
            this.updateUrlParams({ pool: poolUID, folderId: folderUID });
        }
    }

    findResource() {
        const { relationships } = this.props;

        const { pool: poolUid, folderId } = this.props.match.params;

        if (!relationships) {
            return null;
        }

        let type = ALL;
        if (folderId || folderId === null) {
            type = FOLDER;
        } else if (poolUid) {
            type = POOL;
        }

        return relationships.find(({ poolUID, folderUID, type: valueType }) => (
            valueType === type &&
            (!poolUid || (poolUid && poolUID === poolUid)) &&
            (!folderId || (folderId === folderUID))
        ));
    }

    toggleFilters(on) {
        this.setState({ disableFilters: !on });
    }

    renderChildContainer(resource) {
        if (!resource) {
            return null;
        }

        const { poolUID: poolUid, folderUID: folderId, arraySn, type } = resource;

        let ChildContainer;
        switch (type) {
        case FOLDER:
            ChildContainer = FolderCapacityContainer;
            break;
        case ALL:
        case POOL:
            ChildContainer = PoolCapacityContainer;
            break;
        default:
            return null;
        }

        const { monthRange } = this.props.match.params;

        // IS-11756 Add a key so each filter change forces a destroy+mount cycle to clear any internal state
        const key = `${poolUid}${folderId}${monthRange}`;
        return (
            <ChildContainer
                history={this.props.history}
                location={this.props.location}
                match={this.props.match}
                key={key}
                lookbackMonths={monthRange}
                poolUid={poolUid}
                folderId={folderId}
                toggleFilters={this.toggleFilters}
                arraySerialNumber={arraySn}
                isAggregate={type === ALL}
                config={infrastructureConfig}
            />
        );
    }

    renderFilters(resource) {
        const { disableFilters: disabled } = this.state;
        const { monthRange } = this.props.match.params;
        /* eslint-disable jsx-a11y/label-has-for */
        return (
            <div className="row expanded align-justify">
                <div className="pool-list">
                    <label className={cx('align-middle', 'label')} htmlFor="capacity-object-dropdown">
                        Pool{this.props.hasFolders && (<span> or Folder</span>)}
                    </label>
                    <PoolFolderTreeViewDropdown
                        id="capacity-object-dropdown"
                        onSelect={this.updateFragmentForResource}
                        className={cx('pool-selector', 'align-middle')}
                        menuClassName={cx('menu')}
                        value={resource}
                        disabled={disabled}
                    />
                </div>
                <div>
                    <select
                        onChange={this.onSelectMonthRange}
                        className={cx('align-middle', 'time-range')}
                        value={monthRange}
                        disabled={disabled}
                    >
                        <option value="3">Past 3 months</option>
                        <option value="6">Past 6 months</option>
                        <option value="9">Past 9 months</option>
                        <option value="12">Past year</option>
                    </select>
                </div>
            </div>
        );
        /* eslint-enable jsx-a11y/label-has-for */
    }

    render() {
        const resource = this.findResource(this.props);
        return (
            <div className={cx('container')}>
                <div className={style.title}>
                    Capacity Report
                </div>
                <Async loading={this.props.loadingRelationships}>
                    <div className={classnames('container-fluid', cx('header-container'))}>
                        {this.renderFilters(resource)}
                        <div className="row expanded">
                            <Selection className={cx('header')} value={resource} />
                        </div>
                    </div>
                </Async>
                {this.renderChildContainer(resource)}
            </div>
        );
    }
}

CapacityContainer.propTypes = {
    relationships: PropTypes.array,
    loadingRelationships: PropTypes.bool.isRequired,
    fetchRelationships: PropTypes.func.isRequired,
    resetRelationships: PropTypes.func.isRequired,
    hasFolders: PropTypes.bool.isRequired,

    // Injected by withQueryParams
    history: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    match: PropTypes.shape({
        params: PropTypes.object,
    }).isRequired,
};

const mapStateToProps = state => ({
    ...objectRelationshipsSelector(state),
    relationships: relationshipsByTypeSelector(state),
    hasFolders: hasFoldersSelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    fetchRelationships,
    resetRelationships,
}, dispatch);

export default withQueryParams(connect(mapStateToProps, mapDispatchToProps)(CapacityContainer));
