import React from 'react';
import PropTypes from 'prop-types';
import KeyStatistic from '@infosight/elmer/dist/components/KeyStatistic';
import NamedNimbleIcon from '../../../nimble/components/NamedNimbleIcon';
import { UNKNOWN } from '../util';

// Types of Volume Protection
const PROTECTION_TYPES = Object.freeze({
    UNPROTECTED: 'Unprotected',
    LOCAL_ONLY: 'Local Only',
    REMOTE: 'Remote',
    REPLICA_VOLUME: 'Replica Volume',
});

const DataProtection = ({ className, protectionLevel, inline }) => {
    const displayProtectionLevel = protectionLevel || UNKNOWN;

    if (protectionLevel === PROTECTION_TYPES.UNPROTECTED || protectionLevel === PROTECTION_TYPES.REPLICA_VOLUME || !protectionLevel) {
        return inline ?
            <NamedNimbleIcon className={className} iconName="Spacer" inline={inline} aliasName={displayProtectionLevel} /> :
            <KeyStatistic>{displayProtectionLevel}</KeyStatistic>;
    }
    return <NamedNimbleIcon className={className} iconName={protectionLevel} inline={inline} />;
};

DataProtection.propTypes = {
    protectionLevel: PropTypes.string,
    inline: PropTypes.bool,
};

DataProtection.PROTECTION_TYPES = PROTECTION_TYPES;
export default DataProtection;
