import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import KeyStatistic from '@infosight/elmer/dist/components/KeyStatistic';
import { INFO_NOT_AVAILABLE } from '../../../../../util/formatter/constants';
import NimbleIcon from '../../../components/NimbleIcon';
import NamedNimbleIcon from '../../../components/NamedNimbleIcon';
import styles from './hardwareStatus.scss';

const cx = classnames.bind(styles);

// Any state other than 'active' or 'standby' indicate a warning
const hasControllerIssue = controller => controller.state !== 'active' && controller.state !== 'standby';

const hasControllerFanIssue = controller => (controller.components).some(component => component.name === 'fan' && component.status !== 'OK');

const hasPowerSupplyIssue = latestHeartbeat => latestHeartbeat.controllers
            && latestHeartbeat.controllers.some(controller => (controller.components).some(component => component.name === 'psu' && component.status !== 'OK'));

const hasSSDIssues = hb => hb.ssdFailedCount > 0;

const hasHDDIssues = hb => hb.hddFailedCount > 0;

const ICON_DIMENSION = '35px';

const HardwareStatus = ({ latestHeartbeat, isAllFlash }) => {
    if (!latestHeartbeat) {
        return <KeyStatistic>{INFO_NOT_AVAILABLE}</KeyStatistic>;
    }
    // TODO: Decision has not been made if we have to emphaisize controller state
    const controllerA = latestHeartbeat.controllers.find(controller => controller.name === 'a');
    const controllerB = latestHeartbeat.controllers.find(controller => controller.name === 'b');
    const controllerAStatus = !hasControllerIssue(controllerA);
    const controllerBStatus = !hasControllerIssue(controllerB);
    const controllerAFanStatus = !hasControllerFanIssue(controllerA);
    const controllerBFanStatus = !hasControllerFanIssue(controllerB);
    const powerSupplyStatus = !hasPowerSupplyIssue(latestHeartbeat);
    const ssdStatus = !hasSSDIssues(latestHeartbeat);
    const hddStatus = !hasHDDIssues(latestHeartbeat);

    return (
        <React.Fragment>
            <div className="column medium-13">
                <div className="row">
                    <div className="column">
                        <div
                            title={ssdStatus ? 'SSD Status: Good' :
                                `${latestHeartbeat.ssdFailedCount} out of ${latestHeartbeat.ssdFailedCount + latestHeartbeat.ssdGoodCount} SSDs failed`}
                        >
                            <span>SSD</span>
                            <NamedNimbleIcon
                                iconName="Array SSD"
                                status={ssdStatus}
                                width={ICON_DIMENSION}
                                height={ICON_DIMENSION}
                                aliasName={`${latestHeartbeat.ssdGoodCount} of ${latestHeartbeat.ssdFailedCount + latestHeartbeat.ssdGoodCount}`}
                            />
                        </div>
                    </div>
                    { !isAllFlash &&
                    <div className="column">
                        <div
                            title={hddStatus ? 'HDD Status: Good' :
                                `${latestHeartbeat.hddFailedCount} out of ${latestHeartbeat.hddFailedCount + latestHeartbeat.hddGoodCount} HDDs failed`}
                        >
                            <span>HDD</span>
                            <NamedNimbleIcon
                                iconName="Array HDD"
                                status={hddStatus}
                                width={ICON_DIMENSION}
                                height={ICON_DIMENSION}
                                aliasName={`${latestHeartbeat.hddGoodCount} of ${latestHeartbeat.hddFailedCount + latestHeartbeat.hddGoodCount}`}
                            />
                        </div>
                    </div>
                    }
                    <div className="column">
                        <div
                            title={`Power Supply Status : ${powerSupplyStatus ? 'Good' : 'Warning/Error'}`}
                        >
                            <span>PSU</span>
                            <div>
                                <NimbleIcon name="Array Power Supply" status={powerSupplyStatus} width={ICON_DIMENSION} height={ICON_DIMENSION} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className={classnames('column medium-11', cx('leftPartitionBorder'))}>
                <div className="row">
                    <span>Controller</span>
                </div>
                <div className={classnames('row', cx('addTopPadding'))}>
                    <div className="column shrink">
                        <div className={classnames(cx('controllerBox'), !controllerAStatus ? cx('badController') : '')}>
                                A
                        </div>
                    </div>
                    <div className={classnames('column', cx('capitalize'))}>
                        {latestHeartbeat.controllers.find(controller => controller.name === 'a').state}
                    </div>
                    <div className="column">
                        <div
                            title={`Controller A's Fan Status : ${controllerAFanStatus ? 'Good' : 'Warning/Error'}`}
                        >
                            <NimbleIcon name="Array Fan" status={controllerAFanStatus} width="2em" height="2em" />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="column shrink">
                        <div className={classnames(cx('controllerBox'), !controllerBStatus ? cx('badController') : '')}>
                                B
                        </div>
                    </div>
                    <div className={classnames('column', cx('capitalize'))}>
                        {latestHeartbeat.controllers.find(controller => controller.name === 'b').state}
                    </div>
                    <div className="column">
                        <div
                            title={`Controller B's Fan Status : ${controllerBFanStatus ? 'Good' : 'Warning/Error'}`}
                        >
                            <NimbleIcon name="Array Fan" status={controllerBFanStatus} width="2em" height="2em" />
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
};

HardwareStatus.propTypes = {
    isAllFlash: PropTypes.bool,
    latestHeartbeat: PropTypes.shape({
        serialNumber: PropTypes.string,
        timestamp: PropTypes.number,
        controllers: PropTypes.arrayOf(PropTypes.shape({
            name: PropTypes.oneOf(['a', 'b']),
            state: PropTypes.string,
            components: PropTypes.arrayOf(PropTypes.shape({
                name: PropTypes.oneOf(['psu', 'fan', 'temperature']),
                status: PropTypes.string,
            })),
        })),
        ssdGoodCount: PropTypes.number,
        ssdFailedCount: PropTypes.number,
        hddGoodCount: PropTypes.number,
        hddFailedCount: PropTypes.number,
    }),
};

export default HardwareStatus;
