import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import OverlayTrigger from '@infosight/elmer/dist/components/OverlayTrigger';
import style from './Trends.scss';

const cx = classnames.bind(style);

function Trend({ type, message, tooltip }) {
    const iconClass = {
        'fa-info': type === 'info',
        'fa-check': type === 'success',
        'fa-exclamation': type === 'error' || type === 'warning',
        'text-success': type === 'success',
        'text-danger': type === 'error',
        'text-warning': type === 'warning',
        'text-primary': type === 'info',
    };
    const icon = <span><i className={cx('icon', 'fa', iconClass)} /></span>;

    if (type === 'success' || !tooltip) {
        return <div className={cx('trend')}>{icon} {message}</div>;
    }

    return (
        <OverlayTrigger overlay={<div className={cx('tooltip')}>{tooltip}</div>} placement="left">
            <div className={cx('trend', 'popover-link')}>{icon} {message}</div>
        </OverlayTrigger>
    );
}

Trend.propTypes = {
    type: PropTypes.oneOf(['info', 'success', 'error', 'warning']),
    message: PropTypes.string.isRequired,
    tooltip: PropTypes.string,
};

Trend.defaultProps = {
    type: 'info',
};

function Trends({ className, trends }) {
    if (trends) {
        return (
            <div className={className}>{trends.map(trend => <Trend {...trend} key={trend.message} />)}</div>
        );
    }

    return null;
}

Trends.propTypes = {
    trends: PropTypes.array,
};

export default Trends;
