import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { values } from 'underscore';
import { ClientControlledTable } from '@infosight/elmer/dist/components/Table';
import { userSelector } from '../../../../../user/reducer';
import { fetchPools, reset, getPoolsCsv } from '../../actionCreators';
import {
    poolsCollectionSelector,
    csvSelector,
} from '../../reducer';
import getConfig from './getConfig';

export class PoolsTableView extends Component {
    componentDidMount() {
        const { scope, config } = this.props;
        this.props.fetchPools(config.convertToGenericQueryScope(scope));
    }

    componentWillUnmount() {
        this.props.resetPoolCollection();
    }

    render() {
        const { userName, pools, loadingPools, config, loadingPoolsCsv, scope, enabledFieldSets, disableDefaultSort } = this.props;
        const { columns, categories } = getConfig(config);

        return (
            <ClientControlledTable
                tableId={`${userName}_NIMBLE_POOLS`}
                data={pools ? values(pools) : []}
                columns={columns}
                categories={enabledFieldSets || categories}
                loading={loadingPools}
                defaultSorted={!disableDefaultSort ? [{ id: 'poolName', desc: false }] : undefined}
                downloadCsv={() => this.props.getPoolsCsv(config.convertToGenericQueryScope(scope))}
                downloadingCsv={loadingPoolsCsv}
                {...this.props}
            />
        );
    }
}

PoolsTableView.propTypes = {
    userName: PropTypes.string,
    resetPoolCollection: PropTypes.func.isRequired,
    pools: PropTypes.arrayOf(PropTypes.shape({
        allFlash: PropTypes.bool,
        allocatedBytes: PropTypes.number,
        arrayHostName: PropTypes.string,
        arraySearchableName: PropTypes.string,
        arraySn: PropTypes.string,
        avgCacheMissRate: PropTypes.number,
        avgCpuUtilization: PropTypes.number,
        avgIopsPerWeek: PropTypes.number,
        avgLatencyMs: PropTypes.number,
        avgReadLatencyMs: PropTypes.number,
        avgReadThroughput: PropTypes.number,
        avgReadsPerWeek: PropTypes.number,
        avgThroughput: PropTypes.number,
        avgWriteLatencyMs: PropTypes.number,
        avgWriteThroughput: PropTypes.number,
        avgWritesPerWeek: PropTypes.number,
        daysUntilFull: PropTypes.number,
        folderCount: PropTypes.number,
        freeBytes: PropTypes.number,
        groupName: PropTypes.string,
        groupUid: PropTypes.string,
        growthLast30Days: PropTypes.number,
        growthLast60Days: PropTypes.number,
        growthLast90Days: PropTypes.number,
        lastWeekGrowthPercent: PropTypes.number,
        pinnableCacheCapacity: PropTypes.number,
        pinnedCacheCapacity: PropTypes.number,
        poolArrays: PropTypes.number,
        poolName: PropTypes.string,
        poolUid: PropTypes.string,
        savingsCloneBytes: PropTypes.number,
        savingsCompressionBytes: PropTypes.number,
        savingsDedupBytes: PropTypes.number,
        savingsFactor: PropTypes.number,
        snapshotCount: PropTypes.number,
        totalSavingsBytes: PropTypes.number,
        unusedReserveBytes: PropTypes.number,
        usedBytes: PropTypes.number,
        volumeCount: PropTypes.number,
    })),
    loadingPools: PropTypes.bool,
    fetchPools: PropTypes.func.isRequired,
    getPoolsCsv: PropTypes.func.isRequired,
    loadingPoolsCsv: PropTypes.bool.isRequired,

    // Injected by XX_INFRASTRUCTURE_XX
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),
    config: PropTypes.object.isRequired,
    enabledFieldSets: PropTypes.array,

    // TODO: Until elmer Table is fixed to support namespaced url params
    disableDefaultSort: PropTypes.bool,
};

PoolsTableView.defaultProps = {
    pools: [],
    loadingPools: false,
    loadingPoolsCsv: false,
    disableDefaultSort: false,
};

const mapStateToProps = state => ({
    ...poolsCollectionSelector(state),
    ...csvSelector(state),
    userName: userSelector(state).username,
});

const mapDispatchToProps = dispatch => bindActionCreators({
    fetchPools,
    resetPoolCollection: reset,
    getPoolsCsv,
}, dispatch);

/*
 Reason for passing pure: false
 This component is being used in multiple places.
 In one of the place ie., Capacity Dashboard, the parent of this component does not pass match as a prop.
 So when the match (prop from child of this component ie., FacetedCollectionPage) changes
 this is not being rerendered as by default react-redux connect will make this component a Pure Component.
 So by passing {pure: false}, it will rerender even if none of the props were changed
*/
export default connect(mapStateToProps, mapDispatchToProps, null, { pure: false })(PoolsTableView);
