import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import moment from 'moment-timezone';
import { saveAs } from 'file-saver';
import {
    ARRAYS_LIST_FETCH,
    ARRAYS_CSV_FETCH,
    RESET,
} from './constants';
import Request from '../../../../services/Request';
import { ArrayHardwareStatus, isHeartbeatDelayed } from './components/list/ArrayHealth';
import { parseSupportStatus } from './components/list/ArraySupportStatus';
import getNetworkCardInfo from '../detail/components/NetworkInfo';


/**
 * @param {object} scope
 */
function fetchArrays(scope) {
    return new Request().pod().genericQuery('arrays')
        .post({ scope });
}

/**
 * @param {object} scope
 */
function fetchHeartbeats() {
    return new Request().pod().account().urlSegment('heartbeats')
        .get();
}

function fetchAsups() {
    return new Request().pod().account()
        .urlSegment('assets')
        .urlSegment('lastAsupReceivedTimestamps')
        .get();
}

function extendWithTransformedArrayValues(arraysData) {
    /*
        With new Table component, we can add custom Sort and filter methods as well to get around the
        sort and filtering issues when we have this kind of situation where we render a transformed
        value instead of original data from the response json. Resorted to this approach
        to avoid adding custom sort and filter method for regular string all over the code
        especially because since we keep debating/changing how the null values should be sorted across the lists
    */
    return arraysData.map(array => ({
        ...array,
        parseSupportStatus: parseSupportStatus(array.supportStatus, array.supportEndDate),
        supportEndDate: moment(array.supportEndDate).isAfter(moment().add(10, 'years')) ? null : array.supportEndDate,
        networkCardInfo: getNetworkCardInfo({
            tenGigSfpPortCount: array.tenGigSfpPortCount,
            tenGigBaseTPortCount: array.tenGigBaseTPortCount,
            gigPortCount: array.gigPortCount,
            fcPortCount: array.fcPortCount,
        }),
    }));
}

/**
 * Get the list of  All Arrays for the selected account
 * This is also used by the legacy app, so take care when changing the shape.
 */
export const getArrays = scope => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(ARRAYS_LIST_FETCH, dispatch);
    dispatchStart();

    return new Request().all([
        fetchArrays(scope),
        fetchHeartbeats(),
        fetchAsups(),
    ])
        .then(([arrays, heartbeats, asups]) => {
            const originalArraysData = (arrays && arrays.data && arrays.data.data) || [];
            const arraysData = extendWithTransformedArrayValues(originalArraysData);
            const heartbeatsData = (heartbeats && heartbeats.data) || {};
            const asupsData = (asups && asups.data) || {};
            const arraysWithHeartbeats = arraysData.map((array) => {
                const latestHeartbeat = heartbeatsData[array.arraySn];
                const isHbDelayed = latestHeartbeat ? !isHeartbeatDelayed(latestHeartbeat.timestamp) : null;
                return ({
                    ...array,
                    arrayHostName: `${array.arraySn}${array.hostName ? array.hostName : ''}`,
                    hardwareStatusComparator: ArrayHardwareStatus.comparator(latestHeartbeat),
                    latestHeartbeat,
                    latestHbSort: latestHeartbeat ? latestHeartbeat.timestamp : null,
                    isHbDelayed,
                    asupReceivedTime: asupsData[array.arraySn] || null,
                });
            });
            return arraysWithHeartbeats;
        })
        .then(dispatchSuccess)
        .catch(dispatchError);
};

/**
 * Download the nimble arrays CSV file. NOTE: this actually saves the file
 */
export const getArraysCsv = scope => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(ARRAYS_CSV_FETCH, dispatch);
    dispatchStart();

    new Request().pod().genericQuery('arrays').csv()
        .post({ scope }, { responseType: 'blob' })
        .then(x => saveAs(x.data, `HPE InfoSight Arrays ${moment().format('L').replace(/\\|\//g, '-')}.csv`))
        .then(() => dispatchSuccess({})) // we want to end the loading but don't actually want to save the CSV data in redux
        .catch(dispatchError);
};

export const reset = () => ({ type: RESET });
