import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import {
    GROUPS_LIST_FETCH,
    RESET,
} from './constants';
import Request from '../../../../services/Request';

/**
 * Get the list of  All Groups for the selected account
 */
export const getGroups = () => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(GROUPS_LIST_FETCH, dispatch);
    dispatchStart();

    const options = {};

    new Request().pod().genericQuery('groups')
        .post(options)
        .then(x => x.data.data)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const reset = () => ({ type: RESET });

