import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import {
    FOLDER_CAPACITY_APPLICATIONS_FETCH,
    FOLDER_CAPACITY_SUMMARY_FETCH,
    FOLDER_CAPACITY_TIME_SERIES_FETCH,
    FOLDER_CAPACITY_RESET,
    DEFAULT_FOLDER,
} from '../constants';
import Request from '../../../services/Request';

// IS-11262 - "default" is an alias for null when a pool has multiple folders
const getFolderId = folderId => (folderId === DEFAULT_FOLDER ? 'null' : folderId);

export const getFolderCapacityTimeSeries = ({ poolUid, folderId, lookback }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(FOLDER_CAPACITY_TIME_SERIES_FETCH, dispatch);
    dispatchStart();

    return new Request().pod().account()
        .urlSegment(`capacity/pools/${poolUid}/folders/${getFolderId(folderId)}/timeSeries`)
        .uriAddQuery({ monthRange: lookback })
        .get()
        .then((response) => {
            const dispatchData = (response.data && response.data.length) ? response.data : null;
            dispatchSuccess(dispatchData);
        })
        .catch(dispatchError);
};

export const getFolderCapacitySummary = ({ poolUid, folderId, lookback }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(FOLDER_CAPACITY_SUMMARY_FETCH, dispatch);
    dispatchStart();

    return new Request().pod().account()
        .urlSegment(`capacity/pools/${poolUid}/folders/${getFolderId(folderId)}`)
        .uriAddQuery({ monthRange: lookback })
        .get()
        .then((response) => {
            const dispatchData = (response.data && response.data.length) ? response.data[0] : null;
            dispatchSuccess(dispatchData);
        })
        .catch(dispatchError);
};

export const getFolderCapacityApplications = ({ poolUid, folderId, lookback }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(FOLDER_CAPACITY_APPLICATIONS_FETCH, dispatch);
    dispatchStart();

    return new Request().pod().account()
        .urlSegment(`capacity/pools/${poolUid}/folders/${getFolderId(folderId)}/applications`)
        .uriAddQuery({ monthRange: lookback })
        .get()
        .then((response) => {
            const dispatchData = (response.data && response.data.length) ? response.data : null;
            dispatchSuccess(dispatchData);
        })
        .catch(dispatchError);
};

export function getFolderCapacity(options) {
    return (dispatch) => {
        getFolderCapacityTimeSeries(options)(dispatch);
        getFolderCapacityApplications(options)(dispatch);
        getFolderCapacitySummary(options)(dispatch);
    };
}

export function resetFolderCapacity() {
    return { type: FOLDER_CAPACITY_RESET };
}
