/* eslint-disable react/no-danger */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { connect } from 'react-redux';
import { Modal } from '@infosight/elmer/dist/components/Modal';
import Request from '../../../services/Request';
import { reset } from '../actionCreators';
import style from './Bulletins.scss';

const cx = classnames.bind(style);

class BulletinsModal extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    dismiss() {
        const { bulletins } = this.props;
        this.removeBulletins();

        if (bulletins && bulletins.length) {
            new Request()
                .slash('user', 'banners', 'dismissed')
                .put(bulletins.map(x => x.id));
        }
    }

    removeBulletins() {
        this.props.reset();
    }

    render() {
        return (
            <Modal
                size="large"
                hideModalOnPrimaryButtonClick
                hideModalOnSecondaryButtonClick
                primaryButtonContent="Dismiss All"
                secondaryButtonContent="Hide"
                title="Bulletins"
                onClickPrimaryButton={this.dismiss}
                onClickSecondaryButton={this.removeBulletins}
                bodyClassName={cx('body')}
                footerClassName={cx('footer')}
                hide={this.props.closeModal}
                isStoreDriven={false}
                isOpen
                hidePrimaryButton={this.props.hidePrimaryButton}
            >
                {this.props.bulletins && this.props.bulletins.map(({ contents, id }) => (
                    <div
                        key={id}
                        dangerouslySetInnerHTML={{ __html: contents }}
                        className={classnames('card-section card', cx('bulletin'))}
                    />
                ))}
            </Modal>
        );
    }
}

BulletinsModal.propTypes = {
    bulletins: PropTypes.arrayOf(PropTypes.shape({
        id: PropTypes.number.isRequired,
        contents: PropTypes.string.isRequired,
    })),
    reset: PropTypes.func.isRequired,
    closeModal: PropTypes.func.isRequired,
    hidePrimaryButton: PropTypes.bool,
};

BulletinsModal.defaultProps = {
    hidePrimaryButton: false,
};

export default connect(null, { reset })(BulletinsModal);
