import React from 'react';
import PropTypes from 'prop-types';
import { property } from 'underscore';
import Card from '@infosight/elmer/dist/components/Card';
import CapacityTimeSeries, { FORECAST_RANGE_SERIES } from '../../../../../charts/CapacityTimeSeries';
import chartOptions from './capacityChartOptions';
import { VolumeDataUsageInfo } from '../../../common';

const VolumeCapacityChart = ({ volumeCapacity, onChartSelect }) => (
    <Card title={<h2><VolumeDataUsageInfo title="Usage" /></h2>}>
        <div className="chart-container--medium">
            <CapacityTimeSeries
                timeSeries={volumeCapacity.data.timeseries}
                chartRangeStart={volumeCapacity.meta.time.start}
                chartRangeEnd={volumeCapacity.meta.time.end}
                getTotalCapacity={property('allocatedBytes')}
                getForecastedCapacity={property('forecastedUsageBytes')}
                chartConfig={chartOptions({ timeseries: volumeCapacity.data.timeseries, forecastRangeId: FORECAST_RANGE_SERIES })}
                forecastChart
                onChartSelect={onChartSelect}
            />
        </div>
    </Card>
);

VolumeCapacityChart.propTypes = {
    volumeCapacity: PropTypes.shape({
        data: PropTypes.shape({
            timeseries: PropTypes.arrayOf(PropTypes.shape({
                dataUsageBytes: PropTypes.number,
                sizeBytes: PropTypes.number,
                snapshotUsageBytes: PropTypes.number,
                timestamp: PropTypes.number,
            })).isRequired,
        }).isRequired,
        meta: PropTypes.shape({
            time: PropTypes.shape({
                start: PropTypes.number.isRequired,
                end: PropTypes.number.isRequired,
            }).isRequired,
        }).isRequired,
    }),
    onChartSelect: PropTypes.func,
};

export default VolumeCapacityChart;
