import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import moment from 'moment-timezone';
import { saveAs } from 'file-saver';
import {
    VOLUMES_LIST_FETCH,
    VOLUMES_CAPACITY_FETCH,
    VOLUMES_THROUGHPUT_FETCH,
    VOLUMES_LATENCY_FETCH,
    POOL_CPU_CACHE_FETCH,
    SCOPING_OBJECT_SIZE_FETCH,
    VOLUMES_CSV_FETCH,
    RESET } from './constants';
import Request from '../../../../services/Request';
import { firstOrNull } from '../../../../util/genericQuery';

export const getVolumes = (scope, filters) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(VOLUMES_LIST_FETCH, dispatch);
    dispatchStart();

    const options = { scope };
    if (filters) {
        options.filters = filters;
    }

    new Request().pod().genericQuery('volumes')
        .post(options)
        .then(x => x.data.data)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

/*
*  Filter the forecast data ( this is needed only until the real time data is available)
* (Added to address issues like: IS-14339)
*/
const getExcludeForecastFilters = () => ({
    field: 'dataSource',
    $in: [
        'Actual',
        'Interpolated',
    ],
});

const getTopN = ({ scope, timeRange = {}, rankBy }, type, view, n = 9, excludeForecast = false) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(type, dispatch);
    dispatchStart();

    const options = {
        ...timeRange,
        scope,
        rank: {
            limit: n,
            orderBy: rankBy,
            aggregateRemainder: true,
            objectType: 'volume',
        },
    };

    if (excludeForecast) {
        options.filters = [getExcludeForecastFilters()];
    }


    new Request().pod().genericQuery('volume', [view])
        .post(options)
        .then((x) => {
            const last = x.data.data.slice(-1)[0];
            if (last && last.isAggregatedRemainder) {
                last.id = 'aggregatedRemainder';
                last.name = 'All other volumes';
            }

            return x.data;
        })
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const getPoolCpuCacheUsageSeries = ({ scope, timeRange = {} }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(POOL_CPU_CACHE_FETCH, dispatch);
    dispatchStart();

    const options = {
        ...timeRange,
        scope,
    };

    return new Request().pod().genericQuery('pool', ['cpuCacheUsageSeries'])
        .post(options)
        .then(firstOrNull)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const getTopVolumesCapacity = options => getTopN(options, VOLUMES_CAPACITY_FETCH, 'capacity', 5, true);
export const getTopVolumesLatencyTimeSeries = options => getTopN(options, VOLUMES_LATENCY_FETCH, 'performance');
export const getTopVolumesThroughputTimeSeries = options => getTopN(options, VOLUMES_THROUGHPUT_FETCH, 'performance');

/**
 * Fetch the Size series of the Scoping Object.
 * It can be a Folder, a Pool ( Or may be even a Group in future)
 * @param {*}
 * @param excludeForecast - Filter the forecast data ( this is needed only until the real time data is available)
 *  (Added to address issues like: IS-14339)
 */
export const getScopingObjectSizeSeries = ({ scope, timeRange = {} }, excludeForecast = true) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(SCOPING_OBJECT_SIZE_FETCH, dispatch);
    dispatchStart();
    const options = {
        ...timeRange,
        scope,
    };

    if (excludeForecast) {
        options.filters = [getExcludeForecastFilters()];
    }

    const viewType = scope && scope.type;
    if (!viewType) {
        console.error('Scope not available');
        return;
    }
    new Request().pod().genericQuery(viewType, ['size'])
        .post(options)
        .then(firstOrNull)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

/**
 * Download the volumes CSV file. NOTE: this actually saves the file
 */
export const getVolumesCsv = scope => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(VOLUMES_CSV_FETCH, dispatch);
    dispatchStart();

    new Request().pod().genericQuery('volumes').csv()
        .post({ scope }, { responseType: 'blob' })
        .then(x => saveAs(x.data, `HPE InfoSight Volumes ${moment().format('L').replace(/\\|\//g, '-')}.csv`))
        .then(() => dispatchSuccess({})) // we want to end the loading but don't actually want to save the CSV data in redux
        .catch(dispatchError);
};

export const reset = () => ({ type: RESET });

