import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import Progress from './Progress';
import StackedProgress from './StackedProgress';
import BEM from '../util/bem';
import './style.scss';

const bem = new BEM('nmbl-ProgressWithValue');

const ProgressWithValue = ({ className, children, leftLabel, rightLabel }) => {
    if (children.type !== Progress && children.type !== StackedProgress) {
        if (__DEBUG__) {
            throw new Error('props.children must be an element of type Progress or StackedProgress.');
        }

        return null;
    }

    return (
        <div className={classnames(className, bem.valueOf(), 'clearfix')}>
            {!!leftLabel && <span className={classnames(bem.e('left'))}>{leftLabel}</span>}
            {!!rightLabel && <span className={classnames(bem.e('right'))}>{rightLabel}</span>}
            {children}
        </div>
    );
};

ProgressWithValue.propTypes = {
    /**
     * Should be a Progress or StackedProgress element
     */
    children: PropTypes.element.isRequired,

    /**
     * Label on left edge of progress bar, typically the value or sum of values represented by the progress bar
     */
    leftLabel: PropTypes.node,

    /**
     * Label on right edge of progress bar, typically the max or terminal value of the progress bar
     */
    rightLabel: PropTypes.node,
};

export default ProgressWithValue;
