import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { OBJECTS } from '../../../nimble/inventory';
import PoolsList from '../../../nimble/pools/collection/components/table/PoolsTableView';
import { SPACE_CATEGORY } from '../../../nimble/pools/collection/constants';
import style from './style.scss';

const cx = classnames.bind(style);

export const PoolsTable = ({ poolUid, config, userName }) => {
    const scope = poolUid ? {
        type: OBJECTS.POOL,
        id: poolUid,
    } : null;
    return (
        <div className={cx('container')}>
            <PoolsList
                tableId={`${userName}_NIMBLE_POOLS_CAPACITY`}
                scope={scope}
                config={config}
                enabledFieldSets={[SPACE_CATEGORY]}
                syncUrl={false}
                disableDefaultSort
            />
        </div>);
};

PoolsTable.propTypes = {
    poolUid: PropTypes.string,
    userName: PropTypes.string,
    config: PropTypes.object,
};

export default PoolsTable;
