import numeral from 'numeral';

export const INFO_NOT_AVAILABLE = 'N/A';
export const UNKNOWN = 'Unknown';

// Data Format
export const BYTE_FORMAT_PRECISION = '0.0 ib';
export const BYTE_FORMAT_NO_PRECISION = '0 ib';

// Number Format
export const NUMBER_FORMAT_NO_PRECISION = '0,0';
export const NUMBER_FORMAT_PRECISION = '0,0.0';

// Abbreviation Format
export const NUMBER_ABBREVIATE_FORMAT = '0.0a';

// PERCENT Format
export const PERCENT_FORMAT_PRECISION = '0.0 %';

// Milli Seconds
export const LATENCY_UNIT_ABBR = 'ms';

export const IOPS_UNIT_ABBR = 'IO/sec';

// Percentage
export const PERCENT_ABBR = '%';

// TODO: Move all these 3 fucntions to common util & Add Headline Value Formatter function
export const isEmpty = value => !!((value === null || value === undefined || value === ''));

export const formatValue = (value, format) => ((value === null || value === undefined) ? INFO_NOT_AVAILABLE : numeral(value).format(format));

// Do not show anything if the value is empty
export const listValueFormatter = (value, format, unitAbbr) => {
    let numeralValue = formatValue(value, format);

    // numeral doesnt return the B when value is less than one
    if (value > 0 && value < 1 && format.includes('b')) {
        numeralValue += 'B';
    }
    return ((isEmpty(value)) ? null : `${numeralValue}${!isEmpty(unitAbbr) ? ` ${unitAbbr}` : ''}`);
};
