import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { INFO_NOT_AVAILABLE } from '../util';

/**
 * If the days until full is less than the noted period it falls under that particular category
 */
const RISK_CATEGORIES = Object.freeze({
    AT_RISK_1: '1 Week',
    AT_RISK_2: '2 Weeks',
    NOT_SO_URGENT_1: '1 Month',
    NOT_SO_URGENT_2: '2 Months',
    NOT_SO_URGENT_3: '3 Months',
    NO_RISK: 'No Risk',
});

export const categorizeDaysUntilFull = (days) => {
    // TODO: Use isEmpty() once its moved to common util lib
    if (days === null || days === undefined) {
        return { category: INFO_NOT_AVAILABLE };
    } else if (days <= 7) {
        return { category: RISK_CATEGORIES.AT_RISK_1, colorClass: 'text-danger' };
    } else if (days <= 15) {
        return { category: RISK_CATEGORIES.AT_RISK_2, colorClass: 'text-danger' };
    } else if (days <= 31) {
        return { category: RISK_CATEGORIES.NOT_SO_URGENT_1 };
    } else if (days <= 60) {
        return { category: RISK_CATEGORIES.NOT_SO_URGENT_2 };
    } else if (days <= 90) {
        return { category: RISK_CATEGORIES.NOT_SO_URGENT_3 };
    }
    return { category: RISK_CATEGORIES.NO_RISK };
};

const DaysUntilFull = ({ className, days, inline }) => {
    const daysCategory = categorizeDaysUntilFull(days);
    // Do not show empty values in list ( TODO: Tech Debt this is different from all other list values)
    if (daysCategory.category === INFO_NOT_AVAILABLE && inline) {
        return null;
    }
    return (
        <div className={className}>
            <span className={classnames(daysCategory.colorClass)}>{daysCategory.category}</span>
        </div>
    );
};

DaysUntilFull.propTypes = {
    days: PropTypes.number,
    /**
     * True for lists, false for Headline etc.,
     */
    inline: PropTypes.bool,
};

DaysUntilFull.defaultProps = {
    inline: false,
};


export default DaysUntilFull;
