const _ = require('underscore');
var Marionette = require('backbone.marionette');
var Backbone = require('backbone');
var tpl = require('raw-loader!./FactorsHistogram.html');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    initialize: function () {
        this.series = this.getSeries();
        this.model = new Backbone.Model({
            primaryFactor: _(this.series)
                .chain()
                .sortBy('y')
                .last()
                .value(),
        });
    },

    onRender: function () {
        this.chart = this.$('[data-chart="histogram"]').highcharts({
            credits: {
                enabled: false,
            },
            chart: {
                type: 'bar',
                height: 70,
                spacing: [0, 0, 0, 1], // Need 1px on left or axis seems to get hidden
            },
            legend: {
                enabled: false,
            },
            title: {
                text: null,
            },
            xAxis: {
                categories: _(this.getOption('factors')).pluck('name'),
                labels: {
                    enabled: false,
                },
                minorTickLength: 0,
                tickLength: 0,
            },
            yAxis: {
                min: 0,
                visible: false,
            },
            series: [
                {
                    data: this.series,
                },
            ],
            plotOptions: {
                series: {
                    groupPadding: 0,
                },
            },
            tooltip: {
                formatter: function () {
                    return this.point.name;
                },
            },
        });
    },

    getSeries: function () {
        var factors = _(this.getOption('factors')).pluck('id');
        var totalWeight = 0;
        var threshold = this.getOption('threshold');
        var factorValues = this.collection
            .chain()
            .reduce(function (memo, point) {
                var totalIops = point.get('iopsRead') + point.get('iopsWrite');
                var totalLatency = (point.get('latencyRead') * point.get('iopsRead') + point.get('latencyWrite') * point.get('iopsWrite')) / totalIops;
                if (totalLatency > threshold) {
                    totalWeight += point.get('factorWindowWeight');
                    factors.forEach(function (factor) {
                        memo[factor] += point.get(factor) * point.get('factorWindowWeight');
                    });
                }
                return memo;
            }, _(factors).object(_.times(factors.length, _.constant(0))))
            .mapObject(function (sum) {
                return totalWeight === 0 ? 0 : sum / totalWeight;
            })
            .value();
        return _(this.getOption('factors'))
            .chain()
            .map(function (factor) {
                return _(factor).extend({
                    y: factorValues[factor.id],
                });
            })
            .sortBy('y')
            .value()
            .reverse();
    },

    onAttach: function () {
        if (this.chart) {
            this.chart.highcharts().reflow();
        }
    },

});
