const _ = require('underscore');
var Backbone = require('backbone');
var Marionette = require('backbone.marionette');
var numeral = require('numeral');
var FactorsChart = require('./FactorsChart');
var chartColors = require('./ChartColors');
var tpl = require('raw-loader!./LatencyChart.html');

var METRICS = [
    {
        id: 'latencyRead',
        name: 'Read',
        color: chartColors.read.toHexString(),
    },
    {
        id: 'latencyWrite',
        name: 'Write',
        color: chartColors.write.toHexString(),
    },
];

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    templateHelpers: {
        numeral: numeral,
    },

    regions: {
        factors: '[data-region="factorsChart"]',
    },

    initialize: function (options) {
        var maxLatency = _.max(this.collection.calculateMaximums(_(METRICS).pluck('id')));
        var averages = this.collection.calculateAverages(_(METRICS).pluck('id'));
        this.model = new Backbone.Model({
            metrics: METRICS.map(function (metric) {
                return _(metric).extend({
                    average: averages[metric.id],
                    percent: numeral(averages[metric.id] / maxLatency).format('0%'),
                });
            }),
        });
    },

    onRender: function () {
        var collection = this.collection;
        this.$('[data-chart="raw"]').highcharts('StockChart', _(this.options.chartDefaults({
            title: { text: 'Raw' },
        })).extend({
            series: METRICS.map(function (metric) {
                return _({
                    data: collection.getSeries(metric.id),
                }).extend(metric);
            }),
            yAxis: _(this.options.chartDefaults({}).yAxis).extend({
                title: {
                    text: 'ms',
                },
            }),
        }));

        this.showChildView('factors', new FactorsChart({
            chartDefaults: this.options.chartDefaults,
            collection: this.collection,
            allFlash: this.allFlash,
        }));
    },

});
