import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { ClientControlledTable } from '@infosight/elmer/dist/components/Table';
import { getSortedValues } from './gridViewOptions';
import { foldersConfig } from './foldersConfig';
import style from './style.scss';

const cx = classnames.bind(style);

function FoldersTable({ values, loadingCollection, userName }) {
    // TODO get this from backend. Doing it here to expedite 2017.03-1 release and make Karthik happy
    const sortedData = getSortedValues(values);
    const data = sortedData && sortedData.map(r => ({
        ...r,
        percentFree: !r.folderLimit ? 2 : (1 - (r.totalUsed / r.folderLimit)),
    }));

    const { columns, categories } = foldersConfig();

    return (
        <div className={cx('container')}>
            <ClientControlledTable
                tableId={`${userName}_NIMBLE_FOLDERS_CAPACITY`}
                data={data || []}
                columns={columns}
                categories={categories}
                loading={loadingCollection}
                // TODO: Disabled until table params are namespaced
                // defaultSorted={[{ id: 'folderName', desc: false }]}
                syncUrl={false}
            />
        </div>
    );
}

FoldersTable.propTypes = {
    loadingCollection: PropTypes.bool.isRequired,
    userName: PropTypes.string,
    values: PropTypes.arrayOf(PropTypes.shape({
        folderName: PropTypes.string,
        volumeCount: PropTypes.number.isRequired,
        volumeUsed: PropTypes.number.isRequired,
        snapshotUsed: PropTypes.number.isRequired,
        totalUsed: PropTypes.number.isRequired,
        growthPct30day: PropTypes.number,
        growthPct60day: PropTypes.number,
        growthPct90day: PropTypes.number,
        folderLimit: PropTypes.number,
    })),
};

export default FoldersTable;
