// TODO try to condense all these "queryParams.js" files into a single util
const buildParam = (type, param) => `perf.${type}${param}`;
export const timeParam = param => buildParam('time.', param);
export const perspectiveParam = param => buildParam('perspective.', param);
const performanceParam = param => `perf.${param || ''}`;
export const performanceTimeRangeParam = param => performanceParam(`time.${param || ''}`);
