import { createReducersForAction, createSelector } from '@infosight/elmer/dist/utils/redux';
import createItemUpdateReducersForAction from '../../../store/utils/createItemUpdateReducersForAction';

import rootState from '../rootStateSelector';
import { USER_CONTEXT_CHANGED } from '../../user/constants';
import {
    FETCH_ADDRESSES,
    FETCH_ASSETS,
    ADD_ADDRESS,
    EDIT_ADDRESS,
    SET_DELIVERY_PREFERENCE,
    VALIDATE_ADDRESS,
    RESET,
} from './constants';


const initialState = {
    addressCollection: null,
    assetCollection: null,
};

const ACTION_HANDLERS = {
    [USER_CONTEXT_CHANGED]: () => (initialState),
    [RESET]: () => (initialState),
    ...createReducersForAction({ type: FETCH_ADDRESSES, stateKey: 'addressCollection' }),
    ...createReducersForAction({ type: FETCH_ASSETS, stateKey: 'assetCollection' }),
    ...createItemUpdateReducersForAction({ type: ADD_ADDRESS, stateKey: 'addressCollection', matchKey: 'id' }),
    ...createItemUpdateReducersForAction({ type: EDIT_ADDRESS, stateKey: 'addressCollection', matchKey: 'id' }),
    ...createItemUpdateReducersForAction({ type: SET_DELIVERY_PREFERENCE, stateKey: 'assetCollection', matchKey: 'id' }),
    ...createItemUpdateReducersForAction({ type: VALIDATE_ADDRESS, stateKey: 'assetCollection', matchKey: 'id' }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

const baseSelector = state => rootState(state);

export const addressSelector = createSelector(baseSelector, 'address');
export const assetSelector = createSelector(baseSelector, 'asset');
export const reset = () => ({ type: RESET });
