import classnames from 'classnames/bind';
import numeral from 'numeral';
import { FillPattern } from '@infosight/elmer/dist/charts/highcharts';
import style from './style.scss';
import { lightGrey04 } from '../../../../util/colors';

const cx = classnames.bind(style);

const unusedReservePattern = new FillPattern('unusedReservePattern')
    .svgPath(FillPattern.patterns[0])
    .strokeWidth(1)
    .strokeColor('#B0CED4')
    .dimensions(5)
    .fillColor(lightGrey04);

export default ({ height, width, data }) => ({
    chart: {
        type: 'pie',
        spacingTop: 0,
        spacingBottom: 0,
        spacingLeft: 0,
        spacingRight: 0,
        marginTop: -10,
        marginRight: 0,
        marginBottom: -10,
        marginLeft: -20,
        height,
        width,
    },
    defs: {
        patterns: [unusedReservePattern],
    },
    title: false,
    plotOptions: {
        pie: {
            shadow: false,
            dataLabels: {
                enabled: false,
            },
            center: [height / 2, '50%'],
        },
    },
    tooltip: {
        enabled: false,
    },
    series: [
        {
            name: 'Series',
            data,
            showInLegend: true,
            size: '100%',
            innerSize: '85%',
            borderWidth: 0,
            states: {
                hover: {
                    enabled: false,
                },
            },
        },
    ],
    legend: {
        enabled: true,
        layout: 'vertical',
        // itemStyle: {width: '50%'},
        align: 'left',
        width: 0,
        x: height + 20,
        verticalAlign: 'middle',
        padding: 0,
        margin: 0,
        itemMarginTop: 5,
        itemMarginBottom: 5,
        symbolHeight: 10,
        symbolWidth: 10,
        useHTML: true,
        labelFormatter() {
            return `<div class=""${cx('legend')}>
                        <span class=""${cx('left')}>${this.name}</span>
                        <span class=""${cx('right')}>${numeral(this.y).format('0.0 ib')}</span>
                    </div>`;
        },
    },
});
