import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import classnames from 'classnames/bind';
import autobind from 'react-autobind';
import moment from 'moment-timezone';
import { Tabs, Tab, TabList, TabPanel, TabClasses } from '@infosight/elmer/dist/components/Tabs';
import Async from '@infosight/elmer/dist/components/Async';
import NoData from '@infosight/elmer/dist/components/NoData';
import DonutChart from '../../../charts/DataUsageDonut';
import { PoolCapacityTimeSeries, TopPoolsCapacityTimeSeries } from '../timeSeries';
import TotalUsage from './summary/TotalUsage';
import Volumes from './summary/Volumes';
import Savings from '../../../nimble/pools/detail/components/headline/Savings';
import { userSelector } from '../../../user/reducer';
import { FoldersTable, ApplicationsTable, PoolsTable, VolumesTable } from '../usageDetailTable';
import style from './style.scss';
import PoolTrends from './summary/PoolTrends';
import DaysUntilFull from './summary/DaysUntilFull';
import NimbleOSVersionLink from '../NimbleOSVersionLink';

const cx = classnames.bind(style);

class PoolCapacity extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    renderTabs() {
        const {
            applications,
            loadingApplications,
            folders,
            loadingFolders,
            isAggregate,
            poolUid,
            config,
            userName,
        } = this.props;
        const showFolders = !isAggregate && folders && folders.length;
        return (
            <Tabs forceRenderTabPanel>
                <TabList className={TabClasses.TableHeader}>
                    {isAggregate && <Tab>Pools</Tab>}
                    <Tab>Applications</Tab>
                    <Tab disabled={!showFolders}>Folders</Tab>
                    <Tab>Volumes</Tab>
                </TabList>
                {isAggregate && (
                    <TabPanel>
                        <PoolsTable userName={userName} poolUid={poolUid} config={config} />
                    </TabPanel>
                )}
                <TabPanel>
                    <ApplicationsTable userName={userName} values={applications} loadingCollection={loadingApplications} />
                </TabPanel>
                <TabPanel>
                    <FoldersTable userName={userName} values={folders} loadingCollection={loadingFolders} />
                </TabPanel>
                <TabPanel>
                    <VolumesTable userName={userName} poolUid={poolUid} config={config} />
                </TabPanel>
            </Tabs>
        );
    }

    renderTrends() {
        const { arrayCapacity, summary } = this.props;

        if (!arrayCapacity) {
            return null;
        }

        return (
            <div>
                <div className={cx('subtitle')}>Trends</div>
                <PoolTrends arrayCapacity={arrayCapacity} pool={summary} />
            </div>
        );
    }

    renderShelfConfig() {
        if (!this.props.arrayCapacity) {
            return null;
        }

        const { arrayCapacity: { hostname, group, version, model, serialNumber } } = this.props;

        return (
            <div>
                <div className={cx('subtitle')}>System Configuration</div>
                <div className={cx('system-config')}>
                    <dl>
                        <dt>Array</dt>
                        <dd>{hostname}</dd>

                        <dt>Model</dt>
                        <dd>{model}</dd>

                        <dt>Version</dt>
                        <dd>
                            <NimbleOSVersionLink hostname={hostname} version={version} serialNumber={serialNumber} />
                        </dd>
                    </dl>
                    <dl>
                        <dt>Serial Number</dt>
                        <dd>{serialNumber}</dd>

                        <dt>Group</dt>
                        <dd>{group}</dd>
                    </dl>
                </div>
            </div>
        );
    }

    renderSummary() {
        const { summary, isAggregate } = this.props;
        if (!summary) {
            return <NoData hasData={false} message="No data available." />;
        }

        const {
            savingsDedupBytes,
            savingsCompressionBytes,
            savingsCloneBytes,
            totalSavingsBytes,
            cloneSavingsFactor,
            compressionSavingsFactor,
            dedupSavingsFactor,
            totalSavingsFactor,
            daysUntilFull,
            usedBytes,
            lastWeekGrowthPercent,
            unusedReserveBytes,
            freeBytes,
            volumeCount,
            totalAllocated,
            allocatedBytes,
        } = summary;

        return (
            <div className={cx('summary')}>
                <div className={cx('total-usage')}>
                    <TotalUsage
                        growthPctLastWeek={lastWeekGrowthPercent}
                        totalUsage={usedBytes}
                        capacity={allocatedBytes}
                    />
                    {!isAggregate && daysUntilFull !== null && (
                        <span>
                            Forecasting full capacity in
                            <strong> <DaysUntilFull days={daysUntilFull} /></strong>
                        </span>
                    )}
                </div>
                <div className={cx('chart')}>
                    <DonutChart
                        allocatedBytes={allocatedBytes}
                        usedBytes={usedBytes}
                        freeBytes={freeBytes}
                        unusedReserveBytes={unusedReserveBytes}
                        growthPercent={lastWeekGrowthPercent}
                        warningThreshold={90}
                    />
                </div>
                <Volumes volumeCount={volumeCount} totalAllocated={totalAllocated} />
                <div>
                    <div className={cx('subtitle')}>Savings</div>
                    <Savings
                        dedupSavings={savingsDedupBytes}
                        compressionSavings={savingsCompressionBytes}
                        cloneSavings={savingsCloneBytes}
                        cloneSavingsFactor={cloneSavingsFactor}
                        compressionSavingsFactor={compressionSavingsFactor}
                        dedupSavingsFactor={dedupSavingsFactor}
                        totalSavingsFactor={totalSavingsFactor}
                        totalSavings={totalSavingsBytes}
                        usedBytes={usedBytes}
                    />
                </div>
                {!isAggregate && this.renderTrends()}
                {!isAggregate && this.renderShelfConfig()}
            </div>
        );
    }

    renderTimeSeries() {
        const { timeSeries, topPoolsTimeSeries, isAggregate, lookbackMonths } = this.props;
        const chartRangeEnd = moment().startOf('day');
        const chartRangeStart = moment(chartRangeEnd).subtract(lookbackMonths, 'months').add(1, 'day');

        return (
            <div className={cx('time-series-chart', { 'top-pools': isAggregate })}>
                {isAggregate
                    ? (
                        <TopPoolsCapacityTimeSeries
                            pools={topPoolsTimeSeries}
                            chartRangeStart={chartRangeStart}
                            chartRangeEnd={chartRangeEnd}
                        />
                    )
                    : (
                        <PoolCapacityTimeSeries
                            timeSeries={timeSeries}
                            chartRangeStart={chartRangeStart}
                            chartRangeEnd={chartRangeEnd}
                        />
                    )}
            </div>
        );
    }

    render() {
        const {
            loadingSummary,
            loadingTimeSeries,
            loadingArrayCapacity,
            loadingTopPoolsTimeSeries,
            summary,
            timeSeries,
            topPoolsTimeSeries,
        } = this.props;

        // Has the summary and either all pools or single pool summary. Don't care about table data here.
        const hasData = !loadingSummary && summary &&
            ((!loadingTimeSeries && timeSeries) || (!loadingTopPoolsTimeSeries && topPoolsTimeSeries));

        const loading = loadingSummary || loadingArrayCapacity || loadingTimeSeries || loadingTopPoolsTimeSeries;

        return (
            <Async loading={loading}>
                <NoData hasData={!!hasData}>
                    <article className={cx('layout')}>
                        <section>{this.renderSummary()}</section>
                        <section className={cx('time-series', 'inset')}>
                            <h1>Usage</h1>
                            {this.renderTimeSeries()}
                        </section>
                        <section className={cx('facet-tables', 'inset')}>{this.renderTabs()}</section>
                    </article>
                </NoData>
            </Async>
        );
    }
}

PoolCapacity.propTypes = {
    poolUid: PropTypes.string,
    loadingTimeSeries: PropTypes.bool.isRequired,
    loadingSummary: PropTypes.bool.isRequired,
    loadingApplications: PropTypes.bool.isRequired,
    loadingFolders: PropTypes.bool.isRequired,
    loadingArrayCapacity: PropTypes.bool.isRequired,
    loadingTopPoolsTimeSeries: PropTypes.bool,
    summary: PropTypes.shape({
        allocatedBytes: PropTypes.number,
        arrayHostName: PropTypes.string,
        arraySearchableName: PropTypes.string,
        arraySn: PropTypes.string,
        daysUntilFull: PropTypes.number,
        folderCount: PropTypes.number,
        freeBytes: PropTypes.number,
        groupName: PropTypes.string,
        groupUid: PropTypes.string,
        lastWeekGrowthPercent: PropTypes.number,
        poolName: PropTypes.string,
        poolUid: PropTypes.string,
        savingsCloneBytes: PropTypes.number,
        savingsCompressionBytes: PropTypes.number,
        savingsDedupBytes: PropTypes.number,
        cloneSavingsFactor: PropTypes.number,
        compressionSavingsFactor: PropTypes.number,
        dedupSavingsFactor: PropTypes.number,
        totalSavingsFactor: PropTypes.number,
        snapshotCount: PropTypes.number,
        totalSavingsBytes: PropTypes.number,
        unusedReserveBytes: PropTypes.number,
        usedBytes: PropTypes.number,
        volumeCount: PropTypes.number,
        foldersFull1Month: PropTypes.number,
        foldersFull2Month: PropTypes.number,
    }),
    arrayCapacity: PropTypes.shape({
        group: PropTypes.string.isRequired,
        hostname: PropTypes.string.isRequired,
        model: PropTypes.string.isRequired,
        serialNumber: PropTypes.string.isRequired,
        version: PropTypes.string.isRequired,
    }),
    applications: PropTypes.array,
    folders: PropTypes.array,
    timeSeries: PropTypes.array,
    topPoolsTimeSeries: PropTypes.array,
    isAggregate: PropTypes.bool.isRequired,
    lookbackMonths: PropTypes.number.isRequired,
    config: PropTypes.object,
    userName: PropTypes.string,
};

const mapStateToProps = state => ({
    userName: userSelector(state).username,
});

export default connect(mapStateToProps)(PoolCapacity);
