import numeral from 'numeral';
import moment from 'moment-timezone';
import { INFO_NOT_AVAILABLE,
    FORMAT_BYTE_TYPE,
} from './constants';

export const isEmpty = value => !!((value === null || value === undefined || value === ''));

/**
 * Formats the value with the given format and appends unit abbreviation when provided
 * If either value & format is not provided, it returns null
 * @param {*} value
 * @param {*} format
 * @param {*} unitAbbr
 */
export const formatValue = (value, format, unitAbbr) => {
    if (!format || isEmpty(value)) {
        return null;
    }

    let numeralFormattedValue = numeral(value).format(format);

    // numeral doesn't return the B when value is less than one
    if (format.includes(FORMAT_BYTE_TYPE) && value > 0 && value < 1) {
        numeralFormattedValue += 'B';
    }

    return `${numeralFormattedValue}${!isEmpty(unitAbbr) ? ` ${unitAbbr}` : ''}`;
};

/**
 * On Headlines, it is expected to show INFO_NOT_AVAILABLE when the data is empty
 */
export const headlineValueFormatter = (value, format, unitAbbr) =>
    (isEmpty(value) ? INFO_NOT_AVAILABLE : formatValue(value, format, unitAbbr));
// Currently an alias.  We want N/A for empty headline and list values.
export const listValueFormatter = headlineValueFormatter;


export const formatDateTime = (dateTimeValue, dateTimeFormat, isUTC = false) => {
    if (!dateTimeValue || !dateTimeFormat) {
        return null;
    }
    return isUTC ? moment.utc(dateTimeValue).format(dateTimeFormat) : moment(dateTimeValue).format(dateTimeFormat);
};

// TODO: Better name this function
export const formatDateTimeWithTimeZone = (dateTimeValue, dateTimeFormat, tz) => {
    if (!dateTimeValue || !dateTimeFormat) {
        return null;
    }
    let momentObj = moment(dateTimeValue);
    if (tz) {
        momentObj = momentObj.tz(tz);
    }
    return `${momentObj.fromNow()} (${momentObj.format(dateTimeFormat)})`;
};
