import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { isFunction } from 'underscore';
import { resetFolderCapacity, getFolderCapacity } from '../../actionCreators/folder';
import FolderCapacity from '../layout/FolderCapacity';
import { folderContextSelector, isLoadingSelector } from '../../reducer/folderContext/selectors';

class FolderCapacityContainer extends Component {
    componentDidMount() {
        if (!this.props.loading) {
            this.props.getData({
                folderId: this.props.folderId,
                poolUid: this.props.poolUid,
                lookback: this.props.lookbackMonths,
            });
        }
    }

    componentWillReceiveProps({ poolUid, folderId, lookbackMonths, loading }) {
        if (
            this.props.folderId !== folderId ||
            this.props.poolUid !== poolUid ||
            this.props.lookbackMonths !== lookbackMonths
        ) {
            this.props.getData({ folderId, poolUid, lookback: lookbackMonths });
        }

        if (this.props.toggleFilters) {
            this.props.toggleFilters(!loading);
        }
    }

    componentWillUnmount() {
        if (isFunction(this.props.reset)) {
            this.props.reset();
        }
    }

    render() {
        return <FolderCapacity {...this.props} />;
    }
}

FolderCapacityContainer.propTypes = {
    getData: PropTypes.func.isRequired,
    reset: PropTypes.func,
    loading: PropTypes.bool.isRequired,

    // These should be passed from parent component
    lookbackMonths: PropTypes.number.isRequired,
    folderId: PropTypes.string.isRequired,
    poolUid: PropTypes.string.isRequired,
    toggleFilters: PropTypes.func,
};

FolderCapacityContainer.defaultProps = {
    lookbackMonths: 3,
    loading: false,
};

const mapStateToProps = state => ({
    ...folderContextSelector(state),
    loading: isLoadingSelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    getData: getFolderCapacity,
    reset: resetFolderCapacity,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(FolderCapacityContainer);
