import React from 'react';
import PropTypes from 'prop-types';

const totalColumns = 24;

function generateColumnConfig(columnRatios) {
    const colCount = [];
    let total = 0;

    columnRatios.forEach((item) => {
        total += item;
    });

    columnRatios.forEach((item) => {
        const gridColumnsInColumn = (item / total) * totalColumns;
        if (gridColumnsInColumn !== Math.floor(gridColumnsInColumn)) {
            throw (new Error('column ratios must neatly fill 24 total columns'));
        }
        colCount.push(gridColumnsInColumn);
    });

    return colCount;
}

function getResponsiveGridColumnClassName(i, sm, md, lg) {
    const small = sm ? `small-${sm[i]}` : '';
    const medium = md ? `medium-${md[i]}` : '';
    const large = lg ? `large-${lg[i]}` : '';

    return `${small} ${medium} ${large} columns`;
}

// TODO can this be used? The idea is good, but need to make it easy to use or hide it by exporting prescriptive
// components that build on this.
export default function LayoutGenerator(options) {
    const {
        columnRatiosSmall,
        columnCountSmall,
        columnRatiosMedium,
        columnCountMedium,
        columnRatiosLarge,
        columnCountLarge,
        rowClasses,
    } = options;

    let colCountSmall;
    let colCountMedium;
    let colCountLarge;

    if (columnRatiosSmall) {
        colCountSmall = generateColumnConfig(columnRatiosSmall);
    } else if (columnCountSmall) {
        colCountSmall = columnCountSmall;
    }

    if (columnRatiosMedium) {
        colCountMedium = generateColumnConfig(columnRatiosMedium);
    } else if (columnCountMedium) {
        colCountMedium = columnCountMedium;
    }

    if (columnRatiosLarge) {
        colCountLarge = generateColumnConfig(columnRatiosLarge);
    } else if (columnCountLarge) {
        colCountLarge = columnCountLarge;
    }

    const colCount = colCountSmall || colCountMedium || colCountLarge;

    const Layout = ({ children }) => (
        <div className={`row ${rowClasses ? rowClasses.join(' ') : null}`}>
            {colCount.map((item, i) => (
                <div
                    className={getResponsiveGridColumnClassName(
                        i,
                        colCountSmall,
                        colCountMedium,
                        colCountLarge
                    )}
                    key={i.toString()}
                >
                    {children.filter(obj => (obj.key === i.toString()))}
                </div>
            ))}
        </div>
    );

    Layout.propTypes = {
        children: PropTypes.arrayOf(PropTypes.node),
    };

    return Layout;
}
