var _ = require('underscore');
var Marionette = require('backbone.marionette');
var GridFilterModel = require('./GridFilterModel');
var tpl = require('raw-loader!./GridFilterView.html');
var $ = require('jquery');
require('multiple-select');
require('multiple-select/multiple-select.css');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    className: 'filter-widget',

    ui: {
        toggleFiltersLink: '.toggle-filters-link',
        filterWrapper: '.grid-filter-wrapper',
        clearFiltersLink: '.clear-filters',
        filterSection: '.grid-filter-section',
    },

    events: {
        'click @ui.toggleFiltersLink': 'toggleFilters',
        'click @ui.clearFiltersLink': 'clearFilters',
    },

    modelEvents: {
        'change': 'onChange',
    },

    gridInitialized: false,

    initialize: function (options) {
        this.columnIndexMap = {};
        this.grid = options.grid;
        console.assert(this.grid, 'grid option is required');

        this.model = options.grid.options.filterModel || new GridFilterModel();

        this.grid.filterValueHasMatchingRowsByColumn = {};
        this.grid.unfilteredRowCache = [];
        this.grid.filteredRowCache = [];

        this.listenTo(this.grid, 'init', this.createFilters);
        this.listenTo(this.grid, 'reload', this.createFilters);
    },

    onRender: function () {
        this.$el.hide();
        this.ui.clearFiltersLink.toggle(!_(this.model.values()).every(x => (_.isNull(x) || _.isEmpty(x))));
    },

    resetMultiselectFilters: function () {
        this.grid.filteredRowCache = [];
        this.grid.unfilteredRowCache = [];
    },

    createFilters: function () {
        this.ui.filterSection.empty();

        _(this.grid.getDataTableApi().settings().init().columns).each(this.createFilter.bind(this));

        this.$el.toggle(!_.isEmpty(this.model.attributes));

        this.gridInitialized = true;

        this.ui.clearFiltersLink.toggle(!_(this.model.values()).every(x => (_.isNull(x) || _.isEmpty(x))));
    },

    createFilter: function (columnDef, columnIndex) {
        var api = this.grid.getDataTableApi();

        if (columnDef.filter) {
            var column = api.column(columnIndex);
            var columnDisplayName = $(column.header()).html();
            var columnName = columnDef.name;

            var filterValues;
            if (!columnDef.filter.splitValueOn) {
                filterValues = column.cache('search').unique().toArray();
            } else {
                filterValues = column.cache('search').unique().toArray().reduce(function (arr, val) {
                    return arr.concat(val.split(',').map(function (str) {
                        return str.trim();
                    }));
                }, []);
            }

            var uniqueVals = _.union(
                filterValues,
                [this.model.get(columnName)],  // Always include preset filter value
            ).filter(function (val) {
                if (typeof val === 'string') {
                    return val != null && val.trim();
                } else {
                    return val != null;
                }
            }).sort(function (s1, s2) {
                return s2.toUpperCase().localeCompare(s1.toUpperCase());
            });

            if (_.isEmpty(uniqueVals)) {
                return;
            }

            var label = $('<label><span>' + columnDisplayName + ':</span></label>');
            var select = $('<select/>')
                .attr('data-column-name', columnName);

            for (var i = 0, len = uniqueVals.length; i < len; i++) {
                var val = uniqueVals[i];
                select.append('<option value="' + val + '">' + val + '</option>');
            }

            this.ui.filterSection
                .append($('<div class="filter-component"/>')
                    .append(label)
                    .append(select),
                );

            if (!this.model.get(columnName)) {
                this.model.set(columnName, null);
            }

            const self = this;

            if (columnDef.filter.multipleSelect) {

                var filterClearOption = function () {
                    Object.keys(self.grid.filterValueHasMatchingRowsByColumn).forEach(function (columnName) {
                        // If there are no selections made -- disable "Clear Filter"
                        var $clearEl = $('select[data-column-name="' + columnName + '"] + .ms-parent .ms-drop input[data-name="selectAll"]');
                        var filterSet = $('select[data-column-name="' + columnName + '"]').multipleSelect('getSelects').length;
                        if (filterSet) {
                            $clearEl
                                .attr('disabled', null)
                                .parent('label').css('color', '');
                        } else {
                            $clearEl
                                .attr('disabled', 'disabled')
                                .parent('label').css('color', '#DDDDDD');
                        }
                    });
                };

                var filterColumnOptionsByOtherColumns = function (currentColumnName) {
                    var columns = api.settings().init().columns;

                    // start marking all values for all rows disabled
                    for (var unfilteredRow of self.grid.unfilteredRowCache) {
                        for (var column of columns) {
                            var cn = column.name;
                            if (self.grid.filterValueHasMatchingRowsByColumn[cn] === undefined) {
                                self.grid.filterValueHasMatchingRowsByColumn[cn] = {};
                            }

                            var rv = unfilteredRow[column.index];
                            if (column.filter && column.filter.splitValueOn) {
                                _(rv.split(column.filter.splitValueOn))
                                    .forEach(function (x) {
                                        self.grid.filterValueHasMatchingRowsByColumn[cn][x.trim()] = { row: unfilteredRow, enabled: false };
                                    });
                            } else {
                                self.grid.filterValueHasMatchingRowsByColumn[cn][rv] = { row: unfilteredRow, enabled: false };
                            }
                        }
                    }

                    // now marking all values for all INCLUDED rows ENabled
                    for (var filteredRow of self.grid.filteredRowCache) {
                        for (column of columns) {
                            cn = column.name;
                            rv = filteredRow[column.index];

                            if (column.filter && column.filter.splitValueOn) {
                                _(rv.split(column.filter.splitValueOn))
                                    .forEach(function (x) {
                                        self.grid.filterValueHasMatchingRowsByColumn[cn][x.trim()] = { row: filteredRow, enabled: true };
                                    });
                            } else {
                                self.grid.filterValueHasMatchingRowsByColumn[cn][rv] = { row: filteredRow, enabled: true };
                            }
                        }
                    }

                    Object.keys(self.grid.filterValueHasMatchingRowsByColumn).forEach(function (columnName) {
                        var col = self.grid.filterValueHasMatchingRowsByColumn[columnName];
                        Object.keys(col).forEach(function (value) {
                            var filterResult = col[value];
                            var $el = $('select[data-column-name="' + columnName + '"] + .ms-parent .ms-drop input[value="' + value + '"]');

                            //  re-run the filter logic with this column skipped (last argument)
                            var filterWithoutCurrentColumn = self.filterLogic(null, null, filterResult.row, columnName);

                            if (filterResult.enabled !== false || filterWithoutCurrentColumn) {
                                $el
                                    .attr('disabled', null)
                                    .parent('label').css('color', '');
                            } else {
                                $el
                                    .attr('disabled', 'disabled')
                                    .parent('label').css('color', '#DDDDDD');
                            }
                        });
                    });
                };

                $(select)
                    .multipleSelect({
                        width: '200px',
                        selectAll: true,
                        selectAllText: 'Clear Filter',
                        allSelected: '',
                        onCheckAll: function (x) {
                            $(select).multipleSelect('uncheckAll');
                        },
                        onUncheckAll: function (x) {
                            self.model.set(columnName, $(select).multipleSelect('getSelects'));
                            filterClearOption();
                        },
                        onClick: function (x) {
                            self.resetMultiselectFilters();
                            self.model.set(columnName, x.instance.getSelects());
                            filterClearOption();
                        },
                        onOpen: function () {
                            filterColumnOptionsByOtherColumns(columnName);
                            filterClearOption();
                        },
                    });

                $(select).multipleSelect('uncheckAll');
            } else {
                this.addBinding(null, 'select[data-column-name="' + columnName + '"]', {
                    observe: columnName,
                    selectOptions: {
                        collection: uniqueVals, //_.object(uniqueVals, uniqueVals),
                        defaultOption: {
                            label: '',
                            value: null,
                        },
                    },
                });

            }

        }
    },

    toggleFilters: function () {
        this.ui.toggleFiltersLink.find('.fa').toggleClass('fa-caret-right fa-caret-down');
        this.ui.filterWrapper.toggle();
    },

    clearFilters: function () {
        this.model.clearValues();

        var columns = this.grid.getDataTableApi().settings().init().columns;
        for (var column of columns) {
            if (column.filter && column.filter.multipleSelect) {
                $($('.filter-component select')[column.index]).multipleSelect('uncheckAll');
            }
        }

        this.ui.clearFiltersLink.hide();
    },

    onChange: function () {
        this.ui.clearFiltersLink.toggle(!_(this.model.values()).every(x => (_.isNull(x) || _.isEmpty(x))));

        if (!this.gridInitialized) {
            return;
        }

        // Will trigger a re-evaluation of our fn.dataTable.ext.search plugin for every row in the table
        // Which will (eventually) call back into this::isRowIncluded() SEE BELOW
        this.grid.redraw();
    },

    filterLogic: function (rowIndex, rowObject, rowSearchData, ignoreColumnName) {
        var columns = this.grid.getDataTableApi().settings().init().columns;

        for (var column of columns) {
            var columnName = column.name;

            // multiselect option filtering requires being able to skip over the currently open column filter
            if (columnName == ignoreColumnName) {
                continue;
            }

            // the value for this field in the current row
            var rowValue = rowSearchData[column.index];

            // the value that is allowed (ie what the filter is set to for this column)
            var allowedValue = this.model.get(columnName);
            if (allowedValue == null) {
                // The filter is not set for this attribute, all values are allowed
                continue;
            }

            var columnFilterOptions = column.filter;
            if (columnFilterOptions && columnFilterOptions.splitValueOn) {
                if (typeof allowedValue === 'object') {
                    if (allowedValue.length === 0) {
                        continue;
                    }
                    let list = _(rowValue.split(columnFilterOptions.splitValueOn))
                        .chain()
                        .map(function (x) {
                            return x.trim();
                        })
                        .value();

                    if (!list.some(function (v) {
                        return allowedValue.indexOf(v) >= 0;
                    })) {
                        return false;
                    }
                } else if (!_(rowValue.split(columnFilterOptions.splitValueOn))
                    .chain()
                    .map(function (x) {
                        return x.trim();
                    })
                    .contains(allowedValue)
                    .value()
                ) {
                    return false;
                }
            } else {
                if (typeof allowedValue === 'object') {
                    if (allowedValue.length === 0) {
                        continue;
                    }
                    if (allowedValue.indexOf(rowValue) === -1) {
                        return false;
                    }
                } else {
                    if (rowValue !== allowedValue) {
                        return false;
                    }
                }
            }
        }
        return true;
    },

    // isRowIncluded is called by the datatable api, logic is broken out for multiselect option filtering
    isRowIncluded: function (rowIndex, rowObject, rowSearchData) {
        this.grid.unfilteredRowCache.push(rowSearchData);

        var filterResult = this.filterLogic(rowIndex, rowObject, rowSearchData);

        if (filterResult) {
            this.grid.filteredRowCache.push(rowSearchData);
        }

        return filterResult;
    },
});
