import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import autobind from 'react-autobind';
import Card from '@infosight/elmer/dist/components/Card';
import { QueryTimeRangeSelector, TimeRangeSelection, TIME_RANGE_DEFAULTS } from '@infosight/elmer/dist/components/TimeRangeSelector';
import { capacityTimeRangeParam } from '../../queryParams';
import { getPoolCapacity } from '../../actionCreators';
import { capacitySelector } from '../../reducer';
import CapacityChart from './CapacityChart';

class PoolCapacityContainer extends PureComponent {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = { displayTimeRange: '' };
    }

    updateDisplayTimeRange({ capacityDataMetaInfo }) {
        this.setState({
            displayTimeRange: TimeRangeSelection.toTimeRangeSelection({ start: capacityDataMetaInfo.dataStart, end: capacityDataMetaInfo.dataEnd }),
        });
    }

    render() {
        const { timeRange, scope, config } = this.props;
        return (
            <div>
                <Card title="Usage">
                    <div className="row expanded align-right">
                        <QueryTimeRangeSelector
                            parameterPrefix={capacityTimeRangeParam()}
                            value={timeRange}
                            presets={QueryTimeRangeSelector.PRESETS.NONVOLATILE}
                            displayValue={this.state.displayTimeRange}
                        />
                    </div>
                    <div className="chart-container--medium">
                        <CapacityChart
                            {...this.props}
                            timeRange={timeRange}
                            scope={scope}
                            config={config}
                            onDataReceived={this.updateDisplayTimeRange}
                        />
                    </div>
                </Card>
            </div>
        );
    }
}

PoolCapacityContainer.propTypes = {
    timeRange: PropTypes.instanceOf(TimeRangeSelection).isRequired,
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),
    config: PropTypes.object.isRequired,
};

PoolCapacityContainer.defaultProps = {
    timeRange: TIME_RANGE_DEFAULTS.NONVOLATILE,
};

const mapStateToProps = state => ({
    ...capacitySelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    getPoolCapacity,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(PoolCapacityContainer);
