const App = require('app/Application');
var Marionette = require('backbone.marionette');
var AccountSelection = require('./AccountSelection');
var AccountModel = require('./AccountModel');

var AccountController = Marionette.Object.extend({
    initialize: function () {
        App.handleRequest('global', 'get:account:selection', () => this.accountSelection, this);
        this.accountSelection = new AccountSelection();
    },

    selectOneAccount: function (tenant) {
        this.accountSelection.setAccount(new AccountModel(tenant));
        App.event('global', 'change:account', this.accountSelection);
    },
});

var instance;

module.exports = {
    setTenant(tenant) {
        this.getInstance().selectOneAccount(tenant);
    },

    getInstance() {
        if (!instance) {
            instance = new AccountController();
        }

        return instance;
    },
};
