import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import ReactDimensions from 'react-dimensions';
import { Highstock as Highcharts } from 'progeny/modules/nimble/highchartsProxy';
import { noop } from 'underscore';
import MouseOutTrackingHighchart from './MouseOutTrackingHighchart';

class ResponsiveChartWrapper extends PureComponent {
    config() {
        const { containerHeight, containerWidth, callback, config } = this.props;
        return Highcharts.merge(config, {
            chart: {
                height: containerHeight,
                width: containerWidth,
                events: {
                    redraw() {
                        if (typeof callback === 'function') {
                            callback(this);
                        }
                    },
                },
            },
        });
    }

    render() {
        return <MouseOutTrackingHighchart onMouseOut={noop} {...this.props} config={this.config()} />;
    }
}

ResponsiveChartWrapper.propTypes = {
    /**
     * The container height/width is merged into this Highcharts config object
     */
    config: PropTypes.object,

    /**
     * Executed by ReactHighcharts after the chart is rendered
     * @param {Highcharts.Chart} chart
     */
    callback: PropTypes.func.isRequired,

    onMouseOut: PropTypes.func.isRequired,

    // From ReactDimensions
    containerHeight: PropTypes.number.isRequired,
    containerWidth: PropTypes.number.isRequired,
};

export default ReactDimensions({ elementResize: true })(ResponsiveChartWrapper);
