var Cocktail = require('backbone.cocktail');
var Backbone = require('backbone');
Cocktail.patch(Backbone);
const $ = require('jquery');
var Marionette = require('backbone.marionette');
var ModalRegion = require('app/common/ModalRegion');
var moment = require('moment');

// TODO Why do we need this for an English-only UI?
try {
    moment.locale(window.navigator.language);
} catch (e) {
    moment.locale('en');
}

var MyApplication = Marionette.Application.extend({

    /**
     * Fetches a given channel by name.  If the given channel name is null,
     * then the global channel will be returned.
     * @param  {String} channel The name of the channel.
     * @return {Channel}        The appropriate channel.
     * @private
     */
    _getChannel: function (channel) {
        return channel ?
            Backbone.Wreqr.radio.channel(channel) :
            Backbone.Wreqr.radio.channel('global');
    },

    // REQUEST/RESPONSE

    /**
     * Makes a request.  Used to request some piece of data from another part of the system.
     * @param  {String} channel Name of the channel to scope the request.
     * @param  {String} name    Name of the request.
     * @param  {Object} data    The data to pass in the request.
     */
    request: function (channel, name, data) {
        return this._getChannel(channel).reqres.request(name, data);
    },

    /**
     * Responds to a request for data in another part of the system.
     * @param  {String}   channel  Name of the channel to scope the request.
     * @param  {String}   name     Name of the request to respond to.
     * @param  {Function} callback The callback function to execute in response to the request.
     * @param  {Object}   scope    The scope for the callback.
     * @return {Object}   Returns the result of the request.
     */
    handleRequest: function (channel, name, callback, scope) {
        return this._getChannel(channel).reqres.setHandler(name, callback, scope);
    },

    // COMMANDS/HANDLERS

    /**
     * Issues a command to another part of the system.
     * @param  {String} channel Name of the channel to scope the request.
     * @param  {String} name    Name of the request.
     * @param  {Object} data    The data to pass in the request.
     */
    command: function (channel, name, args) {
        var commands = this._getChannel(channel).commands;
        args = Array.prototype.slice.call(arguments, 1);
        commands.execute.apply(commands, args);
    },

    /**
     * Handles a command issued by another part of the system.
     * @param  {String}   channel  Name of the channel to scope the command.
     * @param  {String}   name     Name of the request to respond to.
     * @param  {Function} callback The callback function to execute in response to the request.
     * @param  {Object}   scope    The scope for the callback.
     */
    handleCommand: function (channel, name, callback, scope) {
        this._getChannel(channel).commands.setHandler(name, callback, scope);
    },

    // EVENTS

    /**
     * Fires an event on the given channel.
     * @param  {String} channel Name of the channel to scope the event.
     * @param  {String} name    Name of the event.
     * @param  {Object} data    The data to pass in the request.
     */
    event: function (channel, name, data) {
        this._getChannel(channel).vent.trigger(name, { ...(data || {}), eventName: name });
    },

    /**
     * Handles a event triggered by another part of the system.
     * @param  {String}   channel  Name of the channel to scope the event.
     * @param  {String}   name     Name of the request to respond to.
     * @param  {Function} callback The callback function to execute in response to the request.
     * @param  {Object}   scope    The scope for the callback.
     */
    handleEvent: function (channel, name, callback, scope) {
        this._getChannel(channel).vent.on(name, callback, scope);
    },

    handleProgenyEvent: function (name, callback, scope) {
        this.handleEvent('progeny', name, callback, scope);
    },

    removeEvent: function (channel, name, callback, scope) {
        this._getChannel(channel).vent.off(name, callback, scope);
    },

});

// setup a global App variable to make it easy to gain access to
var App = new MyApplication();

// configure the application regions
App.addRegions({
    modalRegion: {
        selector: '#modal-region',
        regionClass: ModalRegion,
    },
});

App.initializeLegacyControllers = function initializeLegacyControllers() {
    require('./AppRequestHandlers').default(App);
    require('./modules/header/AccountController').getInstance();

    const AssetSupportRenewalController = require('./modules/notification/AssetSupportRenewalController');
    new AssetSupportRenewalController();

    const NoncompliantReleaseController = require('./modules/notification/NoncompliantReleaseController');
    new NoncompliantReleaseController();
};

// TODO Is this needed?
$.ajaxPrefilter(function (options, originalOptions, jqXhr) {
    /**
     * TODO
     * Consider using moment.tz.guess() and passing the Timezone ID in addition to offset
     * This would allow for proper alignment of hour/day offset when rolling up historical timestamps
     * By only passing the current offset, historical rollups can be misaligned due to daylight savings time
     */
    jqXhr.setRequestHeader('X-Timezone-Offset', new Date().getTimezoneOffset());
});

module.exports = App;
