export default function titleMixin(title) {
    return () => ({
        title: {
            align: 'left',
            margin: 0,
            style: {
                fontFamily: null,
                fontSize: null,
                fontWeight: null,
            },
            text: title,
            useHTML: true,
            x: 70,
            y: -23,
        },
    });
}
