const _ = require('underscore');
var BaseGridModel = require('app/models/grid/BaseGridModel');

module.exports = BaseGridModel.extend({

    defaults: _.defaults({}, BaseGridModel.prototype.defaults),

    validate: function (attrs, options) {

        if (!attrs.collection) {
            return '"collection" must be non-null';
        }

        return BaseGridModel.prototype.validate.apply(this, arguments);
    },
});
