import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { GlobalBannerList } from '@infosight/elmer/dist/components/GlobalBanner';
import { dismissNotification as dismiss } from './actionCreators';
import { notificationsSelector } from './reducer';

const mapStateToProps = state => ({
    banners: notificationsSelector(state.globalNotifications),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    dismiss,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(GlobalBannerList);
