const _ = require('underscore');
var Backbone = require('backbone');

module.exports = Backbone.Model.extend({

    /**
     * Attributes in this model are in the form: columnName = filterValue
     */

    clearValues: function () {
        this.set(_.reduce(this.keys(), function (memo, attr) {
            memo[attr] = null;
            return memo;
        }, {}));
    },

});
