import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import numeral from 'numeral';
import style from './style.scss';

const cx = classnames.bind(style);

function SavingsDetail({ className, name, savings, factor }) {
    return (
        <div className={cx('detail', className)}>
            <div className={cx('savings-name')}>{name}</div>
            <div className={cx('savings-value')}>
                {savings !== undefined ? numeral(savings).format('0.0 ib') : null}
                {factor !== undefined ? ` | ${factor}x` : null}
                {savings === undefined && factor === undefined ? 'N/A' : null}
            </div>
        </div>
    );
}

SavingsDetail.propTypes = {
    name: PropTypes.string.isRequired,
    savings: PropTypes.number,
    factor: PropTypes.number,
};

export default SavingsDetail;
