// import { successType } from './actionTypeGenerators';
import { getSuccessActionType } from '@infosight/elmer/dist/utils/redux';


function getKeys(stateKey) {
    const baseKey = `${stateKey.charAt(0).toUpperCase()}${stateKey.slice(1)}`;

    return {
        valueKey: stateKey,
        loadingKey: `loading${baseKey}`,
        loadedKey: `loaded${baseKey}`,
        errorKey: `error${baseKey}`,
    };
}

export default function createItemUpdateReducersForAction({ type, stateKey, matchKey }) {
    const { loadingKey, loadedKey, errorKey } = getKeys(stateKey);

    return {
        [getSuccessActionType(type)]: (state, { payload }) => {
            console.log('createItemUpdateReducersForAction', state, payload);
            const updatedCollection = [...state[stateKey]];

            const matchIndex = updatedCollection.findIndex(x => x[matchKey] === payload[matchKey]);
            if (matchIndex !== -1) {
                updatedCollection[matchIndex] = payload;
            }

            return {
                ...state,
                [stateKey]: updatedCollection,
                [loadingKey]: false,
                [loadedKey]: true,
                [errorKey]: false,
            };
        },
    };
}
