import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import {
    POOL_CAPACITY_RESET,
    POOL_CAPACITY_FOLDERS_FETCH,
    POOL_CAPACITY_APPLICATIONS_FETCH,
    POOL_CAPACITY_SUMMARY_FETCH,
    POOL_CAPACITY_TIME_SERIES_FETCH,
    POOL_CAPACITY_ARRAY_CAPACITY_FETCH,
    TOP_POOLS_CAPACITY_TIME_SERIES_FETCH,
} from '../constants';
import Request from '../../../services/Request';
import { firstOrNullData } from '../../../util/genericQuery';

const poolURISegment = poolUid => (!poolUid ? '' : `/${poolUid}`);

export const getTopPoolsCapacityTimeSeries = ({ count = 5, lookback }) => (dispatch) => {
    const {
        dispatchStart,
        dispatchSuccess,
        dispatchError,
    } = actionsFor(TOP_POOLS_CAPACITY_TIME_SERIES_FETCH, dispatch);

    dispatchStart();

    return new Request().pod().account()
        .urlSegment('capacity/pools/topnTimeSeries')
        .uriAddQuery({ monthRange: lookback, topnCount: count })
        .get()
        .then((response) => {
            // Each pool's timeseries data is in an object where the key is the poolUid
            const dispatchData = (response.data && Object.keys(response.data).length) ? response.data : null;
            dispatchSuccess(dispatchData);
        })
        .catch(dispatchError);
};

export const getPoolCapacityTimeSeries = ({ poolUid, lookback }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(POOL_CAPACITY_TIME_SERIES_FETCH, dispatch);

    dispatchStart();
    new Request().pod().account()
        .urlSegment(`capacity/pools${poolURISegment(poolUid)}/timeSeries`)
        .uriAddQuery({ monthRange: lookback })
        .get()
        .then((response) => {
            const dispatchData = (response.data && response.data.length) ? response.data : null;
            dispatchSuccess(dispatchData);
        })
        .catch(dispatchError);
};

export const getAggregatedPoolCapacitySummary = ({ lookback }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(POOL_CAPACITY_SUMMARY_FETCH, dispatch);

    dispatchStart();
    new Request().pod().account()
        .urlSegment('capacity/pools')
        .uriAddQuery({ monthRange: lookback })
        .get()
        .then((response) => {
            const first = response.data;
            dispatchSuccess(first[0]);
        })
        .catch((error) => {
            dispatchError(error);
        });
};

export const getPoolCapacitySummary = ({ poolUid }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(POOL_CAPACITY_SUMMARY_FETCH, dispatch);
    dispatchStart();
    const options = {};

    if (poolUid) {
        options.scope = { type: 'pool', id: poolUid };
    }

    new Request().pod().genericQuery('pool', ['capacityDashboardDetail'])
        .post(options)
        .then(firstOrNullData)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const getPoolCapacityFolders = ({ poolUid, lookback }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(POOL_CAPACITY_FOLDERS_FETCH, dispatch);

    dispatchStart();
    return new Request().pod().account()
        .urlSegment(`capacity/pools${poolURISegment(poolUid)}/folders`)
        .uriAddQuery({ monthRange: lookback })
        .get()
        .then((folders) => {
            // Pool only has one folder, the default folder, which means customer is not using folders
            if (folders.data.length === 1 && !folders.data[0].folderName) {
                return [];
            }
            return folders.data;
        })
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const getPoolCapacityApplications = ({ poolUid, lookback }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(POOL_CAPACITY_APPLICATIONS_FETCH, dispatch);

    dispatchStart();
    return new Request().pod().account()
        .urlSegment(`capacity/pools${poolURISegment(poolUid)}/applications`)
        .uriAddQuery({ monthRange: lookback })
        .get()
        .then((response) => {
            const dispatchData = (response.data && response.data.length) ? response.data : null;
            dispatchSuccess(dispatchData);
        })
        .catch(dispatchError);
};

export const getArrayCapacity = ({ arraySerialNumber, lookback }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(POOL_CAPACITY_ARRAY_CAPACITY_FETCH, dispatch);

    dispatchStart();
    return new Request().pod().account()
        .urlSegment('capacity')
        .uriAddQuery({ monthRange: lookback, arraySn: arraySerialNumber })
        .get()
        .then((response) => {
            const dispatchData = (response.data && response.data.length) ? response.data[0] : null;
            dispatchSuccess(dispatchData);
        })
        .catch(dispatchError);
};

export function getPoolCapacity(options) {
    return (dispatch) => {
        getPoolCapacityTimeSeries(options)(dispatch);
        getPoolCapacityFolders(options)(dispatch);
        getPoolCapacityApplications(options)(dispatch);
        getPoolCapacitySummary(options)(dispatch);
        getArrayCapacity(options)(dispatch);
    };
}

export function getAggregatePoolCapacity(options) {
    return (dispatch) => {
        getTopPoolsCapacityTimeSeries(options)(dispatch);
        getPoolCapacityApplications(options)(dispatch);
        getAggregatedPoolCapacitySummary(options)(dispatch);
    };
}

export function resetPoolCapacity() {
    return { type: POOL_CAPACITY_RESET };
}
