import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import ReactDimensions from 'react-dimensions';
import { ReactHighcharts } from '@infosight/elmer/dist/components/ReactHighcharts';
import { uniqueId, pluck } from 'underscore';
import { xAxisDataRange } from '../mixins/xAxis';
import applyMixins from '../mixins/applyMixins';
import dragZoomMixin from '../mixins/dragZoom';

// Amount of space between each cell
export const POINT_BORDER_WIDTH_PIXELS = 2;

export class ChartWrapper extends PureComponent {
    constructor(props) {
        super(props);
        autobind(this);

        this.containerElementId = uniqueId(this.constructor.name);
    }

    componentWillUnmount() {
        const container = document.getElementById(this.containerElementId);
        if (!container) {
            return;
        }

        container.removeEventListener('mouseleave', this.props.onMouseOut);
    }

    onAfterChartRendered(chart) {
        const container = document.getElementById(this.containerElementId);
        if (!container) {
            return;
        }

        container.addEventListener('mouseleave', this.props.onMouseOut);
        this.props.onAfterChartRendered(chart);
    }

    config() {
        const thisComponent = this;

        const {
            containerHeight,
            containerWidth,
            chartRangeStart,
            chartRangeEnd,
            baseTimeSeries,
            field,
            onAfterChartRendered,
            onMouseOverPoint,
            granularity,
            min,
            max,
            minColor,
            maxColor,
            spacingLeft,
            onChartSelect,
        } = this.props;

        return applyMixins(
            dragZoomMixin(onChartSelect),
            {
                chart: {
                    type: 'heatmap',
                    height: containerHeight,
                    width: containerWidth,
                    plotBorderWidth: 0,
                    spacingTop: 0,
                    spacingBottom: 0,
                    spacingLeft,
                    spacingRight: 0,
                    marginTop: 24,
                    ignoreHiddenSeries: true,
                    events: {
                        redraw() {
                            if (typeof onAfterChartRendered === 'function') {
                                onAfterChartRendered(this);
                            }
                        },
                    },
                },

                colorAxis: { min, max, minColor, maxColor, marker: { color: 'transparent' } },

                legend: {
                    enabled: true,
                    align: 'right',
                    layout: 'horizontal',
                    verticalAlign: 'bottom',
                    symbolHeight: 10,
                    symbolWidth: 200,
                },

                plotOptions: {
                    heatmap: {
                        borderWidth: POINT_BORDER_WIDTH_PIXELS,
                        borderColor: 'white',
                        colsize: granularity.asMilliseconds(),
                        color: 'transparent',
                        nullColor: '#FFF',
                        nullInteraction: true,
                        states: {
                            hover: {
                                enabled: false,
                            },
                        },
                    },
                },

                series: baseTimeSeries.map((obj, index) => {
                    const { name, timeseries, id, isAggregatedRemainder } = obj;
                    return {
                        index,
                        isAggregatedRemainder,
                        id,
                        name,
                        data: timeseries.map(val => ([
                            val.timestamp,
                            index,
                            val[field],
                        ])),
                        legendIndex: index,
                        aggregatedValue: obj[field],
                        point: {
                            events: {
                                mouseOver(event) {
                                    if (thisComponent.lastPointHovered !== this) {
                                        thisComponent.lastPointHovered = this;
                                        onMouseOverPoint({ point: this, event });
                                    }
                                },
                            },
                        },
                    };
                }),

                title: {
                    text: null,
                },

                tooltip: {
                    enabled: false,
                },

                xAxis: {
                    ...xAxisDataRange(chartRangeStart, chartRangeEnd),
                },

                yAxis: {
                    title: false,
                    categories: pluck(baseTimeSeries, 'name'),
                    minPadding: 0,
                    maxPadding: 0,
                    reversed: true,
                    min: 0,
                    labels: {
                        enabled: false,
                    },
                    gridLineWidth: 0,
                    lineWidth: 0,
                },
            }
        );
    }

    render() {
        return (
            <ReactHighcharts
                config={this.config()}
                domProps={{ id: this.containerElementId }}
                callback={this.onAfterChartRendered}
            />
        );
    }
}

ChartWrapper.propTypes = {
    chartRangeStart: PropTypes.oneOfType([
        /**
         * Moment
         */
        PropTypes.object,
        PropTypes.number,
    ]),

    chartRangeEnd: PropTypes.oneOfType([
        /**
         * Moment
         */
        PropTypes.object,
        PropTypes.number,
    ]),

    /**
     * Key of data point used for the value in `props.baseTimeSeries.timeseries[n]` object
     */
    field: PropTypes.string.isRequired,

    baseTimeSeries: PropTypes.arrayOf(PropTypes.shape({
        id: PropTypes.string,
        name: PropTypes.string.isRequired,
        timeseries: PropTypes.arrayOf(PropTypes.shape({
            timestamp: PropTypes.number.isRequired,
        })).isRequired,
    })),
    onMouseOverPoint: PropTypes.func.isRequired,
    onMouseOut: PropTypes.func.isRequired,
    onAfterChartRendered: PropTypes.func.isRequired,

    /**
     * The minimum value of the axis in terms of map point values.
     * If null, the min value is automatically calculated.
     */
    min: PropTypes.number,

    /**
     * The minimum value of the axis in terms of map point values.
     * If null, the min value is automatically calculated.
     */
    max: PropTypes.number,

    /**
     * The color to represent the minimum of the color axis.
     */
    minColor: PropTypes.string.isRequired,

    /**
     * The color to represent the maximum of the color axis.
     */
    maxColor: PropTypes.string.isRequired,

    /**
     * Granularity (per-sample resolution) as Moment.Duration, which is the X-Axis unit
     */
    granularity: PropTypes.object,

    // From ReactDimensions
    containerHeight: PropTypes.number.isRequired,
    containerWidth: PropTypes.number.isRequired,

    /**
     * Some optional padding/spacing to put on the left side of the chart. Defaults to 0.
     */
    spacingLeft: PropTypes.number,

    onChartSelect: PropTypes.func,
};

ChartWrapper.defaultProps = {
    spacingLeft: 0,
    onChartSelect: event => event.preventDefault(),
};

export default ReactDimensions({ elementResize: true })(ChartWrapper);
