var Backbone = require('backbone');
const { AuthorizationEvaluator, AUTHORITIES } = require('../../progeny/modules/user');
const deepGet = require('utils-deep-get');

var assertionError = function () {
    console.assert('SessionModel must not be fetched from server');
};

module.exports = Backbone.Model.extend({
    defaults: {
        userLogin: null,
        role: null,
        internal: false,
        acmPurchased: false,
        arrays: [],
        ndaSigned: false,
        firstLogin: false,
        vmImageApproved: false,
        vmImageLicenseLimit: null,
    },

    parse(object) {
        const { username } = object;
        return {
            ...object,
            userLogin: username,
            vmImageLicenseLimit: deepGet(object, 'customerContexts.nimble.vmImageLicenseLimit') || 0,
        };
    },

    /*
     * SessionModel should always be obtained from the page, not via an AJAX call
     */
    sync: assertionError,
    fetch: assertionError,
    save: assertionError,

    isInternal: function () {
        return AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.REALM_INTERNAL } });
    },

    isExternal: function () {
        return AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.REALM_EXTERNAL } });
    },

});
