import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { withQueryParams } from '@infosight/elmer/dist/components/Router';
import { Filter, FacetTags, FacetedFilter } from '@infosight/elmer/dist/components/FacetedFilter';
import { CollectionConfigBuilder, CollectionConfig } from '@infosight/elmer/dist/lib/CollectionConfig';
import ScrollContainer from '@infosight/elmer/dist/components/ScrollContainer';
import Async from '@infosight/elmer/dist/components/Async';
import Button from '@infosight/elmer/dist/components/Button';
import style from './style.scss';
import Tabs from './Tabs';
import Table from './Table';

const cx = classnames.bind(style);

// TODO test
export class FacetedCollectionPage extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.state = { filter: new Filter() };
    }

    onChangeFilter(filter) {
        this.setState({ filter });
    }

    render() {
        const { filter } = this.state;
        const {
            match: { params: { fieldSet } },
            loadingCollection,
            collection,
            config,
            tableClassName,
            keyField,
            defaultSort,
            sorting,
            sortParameter,
            showCategories,
            enableFieldSet,
            filterParameterName,
            downloadCsv,
            downloadingCsv,
        } = this.props;

        const { getColumns, getCategories } = config.valueOf();
        const renderCategories = showCategories && getCategories().length !== 1;

        return (
            <ScrollContainer className={cx('container')}>
                <ScrollContainer.Fixed>
                    <div className="flex-container align-justify align-middle">
                        {/* we need this div regardless of whether it contains tabs
                            in order to keep the download csv button on the right */}
                        <div>
                            {renderCategories && <Tabs tabName={fieldSet} getCategories={getCategories} />}
                        </div>
                        {downloadCsv && <Button
                            className={cx('csv-button')}
                            onClick={downloadCsv}
                            label={downloadingCsv ? 'Downloading CSV...' : 'Download CSV'}
                            disabled={downloadingCsv}
                        />}
                    </div>
                    <div className="card card-no-margin card-no-border">
                        <FacetTags
                            filter={filter}
                            onClick={this.onChangeFilter}
                            context="table"
                            className={cx('tags')}
                        />
                    </div>
                </ScrollContainer.Fixed>
                <ScrollContainer.Scrollable>
                    <Async loading={loadingCollection}>
                        {() => (
                            <FacetedFilter filter={filter} onChange={this.onChangeFilter} parameterName={filterParameterName} >
                                <Table
                                    className={tableClassName}
                                    getColumns={getColumns}
                                    rowKey={keyField}
                                    rowsPerPage={50}
                                    collection={filter.filter(collection) || []}
                                    fieldSet={renderCategories && fieldSet ? fieldSet : (enableFieldSet || getCategories()[0].value)}
                                    filter={filter}
                                    defaultSort={defaultSort}
                                    sorting={sorting}
                                    sortParameter={sortParameter}
                                />
                            </FacetedFilter>
                        )}
                    </Async>
                </ScrollContainer.Scrollable>
            </ScrollContainer>
        );
    }
}

FacetedCollectionPage.propTypes = {
    /**
     * Facet and GridView configuration
     */
    config: PropTypes.oneOfType([
        PropTypes.instanceOf(CollectionConfig),
        PropTypes.instanceOf(CollectionConfigBuilder),
    ]).isRequired,

    /**
     * The default sort to be applied to this table.
     * For `order`, valid values are -1 (descending), 1 (ascending), or 0 (no sort)
     */
    defaultSort: PropTypes.arrayOf(PropTypes.shape({
        colname: PropTypes.string,
        order: PropTypes.number,
    })),

    /**
     * The current sort on this table (from the URL)
     * For `order`, valid values are -1 (descending), 1 (ascending), or 0 (no sort)
     */
    sorting: PropTypes.arrayOf(PropTypes.shape({
        colname: PropTypes.string,
        order: PropTypes.number,
    })),
    /**
     * Table data
     */
    collection: PropTypes.arrayOf(PropTypes.object).isRequired,

    loadingCollection: PropTypes.bool.isRequired,

    /**
     * Parent class that contains nested cell (td) and header cell (th) styles. Required if
     * you don't want columns equally distributed.
     */
    tableClassName: PropTypes.string,

    /**
     * Key (unique) field on the table
     */
    keyField: PropTypes.string,

    /**
     * Name of the URL parameter to use for sorting this page
     */
    sortParameter: PropTypes.string,


    /**
     * Show/Hide the categories
     * Most likely to be used in conjunction with enableFieldSet
     *
     */
    showCategories: PropTypes.bool,

    /**
     * Only the column of this field set will be rendered
     * Use this to specify if you want to show only fields of a particular category
     */
    enableFieldSet: PropTypes.string,

    /**
     * @ignore
     */
    match: PropTypes.shape({
        params: PropTypes.object,
    }),

    /**
     * Name of root query parameter
    */
    filterParameterName: PropTypes.string,

    /**
     * Function to download the CSV.
     */
    downloadCsv: PropTypes.func,
    downloadingCsv: PropTypes.bool,
};

FacetedCollectionPage.defaultProps = {
    collection: [],
    sortParameter: 'sort',
    showCategories: true,
};

export default withQueryParams(FacetedCollectionPage);
