import { createReducersForAction, createSelector, getInitialState } from '@infosight/elmer/dist/utils/redux';
import rootState from '../../rootStateSelector';
import { USER_CONTEXT_CHANGED } from '../../../user/constants';

import {
    VOLUME_FETCH,
    VOLUME_DETAIL_CAPACITY_FETCH,
    VOLUME_DETAIL_PERFORMANCE_FETCH,
    RESET,
} from './constants';

const initialState = {
    ...getInitialState('volume'),
    ...getInitialState('volumeCapacity'),
    ...getInitialState('volumePerformance'),
};

const ACTION_HANDLERS = {
    [USER_CONTEXT_CHANGED]: () => (initialState),
    [RESET]: () => (initialState),
    ...createReducersForAction({ type: VOLUME_FETCH, stateKey: 'volume' }),
    ...createReducersForAction({ type: VOLUME_DETAIL_CAPACITY_FETCH, stateKey: 'volumeCapacity' }),
    ...createReducersForAction({ type: VOLUME_DETAIL_PERFORMANCE_FETCH, stateKey: 'volumePerformance' }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

const baseSelector = state => rootState(state).volumeDetail;
export const volumeSelector = createSelector(baseSelector, 'volume');
export const capacitySelector = createSelector(baseSelector, 'volumeCapacity');
export const performanceSelector = createSelector(baseSelector, 'volumePerformance');
