import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import Backbone from 'backbone';
import SoftwareUpgradesView from 'app/views/components/asset/SoftwareUpgradesView';
import App from 'app/Application';

// TODO this will break in "infosight" bundle. I just don't feel like moving this to React pre-lasik
class NimbleOSVersionLink extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    onClickVersion(event) {
        event.preventDefault();
        App.modalRegion.show(new SoftwareUpgradesView({ model: new Backbone.Model(this.props) }));
    }

    render() {
        const { version, className } = this.props;
        return <a className={className} href="_null" onClick={this.onClickVersion}>{version}</a>;
    }
}

NimbleOSVersionLink.propTypes = {
    version: PropTypes.string.isRequired,
    serialNumber: PropTypes.string.isRequired, // eslint-disable-line react/no-unused-prop-types
    hostname: PropTypes.string.isRequired, // eslint-disable-line react/no-unused-prop-types
};

export default NimbleOSVersionLink;
