import URI from 'urijs';
import axios from './axios';
import { getStore } from '../util/storeProvider';
import { API_BASE_URL } from '../config';
import { isNimbleBoundary } from '../modules/user/util';
import { tenantSelector } from '../modules/user/reducer';

/**
 * Fluent interface to make requests to Pod and Central APIs
 * HTTP-based methods proxy to [axios](https://github.com/mzabriskie/axios#request-method-aliases) to execute the
 * request. The others are backed by [URIjs](https://medialize.github.io/URI.js) to build URI, which is used by the
 * HTTP methods.
 *
 * Example:
 *  const promise = new Request().pod().genericQuery('folders).post();
 */
export default class Request {
    constructor() {
        this.uri = new URI(API_BASE_URL);
    }

    uriAddQuery(q) {
        this.uri.addQuery(q);
        return this;
    }

    /**
     * Get the URIjs instance. Note that this will break the method chain.
     * @return {URI}
     */
    uriBuilder() {
        return this.uri;
    }

    urlSegment(...args) {
        this.uri.segment(...args);
        return this;
    }

    // Alias of "slash" for my sanity
    urlSegments(...args) {
        return this.slash(...args);
    }

    // give this any number of string args and it will append each one to the current uri
    slash(...args) {
        for (let i = 0; i < args.length; i += 1) {
            // segment only appends if you use exactly one string argument
            // so to prevent something unexpected happening, we only accept string args
            if (typeof args[i] !== 'string') {
                throw new Error('slash() only accepts string arguments');
            }
            this.uri.segment(args[i]);
        }
        return this;
    }

    query(queries) {
        this.uri.addQuery(queries);
        return this;
    }

    // Nimble request builders

    /**
     * Construct a URL for pod resource that is managed in pod app
     */
    pod(boundaryArg) {
        this.uri.segment('pod');

        const state = getStore().getState();

        const nimbleTenantSelection = tenantSelector(state);
        let podLocator;
        let boundary;

        /*
         * This overrides the tenant selection in the case of internal users or external users with heterogenous customer contexts
         * Note that these can be overwritten by the request and usually are for any heterogenous customers
         */
        if (nimbleTenantSelection && isNimbleBoundary(boundaryArg)) {
            podLocator = `Nimble.Tenant.${nimbleTenantSelection.tenant.id}`;
        } else {
            podLocator = 'StoreServ';
        }

        if (boundaryArg) {
            boundary = boundaryArg;
        } else if (nimbleTenantSelection) {
            boundary = `Nimble.Tenant.${nimbleTenantSelection.tenant.id}`;
        } else {
            boundary = 'StoreServ._implicit';
        }

        this.uri.addQuery({
            podLocator,
            boundary,
        });

        return this;
    }

    /**
     * Construct a URL for central resource that is managed in central app
     */
    central() {
        this.uri.segment('central');
        return this;
    }

    /**
     * Using accountSelection from store, build the proper URL for an "own-account" vs. a "cross-account" access
     * @return {URI}
     */
    account() {
        const state = getStore().getState();
        const nimbleTenantSelection = tenantSelector(state);
        // const accountSelection = currentAccountSelector(state);

        // if there is no nimble tenant, we want to just use the 'primary' account because this
        // should either be routed to the StoreServ pod or fail on fetch.
        const { isPrimary, tenant } = nimbleTenantSelection || { isPrimary: true };
        const accountSegment = isPrimary ? 'account' : `accounts/${tenant.id}`;

        this.uri.segment(accountSegment);
        return this;
    }

    /**
     * Using accountSelection from store, build the proper URL for an "own-account" vs. a "cross-account" access
     * @return {URI}
     */
    tenant() {
        const state = getStore().getState();
        const nimbleTenantSelection = tenantSelector(state);
        // const accountSelection = currentAccountSelector(state);

        // if there is no nimble tenant, we want to just use the 'primary' account because this
        // should either be routed to the StoreServ pod or fail on fetch.
        const { isPrimary, tenant } = nimbleTenantSelection || { isPrimary: true };
        const accountSegment = isPrimary ? 'tenant' : `tenants/${tenant.id}`;

        this.uri.segment(accountSegment);
        return this;
    }

    /**
     * Construct a URL for an array resource
     */
    array(arraySn) {
        this.uri.segment('arrays').segment(arraySn);
        return this;
    }

    user() {
        this.uri.segment('user');
        return this;
    }

    csv() {
        this.uri.addQuery({ export: true });
        return this;
    }

    /**
     * @param view
     * @param {Array<String>} subviews
     * @return {Request}
     */
    genericQuery(view, subviews) {
        this.uri.segment('genericQuery').segment(view);

        if (subviews) {
            subviews.reduce((finalUrl, subView) => finalUrl.segment(subView), this.uri);
        }

        return this;
    }

    // SFR request builders

    /**
     * This resets the URI to a SFR api request from a Nimble request.
     * Eventually this will be flipflopped when the far superior
     * SFR API becomes the dominant API of InfoSight ;)
     */
    sfr(endpoint) {
        if (endpoint === 'auth') {
            this.uri = new URI('/auth/');
        } else {
            this.uri = new URI('/api/');
            this.uri.segment(endpoint);
        }
        return this;
    }

    /**
     * This resets the URI to a SFR auth request from a Nimble request.
     */
    auth() {
        this.uri = new URI('/auth/');
        return this;
    }

    // request calls

    get(...args) {
        return axios.get(this.uri.valueOf(), ...args);
    }

    delete(...args) {
        return axios.delete(this.uri.valueOf(), ...args);
    }

    put(...args) {
        return axios.put(this.uri.valueOf(), ...args);
    }

    post(...args) {
        return axios.post(this.uri.valueOf(), ...args);
    }

    patch(data, config = {}) {
        config = {
            ...config,
            headers: {
                ...config.headers,
                'X-HTTP-Method-Override': 'PATCH',
            },
        };
        return axios.post(this.uri.valueOf(), data, config);
    }

    all(requestsArr) { // eslint-disable-line class-methods-use-this
        return Promise.all(requestsArr);
    }
}
