import { AUTHORITIES, USER_CONTEXT_CHANGED, SET_AUTHORIZATION_EVALUATOR, SET_TENANT, SET_PROFILE, SET_CONFIG } from './constants';

const initialState = {
    authorizationEvaluator: null,
    tenant: null,
    permissions: null,
    profile: null,
    config: {
        appBaseUrl: '/',
        minWidthPixels: 1280,
    },
};

const ACTION_HANDLERS = {
    [USER_CONTEXT_CHANGED]: () => (initialState),
    [SET_AUTHORIZATION_EVALUATOR]: (state, { payload }) => ({ ...state, authorizationEvaluator: payload }),
    [SET_TENANT]: (state, { payload }) => ({ ...state, tenant: payload }),
    [SET_PROFILE]: (state, { payload }) => ({ ...state, profile: payload }),
    [SET_CONFIG]: (state, { payload }) => ({ ...state, config: payload }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

// Selectors
const baseSelector = state => state.user;
export const authorizationEvaluatorSelector = state => baseSelector(state).authorizationEvaluator;
export const tenantSelector = state => baseSelector(state).tenant;
const configSelector = state => baseSelector(state).config;
export const appBaseUrlSelector = state => configSelector(state).appBaseUrl;
export const minWidthPixelsSelector = state => configSelector(state).minWidthPixels;

export const userSelector = state => baseSelector(state).profile;

const authorities = state => userSelector(state).authorities;
export const permissionsSelector = state => userSelector(state).permissions;

export const hasSupportCase = state => (
    authorities(state)
        ? authorities(state)
            .some(authority => authority === AUTHORITIES.SUPPORT_CASE)
        : null
);
