import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import autobind from 'react-autobind';
import TreeView from './TreeView';
import createTree from './createTree';
import createObjectGraph from './createObjectGraph';
import { objectRelationshipsSelector } from '../../reducer/objectRelationships';

class PoolFolderTreeView extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    createTree() {
        const { data } = this.props;
        if (data && data.length) {
            return createTree(createObjectGraph(data));
        }

        return [];
    }

    render() {
        const { onSelect, data, className } = this.props;
        return <TreeView data={data} createTree={this.createTree} onSelect={onSelect} className={className} />;
    }
}

PoolFolderTreeView.propTypes = {
    data: PropTypes.arrayOf(PropTypes.shape({
        arraySn: PropTypes.string.isRequired,
        groupUID: PropTypes.string.isRequired,
        groupName: PropTypes.string.isRequired,
        poolUID: PropTypes.string.isRequired,
        poolName: PropTypes.string.isRequired,
        folderUID: PropTypes.string,
        folderName: PropTypes.string,
    })),
    onSelect: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
    data: objectRelationshipsSelector(state).relationships,
});

export default connect(mapStateToProps)(PoolFolderTreeView);
