import React, { Component } from 'react';
import autobind from 'react-autobind';
import getDisplayName from 'recompose/getDisplayName';
import wrapDisplayName from 'recompose/wrapDisplayName';
import hoistNonReactStatics from 'hoist-non-react-statics';

export default function providePointTracking(WrappedComponent) {
    class PointTrackingProvider extends Component {
        constructor(props) {
            super(props);
            autobind(this);

            this.state = {
                legendValues: null,
                legendSeries: [],
                legendTimestamp: null,
            };
        }

        onMouseOut() {
            this.setState(() => ({ legendValues: null, legendTimestamp: null }));
        }

        // TODO optimize performance
        onMouseOverPoint({ x, points }) {
            const legendValues = points.reduce((memo, { y, series }) => {
                const data = series.data[series.index];
                memo[series.options.id] = { value: y, data };
                return memo;
            }, {});

            this.setState(() => ({ legendValues, legendTimestamp: x }));
        }

        onAfterChartRendered(chart) {
            this.chart = chart;
            this.setState(() => ({
                legendSeries: chart.series.map((set) => {
                    const { color, options: { id }, name } = set;
                    return ({
                        color,
                        id,
                        name,
                        highchartsSeries: set,
                    });
                }),
            }));
        }

        render() {
            const { legendValues, legendSeries, legendTimestamp } = this.state;

            const pointTracking = {
                values: legendValues,
                timestamp: legendTimestamp,

                // These references are maintained for the lifetime of the chart
                chart: this.chart,
                series: legendSeries,

                // These references are maintained for the lifetime of this component
                onAfterChartRendered: this.onAfterChartRendered,
                onMouseOverPoint: this.onMouseOverPoint,
                onMouseOut: this.onMouseOut,
            };

            return <WrappedComponent {...this.props} pointTracking={pointTracking} />;
        }
    }

    PointTrackingProvider.displayName = wrapDisplayName(
        WrappedComponent,
        getDisplayName(PointTrackingProvider));

    return hoistNonReactStatics(PointTrackingProvider, WrappedComponent);
}
