import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import autobind from 'react-autobind';
import style from './style.scss';
import { ComplexDropdown } from '../../../../components/Dropdown';
import PoolFolderTreeView from './PoolFolderTreeView';
import Selection from '../containers/Selection';
import { ALL } from '../../constants';

const cx = classnames.bind(style);

class PoolFolderTreeViewDropdown extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    onSelectAll() {
        this.props.onSelect({ type: ALL });
    }

    render() {
        const { className, menuClassName, onSelect, value, disabled } = this.props;

        return (
            <ComplexDropdown
                className={className}
                menuClassName={menuClassName}
                value={value}
                valueRenderer={() => <Selection value={value} className={cx('selection')} />}
                disabled={disabled}
            >
                <button className={cx('all-pools')} onClick={this.onSelectAll}>
                    All Pools
                </button>
                <PoolFolderTreeView onSelect={onSelect} className={cx('treeview')} />
            </ComplexDropdown>
        );
    }
}

PoolFolderTreeViewDropdown.propTypes = {
    menuClassName: PropTypes.string,
    value: PropTypes.shape({
        type: PropTypes.string.isRequired,
        name: PropTypes.string,
        id: PropTypes.string,
    }),
    onSelect: PropTypes.func.isRequired,
    disabled: PropTypes.bool.isRequired,
};

PoolFolderTreeViewDropdown.defaultProps = {
    disabled: false,
};

export default PoolFolderTreeViewDropdown;
