import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { uniqueId } from 'underscore';
import { ReactHighcharts } from '@infosight/elmer/dist/components/ReactHighcharts';

class MouseOutTrackingHighchart extends PureComponent {
    constructor(props) {
        super(props);
        autobind(this);

        this.containerElementId = uniqueId(this.constructor.name);
    }

    // TODO what if the chart is destroyed but the component is not unmounted?
    componentWillUnmount() {
        const container = document.getElementById(this.containerElementId);
        if (!container) {
            return;
        }

        container.removeEventListener('mouseleave', this.props.onMouseOut);
    }

    callback(chart) {
        const container = document.getElementById(this.containerElementId);
        if (!container) {
            return;
        }

        container.addEventListener('mouseleave', this.props.onMouseOut);

        if (this.props.callback) {
            this.props.callback(chart);
        }
    }

    render() {
        return (
            <ReactHighcharts
                {...this.props}
                domProps={{ ...this.props.domProps, id: this.containerElementId }}
                callback={this.callback}
            />
        );
    }
}

MouseOutTrackingHighchart.propTypes = {
    onMouseOut: PropTypes.func.isRequired,
    callback: PropTypes.func,
    domProps: PropTypes.object,
};

export default MouseOutTrackingHighchart;
