/**
 * Subclass of Marionette.Region that renders the region's view as a Bootstrap modal.
 * The region's el must be a div with Bootstrap's 'modal' class applied.
 */
var Marionette = require('backbone.marionette');
require('bootstrap');

module.exports = Marionette.Region.extend({

    constructor: function () {
        Marionette.Region.prototype.constructor.apply(this, arguments);
    },

    onShow: function () {
        console.assert(this.$el.hasClass('modal'));
        this.$el.attr('data-app-id', 'nimble');

        var self = this;
        this.$el.on('hidden.bs.modal', function (event) {
            self.empty();
        });

        this.$el.modal('show');
    },

    onEmpty: function () {
        this.$el.modal('hide');
    },

});
