import React, { Component } from 'react';
import PropTypes from 'prop-types';
import numeral from 'numeral';
import ReactDimensions from 'react-dimensions';
import NoData from '@infosight/elmer/dist/components/NoData';
import mergeSharedTooltip, { Timestamp } from '@infosight/elmer/dist/charts/mergeSharedTooltip';
import { xAxisDataRange } from '../../../charts/mixins/xAxis';

const Chart = mergeSharedTooltip({
    valueFormatter(point) {
        return numeral(point.y).format('0.0 ib');
    },
    xValueFormatter(point) {
        return Timestamp(point.x, 'L');
    },
    leftJustifyPoints: true,
});

// Exported for testing
export class TopPoolsCapacityTimeSeries extends Component {
    /**
     * chartRangeStart and chartRangeEnd are moment objects, so referential equality checks fail,
     * resulting in unnecessary user-visible re-rendering
     */
    shouldComponentUpdate({ pools, containerHeight, containerWidth, chartRangeStart, chartRangeEnd }) {
        return !(
            chartRangeStart.isSame(this.props.chartRangeStart) &&
            chartRangeEnd.isSame(this.props.chartRangeEnd) &&
            pools === this.props.pools &&
            containerHeight === this.props.containerHeight &&
            containerWidth === this.props.containerWidth
        );
    }

    config() {
        const { pools, chartRangeStart, chartRangeEnd } = this.props;

        return {
            chart: {
                width: this.props.containerWidth,
                height: this.props.containerHeight,
                spacingBottom: 0,
                spacingTop: 20,
                marginTop: 40,
                type: 'area',
                resetZoomButton: {
                    theme: {
                        display: 'none',
                    },
                },
            },
            title: false,
            xAxis: xAxisDataRange(chartRangeStart, chartRangeEnd),
            yAxis: {
                title: {
                    text: 'USAGE (TiB)',
                },
                labels: {
                    formatter() {
                        return numeral(this.value / (2 ** 40)).format('0.0');
                    },
                },
            },
            legend: {
                enabled: true,
                symbolHeight: 10,
                symbolWidth: 10,
            },
            series: pools.map(({ poolName, timeSeries }, index) => ({
                name: poolName,
                data: timeSeries.map(val => ({
                    x: val.asupLocalDate,
                    y: val.used,
                })),
                legendIndex: index,
            })),
            plotOptions: {
                area: {
                    animation: false,
                    stacking: 'normal',
                    marker: {
                        enabled: false,
                    },
                },
                line: {
                    marker: {
                        enabled: false,
                        width: 0,
                        height: 0,
                        states: {
                            hover: {
                                enabled: false,
                            },
                        },
                    },
                },
            },
        };
    }

    render() {
        const hasData = this.props.pools && this.props.pools.length > 0;

        return (
            <NoData hasData={hasData} message="No capacity history data available.">
                {() => <Chart config={this.config()} />}
            </NoData>
        );
    }
}

TopPoolsCapacityTimeSeries.propTypes = {
    pools: PropTypes.arrayOf(PropTypes.shape({
        poolUID: PropTypes.string,
        poolName: PropTypes.string,
        timeSeries: PropTypes.arrayOf(PropTypes.shape({
            asupLocalDate: PropTypes.number,
            used: PropTypes.number,
        })),
    })),

    /**
     * Moment
     */
    chartRangeStart: PropTypes.object,

    /**
     * Moment
     */
    chartRangeEnd: PropTypes.object,

    // From ReactDimensions
    containerWidth: PropTypes.number,
    containerHeight: PropTypes.number.isRequired,
};

TopPoolsCapacityTimeSeries.defaultProps = {
    containerWidth: 0,
    containerHeight: 0,
};

export default ReactDimensions({ elementResize: true })(TopPoolsCapacityTimeSeries);
