import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { ReactHighcharts } from '@infosight/elmer/dist/components/ReactHighcharts';
import ReactDimensions from 'react-dimensions';
import NoData from '@infosight/elmer/dist/components/NoData';
import { isNumber } from 'underscore';
import chartOptions from './chartOptions';
import { CHART_COLORS } from '../../../util/chartColors';

export class Donut extends PureComponent {
    constructor(props) {
        super(props);
        autobind(this);
    }

    data() {
        const { usedBytes, freeBytes, unusedReserveBytes } = this.props;
        const data = [{
            name: 'Total Usage',
            y: usedBytes,
            color: CHART_COLORS.teal,
        }];

        if (unusedReserveBytes && isNumber(unusedReserveBytes)) {
            data.push({
                name: 'Unused Reserve',
                y: unusedReserveBytes,
                color: 'url(#unusedReservePattern)',
            });
        }

        data.push({
            name: 'Free Space',
            y: freeBytes,
            color: CHART_COLORS.plotBandGrey,
        });

        return data;
    }

    config() {
        const { usedBytes, freeBytes, containerHeight, containerWidth } = this.props;
        if (![usedBytes, freeBytes].every(isNumber)) {
            return null;
        }
        return chartOptions({ height: containerHeight, width: containerWidth, data: this.data() });
    }

    callback(chart) {
        this.props.callback(chart);
    }

    render() {
        const config = this.config();

        return (
            <NoData hasData={!!config}>
                {() => <ReactHighcharts config={config} callback={this.callback} />}
            </NoData>
        );
    }
}

Donut.propTypes = {
    usedBytes: PropTypes.number,
    freeBytes: PropTypes.number,
    unusedReserveBytes: PropTypes.number,
    containerHeight: PropTypes.number.isRequired,
    containerWidth: PropTypes.number.isRequired,
    callback: PropTypes.func,
};

Donut.defaultProps = {
    containerHeight: 0,
    containerWidth: 0,
};

export default ReactDimensions({ elementResize: true })(Donut);
