import { isNumber, findLastIndex } from 'underscore';
import { CHART_COLORS } from '../../../../../../util/chartColors';

const preventDefault = e => e.preventDefault();

let legendIndex = 0;
const getLegendIndex = () => {
    legendIndex += 1;
    return legendIndex;
};

function dataUsageSeries(timeSeries) {
    return {
        type: 'area',
        id: 'volumeUsage',
        name: 'Volume Usage',
        color: CHART_COLORS.blue,
        fillColor: CHART_COLORS.blueLight,
        data: timeSeries.map(val => ({
            x: val.timestamp,
            y: val.dataUsageBytes,
        })),
        legendIndex: getLegendIndex(),
        events: {
            legendItemClick: preventDefault,
        },
    };
}

function snapshotUsageSeries(timeSeries) {
    return {
        type: 'area',
        id: 'snapshotUsage',
        name: 'Snapshot Usage',
        color: CHART_COLORS.teal,
        fillColor: CHART_COLORS.tealLight,
        data: timeSeries.map(val => ({
            x: val.timestamp,
            y: val.snapshotUsageBytes,
        })),
        legendIndex: getLegendIndex(),
        events: {
            legendItemClick: preventDefault,
        },
    };
}

function allocatedCapacitySeries(timeSeries) {
    // Reverse search the data array for the last actual data point ( data point just before the forecast starts)
    const lastActualDataPoint = timeSeries[findLastIndex(timeSeries, { dataSource: 'Actual' })];
    return {
        type: 'line',
        id: 'Capacity',
        name: 'Volume Capacity',
        color: CHART_COLORS.redOrange,
        lineWidth: 2,
        data: lastActualDataPoint && timeSeries.map(val => ({
            x: val.timestamp,
            y: isNumber(val.allocatedBytes) ? val.allocatedBytes : lastActualDataPoint.allocatedBytes,
        })),
        step: true,
        legendIndex: getLegendIndex(),
        events: {
            legendItemClick: preventDefault,
        },
    };
}

function usageForecastSeries(timeSeries) {
    return {
        type: 'line',
        id: 'forecastedUsage',
        name: 'Forecasted Volume Usage',
        color: CHART_COLORS.blue,
        dashStyle: 'shortdash',
        data: timeSeries.map(val => ({
            x: val.timestamp,
            y: val.forecastedUsageBytes,
        })),
        legendIndex: getLegendIndex(),
        events: {
            legendItemClick: preventDefault,
        },
    };
}

function usageRangeForecastSeries(timeSeries, forecastRangeId) {
    return {
        type: 'arearange',
        id: forecastRangeId,
        name: 'Forecasted Range',
        color: CHART_COLORS.blue,
        linkedTo: 'forecastedUsage',
        data: timeSeries.map(val => ({
            x: val.timestamp,
            low: val.forecastedLowerBoundBytes,
            high: val.forecastedUpperBoundBytes,
        })),
        lineWidth: 0,
        fillOpacity: 0.3,
        zIndex: 0,
        events: {
            legendItemClick: preventDefault,
        },
    };
}

export default ({ timeseries, forecastRangeId }) => ({
    series: [
        allocatedCapacitySeries(timeseries),
        snapshotUsageSeries(timeseries),
        dataUsageSeries(timeseries),
        usageRangeForecastSeries(timeseries, forecastRangeId),
        usageForecastSeries(timeseries),
    ],
    plotOptions: {
        area: {
            animation: false,
            stacking: 'normal',
            marker: {
                enabled: false,
                symbol: 'circle',
                states: {
                    hover: {
                        enabled: true,
                    },
                },
            },
        },
        line: {
            marker: {
                enabled: false,
                symbol: 'circle',
                states: {
                    hover: {
                        enabled: true,
                    },
                },
            },
        },
    },
});
