import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import { groupBy } from 'underscore';
import {
    POOL_FETCH,
    OVERVIEW_FETCH,
    CAPACITY_FETCH,
    RESET,
} from './constants';
import Request from '../../../../services/Request';
import { firstOrNullData, firstOrNull } from '../../../../util/genericQuery';

export const getPool = poolUid => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(POOL_FETCH, dispatch);
    dispatchStart();
    const options = {};

    if (poolUid) {
        options.scope = { type: 'pool', id: poolUid };
    }

    new Request().pod().genericQuery('pools')
        .post(options)
        .then(firstOrNullData)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

/**
 * @param {*} timeRange - instance of TimeRangeSelection
 */
export const getCapacityTimeRange = (timeRange) => {
    if (timeRange.type === 'DURATION') {
        /*
           By default, when the lookback is selected it fetches data only till the current date
           but for Capacity Charts, we need to fetch additional data as well (as we have to include forecast data).
           So orchestrate the request as normal range instead of lookback
        */
        const { from } = timeRange.asFixedRange();
        timeRange = {
            time: {
                range: {
                    start: from.valueOf(),
                },
            },
        };
    } else {
        timeRange = timeRange.toGenericQueryTime();
    }
    return timeRange;
};

const getPoolCapacityRequest = ({ scope, timeRange }) => {
    const options = {
        scope,
        ...getCapacityTimeRange(timeRange),
    };

    return new Request().pod().genericQuery('pool', ['capacity'])
        .post(options);
};

export const getPoolCapacity = ({ scope, timeRange = {} }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(CAPACITY_FETCH, dispatch);
    dispatchStart();

    getPoolCapacityRequest({ scope, timeRange })
        .then(firstOrNull)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

const getPoolEventsRequest = ({ scope, timeRange, eventFilters = {} }) => {
    const finalTimeRange = (timeRange && timeRange.toGenericQueryTime()) || {};
    const options = {
        scope,
        ...finalTimeRange,
    };

    options.filters = [
        ...eventFilters,
    ];

    return new Request().pod().genericQuery('pool', ['events'])
        .post({
            ...options,
        });
};

const getPoolCpuCacheRecommendationsRequest = (poolArrayList) => { // eslint-disable-line consistent-return
    // In ideal world, Check for arrays is never needed as a pool should have atleast one array
    // But due to the data issues, we resort to this for now
    if (poolArrayList && poolArrayList.length > 0) {
        return new Request().pod().account().urlSegments('performance/recommendations')
            .query({ array: poolArrayList })
            .get();
    }
};

export const getPoolOverviewData = ({ scope, timeRange = {}, eventFilters = {}, poolArrayList }) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(OVERVIEW_FETCH, dispatch);
    dispatchStart();

    Promise.all([
        getPoolCapacityRequest({ scope, timeRange }),
        getPoolEventsRequest({ scope, timeRange, eventFilters }),
        getPoolCpuCacheRecommendationsRequest(poolArrayList),
    ])
        .then((responses) => {
            const [poolCapacity, poolEvents, poolCpuCacheUsage] = responses;
            return ({
                poolCapacity: firstOrNull(poolCapacity),
                poolEvents: groupBy(poolEvents && poolEvents.data && poolEvents.data.data, 'timestamp'),
                poolCpuCacheUsage: poolCpuCacheUsage && poolCpuCacheUsage.data,
            });
        })
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const reset = () => ({ type: RESET });
