import React from 'react';
import { sortBy, pluck, compact } from 'underscore';
import PercentGrowth from '@infosight/elmer/dist/components/PercentGrowth';

export const percentGrowthRenderer = value => (value === null ? 'N/A' : <PercentGrowth value={value} isPercent />);

export const getMaxTotalUsage = (data, key = 'totalUsed') => {
    if (data && data.length) {
        return Math.max.apply(null, compact(pluck(data, key)));
    }

    return null;
};

export const getSortedValues = values => (values ? sortBy(values, ({ totalUsed }) => 0 - totalUsed) : null);
