const React = require('react');
const App = require('app/Application');
const _ = require('underscore');
const objectHash = require('object-hash');
var Marionette = require('backbone.marionette');
const { addBanner, removeBanner } = require('@infosight/shell-api/lib/Banner');
const { NIMBLE_APP_ID } = require('progeny/config');
var AssetModel = require('app/modules/assets_report/models/AssetModel');
var template = require('raw-loader!./templates/AssetSupportRenewal.html');
var UserPreferencesController = require('app/modules/preferences/UserPreferencesController');
const { buildUrlFromBase } = require('progeny/util/url');
const { getStore } = require('../../../progeny/util/storeProvider');
const { arraySelector } = require('../../../progeny/modules/nimble/arrays/detail/reducer');
var tpl = _.template(template);
var MAX_ASSETS_PER_NOTIFICATION = 5;
var notificationIds = {};
var shouldRenderNotification = function (asset) {
    if (asset.get('supportStatus') === AssetModel.SupportStatus.CURRENT) {
        return false;
    }

    var lastDismissedForSupportEndDate = UserPreferencesController.getNotification({
        type: 'support-renewal',
        subtype: asset.get('serialNumber'),
    });
    var isFirstEncounter = lastDismissedForSupportEndDate === undefined;
    var isForCurrentSupportEndDate = lastDismissedForSupportEndDate === asset.get('supportEndDate');
    return isFirstEncounter || !isForCurrentSupportEndDate;
};

const onDismiss = (id) => {
    if (!notificationIds[id]) {
        return;
    }

    const notifications = notificationIds[id].map((asset) => ({
        type: 'support-renewal',
        subtype: asset.get('serialNumber'),
        value: asset.get('supportEndDate'),
    }));
    UserPreferencesController.setNotifications(notifications);

    delete notificationIds[id];
};

var renderNotification = function (assets) {
    let statusMessage = assets.reduce(function (status, asset) {
        return status === asset.get('supportStatus') ? status : 'mixed';
    }, assets[0].get('supportStatus'));

    if (assets.length > 1) {
        switch (statusMessage) {
        case AssetModel.SupportStatus.EXPIRING:
            statusMessage = 'are expiring';
            break;
        case AssetModel.SupportStatus.EXPIRED:
            statusMessage = 'have expired';
            break;
        default:
            statusMessage = 'are expiring or have expired';
            break;
        }
    } else {
        switch (statusMessage) {
        case AssetModel.SupportStatus.EXPIRING:
            statusMessage = 'is expiring';
            break;
        case AssetModel.SupportStatus.EXPIRED:
            statusMessage = 'has expired';
            break;
        }
    }

    // TODO This is a hacky fix for the fact that renderNotifications is getting called at least twice, which duplicates the notification
    const id = `support_${objectHash(assets.map(x => x.get('serialNumber')))}`;
    let content = tpl({
        status: statusMessage,
        assets: assets.map((asset) => ({
            name: asset.get('hostname') ? asset.get('hostname') + ' (' + asset.get('serialNumber') + ')' : asset.get('serialNumber'),
            href: buildUrlFromBase('/infrastructure/storage/nimble/arrays', asset.get('serialNumber')),
        })),
    });

    addBanner({ id, onDismiss, appId: NIMBLE_APP_ID, severity: 'warning', content: <div dangerouslySetInnerHTML={{ __html: content }} /> });

    notificationIds[id] = assets;
};

module.exports = Marionette.Object.extend({
    initialize: function () {
        UserPreferencesController.onPreferencesLoaded(() => this.renderNotifications());
        App.handleEvent('global', 'change:account', this.renderNotifications, this);
    },

    // Render a notification for each group of MAX_ASSETS_PER_NOTIFICATION assets
    renderNotifications: async function () {
        Object.keys(notificationIds).forEach(id => {
            removeBanner({ id, appId: NIMBLE_APP_ID });
            delete notificationIds[id];
        });

        const arrayCount = arraySelector(getStore().getState()).array;

        if (arrayCount && arrayCount.length > 0) {
            const assets = (await App.request('global', 'get:assets:async'))
                .filter(shouldRenderNotification);

            while (assets.length) {
                renderNotification(assets.splice(0, MAX_ASSETS_PER_NOTIFICATION));
            }
        }
    },
});
