const App = require('app/Application');
const _ = require('underscore');
var Backbone = require('backbone');
var ArrayEventModel = require('app/models/ArrayEventModel');

var cache;

function getCache() {
    if (!cache) {
        cache = {};
        App.handleEvent('global', 'change:account', function () {
            cache = {};
        });
    }
    return cache;
}

var ArrayEventsCollection = Backbone.Collection.extend({
    model: ArrayEventModel,

    serialNumber: undefined,
    category: null,
    monthRange: 12,

    initialize: function (models, options) {
        _.defaults(this, options);
        console.assert(!options || (options && options.serialNumber), 'serialNumber must be provided');
    },

    url: function () {
        return this.api().array(this.serialNumber).segment('events').addQuery({
            monthRange: this.monthRange,
            category: this.category,
        });
    },

    fetch: function (options) {
        if (!this.deferred) {
            this.deferred = Backbone.Collection.prototype.fetch.call(this, options);
        }
        return this.deferred;
    },

}, {
    get: function (arraySn) {
        var cached = getCache()[arraySn];
        if (!cached) {
            cached = getCache()[arraySn] = new ArrayEventsCollection([], {
                serialNumber: arraySn,
            });
        }
        return cached;
    },
});

module.exports = ArrayEventsCollection;
