import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import chroma from 'chroma-js';
import { createSelector } from 'reselect';
import Async from '@infosight/elmer/dist/components/Async';
import NoData from '@infosight/elmer/dist/components/NoData';
import Card from '@infosight/elmer/dist/components/Card';
import { IconAdaptor } from '@infosight/elmer/dist/components/IconAdaptor';
import { CHART_COLORS } from '../../../../../../util/chartColors';
import PoolPerformanceSnapshotView from './PoolPerformanceSnapShotView';
import styles from './style.scss';
import MultiObjectChart from '../../../../../charts/MultiObjectChart';
import { SynchronizedChart } from '../../../../../charts/SyncChart';
import { xAxisDataRange } from '../../../../../charts/mixins/xAxis';
import applyMixins from '../../../../../charts/mixins/applyMixins';
import dragZoomMixin from '../../../../../charts/mixins/dragZoom';
import { headlineValueFormatter } from '../../../../../../util/formatter';
import { FORMAT_NUMBER_EXTRA_PRECISION } from '../../../../../../util/formatter/constants';

const cx = classnames.bind(styles);

const CPU_SERIES_ID = 'cpu';
const CACHE_SERIES_ID = 'cache';

// Use this if it is decided to hide for the beta release
const SHOW_POOL_PERFORMANCE_SNAPSHOT_VIEW = false;
const SHOW_PLOT_BANDS_WHEN_METRIC_EXCEEDS_THRESHOLD = false;

const buildMedianSeries = (timeseries, timestamp, medianValuekey) => timeseries.map(datapoint => ([datapoint[timestamp], datapoint[medianValuekey]]));

/**
 * This function helps to calculate average of averages(50th percentile values)
 * @param {*} key
 * @param {*} cpuCacheTimeseries
 */
const getAvgUsage = (key, cpuCacheTimeseries) => {
    if (!cpuCacheTimeseries || !cpuCacheTimeseries.length) {
        return null;
    }

    return cpuCacheTimeseries.reduce((sum, datapoint) => sum + (datapoint[key] || 0), 0) / cpuCacheTimeseries.length;
};

const cpuCacheTimeSeriesSelector = props => props.cpuCacheTimeseries;

/**
 *  Compute and Memoize the default values of the Legend
 */
const computeDefaultPointValues = createSelector(
    cpuCacheTimeSeriesSelector,
    cpuCacheTimeseries => ({
        [CPU_SERIES_ID]: { id: CPU_SERIES_ID, value: getAvgUsage('cpuUsagePercent', cpuCacheTimeseries) },
        [CACHE_SERIES_ID]: { id: CACHE_SERIES_ID, value: getAvgUsage('cacheUsagePercent', cpuCacheTimeseries) },
    })
);

/**
 * reselect library expects every selector to be a function, hence this redirection
 *  This function returns the props that are required to compute the Pool Performance Chart Config
 */
const chartConfigSelector = props => ({
    isAllFlashPool: props.isAllFlashPool,
    cpuCacheTimeseries: props.cpuCacheTimeseries,
    cpuThresholdPercent: props.cpuThresholdPercent,
    cacheThresholdPercent: props.cacheThresholdPercent,
    chartRangeStart: props.chartRangeStart,
    chartRangeEnd: props.chartRangeEnd,
    onChartSelect: props.onChartSelect,
});

/**
 * Compute and memoize the chart config
 */
const config = createSelector(
    chartConfigSelector,
    (chartConfigProps) => {
        const {
            isAllFlashPool,
            cpuCacheTimeseries,
            cpuThresholdPercent,
            cacheThresholdPercent,
            chartRangeStart,
            chartRangeEnd,
            onChartSelect,
        } = chartConfigProps;
        const cpuAverages = buildMedianSeries(cpuCacheTimeseries, 'timestamp', 'cpuUsagePercent');

        // TODO: Add plot line when there is only data point which exceeds the Threshold
        const performanceImpactColor = chroma(CHART_COLORS.redOrange).alpha(0.3).css();
        let index = -1;
        let isNewPlotBand = true;
        let store = null;

        let plotBands;
        if (SHOW_PLOT_BANDS_WHEN_METRIC_EXCEEDS_THRESHOLD) {
            plotBands = cpuCacheTimeseries.reduce((memo, point) => {
                if (point.cpuUsagePercent > cpuThresholdPercent || point.cacheUsagePercent > cacheThresholdPercent) {
                    if (isNewPlotBand) {
                        index += 1;
                        memo[index] = { color: performanceImpactColor, from: point.timestamp };
                    } else {
                        store = point;
                    }
                    isNewPlotBand = false;
                } else if (store || !isNewPlotBand) {
                    const toValue = (store && store.timestamp) || point.timestamp;
                    memo[index] = Object.assign({}, memo[index], { to: toValue });
                    store = null;
                    isNewPlotBand = true;
                }
                return memo;
            }, []);
        }
        const series = [
            {
                id: CPU_SERIES_ID,
                name: 'CPU Usage',
                data: cpuAverages,
                zIndex: 1,
                thresholdValue: cpuThresholdPercent,
            },
        ];

        if (!isAllFlashPool) {
            const cacheAverages = buildMedianSeries(cpuCacheTimeseries, 'timestamp', 'cacheUsagePercent');
            series.push({
                id: CACHE_SERIES_ID,
                name: 'Cache Miss Rate',
                data: cacheAverages,
                thresholdValue: cacheThresholdPercent,
                zIndex: 1,
            });
        }
        return applyMixins(
            dragZoomMixin(onChartSelect),
            {
                series,

                xAxis: {
                    plotBands,
                    type: 'datetime',
                    ...xAxisDataRange(chartRangeStart, chartRangeEnd),
                },

                yAxis: {
                    title: false,
                    min: 0,
                    max: 100,
                    minPadding: 0,
                    maxPadding: 0,
                    labels: {
                        enabled: true,
                    },
                    lineWidth: 1,
                },
            }
        );
    }
);

const buildLegendItems = ({ chart: { series }, pointValues }) => series.map(highchartSeries => ({
    id: highchartSeries.options.id,
    label: highchartSeries.options.name,
    icon: true,
    color: !highchartSeries.color ? 'transparent' : highchartSeries.color,
    value: pointValues && pointValues[highchartSeries.options.id] &&
        headlineValueFormatter(pointValues[highchartSeries.options.id].value, FORMAT_NUMBER_EXTRA_PRECISION, '%'),
}));

class PoolPerformanceChart extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.state = { showPoolPeformanceDetail: true };
    }

    togglePoolPerformanceDetailVisibility() {
        this.setState(({ showPoolPeformanceDetail }) => ({ showPoolPeformanceDetail: !showPoolPeformanceDetail }));
    }

    render() {
        const { loading, loaded } = this.props;
        let chartConfig = null;
        let defaultPointValues = null;
        // Compute only after the data is loaded
        if (loaded) {
            chartConfig = config(this.props);
            defaultPointValues = computeDefaultPointValues(this.props);
        }

        return (
            <Card
                title={
                    <div className="flex-container align-justify">
                        <h2 className="no-margin">Pool Performance</h2>
                        {SHOW_POOL_PERFORMANCE_SNAPSHOT_VIEW &&
                        <button className={cx('show-more-button')} onClick={this.togglePoolPerformanceDetailVisibility}>
                            <IconAdaptor type="font-awesome" size="small">
                                <i className={`fa ${this.state.showPoolPeformanceDetail ? 'fa-angle-down' : 'fa-angle-up'}`} />
                            </IconAdaptor>
                        </button>
                        }
                    </div>
                }
            >
                <Async loading={loading}>
                    <NoData hasData={loaded} message="CPU Usage & Cache Miss Rate data is not available">
                        {() => {
                            const { cpuCacheTimeseries, cpuThresholdPercent, cacheThresholdPercent } = this.props;
                            const { showPoolPeformanceDetail } = this.state;
                            if (!showPoolPeformanceDetail) {
                                return (
                                    <div className="row expanded chart-container--xxsmall">
                                        <div className="column large-19">
                                            <PoolPerformanceSnapshotView
                                                cpuCacheTimeseries={cpuCacheTimeseries}
                                                cpuThresholdPercent={cpuThresholdPercent}
                                                cacheThresholdPercent={cacheThresholdPercent}
                                            />
                                        </div>
                                    </div>
                                );
                            }

                            return (
                                <SynchronizedChart>
                                    <MultiObjectChart
                                        className="chart-container--xsmall"
                                        config={chartConfig}
                                        defaultPointValues={defaultPointValues}
                                        buildLegendItems={buildLegendItems}
                                        useHighchartsCrosshair
                                    />
                                </SynchronizedChart>
                            );
                        }}
                    </NoData>
                </Async>
            </Card>
        );
    }
}

PoolPerformanceChart.propTypes = {
    cpuCacheTimeseries: PropTypes.arrayOf(PropTypes.shape({
        timestamp: PropTypes.number,
        cpuUsagePercent: PropTypes.number,
        cacheUsagePercent: PropTypes.number,
    })),
    cpuThresholdPercent: PropTypes.number,
    cacheThresholdPercent: PropTypes.number,
    isAllFlashPool: PropTypes.oneOf([1, 0]), // eslint-disable-line react/no-unused-prop-types
    loading: PropTypes.bool.isRequired,
    loaded: PropTypes.bool.isRequired,
    onChartSelect: PropTypes.func, // eslint-disable-line react/no-unused-prop-types
};

PoolPerformanceChart.defaultProps = {
    containerWidth: 0,
    containerHeight: 0,
    loading: false,
    loaded: false,
    onChartSelect: event => event.preventDefault(),
};

export default PoolPerformanceChart;
