import React from 'react';
import PropTypes from 'prop-types';
import { isNumber } from 'underscore';
import classnames from 'classnames';
import calcPercent from './calcPercent';

const Bar = ({ className, value, min, max, color }) => {
    if (!isNumber(min) || !isNumber(max) || !isNumber(value)) {
        return null;
    }

    const style = {
        width: `${calcPercent(min, max, value)}%`,
        backgroundColor: color,
    };

    return (
        <span className={classnames('progress-bar', className)} style={style} />
    );
};

Bar.propTypes = {
    value: PropTypes.number,
    min: PropTypes.number,
    max: PropTypes.number,
    color: PropTypes.string,
};

Bar.defaultProps = {
    value: 0,
    min: 0,
    max: 100,
};

export default Bar;
