import { createReducersForAction, createSelector, getInitialState } from '@infosight/elmer/dist/utils/redux';
import rootState from '../../rootStateSelector';
import { USER_CONTEXT_CHANGED } from '../../../user/constants';

import {
    POOL_FETCH,
    OVERVIEW_FETCH,
    CAPACITY_FETCH,
    RESET,
} from './constants';

const initialState = {
    ...getInitialState('pool'),
    ...getInitialState('poolCapacity'),
    ...getInitialState('poolOverview'),
    ...getInitialState('poolPerformance'),
    ...getInitialState('poolProtection'),
};

const ACTION_HANDLERS = {
    [USER_CONTEXT_CHANGED]: () => (initialState),
    [RESET]: () => (initialState),
    ...createReducersForAction({ type: POOL_FETCH, stateKey: 'pool' }),
    ...createReducersForAction({ type: OVERVIEW_FETCH, stateKey: 'poolOverview' }),
    ...createReducersForAction({ type: CAPACITY_FETCH, stateKey: 'poolCapacity' }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

const baseSelector = state => rootState(state).poolDetail;
export const poolSelector = createSelector(baseSelector, 'pool');
export const poolOverviewSelector = createSelector(baseSelector, 'poolOverview');
export const capacitySelector = createSelector(baseSelector, 'poolCapacity');
export const performanceSelector = createSelector(baseSelector, 'poolPerformance');
export const protectionSelector = createSelector(baseSelector, 'poolProtection');
