import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { isFinite, isUndefined, isNaN } from 'underscore';
import PercentGrowth from '@infosight/elmer/dist/components/PercentGrowth';
import Legend from '@infosight/elmer/dist/components/Legend';
import classnames from 'classnames/bind';
import numeral from 'numeral';
import DataUsageDonut from './Donut';

import style from './styles.scss';

const cx = classnames.bind(style);
const BYTE_FORMAT = '0.00 ib';

const isValidNumber = number => !isNaN(number) && !isUndefined(number) && isFinite(number);

const DonutTitle = ({ usedBytes, allocatedBytes, growthPercent, warningThreshold }) => {
    const percentUsed = 100 * (usedBytes / allocatedBytes);

    // Data from backend can be null or undefined
    if (!isValidNumber(percentUsed)) {
        return null;
    }

    const percentUsedClass = percentUsed > warningThreshold ? 'percentUsedWarning' : 'percentUsed';

    return (
        <div className={classnames('flex-container align-middle align-center', cx('donut-title'))} >
            <span className={cx(percentUsedClass)}>{numeral(percentUsed).format('0')}%</span>
            <div className={cx('percentageGrowth')}>
                <PercentGrowth value={growthPercent} isPercent showCaret />
            </div>
        </div>
    );
};

DonutTitle.propTypes = {
    usedBytes: PropTypes.number,
    allocatedBytes: PropTypes.number,
    growthPercent: PropTypes.number,
    warningThreshold: PropTypes.number,
};

class DonutChart extends PureComponent {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = {};
    }

    getSeriesInfo(chart) {
        const chartData = chart && chart.series[0].data;
        const legendConfig = chartData && chartData.map(datapoint => ({
            id: datapoint.options.name,
            label: datapoint.options.name,
            value: numeral(datapoint.options.y).format(BYTE_FORMAT),
            color: datapoint.options.name === 'Unused Reserve' ? '' : datapoint.options.color,
            iconClassName: datapoint.options.name === 'Unused Reserve' ? cx('striped') : '',
            icon: true,
        })).concat({
            id: 'allocatedBytes',
            label: <strong>Total Capacity</strong>,
            value: <strong>{numeral(this.props.allocatedBytes).format(BYTE_FORMAT)}</strong>,
            color: 'transparent',
            icon: true,
        });

        this.setState({ legendConfig });
    }

    render() {
        return (
            <div className="row expanded align-middle">
                {/* TODO Replaced class="column" on these two element with the padding for IE11 */}
                <div className={classnames('shrink medium-unstack', cx('donut'))} style={{ paddingRight: '0.5em' }}>
                    <div className={cx('donut-pie')}>
                        <DataUsageDonut {...this.props} callback={this.getSeriesInfo} />
                    </div>
                    <DonutTitle {...this.props} />
                </div>
                <div className="medium-unstack">
                    <Legend items={this.state.legendConfig} />
                </div>
            </div>
        );
    }
}

DonutChart.propTypes = {
    /* eslint-disable react/no-unused-prop-types */
    allocatedBytes: PropTypes.number,
    usedBytes: PropTypes.number,
    freeBytes: PropTypes.number,
    unusedReserveBytes: PropTypes.number,
    growthPercent: PropTypes.number,
    warningThreshold: PropTypes.number,
};

DonutChart.propTypes = {
    warningThreshold: 100, // i.e. by default there is no warning threshold
};

export default DonutChart;
