const _ = require('underscore');
var Backbone = require('backbone');
var moment = require('moment');

var OPTIONS = [
    {
        label: '1D',
        id: '1-day',
        lookback: moment.duration(1, 'day'),
    },
    {
        label: '1W',
        id: '1-week',
        lookback: moment.duration(1, 'week'),
    },
    {
        label: '1M',
        id: '1-month',
        lookback: moment.duration(1, 'month'),
    },
    {
        label: '3M',
        id: '3-months',
        lookback: moment.duration(3, 'months'),
    },
    {
        label: '1Y',
        id: '1-year',
        lookback: moment.duration(1, 'years'),
    },
];

module.exports = Backbone.Model.extend({

    defaults: _(OPTIONS).findWhere({ id: '1-week' }),

    getQueryParams: function () {
        return this.pick('lookback', 'min', 'max');
    },

    setLookback: function (id) {
        this.clear({ silent: true })
            .set(_(OPTIONS).findWhere({ id: id }));
    },

    setCustomRange: function (range) {
        this.clear({ silent: true })
            .set({
                id: 'custom',
                min: range.min.getTime(),
                max: range.max.getTime(),
            });
    },

    setActualDataRange: function (start, end) {
        const actualDataRange = start && end ? { start, end } : null;
        this.set({ actualDataRange }, { noRefresh: true });
    },

});
