import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import NimbleIcon from '../NimbleIcon';
import style from './style.scss';

const cx = classnames.bind(style);

const NamedNimbleIcon = ({ className, iconName, aliasName, inline, ...props }) => (
    <div className={classnames(className, 'flex-container align-middle', { 'flex-dir-column': !inline, 'flex-dir-row': !inline })}>
        {!inline && <NimbleIcon name={iconName} {...props} />}
        {inline && <span className={cx('icon')}><NimbleIcon name={iconName} {...props} /></span>}
        <span>{!aliasName ? iconName : aliasName}</span>
    </div>
);

NamedNimbleIcon.propTypes = {
    /**
     * Name of the Icon
     * Ex: Oracle, Exchange
     */
    iconName: PropTypes.string.isRequired,

    inline: PropTypes.bool,

    /**
     * If you want to show a different name other than the icon name use this property
     */
    aliasName: PropTypes.string,
};

NamedNimbleIcon.defaultProps = {
    iconName: 'Unassigned',
    inline: false,
};

export default NamedNimbleIcon;
