import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';

import _ from 'underscore';

import { ProgressWithValue, Progress } from '@infosight/elmer/dist/components/Progress';
import Card from '@infosight/elmer/dist/components/Card';
import { Highstock as Highcharts } from 'progeny/modules/nimble/highchartsProxy';
import IopsChart from '../Iops';
import PerfChart from '../Performance';

import { toIops, toLatency, toThroughput } from '../util/formatterLibrary';
import { isEmpty } from '../../../util/formatter';
import ChartSummaryLayout from './layout/ChartSummaryLayout';

import style from './layout/styles.scss';

import { COLOR_SETS } from '../../../util/chartColors';

const colorList = [COLOR_SETS.teal, COLOR_SETS.purple, COLOR_SETS.green, COLOR_SETS.blue];
const transparentFirstColorList = [COLOR_SETS.transparent, ...colorList];

const cx = classnames.bind(style);

function onMouseMove(e) {
    let point;
    let j;
    let seriesLen;
    let event;

    // Guard against charts left hanging around in memory that are not in this view
    Highcharts.charts.filter(x => x && e.currentTarget.contains(x.renderTo)).forEach((chart) => {
        event = chart.pointer.normalize(Object.assign({}, e)); // Find coordinates within the chart
        const hoverPoints = [];
        for (j = 0, seriesLen = chart.series.length; j < seriesLen; j += 1) {
            point = chart.series[j].searchPoint(event, true); // Get the hovered point

            if (point) {
                point.onMouseOver(); // Show the hover marker
                hoverPoints.push(point);
            }
        }

        if (hoverPoints.length) {
            chart.tooltip.refresh(hoverPoints);
        }
        chart.xAxis[0].drawCrosshair(e, point); // Show the crosshair
    });
}

const hasValue = x => !isEmpty(x);
const min = arr => (arr && arr.length > 0 && Math.min(...arr, Number.POSITIVE_INFINITY)) || 0;
/*
Make sure the performance charts max is always at least 1.
This avoids problematic divide-by-zero problems with data that's all zeroes.
*/
const max = arr => Math.max(...arr, 1);
const sum = arr => arr.reduce((memo, num) => memo + num, 0);
const avg = (arr) => {
    const values = arr.filter(hasValue);
    if (!values.length) {
        return undefined;
    }

    return values.reduce((memo, num) => memo + num, 0) / (values.length);
};

const weightedAverage = (values, weights, weightedTotal, minVal, maxVal) => {
    const weightedAvg = values.reduce((accumulator, currentValue, currentIndex) =>
        accumulator + (currentValue * weights[currentIndex]), 0) / weightedTotal;
    // Constrain the weighted average value to be within the min and max bounds to correct for JS precision problems
    return Math.min(maxVal, Math.max(minVal, weightedAvg));
};

class PerformanceChartsPage extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    render() {
        const { chartRangeStart, chartRangeEnd, missingSeverity, useDeviceLatency, onChartSelect } = this.props;

        const hasHostLatencyMs = _.find(this.props.timeseries, obj => typeof obj.hostLatencyMs === 'number');
        const hostLatencyMs = hasHostLatencyMs ? _.pluck(this.props.timeseries, 'hostLatencyMs') : [];
        const hostLatencyMsCompact = _.compact(hostLatencyMs);
        const hostLatencyMsAvg = avg(hostLatencyMsCompact);

        const hasNetworkLatencyMs = _.find(this.props.timeseries, obj => typeof obj.networkLatencyMs === 'number');
        const networkLatencyMs = hasNetworkLatencyMs ? _.pluck(this.props.timeseries, 'networkLatencyMs') : [];
        const networkLatencyMsCompact = _.compact(networkLatencyMs);
        const networkLatencyMsAvg = avg(networkLatencyMsCompact);

        const hasStorageLatencyMs = _.find(this.props.timeseries, obj => typeof obj.storageLatencyMs === 'number');
        const storageLatencyMs = hasStorageLatencyMs ? _.pluck(this.props.timeseries, 'storageLatencyMs') : [];
        const storageLatencyMsCompact = _.compact(storageLatencyMs);
        const storageLatencyMsAvg = avg(storageLatencyMsCompact);

        const hasDeviceLatencyMs = _.find(this.props.timeseries, obj => typeof obj.deviceLatencyMs === 'number');
        const deviceLatencyMs = hasDeviceLatencyMs ? _.pluck(this.props.timeseries, 'deviceLatencyMs') : [];
        const deviceLatencyMsCompact = _.compact(deviceLatencyMs);
        const deviceLatencyMsAvg = avg(deviceLatencyMsCompact);

        let latencyFactors = 0;
        let maxTotalLatency = 0;
        let latency = null;
        if (useDeviceLatency) {
            latencyFactors = hostLatencyMs.concat(deviceLatencyMs);
            latency = this.props.timeseries.map(data => data.hostLatencyMs + data.deviceLatencyMs);
            maxTotalLatency = max(_.compact(latency));
        } else {
            latencyFactors = hostLatencyMs.concat(networkLatencyMs).concat(storageLatencyMs);
            latency = this.props.timeseries.map(data => data.hostLatencyMs + data.networkLatencyMs + data.storageLatencyMs);
            maxTotalLatency = max(_.compact(latency));
        }
        const latencyFactorsComapct = _.compact(latencyFactors);
        const latencyFactorsMin = min(latencyFactorsComapct);
        const latencyFactorsMax = max(latencyFactorsComapct);

        const readLatencyMs = _.pluck(this.props.timeseries, 'readLatencyMs');
        const writeLatencyMs = _.pluck(this.props.timeseries, 'writeLatencyMs');
        const totalLatencyMs = _.pluck(this.props.timeseries, 'totalLatencyMs');
        const latencyRaw = _.compact(readLatencyMs.concat(writeLatencyMs).concat(totalLatencyMs));
        const latencyRawMin = min(latencyRaw);
        const latencyRawMax = max(latencyRaw);

        const readIops = _.pluck(this.props.timeseries, 'readIops');
        const readIopsAvg = avg(readIops);
        const writeIops = _.pluck(this.props.timeseries, 'writeIops');
        const writeIopsAvg = avg(writeIops);
        const iops = readIops.concat(writeIops);
        const iopsMin = min(iops);
        const iopsMax = max(iops);

        const totalReadIops = sum(readIops);
        const totalWriteIops = sum(writeIops);
        /**
         * Latency has to be weighted with IOPS
         * Read Latency Avg = SIGMA (ReadLatency * ReadIOPS) / SIGMA (readIOPS)
         */
        const readLatencyMsAvg = weightedAverage(readLatencyMs, readIops, totalReadIops, latencyRawMin, latencyRawMax);
        const writeLatencyMsAvg = weightedAverage(writeLatencyMs, writeIops, totalWriteIops, latencyRawMin, latencyRawMax);

        const readMbps = _.pluck(this.props.timeseries, 'readMbps');
        const readMbpsAvg = avg(readMbps);
        const writeMbps = _.pluck(this.props.timeseries, 'writeMbps');
        const writeMbpsAvg = avg(writeMbps);
        const mbps = readMbps.concat(writeMbps);
        const mbpsMin = min(mbps);
        const mbpsMax = max(mbps);

        const latencyTitles = useDeviceLatency
            ? ['Total', 'Host', 'Storage/Network']
            : ['Total', 'Host', 'Network', 'Storage'];
        const latencyTimeSeriesKeys = useDeviceLatency
            ? ['totalLatencyMs', 'hostLatencyMs', 'deviceLatencyMs']
            : ['totalLatencyMs', 'hostLatencyMs', 'networkLatencyMs', 'storageLatencyMs'];
        const latencyCard = (
            <Card title="Latency">
                {latencyFactors.length > 0 && (
                    <ChartSummaryLayout>
                        <div key="0" className={useDeviceLatency ? 'chart-container--small' : 'chart-container--medium'}>
                            <PerfChart
                                title="Total Latency"
                                titles={latencyTitles}
                                legendDisabled
                                yAxisLabel="ms"
                                yAxisMax={max([latencyFactorsMax, maxTotalLatency])}
                                type="area"
                                timeSeries={this.props.timeseries}
                                timeSeriesKeys={latencyTimeSeriesKeys}
                                chartRangeStart={chartRangeStart}
                                chartRangeEnd={chartRangeEnd}
                                colorList={transparentFirstColorList}
                                invisibleSeries={[0]}
                                onChartSelect={onChartSelect}
                            />
                        </div>
                        <div key="1" className={cx('summaryCharts')}>
                            <h3 className={cx('chartLabel')}>Average Host</h3>
                            <ProgressWithValue
                                className={cx('progressContainer')}
                                leftLabel={toLatency(hostLatencyMsAvg).toString()}
                            >
                                <Progress
                                    min={latencyFactorsMin}
                                    max={latencyFactorsMax}
                                    value={hostLatencyMsAvg}
                                    color={colorList[0].stroke}
                                />
                            </ProgressWithValue>
                            {useDeviceLatency ? (
                                <div>
                                    <h3 className={cx('chartLabel')}>Average Storage/Network</h3>
                                    <ProgressWithValue
                                        className={cx('progressContainer')}
                                        leftLabel={toLatency(deviceLatencyMsAvg).toString()}
                                    >
                                        <Progress
                                            min={latencyFactorsMin}
                                            max={latencyFactorsMax}
                                            value={deviceLatencyMsAvg}
                                            color={colorList[1].stroke}
                                        />
                                    </ProgressWithValue>
                                </div>
                            ) : (
                                <div>
                                    <h3 className={cx('chartLabel')}>Average Network</h3>
                                    <ProgressWithValue
                                        className={cx('progressContainer')}
                                        leftLabel={toLatency(networkLatencyMsAvg).toString()}
                                    >
                                        <Progress
                                            min={latencyFactorsMin}
                                            max={latencyFactorsMax}
                                            value={networkLatencyMsAvg}
                                            color={colorList[1].stroke}
                                        />
                                    </ProgressWithValue>

                                    <h3 className={cx('chartLabel')}>Average Storage</h3>
                                    <ProgressWithValue
                                        className={cx('progressContainer')}
                                        leftLabel={toLatency(storageLatencyMsAvg).toString()}
                                    >
                                        <Progress
                                            min={latencyFactorsMin}
                                            max={latencyFactorsMax}
                                            value={storageLatencyMsAvg}
                                            color={colorList[2].stroke}
                                        />
                                    </ProgressWithValue>
                                </div>
                            )}
                        </div>
                    </ChartSummaryLayout>)}
                <ChartSummaryLayout>
                    <div key="0" className="chart-container--small">
                        <PerfChart
                            title="Read/Write Latency"
                            titles={['Read', 'Write']}
                            legendDisabled
                            yAxisLabel="ms"
                            yAxisMax={max([latencyFactorsMax, latencyRawMax])}
                            type="line"
                            timeSeries={this.props.timeseries}
                            timeSeriesKeys={['readLatencyMs', 'writeLatencyMs']}
                            chartRangeStart={chartRangeStart}
                            chartRangeEnd={chartRangeEnd}
                            onChartSelect={onChartSelect}
                        />
                    </div>
                    <div key="1" className={cx('summaryCharts')}>
                        <h3 className={cx('chartLabel')}>Average Read</h3>
                        <ProgressWithValue
                            className={cx('progressContainer')}
                            leftLabel={toLatency(readLatencyMsAvg).toString()}
                        >
                            <Progress
                                min={latencyRawMin}
                                max={latencyRawMax}
                                value={readLatencyMsAvg}
                                color={colorList[0].stroke}
                            />
                        </ProgressWithValue>

                        <h3 className={cx('chartLabel')}>Average Write</h3>
                        <ProgressWithValue
                            className={cx('progressContainer')}
                            leftLabel={toLatency(writeLatencyMsAvg).toString()}
                        >
                            <Progress
                                min={latencyRawMin}
                                max={latencyRawMax}
                                value={writeLatencyMsAvg}
                                color={colorList[1].stroke}
                            />
                        </ProgressWithValue>
                    </div>
                </ChartSummaryLayout>
            </Card>
        );

        const iopsCard = (
            <Card title="IOPS">
                <ChartSummaryLayout>
                    <div key="0" className="chart-container--small">
                        <IopsChart
                            chartRangeStart={chartRangeStart}
                            chartRangeEnd={chartRangeEnd}
                            legendDisabled
                            timeSeries={this.props.timeseries}
                            timeSeriesKey="readIops"
                            title="Read"
                            type="column"
                            yAxisLabel="IO/sec"
                            yAxisMax={iopsMax}
                            onChartSelect={onChartSelect}
                        />
                    </div>
                    <div key="1" className={cx('summaryCharts')}>
                        <h3 className={cx('chartLabel')}>Average Read</h3>
                        <ProgressWithValue
                            className={cx('progressContainer')}
                            leftLabel={toIops(readIopsAvg).toString()}
                        >
                            <Progress
                                min={iopsMin}
                                max={iopsMax}
                                value={readIopsAvg}
                                color="#CCCCCC"
                            />
                        </ProgressWithValue>
                    </div>
                </ChartSummaryLayout>
                <ChartSummaryLayout>
                    <div key="0" className="chart-container--small">
                        <IopsChart
                            chartRangeStart={chartRangeStart}
                            chartRangeEnd={chartRangeEnd}
                            legendDisabled
                            timeSeries={this.props.timeseries}
                            timeSeriesKey="writeIops"
                            title="Write"
                            type="column"
                            yAxisLabel="IO/sec"
                            yAxisMax={iopsMax}
                            onChartSelect={onChartSelect}
                        />
                    </div>
                    <div key="1" className={cx('summaryCharts')}>
                        <h3 className={cx('chartLabel')}>Average Write</h3>
                        <ProgressWithValue
                            className={cx('progressContainer')}
                            leftLabel={toIops(writeIopsAvg).toString()}
                        >
                            <Progress
                                min={iopsMin}
                                max={iopsMax}
                                value={writeIopsAvg}
                                color="#CCCCCC"
                            />
                        </ProgressWithValue>
                    </div>
                </ChartSummaryLayout>
            </Card>
        );

        // We have 2 versions of the IOPS card because this is a common component and certain types of objects have
        // the severity data while other types of objects do not.
        const iopsCardWithoutSeverity = (
            <Card title="IOPS">
                <ChartSummaryLayout>
                    <div key="0" className="chart-container--small">
                        <PerfChart
                            titles={['Read', 'Write']}
                            legendDisabled
                            yAxisLabel="IO/sec"
                            type="line"
                            timeSeries={this.props.timeseries}
                            timeSeriesKeys={['readIops', 'writeIops']}
                            chartRangeStart={chartRangeStart}
                            chartRangeEnd={chartRangeEnd}
                            onChartSelect={onChartSelect}
                        />
                    </div>
                    <div key="1" className={cx('summaryCharts')}>
                        <h3 className={cx('chartLabel')}>Average Read</h3>
                        <ProgressWithValue
                            className={cx('progressContainer')}
                            leftLabel={toIops(readIopsAvg).toString()}
                        >
                            <Progress
                                min={iopsMin}
                                max={iopsMax}
                                value={readIopsAvg}
                                color={colorList[0].stroke}
                            />
                        </ProgressWithValue>
                        <h3 className={cx('chartLabel')}>Average Write</h3>
                        <ProgressWithValue
                            className={cx('progressContainer')}
                            leftLabel={toIops(writeIopsAvg).toString()}
                        >
                            <Progress
                                min={iopsMin}
                                max={iopsMax}
                                value={writeIopsAvg}
                                color={colorList[1].stroke}
                            />
                        </ProgressWithValue>
                    </div>
                </ChartSummaryLayout>
            </Card>
        );

        const throughputCard = (
            <Card title="Throughput">
                <ChartSummaryLayout>
                    <div key="0" className="chart-container--small">
                        <PerfChart
                            titles={['Read', 'Write']}
                            legendDisabled
                            yAxisLabel="MiB/sec"
                            type="area"
                            timeSeries={this.props.timeseries}
                            timeSeriesKeys={['readMbps', 'writeMbps']}
                            chartRangeStart={chartRangeStart}
                            chartRangeEnd={chartRangeEnd}
                            onChartSelect={onChartSelect}
                        />
                    </div>
                    <div key="1" className={cx('summaryCharts')}>
                        <h3 className={cx('chartLabel')}>Average Read</h3>
                        <ProgressWithValue
                            className={cx('progressContainer')}
                            leftLabel={toThroughput(readMbpsAvg * 1024 * 1024).toString()}
                        >
                            <Progress
                                min={mbpsMin}
                                max={mbpsMax}
                                value={readMbpsAvg}
                                color={colorList[0].stroke}
                            />
                        </ProgressWithValue>
                        <h3 className={cx('chartLabel')}>Average Write</h3>
                        <ProgressWithValue
                            className={cx('progressContainer')}
                            leftLabel={toThroughput(writeMbpsAvg * 1024 * 1024).toString()}
                        >
                            <Progress
                                min={mbpsMin}
                                max={mbpsMax}
                                value={writeMbpsAvg}
                                color={colorList[1].stroke}
                            />
                        </ProgressWithValue>
                    </div>
                </ChartSummaryLayout>
            </Card>
        );

        return (
            <div onMouseMove={onMouseMove}>
                {latencyCard}
                {missingSeverity ? iopsCardWithoutSeverity : iopsCard}
                {throughputCard}
            </div>
        );
    }
}

PerformanceChartsPage.propTypes = {
    timeseries: PropTypes.arrayOf(PropTypes.shape({
        timestamp: PropTypes.number,
        hostLatencyMs: PropTypes.number,
        networkLatencyMs: PropTypes.number,
        storageLatencyMs: PropTypes.number,
        readLatencyMs: PropTypes.number,
        writeLatencyMs: PropTypes.number,
        totalLatencyMs: PropTypes.number,
        readIops: PropTypes.number,
        writeIops: PropTypes.number,
        readMbps: PropTypes.number,
        writeMbps: PropTypes.number,
    })),
    chartRangeStart: PropTypes.object.isRequired,
    chartRangeEnd: PropTypes.object.isRequired,
    missingSeverity: PropTypes.bool,
    useDeviceLatency: PropTypes.bool,
    onChartSelect: PropTypes.func,
};

export default PerformanceChartsPage;
