import { isFunction, isObject } from 'underscore';
import { Highstock } from 'progeny/modules/nimble/highchartsProxy';

const mixinReducer = (accumulation, mixin) => {
    let config;

    if (isFunction(mixin)) {
        config = mixin(accumulation);
    } else if (isObject(mixin)) {
        config = mixin;
    } else {
        return accumulation;
    }

    return config ? Highstock.merge(accumulation, config) : accumulation;
};

export default function applyMixins(...mixins) {
    return mixins.reduce(mixinReducer, {});
}
