import numeral from 'numeral';
import ValueWithUnits from './ValueWithUnits';

export function toIops(value) {
    return new ValueWithUnits(numeral(value).format('0,[.][00]'), 'IOPS');
}

export function toLatency(value) {
    return new ValueWithUnits(numeral(value).format('0,[.][00]'), 'ms');
}

export function toThroughput(value) {
    let formatted = numeral(value).format('0,[.][00] ib');

    if (formatted.trim() === Number(formatted).toString()) {
        formatted = `${formatted.trim()} B`;
    }
    return new ValueWithUnits(formatted, '/sec', '');
}

export function toPercent(value) {
    return new ValueWithUnits(numeral(value).format('0'), '%');
}
