import { dateTimeXAxisMixin } from './dateTimeAxis';
import applyMixins from './applyMixins';
import { hijackSharedTooltipMixin } from './tooltip';
import { disableLegendMixin } from './legend';

export function stackedAreaMixin() {
    return {
        chart: {
            spacingBottom: 0,
            spacingLeft: 0,
            type: 'area',
        },
        plotOptions: {
            area: {
                animation: false,
                marker: {
                    enabled: false,
                    symbol: 'circle',
                },
                stack: 0,
                stacking: 'normal',
                stickyTracking: true,
            },
        },
    };
}

export function stackedAreaTimeSeriesMixin({
    baseTimeSeries,
    chartRangeStart,
    chartRangeEnd,
    onMouseOverPoint,
    onRedraw,
    width,
    height,
    title,
    yAxisTitle,
    yAxisLabelFormatter,
    seriesMapper,
}) {
    return applyMixins(
        dateTimeXAxisMixin({
            min: chartRangeStart.valueOf(),
            max: chartRangeEnd.valueOf(),
        }),
        hijackSharedTooltipMixin({ formatterCallback: onMouseOverPoint }),
        disableLegendMixin,
        stackedAreaMixin,
        {
            chart: {
                width,
                height,
                ignoreHiddenSeries: true,
                events: {
                    redraw() {
                        onRedraw(this);
                    },
                },
            },
            plotOptions: {
                line: {
                    marker: {
                        enabled: false,
                        symbol: 'circle',
                    },
                },
            },
            series: baseTimeSeries.map((series, index) => ({
                index,
                legendIndex: index,
                ...seriesMapper(series, index),
            })),
            title,
            yAxis: {
                title: {
                    text: yAxisTitle,
                },
                labels: {
                    formatter: yAxisLabelFormatter && function formatter() {
                        return yAxisLabelFormatter(this);
                    },
                },
            },
        }
    );
}
