import numeral from 'numeral';
import { isNumber } from 'underscore';

export const getFormat = value => (value >= 1024 ? '0.0 ib' : '0 ib');

// IMPORTANT! function relies on a format with a single space between the units and the number.
export const getValueAndUnit = (value, format) => {
    if (format.indexOf(' ') < 0) {
        throw new Error('getValueAndUnit requires a space in the format');
    }
    if (isNumber(value)) {
        return numeral(value).format(format).split(' ');
    }

    return [null, null];
};
export const getByteValueAndUnit = value => getValueAndUnit(value, getFormat(value));
