import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import ReactDimensions from 'react-dimensions';
import MouseOutTrackingHighchart from '../MouseOutTrackingHighchart';

class ChartProvider extends PureComponent { // eslint-disable-line react/prefer-stateless-function
    render() {
        const { buildConfig, onAfterChartRendered } = this.props;
        return (
            <MouseOutTrackingHighchart
                callback={onAfterChartRendered}
                {...this.props}
                config={buildConfig(this.props)}
                domProps={{
                    className: this.props.className,
                }}
            />
        );
    }
}

ChartProvider.propTypes = {
    /**
     * @param {Object} `this.props`
     * @return {Object} Highcharts config object
     */
    buildConfig: PropTypes.func.isRequired,

    onAfterChartRendered: PropTypes.func.isRequired,
};

export default ReactDimensions({ elementResize: true })(ChartProvider);
