import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import { groupBy, indexBy } from 'underscore';
import axios from 'axios';
import moment from 'moment-timezone';
import { saveAs } from 'file-saver';
import {
    LIST_FETCH,
    SUMMARY_CARDS_FETCH,
    CSV_FETCH,
    RESET,
} from './constants';
import Request from '../../../../services/Request';

/**
 * For better access, index the pools list by Id
 * @param {*} pools
 */
const indexById = pools => indexBy(pools, 'poolUid');

const groupById = pools => groupBy(pools, 'poolUid');

/**
 * Fetches the list of Pools
 * @param {*} scope
 */
export function fetchPools(scope) {
    return (dispatch) => {
        const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(LIST_FETCH, dispatch);
        dispatchStart();

        new Request().pod().genericQuery('pools')
            .post({ scope })
            .then(response => indexById(response.data.data))
            .then(dispatchSuccess)
            .catch(dispatchError);
    };
}

/**
 * Fetch Top Volumes in a Pool ranked by Days Until Full
 * @param {object} scope
 * @param {number} n, defaults to 10
 */
function fetchTopNVolumesByCapacity(scope, n = 10) {
    const options = { scope };

    options.filters = [{
        field: 'rankByCapacity', // TODO: Change this to daysUntilFull once data is available
        $lte: n,
    }];

    return new Request().pod().genericQuery('volume', ['capacityByPool'])
        .post(options);
}

/**
 * Fetch TOP N Volumes By IO and  Latency
 * TODO: Latency part
 * @param {object} scope
 * @param {number} n , defaults to 20
 */
function fetchTopNPerformantVolumes(scope, n = 20) {
    const options = { scope };

    options.filters = [{
        field: 'performanceRank',
        $lte: n,
    }];

    return new Request().pod().genericQuery('volume', ['topPerformanceByPool'])
        .post(options);
}

/**
 * Fetch CPU and cache data for the pools
 * @param {object} scope
 */
function fetchCpuCacheUsage(scope) {
    const options = { scope };

    return new Request().pod().genericQuery('pool', ['cpuCacheUsage'])
        .post(options);
}

/**
 * Dispatches all the api calls required to get data for the executive card charts
 * and updates the state with final wrangled data
 */
export function fetchSummaryCards(scope) {
    return (dispatch) => {
        const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(SUMMARY_CARDS_FETCH, dispatch);
        dispatchStart();

        new Request().all([
            fetchTopNVolumesByCapacity(scope),
            fetchTopNPerformantVolumes(scope),
            fetchCpuCacheUsage(scope),
        ])
            .then(axios.spread((volumesByCapacity, volumesByIO, cpuCacheUsage) => {
                const capacity = groupById(volumesByCapacity.data.data);
                const io = groupById(volumesByIO.data.data);
                const cpuCache = indexById(cpuCacheUsage.data.data);
                const finalData = Object.keys(capacity).map(poolId => ({
                    volumesByCapacity: capacity[poolId],
                    volumesByIO: io[poolId],
                    ...cpuCache[poolId],
                }));
                return finalData;
            }))
            .then(dispatchSuccess)
            .catch(dispatchError);
    };
}
/**
 * Download the pools CSV file. NOTE: this actually saves the file
 */
export const getPoolsCsv = scope => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(CSV_FETCH, dispatch);
    dispatchStart();

    new Request().pod().genericQuery('pools').csv()
        .post({ scope }, { responseType: 'blob' })
        .then(x => saveAs(x.data, `HPE InfoSight Pools ${moment().format('L').replace(/\\|\//g, '-')}.csv`))
        .then(() => dispatchSuccess({})) // we want to end the loading but don't actually want to save the CSV data in redux
        .catch(dispatchError);
};

export function reset() {
    return { type: RESET };
}
