import { getStore } from '../../../progeny/util/storeProvider';
import {
    disableDialog as progenyDisableDialog,
    setNotification as progenySetNotification,
    setNotifications as progenySetNotifications,
    acceptEula as progenyAcceptEula,
} from '../../../progeny/modules/preferences/actionCreators';
import {
    getNotification as progenyGetNotification,
    isDialogEnabled as progenyIsDialogEnabled,
    isEulaAccepted as progenyIsEulaAccepted,
} from '../../../progeny/modules/preferences/reducer';

// actions
export const disableDialog = dialogId => {
    getStore().dispatch(progenyDisableDialog(dialogId));
};

export const setNotification = notification => {
    getStore().dispatch(progenySetNotification(notification));
};

export const setNotifications = notifications => {
    getStore().dispatch(progenySetNotifications(notifications));
};

export const acceptEula = key => {
    getStore().dispatch(progenyAcceptEula(key));
};

// getters
export const getNotification = (opts) => progenyGetNotification(getStore().getState(), opts);

export const isDialogEnabled = (id) => progenyIsDialogEnabled(getStore().getState(), id);

export const isEulaAccepted = (key) => progenyIsEulaAccepted(getStore().getState(), key);

export const getPreferences = () => getStore().getState().preferences;

// callback
export const onPreferencesLoaded = (callback) => {
    if (getStore().getState().preferences.loadedPreferences) {
        callback();
    } else {
        const unsubscribe = getStore().subscribe(() => {
            if (getStore().getState().preferences.loadedPreferences) {
                unsubscribe();
                callback();
            }
        });
    }
}
