import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { CollectionConfigBuilder, FieldBuilder } from '@infosight/elmer/dist/lib/CollectionConfig';
import { ContainsStringFacet } from '@infosight/elmer/dist/components/FacetedFilter';
import FacetedCollectionPage from '../../../../../layouts/FacetedCollectionPage';
import { formatValue } from '../../../../../util/formatter';
import {
    FORMAT_BYTE_10_PRECISION,
} from '../../../../../util/formatter/constants';

import styles from './shelves.scss';

const cx = classnames.bind(styles);

export const DEFAULT_TAB = 'summary';

const getConfig = () => new CollectionConfigBuilder()
    .addCategory(DEFAULT_TAB, 'Summary')
    .addField(
        new FieldBuilder()
            .category(DEFAULT_TAB)
            .field('shelfSn')
            .title('Shelf Serial #')
            .column({
                render: (_, { shelfSn, assetType }) => {
                    if (assetType === 'Shelf') {
                        return shelfSn;
                    }
                    return <span className={cx('head-shelf')}>{ shelfSn }</span>;
                },
            })
            .facet(<ContainsStringFacet />)
    )
    .addField(
        new FieldBuilder()
            .category(DEFAULT_TAB)
            .field('shelfModel')
            .title('Shelf Model')
            .column({
                render: (_, { shelfModel, assetType }) => {
                    if (assetType === 'Shelf') {
                        return shelfModel;
                    }
                    return <span className={cx('head-shelf')}>{ shelfModel }</span>;
                },
            })
            .facet(<ContainsStringFacet />)
    );


const addCapacityColumn = (config) => {
    config.addField(
        new FieldBuilder()
            .category(DEFAULT_TAB)
            .field('shelfHddRawCapacityBytes')
            .title('Capacity')
            .column({
                render: (_, { shelfHddRawCapacityBytes, assetType }) => {
                    const displayValue = formatValue(shelfHddRawCapacityBytes, FORMAT_BYTE_10_PRECISION);
                    if (assetType === 'Shelf') {
                        return <span>{displayValue}</span>;
                    }
                    return <span className={cx('head-shelf')}>{displayValue}</span>;
                },
            })
    );
};

const addCacheColumn = (config, title) => {
    config.addField(
        new FieldBuilder()
            .category(DEFAULT_TAB)
            .field('shelfSsdRawCapacityBytes')
            .title(title)
            .column({
                render: (_, { shelfSsdRawCapacityBytes, assetType }) => {
                    const displayValue = formatValue(shelfSsdRawCapacityBytes, FORMAT_BYTE_10_PRECISION);
                    if (assetType === 'Shelf') {
                        return <span>{displayValue}</span>;
                    }
                    return <span className={cx('head-shelf')}>{displayValue}</span>;
                },
            })
    );
};

const ArrayShelves = ({ ...array, shelves }) => {
    const config = getConfig();
    if (array.isAllFlash) {
        addCacheColumn(config, 'Capacity');
    } else {
        addCapacityColumn(config);
        addCacheColumn(config, 'Cache');
    }
    return (
        <FacetedCollectionPage
            config={config.valueOf()}
            collection={shelves || []}
            keyField="shelfSn"
            loadingCollection={false}
        />
    );
};

ArrayShelves.propTypes = {
    shelves: PropTypes.arrayOf(PropTypes.shape({
        shelfSn: PropTypes.string,
        shelfModel: PropTypes.string,
        shelfHddRawCapacityBytes: PropTypes.number,
        shelfSsdRawCapacityBytes: PropTypes.number,
    })),
};

ArrayShelves.defaultProps = {
    shelves: [],
};


export default ArrayShelves;
