import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { isNumber } from 'underscore';
import style from './style.scss';
import SavingsDetail from './SavingsDetail';

const cx = classnames.bind(style);

const Popover = ({ cloneSavings, dedupSavings, compressionSavings }) => (
    <div className={cx('popover')}>
        {isNumber(cloneSavings) && cloneSavings > 0 && <SavingsDetail name="Clones" savings={cloneSavings} />}
        {isNumber(dedupSavings) && dedupSavings > 0 && <SavingsDetail name="Deduplication" savings={dedupSavings} />}
        {isNumber(compressionSavings) && compressionSavings > 0 && (
            <SavingsDetail name="Compression" savings={compressionSavings} />
        )}
    </div>
);

Popover.propTypes = {
    cloneSavings: PropTypes.number,
    dedupSavings: PropTypes.number,
    compressionSavings: PropTypes.number,
};

export default Popover;
