import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { ReactHighcharts } from '@infosight/elmer/dist/components/ReactHighcharts';
import ReactDimensions from 'react-dimensions';
import NoData from '@infosight/elmer/dist/components/NoData';
import numeral from 'numeral';
import { isNumber } from 'underscore';
import chartOptions from './chartOptions';
import { lightGrey04, darkGrey02 } from '../../../../util/colors';
import { TOTAL_USAGE_COLOR } from '../../util';
import style from './style.scss';


export class PoolUsageChart extends PureComponent {
    data() {
        const { poolCapacityUsed, poolFree, poolUnusedReserve } = this.props;
        const data = [{
            name: 'Total Usage',
            y: poolCapacityUsed,
            color: TOTAL_USAGE_COLOR,
        }];

        if (poolUnusedReserve && isNumber(poolUnusedReserve)) {
            data.push({
                name: 'Unused Reserve',
                y: poolUnusedReserve,
                color: 'url(#unusedReservePattern)',
            });
        }

        data.push({
            name: 'Free Space',
            y: poolFree,
            color: lightGrey04,
        });

        return data;
    }

    config() {
        const { poolCapacityUsed, poolFree, poolUsedPct, containerHeight, containerWidth } = this.props;
        if (![poolCapacityUsed, poolFree, poolUsedPct].every(isNumber)) {
            return null;
        }

        const options = chartOptions({ height: containerHeight, width: containerWidth, data: this.data() });
        options.title = {
            text: `<div style="width: ${containerHeight}px" class="${style.value}">
                        ${numeral(poolUsedPct).format('0')}%
                   </div>`,
            useHTML: true,
            align: 'left',
            y: 10,
            verticalAlign: 'middle',
            style: {
                'text-align': 'center',
                'font-size': '24px',
                'font-family': "'Helvetica Neue', Helvetica, Roboto, Arial, sans-serif",
                color: darkGrey02,
            },
        };

        return options;
    }

    render() {
        const config = this.config();

        return (
            <NoData hasData={!!config}>
                {() => <ReactHighcharts config={config} />}
            </NoData>
        );
    }
}

PoolUsageChart.propTypes = {
    poolCapacityUsed: PropTypes.number.isRequired,
    poolFree: PropTypes.number.isRequired,
    poolUnusedReserve: PropTypes.number.isRequired,
    poolUsedPct: PropTypes.number.isRequired,
    containerHeight: PropTypes.number.isRequired,
    containerWidth: PropTypes.number.isRequired,
};

PoolUsageChart.defaultProps = {
    containerHeight: 0,
    containerWidth: 0,
};

export default ReactDimensions({ elementResize: true })(PoolUsageChart);
