import { createReducersForAction, createSelector, getInitialState } from '@infosight/elmer/dist/utils/redux';
import rootState from '../../rootStateSelector';
import { USER_CONTEXT_CHANGED } from '../../../user/constants';
import {
    VOLUMES_CAPACITY_FETCH,
    VOLUMES_THROUGHPUT_FETCH,
    VOLUMES_LATENCY_FETCH,
    VOLUMES_LIST_FETCH,
    POOL_CPU_CACHE_FETCH,
    SCOPING_OBJECT_SIZE_FETCH,
    VOLUMES_CSV_FETCH,
    RESET,
} from './constants';

const initialState = {
    volumesCapacity: null,
    loadingVolumesCapacity: false,
    loadedVolumesCapacity: false,
    performance: null,
    loadingPerformance: false,
    loadedPerformance: false,
    volumes: [],
    loadingVolumes: false,
    loadedVolumes: false,
    loadingScopingObjectSizeSeries: false,
    loadedScopingObjectSizeSeries: false,
    ...getInitialState('volumesCsv'),
};

const ACTION_HANDLERS = {
    [USER_CONTEXT_CHANGED]: () => (initialState),
    [RESET]: () => (initialState),
    ...createReducersForAction({ type: VOLUMES_CAPACITY_FETCH, stateKey: 'volumesCapacity' }),
    ...createReducersForAction({ type: VOLUMES_THROUGHPUT_FETCH, stateKey: 'throughputTimeseries' }),
    ...createReducersForAction({ type: VOLUMES_LATENCY_FETCH, stateKey: 'latencyTimeseries' }),
    ...createReducersForAction({ type: VOLUMES_LIST_FETCH, stateKey: 'volumes' }),
    ...createReducersForAction({ type: POOL_CPU_CACHE_FETCH, stateKey: 'poolCpuCacheTimeseries' }),
    ...createReducersForAction({ type: SCOPING_OBJECT_SIZE_FETCH, stateKey: 'scopingObjectSizeSeries' }),
    ...createReducersForAction({ type: VOLUMES_CSV_FETCH, stateKey: 'volumesCsv' }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

const baseSelector = state => rootState(state).volumeCollection;
export const volumesCapacitySelector = createSelector(baseSelector, 'volumesCapacity');
export const throughputTimeseriesSelector = createSelector(baseSelector, 'throughputTimeseries');
export const latencyTimeseriesSelector = createSelector(baseSelector, 'latencyTimeseries');
export const listSelector = createSelector(baseSelector, 'volumes');
export const poolCpuCacheTimeseriesSelector = createSelector(baseSelector, 'poolCpuCacheTimeseries');
export const scopingObjectSizeSeriesSelector = createSelector(baseSelector, 'scopingObjectSizeSeries');
export const csvSelector = createSelector(baseSelector, 'volumesCsv');
