import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment-timezone';

import Trends from './Trends';

const getExceedsTooltip = (isHighlyLikely, dateString) =>
    `This array is ${isHighlyLikely ? 'highly likely' : 'likely'} to exceed the performance throttling ` +
`threshold on ${dateString}. ` +
'It is critical that you take corrective action by freeing up space on the array or adding additional capacity.';

const getFoldersTooltip = (foldersFull, numMonthString) => (
    `${foldersFull} folder${foldersFull > 1 ? 's' : ''} full in less than ${numMonthString}`
);

function PoolTrends({ className, arrayCapacity, pool }) {
    if (!arrayCapacity || !pool) {
        return null;
    }

    const { capacity, free, possiblyExceedsThreshold, probablyExceedsThreshold, unmanagedSnapshots } = arrayCapacity;
    const { foldersFull1Month, foldersFull2Month } = pool;
    const trends = [];

    // if less than 10% free
    if (free / capacity <= 0.1) {
        trends.push({
            type: 'error',
            message: 'Critical space alert',
            tooltip: 'This array has exceeded the performance throttling threshold. ' +
            'It is critical that you take corrective action by freeing up space on the array ' +
            'or adding additional capacity.',
        });
    } else {
        if (probablyExceedsThreshold) {
            // these can be the same, so only show probably...
            trends.push({
                type: 'error',
                message: `Highly likely to exceed threshold on ${moment.utc(probablyExceedsThreshold).format('l')}`,
                tooltip: getExceedsTooltip(true, moment.utc(probablyExceedsThreshold).format('l')),
            });
        }

        const TWO_WEEKS_IN_MILLISECONDS = 1000 * 60 * 60 * 24 * 14;
        const spanMoreThan2Weeks = (probablyExceedsThreshold - possiblyExceedsThreshold) > TWO_WEEKS_IN_MILLISECONDS;

        if (possiblyExceedsThreshold && (!probablyExceedsThreshold || spanMoreThan2Weeks)) {
            trends.push({
                type: 'error',
                message: `Likely to exceed threshold on ${moment.utc(possiblyExceedsThreshold).format('l')}`,
                tooltip: getExceedsTooltip(true, moment.utc(possiblyExceedsThreshold).format('l')),
            });
        }
    }

    // show unmanaged snapshots info
    if (unmanagedSnapshots > 0) {
        trends.push({
            type: 'error',
            message: `${unmanagedSnapshots} unmanaged snapshot${unmanagedSnapshots > 1 ? 's' : ''}`,
            tooltip: 'A snapshot is considered unmanaged when a protection schedule or volume collection is deleted ' +
            'and snapshots that were created as part of the schedule still remain. ' +
            'This may be an opportunity to free up some space.',
        });
    }

    if (foldersFull1Month > 0) {
        trends.push({
            type: 'error',
            message: getFoldersTooltip(foldersFull1Month, 'one month'),
        });
    }

    if (foldersFull2Month > 0) {
        trends.push({
            type: 'warning',
            message: getFoldersTooltip(foldersFull2Month, 'two months'),
        });
    }

    if (trends.length === 0) {
        trends.push({
            type: 'success',
            message: 'No capacity risk',
        });
    }

    return <Trends className={className} trends={trends} />;
}

PoolTrends.propTypes = {
    arrayCapacity: PropTypes.shape({
        capacity: PropTypes.number,
        free: PropTypes.number,
        possiblyExceedsThreshold: PropTypes.number, // Date as epoch
        probablyExceedsThreshold: PropTypes.number, // Date as epoch
        unmanagedSnapshots: PropTypes.number,
    }),
    pool: PropTypes.shape({
        foldersFull1Month: PropTypes.number.isRequired,
        foldersFull2Month: PropTypes.number.isRequired,
    }),
};

export default PoolTrends;
