import React, { Component } from 'react';
import PropTypes from 'prop-types';
import numeral from 'numeral';
import autobind from 'react-autobind';
import { MINUTE_MOMENT_FORMAT } from '@infosight/elmer/dist/utils/time';
import { OBJECTS } from '../../../../inventory';
import providePointTracking from '../../../../../charts/PointTrackingChart/providePointTracking';
import { stackedAreaTimeSeriesMixin } from '../../../../../charts/mixins/stackedAreaChart';
import applyMixins from '../../../../../charts/mixins/applyMixins';
import dragZoomMixin from '../../../../../charts/mixins/dragZoom';
import { PointTrackingChart } from '../../../../../charts/PointTrackingChart';
import { CHART_COLORS } from '../../../../../../util/chartColors';

const legendValueFormatter = value => (typeof value === 'number' ? numeral(value).format('0.0 ib') : 'N/A');

/**
 * Scoped Object can be Group, Pool Or Folder
 * NOTE: All folders wont have limits
 * Check if the returned Scoped Object has data and it is valid
 * @param {*}
 */
const hasValidScopingObject = ({ type, timeseries, hasFolderLimit }) => {
    if (timeseries && timeseries.length > 0) {
        return type !== 'folder' || (type === 'folder' && hasFolderLimit);
    }
    return false;
};

export class TopVolumesByCapacity extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    getLinkProps({ options: { id, isAggregatedRemainder, scopingObjectType } }) {
        const { scope, config, location } = this.props;
        if (scopingObjectType) {
            return config.getLinkPropsForView(config.getDetailView(scope.type), { id }, null, location);
        }

        const view = config.findView(OBJECTS.VOLUME, isAggregatedRemainder ? 'list' : 'detail');
        const innerScope = isAggregatedRemainder ? scope : { id, type: OBJECTS.VOLUME };
        return config.getLinkPropsForView(view, innerScope, null, location);
    }

    buildScopingObjectSizeSeries() {
        const { scopingObjectSizeSeries: { id, type, timeseries } } = this.props;
        return {
            type: 'line',
            // Folders doesn't have Hard Limit
            dashStyle: type === 'folder' ? 'dot' : 'solid',
            id,
            scopingObjectType: type,
            name: `${type[0].toUpperCase()}${type.slice(1)} ${type === 'folder' ? 'Limit' : 'Size'}`,
            color: type === 'folder' ? CHART_COLORS.warningYellow : CHART_COLORS.redOrange,
            lineWidth: 2,
            data: timeseries.map(val => ({
                x: val.timestamp,
                y: val.sizeBytes,
            })),
            step: true,
        };
    }

    buildConfig({ containerWidth, containerHeight, onMouseOverPoint, onAfterChartRendered }) {
        const { baseTimeSeries, chartRangeStart, chartRangeEnd, scopingObjectSizeSeries, onChartSelect } = this.props;
        const chartConfig = applyMixins(
            dragZoomMixin(onChartSelect),
            stackedAreaTimeSeriesMixin({
                baseTimeSeries,
                chartRangeStart,
                chartRangeEnd,
                onMouseOverPoint,
                onRedraw: onAfterChartRendered,
                width: containerWidth,
                height: containerHeight,
                yAxisTitle: 'Usage (TiB)',
                yAxisLabelFormatter({ value }) {
                    return numeral(value / (2 ** 40)).format('0.0');
                },
                seriesMapper({ name, timeseries, id, isAggregatedRemainder }) {
                    return {
                        id,
                        isAggregatedRemainder: !!isAggregatedRemainder,
                        name: isAggregatedRemainder ? 'All other volumes' : name,
                        data: timeseries.map(val => ({
                            x: val.timestamp,
                            y: val.dataUsageBytes,
                        })),
                    };
                },
            }));

        if (scopingObjectSizeSeries && hasValidScopingObject(scopingObjectSizeSeries)) {
            chartConfig.series.push(this.buildScopingObjectSizeSeries());
        }

        return chartConfig;
    }

    render() {
        const { baseTimeSeries, scopingObjectSizeSeries } = this.props;
        const defaultLegendValues = baseTimeSeries.reduce((memo, vol) => {
            memo[vol.id] = { value: vol.dataUsageBytes };
            return memo;
        }, {});

        if (scopingObjectSizeSeries && hasValidScopingObject(scopingObjectSizeSeries)) {
            defaultLegendValues[scopingObjectSizeSeries.id] = {
                value: scopingObjectSizeSeries.timeseries[scopingObjectSizeSeries.timeseries.length - 1].sizeBytes,
            };
        }

        return (
            <PointTrackingChart
                className="chart-container--medium"
                pointTracking={this.props.pointTracking}
                buildConfig={this.buildConfig}
                valueFormatter={legendValueFormatter}
                getObjectLinkProps={this.getLinkProps}
                defaultLegendItems={defaultLegendValues}
                timestampFormat={MINUTE_MOMENT_FORMAT}
            />
        );
    }
}

TopVolumesByCapacity.propTypes = {
    baseTimeSeries: PropTypes.arrayOf(PropTypes.shape({
        id: PropTypes.string.isRequired,
        name: PropTypes.string.isRequired,
        timeseries: PropTypes.arrayOf(PropTypes.shape({
            timestamp: PropTypes.number.isRequired,
            dataUsageBytes: PropTypes.number.isRequired,
        })).isRequired,
    })),
    scopingObjectSizeSeries: PropTypes.shape({
        id: PropTypes.string,
        name: PropTypes.string,
        type: PropTypes.string,
        // Only when the Scoped Object is folder
        hasFolderLimit: PropTypes.bool,
        timeseries: PropTypes.arrayOf(PropTypes.shape({
            timestamp: PropTypes.number,
            sizeBytes: PropTypes.number,
        })),
    }),
    chartRangeStart: PropTypes.number,
    chartRangeEnd: PropTypes.number,
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),
    config: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,

    onChartSelect: PropTypes.func,

    // Injected by providePointTracking
    pointTracking: PropTypes.object.isRequired,
};

TopVolumesByCapacity.defaultProps = {
    onChartSelect: event => event.preventDefault(),
};

export default providePointTracking(TopVolumesByCapacity);
