import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import Request from '../../services/Request';
import { FETCH_BULLETINS, RESET } from './constants';

const fetchBulletins = ({ all }) => async (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(FETCH_BULLETINS, dispatch);
    dispatchStart();

    try {
        const response = await new Request()
            .slash('user', 'banners')
            .query({ all })
            .get();

        dispatchSuccess(response.data);
        return response.data;
    } catch (ex) {
        dispatchError(ex);
        return null;
    }
};

export const fetchActiveBulletins = () => fetchBulletins({ all: false });
export const fetchAllBulletins = () => fetchBulletins({ all: true });
export const reset = () => ({ type: RESET });
