import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import autobind from 'react-autobind';
import moment from 'moment-timezone';
import { Tabs, Tab, TabList, TabPanel, TabClasses } from '@infosight/elmer/dist/components/Tabs';
import Async from '@infosight/elmer/dist/components/Async';
import NoData from '@infosight/elmer/dist/components/NoData';
import style from './style.scss';
import { FolderUsageBarChart } from '../usageSummaryCharts';
import { ApplicationsTable, VolumesTable } from '../usageDetailTable';
import { FolderCapacityTimeSeries } from '../timeSeries';
import TotalUsage from './summary/TotalUsage';
import Volumes from './summary/Volumes';
import RecentIssues from './summary/RecentIssues';
import FolderTrends from './summary/FolderTrends';

const cx = classnames.bind(style);

class FolderCapacity extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    renderTabs() {
        const { applications, loadingApplications, config, folderId, poolUid } = this.props;
        return (
            <Tabs forceRenderTabPanel>
                <TabList className={TabClasses.TableHeader}>
                    <Tab>Applications</Tab>
                    <Tab>Volumes</Tab>
                </TabList>
                <TabPanel>
                    <ApplicationsTable values={applications} loadingCollection={loadingApplications} />
                </TabPanel>
                <TabPanel>
                    <VolumesTable poolUid={poolUid} folderId={folderId} config={config} />
                </TabPanel>
            </Tabs>
        );
    }

    renderSummary() {
        const { summary } = this.props;
        if (!summary) {
            return <NoData hasData={false} message="No data available." />;
        }

        const {
            volumeCount,
            volumeUsed,
            snapshotUsed,
            totalUsed,
            totalSizeMB,
            fullCapacityReachDays,
            folderLimit,
            folderUnusedReserve,
        } = summary;

        return (
            <div className={cx('summary')}>
                <div className={cx('total-usage')}>
                    <TotalUsage totalUsage={totalUsed}>
                        <div className={cx('bar-chart')}>
                            <FolderUsageBarChart
                                snapshotUsage={snapshotUsed}
                                volumeUsage={volumeUsed}
                                unusedReserve={folderUnusedReserve}
                                limit={folderLimit}
                            />
                        </div>
                    </TotalUsage>
                </div>
                <div>
                    <div className={cx('subtitle')}>Trends</div>
                    <FolderTrends fullCapacityReachDays={fullCapacityReachDays} limit={folderLimit} />
                </div>
                {/* TODO API in progress; totalAllocated is probably fake */}
                <Volumes totalAllocated={totalSizeMB * (2 ** 20)} volumeCount={volumeCount} />
                <RecentIssues />
            </div>
        );
    }

    renderTimeSeries() {
        const { timeSeries, lookbackMonths } = this.props;
        const chartRangeEnd = moment().startOf('day');
        const chartRangeStart = moment(chartRangeEnd).subtract(lookbackMonths, 'months').add(1, 'day');

        return (
            <div className={cx('time-series-chart')}>
                <FolderCapacityTimeSeries
                    timeSeries={timeSeries}
                    chartRangeStart={chartRangeStart}
                    chartRangeEnd={chartRangeEnd}
                />
            </div>
        );
    }

    render() {
        const { loadingSummary, loadingTimeSeries, summary, timeSeries } = this.props;

        // Has the summary and time series
        const hasData = !loadingSummary && summary && !loadingTimeSeries && timeSeries;

        return (
            <Async loading={loadingSummary || loadingTimeSeries}>
                <NoData hasData={!!hasData}>
                    <article className={cx('layout')}>
                        <section>{this.renderSummary()}</section>
                        <section className={cx('time-series', 'inset')}>
                            <h1>Usage</h1>
                            {this.renderTimeSeries()}
                        </section>
                        <section className={cx('facet-tables', 'inset')}>{this.renderTabs()}</section>
                    </article>
                </NoData>
            </Async>
        );
    }
}

FolderCapacity.propTypes = {
    folderId: PropTypes.string,
    poolUid: PropTypes.string,
    loadingTimeSeries: PropTypes.bool.isRequired,
    loadingSummary: PropTypes.bool.isRequired,
    loadingApplications: PropTypes.bool.isRequired,
    summary: PropTypes.shape({
        volumeCount: PropTypes.number,
        volumeUsed: PropTypes.number,
        snapshotUsed: PropTypes.number,
        totalUsed: PropTypes.number,
        totalSizeMB: PropTypes.number,
        fullCapacityReachDays: PropTypes.number,
        folderLimit: PropTypes.number,
        folderUnusedReserve: PropTypes.number,
    }),
    applications: PropTypes.array,
    timeSeries: PropTypes.array,
    lookbackMonths: PropTypes.number,
    config: PropTypes.object,
};

export default FolderCapacity;
