import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { deserialize } from '@infosight/elmer/dist/components/TimeRangeSelector';
import { QueryTabs, Tab, TabList, TabPanel } from '@infosight/elmer/dist/components/Tabs';
import { performanceTimeRangeParam, capacityTimeRangeParam } from '../queryParams';
import PoolHeadlineContainer from './headline/PoolHeadlineContainer';
import PoolCapacityContainer from './capacity/PoolCapacityContainer';
import PoolPerformanceContainer from './performance/PoolPerformanceContainer';
import PoolOverviewContainer from './overview';
import { reset } from '../actionCreators';

class PoolDetailContainer extends Component {
    componentWillUnmount() {
        this.props.reset();
    }

    render() {
        const { scope, scope: { id }, match, match: { params }, config } = this.props;
        const performanceTimeRange = deserialize(params, performanceTimeRangeParam(''));
        const capacityTimeRange = deserialize(params, capacityTimeRangeParam(''));

        return (
            <div>
                <PoolHeadlineContainer poolUid={id} match={match} />
                <QueryTabs values={['overview', 'performance', 'capacity']}>
                    <TabList>
                        <Tab>Overview</Tab>
                        <Tab>Performance</Tab>
                        <Tab>Capacity</Tab>
                    </TabList>
                    <TabPanel>
                        <PoolOverviewContainer timeRange={performanceTimeRange} config={config} scope={scope} />
                    </TabPanel>
                    <TabPanel>
                        <PoolPerformanceContainer timeRange={performanceTimeRange} config={config} poolUid={id} />
                    </TabPanel>
                    <TabPanel>
                        <PoolCapacityContainer {...this.props} timeRange={capacityTimeRange} config={config} scope={scope} />
                    </TabPanel>
                </QueryTabs>
            </div>
        );
    }
}

PoolDetailContainer.propTypes = {
    // Injected by withQueryParams
    match: PropTypes.shape({
        url: PropTypes.string,
        params: PropTypes.shape({
            // Time Range
            [performanceTimeRangeParam('duration')]: PropTypes.string,
            [performanceTimeRangeParam('from')]: PropTypes.string,
            [performanceTimeRangeParam('to')]: PropTypes.string,
            [capacityTimeRangeParam('duration')]: PropTypes.string,
            [capacityTimeRangeParam('from')]: PropTypes.string,
            [capacityTimeRangeParam('to')]: PropTypes.string,
        }).isRequired,
    }).isRequired,

    // Injected by XX_INFRASTRUCTURE_XX
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),
    config: PropTypes.object,
    reset: PropTypes.func,
};

const mapDispatchToProps = dispatch => bindActionCreators({
    reset,
}, dispatch);

export default connect(null, mapDispatchToProps)(PoolDetailContainer);
