import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { Highstock as Highcharts } from 'progeny/modules/nimble/highchartsProxy';
import Legend from '@infosight/elmer/dist/components/Legend';
import { identity } from 'underscore';
import { moveCrosshair, findPoints } from './util/synchronization';
import ResponsiveChartWrapper from './ResponsiveChartWrapper';

const defaultGetCorrespondingPoints = ({ index, x }, chart) => {
    const hoverPoints = [];
    const pointValues = findPoints(chart, index, x, (point, series) => {
        if (point) {
            hoverPoints.push(point);
            return { color: point.color, value: point.y };
        }

        return { color: series.color, value: null };
    });

    return { pointValues, hoverPoints };
};

// TODO this could become a core part of a ShowerTileViewPage or MultiObjectChartPage component
// TODO test this
class MultiObjectChart extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.state = this.getStateFromProps(this.props);
    }

    componentDidMount() {
        if (this.props.chartSync) {
            this.props.chartSync.monitorChart(this, { syncPoints: this.syncPoints, resetPoints: this.resetPoints });
        }
    }

    componentWillReceiveProps(newProps) {
        if (newProps.defaultPointValues !== this.props.defaultPointValues || newProps.config !== this.props.config) {
            this.setState(this.getStateFromProps(newProps));
        }
    }

    componentWillUnmount() {
        if (this.props.chartSync) {
            this.props.chartSync.removeChart(this);
        }
    }

    onMouseOverPoint(opts) {
        if (!opts.point) {
            return;
        }
        if (this.props.chartSync) {
            this.props.chartSync.syncPoints(opts);
        } else {
            this.syncPoints(opts.point, opts.event);
        }
    }

    onMouseOut(event) {
        if (this.props.chartSync) {
            this.props.chartSync.syncPoints({ event });
        } else {
            this.resetPoints();
        }
    }

    onAfterChartRendered(chart) {
        this.chart = chart;
        this.forceUpdate();
    }

    getStateFromProps(props) {
        return {
            pointTimestamp: null,
            isPointHovered: false,
            pointValues: props.defaultPointValues,
            config: this.config(props.config),
        };
    }

    resetPoints() {
        // Remove markers from any active points
        if (this.chart.hoverPoints) {
            this.chart.hoverPoints.forEach(point => point.setState());
        }

        this.chart.xAxis[0].hideCrosshair();

        this.setState({ isPointHovered: false, pointValues: this.props.defaultPointValues, pointTimestamp: null });
    }

    syncPoints(point, event) {
        const { pointValues, hoverPoints } = this.props.getCorrespondingPoints(point, this.chart, event);
        if (this.props.useHighchartsCrosshair) {
            moveCrosshair(hoverPoints, this.chart, event);
        }

        this.setState({ pointValues, pointTimestamp: point.x, isPointHovered: true });
    }

    buildLegendItems() {
        const { buildLegendItems, legendValueFormatter } = this.props;
        if (buildLegendItems) {
            return buildLegendItems({ chart: this.chart, pointValues: this.state.pointValues });
        }

        // Default buildLegendItems implementation if one is not passed
        const { series } = this.chart;
        const { pointValues } = this.state;
        return series.map(highchartSeries => ({
            id: highchartSeries.options.id,
            label: highchartSeries.options.name,
            icon: true,
            color: !highchartSeries.color ? 'transparent' : highchartSeries.color,
            value: legendValueFormatter(pointValues && pointValues[highchartSeries.options.id] ? pointValues[highchartSeries.options.id].value : null),
        }));
    }

    config(config) {
        const self = this;
        if (config.series) {
            config.series = config.series.map(series => Highcharts.merge(series, {
                marker: {
                    enabled: false,
                },
                point: {
                    events: {
                        mouseOver(event) {
                            if (self.lastPointHovered !== this) {
                                self.lastPointHovered = this;
                                self.onMouseOverPoint({ event, point: this });
                            }
                        },
                    },
                },
            }));
        }

        return Highcharts.merge(config, {
            chart: {
                plotBorderWidth: 0,
                spacingTop: 0,
                spacingBottom: 0,
                spacingLeft: 10,
                spacingRight: 0,
                marginTop: 24,
                ignoreHiddenSeries: true,
            },

            title: false,

            /**
             * Required for synced charts
             */
            tooltip: {
                enabled: true,
                shared: true,

                /**
                 * Position the tooltip outside of the chart area so it never appears
                 */
                positioner(w, h) {
                    return { x: -this.chart.plotLeft - w, y: -this.chart.plotTop - h };
                },
            },
        });
    }

    render() {
        const { chart, state: { isPointHovered, pointTimestamp, config }, props: { className } } = this;

        return (
            <div className={classnames('row expanded', className)}>
                <div className="column large-19">
                    <ResponsiveChartWrapper callback={this.onAfterChartRendered} onMouseOut={this.onMouseOut} config={config} />
                </div>
                {chart && chart.series && (
                    <Legend
                        className="column large-5"
                        items={this.buildLegendItems()}
                        timestamp={(isPointHovered && pointTimestamp) || null}
                        reserveTimestampSpace
                        valueTitle="Avg. Usage"
                    />
                )}
            </div>
        );
    }
}

MultiObjectChart.propTypes = {
    /**
     * Generates a Highchart config. Each series should have an id property.
     */
    config: PropTypes.object.isRequired,

    /**
     * Get the values to display in the legend when the user hovers over a point.
     * @param opt.chart {Highcharts.Chart}
     * @param opt.pointValues {Array<{value, color}>}
     * @return {Array<LegendItem>} Value used for for elmer's Legend.props.items
     */
    buildLegendItems: PropTypes.func,

    legendValueFormatter: PropTypes.func,

    /**
     * Get the values to display when no point is active (the user isn't hovering over a point)
     * @return {Object} Each key should be the series id. The value is {color, value}
     */
    defaultPointValues: PropTypes.func.isRequired,

    /**
     * Get all the values at a timestamp that should be displayed in the legend when the user hovers over a point.
     * This function is allowed to have synchronous side effects, such as updating crosshairs.
     * @param point {Highcharts.Point}
     * @param chart {Highcharts.Chart}
     * @param event {Event}
     * @return {pointValues, hoverPoints}
     *      {Object} pointValues Each key should be the series id. The value is {color, value}
     *      {Array<Highcharts.Point>} hoverPoints
     */
    getCorrespondingPoints: PropTypes.func,

    /**
     * Update the crosshairs and markers on hover and mouse out on a point
     */
    useHighchartsCrosshair: PropTypes.bool,

    /**
     * Wrap this element in a `<SynchronizedChart>` element to inject this prop.
     */
    chartSync: PropTypes.shape({
        monitorChart: PropTypes.func.isRequired,
        removeChart: PropTypes.func.isRequired,
        syncPoints: PropTypes.func.isRequired,
    }),
};

MultiObjectChart.defaultProps = {
    legendValueFormatter: identity,
    getCorrespondingPoints: defaultGetCorrespondingPoints,
};

export default MultiObjectChart;
