/* eslint-disable no-unused-vars, spaced-comment */
import React from 'react';
import { IconAdaptor } from '@infosight/elmer/dist/components/IconAdaptor';
import PoolIcon from 'react-icons/lib/fa/th';
import GroupIcon from 'react-icons/lib/fa/sitemap';
import ArrayIcon from 'react-icons/lib/fa/server';
import FolderIcon from 'react-icons/lib/fa/folder-o';
import VolumeIcon from 'react-icons/lib/fa/database';
import AppCategoryIcon from 'react-icons/lib/fa/tag';
import { VIEW_TYPES, ProductInterface } from '@infosight/elmer/dist/infrastructure';
import { OBJECTS, findObject, getObjectTopology, isInventoryLoaded } from './inventory';

import VolumeDetailContainer from './volumes/detail';
import VolumesList from './volumes/collection/components/list';
import VolumesCapacityChart from './volumes/collection/components/capacity/CapacityContainer';
import VolumesPerformanceChart from './volumes/collection/components/performance/VolumePerformanceContainer';

import ArrayDetailContainer from './arrays/detail/components/ArrayDetailContainer';

import PoolDetailContainer from './pools/detail/components/PoolDetailContainer';
import PoolsList from './pools/collection/components/table/PoolsTableView';

import GroupsList from './groups/collection/components/list/ListContainer';

import ArrayList from './arrays/collection/components/list';

import { buildUrlForCurrentTenant } from './tenantSelection';
import DetailPageTitle from './components/DetailPageTitle';

const { DETAIL, CHART, TABLE } = VIEW_TYPES;
const { FOLDER, POOL, GROUP, APP_CATEGORY, ARRAY, VOLUME } = OBJECTS;
const NOT_SCOPED = null;

// Exported for testing
export const config = {
    id: 'NIMBLE',

    attributes: {
        tier: 'Storage',
        name: 'Nimble Storage',
        scopingObjects: [FOLDER, POOL, GROUP],
        leafObjects: [VOLUME],
        rootPath: '/infrastructure/storage/nimble',
        getRootPath(opts) {
            const excludeTenant = opts && opts.NIMBLE && opts.NIMBLE.excludeTenant;
            return excludeTenant ? this.rootPath : buildUrlForCurrentTenant(this.rootPath);
        },
    },

    topology: {
        getObjectTopology,
        findObject,
        search: {
            type: 'client-side',
            isReady: isInventoryLoaded,
        },
    },

    titles: {
        [GROUP]: ['Groups', 'Group'],
        [ARRAY]: ['Arrays', 'Array'],
        [POOL]: ['Pools', 'Pool'],
        [FOLDER]: ['Folders', 'Folder'],
        [VOLUME]: ['Volumes', 'Volume'],
        [APP_CATEGORY]: ['Applications', 'Application'],
    },

    genericQueryTypes: {
        [GROUP]: 'group',
        [ARRAY]: 'array',
        [POOL]: 'pool',
        [FOLDER]: 'folder',
        [VOLUME]: 'volume',
        [APP_CATEGORY]: 'application',
    },

    icons: {
        [GROUP]: props => <IconAdaptor {...props} type="svg"><GroupIcon /></IconAdaptor>,
        [ARRAY]: props => <IconAdaptor {...props} type="svg"><ArrayIcon /></IconAdaptor>,
        [POOL]: props => <IconAdaptor {...props} type="svg"><PoolIcon /></IconAdaptor>,
        [FOLDER]: props => <IconAdaptor {...props} type="svg"><FolderIcon /></IconAdaptor>,
        [VOLUME]: props => <IconAdaptor {...props} type="svg"><VolumeIcon /></IconAdaptor>,
        [APP_CATEGORY]: props => <IconAdaptor {...props} type="svg"><AppCategoryIcon /></IconAdaptor>,
    },

    routes: {
        [GROUP]: 'groups',
        [POOL]: 'pools',
        [FOLDER]: 'folders',
        [VOLUME]: 'volumes',
        [ARRAY]: 'arrays',
    },

    nav: {
        objects: [POOL, ARRAY, VOLUME],
    },

    objectViews: {
        [VOLUME]: {
            scopes: [POOL],
            views: [
                {
                    type: TABLE,
                    urlSegment: 'list',
                    name: 'Volume List',
                    component: VolumesList,
                    scopes: [POOL, GROUP, ARRAY, NOT_SCOPED],
                },
                {
                    type: CHART,
                    urlSegment: 'capacity',
                    name: 'Volume Capacity Trend',
                    component: VolumesCapacityChart,
                },
                {
                    type: CHART,
                    urlSegment: 'performance',
                    name: 'Volume Performance Contention',
                    component: VolumesPerformanceChart,
                },
                {
                    type: DETAIL,
                    urlSegment: 'detail',
                    component: VolumeDetailContainer,
                    scopes: [NOT_SCOPED],
                },
            ],
        },
        [POOL]: {
            scopes: [GROUP, NOT_SCOPED],
            views: [
                {
                    type: TABLE,
                    urlSegment: 'list',
                    name: 'Pool List',
                    component: PoolsList,
                },
                {
                    type: DETAIL,
                    urlSegment: 'detail',
                    component: PoolDetailContainer,
                    scopes: [NOT_SCOPED],
                },
            ],
        },
        [GROUP]: {
            scopes: [NOT_SCOPED],
            views: [
                {
                    type: TABLE,
                    urlSegment: 'list',
                    name: 'Group List',
                    component: GroupsList,
                },
            ],
        },
        [ARRAY]: {
            scopes: [GROUP, POOL],
            views: [
                {
                    type: TABLE,
                    urlSegment: 'list',
                    name: 'Array List',
                    component: ArrayList,
                    scopes: [GROUP, POOL, NOT_SCOPED],
                },
                {
                    type: DETAIL,
                    urlSegment: 'detail',
                    component: ArrayDetailContainer,
                },
            ],
        },
    },

    componentOverrides: {
        DetailPageTitle,
    },
};

export const productInterface = new ProductInterface(config);
