import $ from 'jquery';
import { ARRAYS_LIST_FETCH } from '../progeny/modules/nimble/arrays/collection/constants';
import { getArrays } from '../progeny/modules/nimble/arrays/collection/actionCreators';
import { getStore } from '../progeny/util/storeProvider';
import { emitEventForAction } from '../progeny/store/middleware/legacyEventEmitterActions';
import { getSuccessActionType } from '@infosight/elmer/dist/utils/redux';
import Request from '../progeny/services/Request';
import { userSelector } from '../progeny/modules/user';
import AssetCollection from './modules/assets_report/collections/AssetCollection';
import NimbleOsReleaseCollection from './modules/software/NimbleOsReleaseCollection';
import SessionModel from './models/SessionModel';
import NimbleUserTenantHelpers from '../progeny/modules/nimble/tenantSelection/NimbleUserTenantHelpers';

export default function handlers(App) {
    const sessionModel = new SessionModel(userSelector(getStore().getState())); // global session data
    const assetCollection = new AssetCollection();
    const nimbleOsReleases = new NimbleOsReleaseCollection();

    const asjQueryDeferrered = promise => $.Deferred(defer => promise.then(defer.resolve).catch(defer.reject));
    const asAsync = (promise, type) => {
        if (type && type.toLowerCase() === 'jquery.deferred') {
            return asjQueryDeferrered(promise);
        }

        return promise;
    };

    let assetCollectionPromise;
    App.handleRequest('global', 'get:assets:async', function (type) {
        if (assetCollectionPromise) {
            return asAsync(assetCollectionPromise, type);
        }

        if (!NimbleUserTenantHelpers.currentTenantIsNimbleTenant()) {
            return Promise.reject();
        }

        assetCollectionPromise = new Promise(resolve => {
            getArrays()(getStore().dispatch)
                .then((assets) => {
                    assetCollection.reset(assets, { parse: true });
                    return assetCollection;
                })
                .then(resolve);
        });

        return asAsync(assetCollectionPromise, type);
    });

    let releasesPromise;
    App.handleRequest('global', 'get:nimbleosreleases:async', function (type) {
        if (releasesPromise) {
            return asAsync(releasesPromise, type);
        }

        if (!NimbleUserTenantHelpers.currentTenantIsNimbleTenant()) {
            return Promise.reject();
        }

        releasesPromise = new Promise(resolve => {
            new Request().urlSegments('software', 'releases')
                .get()
                .then((releases) => {
                    nimbleOsReleases.reset(releases.data, { parse: true });
                    return nimbleOsReleases;
                })
                .then(resolve);
        });

        return asAsync(releasesPromise, type);
    });

    // get:current:session
    //   Fetches the current session model object.
    //
    // @returns SessionModel
    App.handleRequest('global', 'get:current:session', function () {
        return sessionModel;
    });

    App.handleEvent('global', 'change:account', function () {
        assetCollectionPromise = null;
    });

    emitEventForAction(getSuccessActionType(ARRAYS_LIST_FETCH));
    App.handleEvent('progeny', ARRAYS_LIST_FETCH, ({ payload }) => {
        assetCollection.clear({ silent: true });
        assetCollection.set(payload);
    });
}
