const Backbone = require('backbone');
const Api = require('app/modules/api/Api');

module.exports = Backbone.Model.extend({
    defaults: {
        id: undefined,
        name: undefined,
    },

    urlRoot: new Api().segment('accounts'),

    isResellerEquivalent: function () {
        return ['Distributor', 'Sub Distributor', 'Reseller'].includes(this.get('type'));
    },

});
