import React from 'react';
import PropTypes from 'prop-types';
import { SimpleGridView } from '@infosight/elmer/dist/components/GridView';
import Pagination from '@infosight/elmer/dist/components/Pagination';

const Table = ({ className, collection, getColumns, fieldSet, rowKey, rowsPerPage, defaultSort, sorting, sortParameter }) => (
    <SimpleGridView
        data={collection}
        rowKey={rowKey}
        className={className}
        defaultSort={defaultSort}
        sorting={sorting}
        sortParameter={sortParameter}
    >
        {getColumns(fieldSet)}
        <Pagination itemCount={collection.length} numberPerPage={rowsPerPage} />
    </SimpleGridView>
);

Table.propTypes = {
    collection: PropTypes.array,
    getColumns: PropTypes.func.isRequired,
    fieldSet: PropTypes.string,
    rowKey: PropTypes.string,
    rowsPerPage: PropTypes.number.isRequired,
    sorting: PropTypes.arrayOf(PropTypes.shape({
        colname: PropTypes.string,
        order: PropTypes.number,
    })),
    defaultSort: PropTypes.arrayOf(PropTypes.shape({
        colname: PropTypes.string,
        order: PropTypes.number,
    })),
    sortParameter: PropTypes.string,
};

Table.defaultProps = {
    rowsPerPage: 25,
    loading: false,
    sortParameter: 'sort',
};

export default Table;
