import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Async from '@infosight/elmer/dist/components/Async';
import { getPool } from '../../actionCreators';
import { poolSelector } from '../../reducer';
import PoolHeadline from './PoolHeadline';

class PoolHeadlineContainer extends PureComponent {
    componentWillMount() {
        if (!this.props.loadingPool && !this.props.loadedPool) {
            this.props.getPool(this.props.poolUid);
        }
    }

    render() {
        const { loadingPool, pool } = this.props;

        return (
            <Async loading={loadingPool}>
                {() => <PoolHeadline {...(pool || {})} />}
            </Async>
        );
    }
}

PoolHeadlineContainer.propTypes = {
    poolUid: PropTypes.string,
    pool: PropTypes.shape({
        allocatedBytes: PropTypes.number,
        usedBytes: PropTypes.number,
        unusedReserveBytes: PropTypes.number,
        freeBytes: PropTypes.number,
        lastWeekGrowthPercent: PropTypes.number,
        snapshotCount: PropTypes.number,
        savingsCompressionBytes: PropTypes.number,
        savingsCloneBytes: PropTypes.number,
        savingsDedupBytes: PropTypes.number,
        totalSavingsBytes: PropTypes.number,
        savingsFactor: PropTypes.number,
        avgIopsPerWeek: PropTypes.number,
        avgLatencyMs: PropTypes.number,
        avgReadLatencyMs: PropTypes.number,
        avgWriteLatencyMs: PropTypes.number,
        avgThroughput: PropTypes.number,
    }),
    loadingPool: PropTypes.bool,
    loadedPool: PropTypes.bool,
    getPool: PropTypes.func,
};

PoolHeadlineContainer.defaultProps = {
    loadingPool: false,
};

const mapStateToProps = state => ({
    ...poolSelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    getPool,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(PoolHeadlineContainer);
