import React from 'react';
import PropTypes from 'prop-types';
import { QueryToggle } from '@infosight/elmer/dist/components/Toggle';

const Tabs = ({ className, tabName, getCategories }) => (
    <QueryToggle
        parameter="fieldSet"
        value={(tabName || getCategories()[0].value)}
        className={className}
    >
        {getCategories().map(({ value: fieldSet, title }) => (
            <button key={fieldSet} value={fieldSet}>{title}</button>
        ))}
    </QueryToggle>
);

Tabs.propTypes = {
    getCategories: PropTypes.func.isRequired,
    tabName: PropTypes.string,
    className: PropTypes.string,
};

export default Tabs;
