import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import OverlayTrigger from '@infosight/elmer/dist/components/OverlayTrigger';
import style from './style.scss';
import Popover from './Popover';

const cx = classnames.bind(style);

function SavingsBreakdownPopover({ className, children, ...props }) {
    return (
        <OverlayTrigger trigger="hover" overlay={<Popover {...props} />} placement="bottom">
            <div className={cx(className, 'popover-link')}>
                {children}
            </div>
        </OverlayTrigger>
    );
}

SavingsBreakdownPopover.propTypes = {
    cloneSavings: PropTypes.number,
    dedupSavings: PropTypes.number,
    compressionSavings: PropTypes.number,
    children: PropTypes.node,
};

export default SavingsBreakdownPopover;
