import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import Backbone from 'backbone';
import SoftwareUpgradesView from '../../../../../../app/views/components/asset/SoftwareUpgradesView';
import { awaitLegacyApp } from '../../../../../containers/NimbleLegacyContainer';

class SoftwareRecommendationsMarionetteWrapper extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentWillUnmount() {
        if (this.view) {
            this.view.destroy();
        }
    }

    renderView(el) {
        if (!this.view) {
            this.view = new SoftwareUpgradesView({ model: new Backbone.Model(this.props), displayAsModal: false });
            this.view.setElement(el).render();
        }
    }

    render() {
        if (!this.props.hostname) {
            return null;
        }
        return (
            <div className={classnames('software-upgrades-dialog flex-container')} ref={this.renderView} />
        );
    }
}

SoftwareRecommendationsMarionetteWrapper.propTypes = {
    version: PropTypes.string.isRequired, // eslint-disable-line react/no-unused-prop-types
    serialNumber: PropTypes.string.isRequired, // eslint-disable-line react/no-unused-prop-types
    hostname: PropTypes.string.isRequired, // eslint-disable-line react/no-unused-prop-types
};

export default awaitLegacyApp(SoftwareRecommendationsMarionetteWrapper);
