import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { ClientControlledTable } from '@infosight/elmer/dist/components/Table';
import { userSelector } from '../../../../../user/reducer';
import { getVolumes, reset, getVolumesCsv } from '../../actionCreators';
import { listSelector, csvSelector } from '../../reducer';
import getConfig from './getConfig';

export class ListContainer extends Component {
    componentDidMount() {
        const { scope, config, filters } = this.props;
        this.props.getVolumes(config.convertToGenericQueryScope(scope), filters);
    }

    componentWillUnmount() {
        this.props.resetVolumeListStore();
    }

    render() {
        const { loadingVolumes, volumes, config, loadingVolumesCsv, scope, userName, enabledFieldSets, disableDefaultSort } = this.props;
        const { columns, categories } = getConfig(config);

        return (
            <ClientControlledTable
                tableId={`${userName}_NIMBLE_VOLUMES`}
                data={volumes}
                columns={columns}
                categories={enabledFieldSets || categories}
                loading={loadingVolumes}
                defaultSorted={!disableDefaultSort ? [{ id: 'name', desc: false }] : undefined}
                downloadCsv={() => this.props.getVolumesCsv(config.convertToGenericQueryScope(scope))}
                downloadingCsv={loadingVolumesCsv}
                {...this.props}
            />
        );
    }
}

ListContainer.propTypes = {
    volumes: PropTypes.arrayOf(PropTypes.shape({
        sn: PropTypes.string,
        name: PropTypes.string,
        groupName: PropTypes.string,
        folderName: PropTypes.string,
        poolName: PropTypes.string,
        arraySn: PropTypes.string,
        hostName: PropTypes.string,
        arraySearchableName: PropTypes.string,
        appCategory: PropTypes.string,
        protectionLevel: PropTypes.string,
        dataUsageBytes: PropTypes.number,
        allocatedBytes: PropTypes.number,
        percentFull: PropTypes.number,
        snapshotCount: PropTypes.number,
        snapshotBytes: PropTypes.number,
        isCompressed: PropTypes.bool,
        volumeCompression: PropTypes.number,
        totalUsageBytes: PropTypes.number,
        daysUntilFull: PropTypes.number,
        last30daysGrowth: PropTypes.number,
        last60daysGrowth: PropTypes.number,
        last90daysGrowth: PropTypes.number,
        performancePolicy: PropTypes.string,
        cacheHit: PropTypes.number,
        perWeekIopsRead: PropTypes.number,
        perWeekIopsWrite: PropTypes.number,
        ioPerWeek: PropTypes.number,
        avgReadsPerWeek: PropTypes.number,
        avgWritesPerWeek: PropTypes.number,
        avgIopsPerWeek: PropTypes.number,
        avgReadLatencyMs: PropTypes.number,
        avgWriteLatencyMs: PropTypes.number,
        avgLatencyMs: PropTypes.number,
        avgReadThroughput: PropTypes.number,
        avgWriteThroughput: PropTypes.number,
        avgThroughput: PropTypes.number,
        volumeCollection: PropTypes.string,
        volumeClone: PropTypes.bool,
        replicationPartner: PropTypes.string,
    })),
    filters: PropTypes.arrayOf(PropTypes.object),
    getVolumes: PropTypes.func.isRequired,
    resetVolumeListStore: PropTypes.func.isRequired,
    loadingVolumes: PropTypes.bool.isRequired,
    getVolumesCsv: PropTypes.func.isRequired,
    loadingVolumesCsv: PropTypes.bool.isRequired,

    // Injected by XX_INFRASTRUCTURE_XX
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),
    config: PropTypes.object.isRequired,
    userName: PropTypes.string,
    enabledFieldSets: PropTypes.array,
    // TODO: Until elmer Table is fixed to support namespaced url params
    disableDefaultSort: PropTypes.bool,
};

ListContainer.defaultProps = {
    loadingVolumes: false,
    loadingVolumesCsv: false,
};

const mapStateToProps = state => ({
    ...listSelector(state),
    ...csvSelector(state),
    userName: userSelector(state).username,
});

const mapDispatchToProps = dispatch => bindActionCreators({ getVolumes, resetVolumeListStore: reset, getVolumesCsv }, dispatch);

/*
 Reason for passing pure: false
 This component is being used in multiple places.
 In one of the place ie., Capacity Dashboard, the parent of this component does not pass match as a prop.
 So when the match (prop from child of this component ie., FacetedCollectionPage) changes
 this is not being rerendered as by default react-redux connect will make this component a Pure Component.
 So by passing {pure: false}, it will rerender even if none of the props were changed
*/
export default connect(mapStateToProps, mapDispatchToProps, null, { pure: false })(ListContainer);
