import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment-timezone';
import StatusIndicator from '@infosight/elmer/dist/components/StatusIndicator';
import NimbleIcon from '../../../../components/NimbleIcon';
import { isEmpty } from '../../../../../../util/formatter';
import { ICONS } from '../../../../components/NimbleIcon/NimbleIcon';

const HeartbeatDelayThresholdInMinutes = 25;

export const isHeartbeatDelayed = heartbeattimestamp => !heartbeattimestamp ||
    moment().diff(moment(heartbeattimestamp), 'minutes') > HeartbeatDelayThresholdInMinutes;

// TODO: Current production uses this logic, Checked with sudhakar..he is going to create a Jira to handle this on data side
// const hasAsupWarning = sendingAsups => sendingAsups !== 'RECEIVED';

// Any state values NOT 'active' or 'standby' indicate a warning
const hasControllerIssue = hb => hb.controllers && hb.controllers.some(controller => controller.state !== 'active' && controller.state !== 'standby');

const hasFanIssue = hb => hb.controllers
            && hb.controllers.some(controller => (controller.components).some(component => component.name === 'fan' && component.status !== 'OK'));

const hasPowerSupplyIssue = hb => hb.controllers
            && hb.controllers.some(controller => (controller.components).some(component => component.name === 'psu' && component.status !== 'OK'));

const hasSSDIssues = hb => hb.ssdFailedCount > 0;

const hasHDDIssues = hb => hb.hddFailedCount > 0;

export const AsupStatus = ({ asupReceivedTime }) => {
    const asupStatus = !isEmpty(asupReceivedTime);
    return <StatusIndicator size={17} status={asupStatus ? 'success' : 'error'} tooltip={`Status: ${asupStatus ? 'RECEIVED' : 'NOT RECEIVED'}`} />;
};

AsupStatus.propTypes = {
    sendingAsups: PropTypes.string, // eslint-disable-line react/no-unused-prop-types
    asupReceivedTime: PropTypes.number,
};

export const HeartbeatStatus = ({ timestamp }) => {
    if (!timestamp) {
        return null;
    }
    const size = 17;
    return isHeartbeatDelayed(timestamp) ?
        <StatusIndicator size={size} status="warning" tooltip="Status: Delayed" />
        : <StatusIndicator size={size} status="success" tooltip="Status: Good" />;
};

HeartbeatStatus.propTypes = {
    timestamp: PropTypes.number,
};

export const ArrayHardwareStatus = ({ latestHeartbeat, isAllFlash }) => {
    if (!latestHeartbeat) {
        return null;
    }
    const leftSpace = {
        paddingLeft: '6px',
    };
    const STATUS = {
        GOOD: 'Good',
        ERROR: 'Warning/Error',
    };

    const controllerStatus = !hasControllerIssue(latestHeartbeat);
    const fanStatus = !hasFanIssue(latestHeartbeat);
    const powerSupplyStatus = !hasPowerSupplyIssue(latestHeartbeat);
    const ssdStatus = !hasSSDIssues(latestHeartbeat);
    const hddStatus = !hasHDDIssues(latestHeartbeat);

    return (
        <div className="flex-container">
            <div className="row">
                <div
                    title={`Controller Status : ${controllerStatus ? STATUS.GOOD : STATUS.ERROR}`}
                >
                    <NimbleIcon name={ICONS.ARRAY_CONTROLLER} status={controllerStatus} />
                </div>
                <div
                    style={leftSpace}
                    title={`Fan Status : ${fanStatus ? STATUS.GOOD : STATUS.ERROR}`}
                >
                    <NimbleIcon name={ICONS.ARRAY_FAN} status={fanStatus} />
                </div>
                <div
                    style={leftSpace}
                    title={`Power Supply Status : ${powerSupplyStatus ? STATUS.GOOD : STATUS.ERROR}`}
                >
                    <NimbleIcon name={ICONS.ARRAY_POWER_SUPPLY} status={powerSupplyStatus} />
                </div>
                <div
                    style={leftSpace}
                    title={`SSD Status : ${ssdStatus ? STATUS.GOOD : STATUS.ERROR}`}
                >
                    <NimbleIcon name={ICONS.ARRAY_SSD} status={ssdStatus} />
                </div>
                {!isAllFlash &&
                <div
                    style={leftSpace}
                    title={`HDD Status : ${hddStatus ? STATUS.GOOD : STATUS.ERROR}`}
                >
                    <NimbleIcon name={ICONS.ARRAY_HDD} status={hddStatus} />
                </div>
                }
            </div>
        </div>
    );
};

/**
 * This aids in sorting the HardwareStatus
 * @param {*} latestHeartbeat
 */
ArrayHardwareStatus.comparator = (latestHeartbeat) => {
    if (!latestHeartbeat) {
        return null;
    }
    let value = '';
    value += hasControllerIssue(latestHeartbeat) ? 0 : 1;
    value += hasFanIssue(latestHeartbeat) ? 0 : 1;
    value += hasPowerSupplyIssue(latestHeartbeat) ? 0 : 1;
    value += hasSSDIssues(latestHeartbeat) ? 0 : 1;
    value += hasHDDIssues(latestHeartbeat) ? 0 : 1;
    return value;
};

ArrayHardwareStatus.propTypes = {
    isAllFlash: PropTypes.bool,
    latestHeartbeat: PropTypes.shape({
        serialNumber: PropTypes.string,
        timestamp: PropTypes.number,
        controllers: PropTypes.arrayOf(PropTypes.shape({
            name: PropTypes.string,
            state: PropTypes.string,
            components: PropTypes.arrayOf(PropTypes.shape({
                name: PropTypes.oneOf(['psu', 'fan', 'temperature']),
                status: PropTypes.string,
            })),
        })),
        ssdGoodCount: PropTypes.number,
        ssdFailedCount: PropTypes.number,
        hddGoodCount: PropTypes.number,
        hddFailedCount: PropTypes.number,
    }),
};
