import { getUserProfileApi, getUserProfile, getActiveTenant, UserContextChangeReason } from '@infosight/shell-api/lib/UserProfile';
import { getConfig } from '@infosight/shell-api/lib/Config';
import AccountController from 'app/modules/header/AccountController';
import LegacyApp from 'app/Application';
import { SET_AUTHORIZATION_EVALUATOR, SET_TENANT, USER_CONTEXT_CHANGED, EXPERIMENTAL_MODE_CHANGED, SET_PROFILE, SET_CONFIG } from './constants';
import Request from '../../services/Request';

export const loadAuthorizationEvaluator = () => async (dispatch) => {
    try {
        const { AuthorizationEvaluator } = await getUserProfileApi();
        dispatch({ type: SET_AUTHORIZATION_EVALUATOR, payload: AuthorizationEvaluator });
    } catch (e) {
        console.error(e);
    }
};

export const loadUser = () => async (dispatch) => {
    try {
        const user = await getUserProfile();
        LegacyApp.request('global', 'get:current:session').set(user);
        dispatch({ type: SET_PROFILE, payload: user });
    } catch (e) {
        console.error(e);
    }
};

export const loadTenant = () => async (dispatch) => {
    try {
        let tenant = await getActiveTenant();

        if (tenant && tenant.type === 'nimble') {
            const id = tenant.id.replace('urn:nimble:');
            const response = await new Request()
                .slash('pod', 'accounts', id)
                .query({ podLocator: `Nimble.Tenant.${id}`, boundary: `Nimble.Tenant.${id}` })
                .get();
            tenant = { tenant: response.data };
        } else if (tenant && tenant.type === 'infosight') {
            // TODO remove this case
            // This is for backward compatibility. It should simply be an `else` rather than an `else if`
            tenant = null;
        }

        dispatch({ type: SET_TENANT, payload: tenant });
        await AccountController.setTenant(tenant && tenant.tenant);
    } catch (e) {
        console.error(e);
    }
};

export const loadConfig = () => async (dispatch) => {
    try {
        const config = await getConfig();
        dispatch({ type: SET_CONFIG, payload: config });
    } catch (e) {
        console.error(e);
    }
};

export const changeUserContext = (reason) => {
    if (reason === UserContextChangeReason.ExperimentalMode) {
        return { type: EXPERIMENTAL_MODE_CHANGED };
    }

    return { type: USER_CONTEXT_CHANGED, payload: reason };
};
