// A lightweight DataTables renderer to style the pagination controls.
// See https://datatables.net/reference/option/renderer
// See https://github.com/DataTables/Dist-DataTables-Foundation/blob/master/js/dataTables.foundation.js

import ReactDOM from 'react-dom';
import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import 'datatables.net';
import $ from 'jquery';

// eslint-disable-next-line prefer-destructuring
const DataTable = $.fn.DataTable;

$.extend(DataTable.ext.classes, {
    sWrapper: 'dataTables_wrapper dt-foundation',
    sProcessing: 'dataTables_processing panel callout',
});

$.extend(true, DataTable.defaults, {
    renderer: 'infosight',
});

function preventDefault(func) {
    return (event) => {
        event.preventDefault();
        func();
    };
}

// This is largely a copy/paste from elmer/dist/components/Pagination
// Consider refactoring elmer/dist/components/Pagination into PaginationContainer and Pagination, reusing the latter here.
// We don't need all the state management in the current elmer/dist/components/Pagination component, just the presentation.

const Pagination = ({ start, end, total, onPrevious, onNext }) => (
    <div className="elmer-Pagination">
        <span className="elmer-Pagination__range">
            <strong>{start} - {end}</strong>
            <span className="elmer-Pagination__count"> of {total}</span>
        </span>
        <ul className="pagination" role="navigation" aria-label="Pagination">
            <li className="pagination-previous">
                <a
                    className={classnames({ disabled: start === 1 })}
                    href="#previous"
                    onClick={preventDefault(onPrevious)}
                    aria-label="Previous page"
                >
                    <i className="fa fa-angle-left" aria-hidden="true" />
                </a>
            </li>
            <li className="pagination-next">
                <a
                    className={classnames({ disabled: end === total })}
                    href="#next"
                    onClick={preventDefault(onNext)}
                    aria-label="Next page"
                >
                    <i className="fa fa-angle-right" aria-hidden="true" />
                </a>
            </li>
        </ul>
    </div>
);

Pagination.propTypes = {
    onPrevious: PropTypes.func.isRequired,
    onNext: PropTypes.func.isRequired,
    start: PropTypes.number.isRequired,
    end: PropTypes.number.isRequired,
    total: PropTypes.number.isRequired,
};


DataTable.ext.renderer.pageButton.infosight = (settings, host) => {
    const api = new DataTable.Api(settings);
    const pageInfo = api.page.info();
    pageInfo.start += 1;
    pageInfo.total = pageInfo.recordsDisplay;

    ReactDOM.render(
        <Pagination
            {...pageInfo}
            onPrevious={() => api.page('previous').draw('page')}
            onNext={() => api.page('next').draw('page')}
        />,
        host);
};
