import React from 'react';
import PropTypes from 'prop-types';
import numeral from 'numeral';
import Headline from '@infosight/elmer/dist/components/Headline';
import KeyStatistic from '@infosight/elmer/dist/components/KeyStatistic';
import DonutChart from '../../../../../charts/DataUsageDonut';
import Savings from './Savings';
import { getValueAndUnit } from '../../../../../../util/numeralHelpers';
import { DaysUntilFull } from '../../../../volumes/common';
import {
    NUMBER_ABBREVIATE_FORMAT,
    INFO_NOT_AVAILABLE,
    formatValue,
    isEmpty,
} from '../../../../../constants';

// TODO: Use common constants & utility functions
const numeralPerSec = (value, format) => {
    const valueAndUnit = getValueAndUnit(value, format);
    const number = valueAndUnit[0];
    const unit = valueAndUnit[1] || 'B';

    if (!number) {
        return INFO_NOT_AVAILABLE;
    }
    return (
        <span>{number}
            <small>{unit}/sec</small>
        </span>);
};

const PoolHeadline = ({
    allocatedBytes,
    usedBytes,
    freeBytes,
    unusedReserveBytes,
    lastWeekGrowthPercent,
    snapshotCount,
    savingsCloneBytes,
    savingsCompressionBytes,
    savingsDedupBytes,
    totalSavingsBytes,
    cloneSavingsFactor,
    compressionSavingsFactor,
    dedupSavingsFactor,
    totalSavingsFactor,
    avgIopsPerWeek,
    avgReadLatencyMs,
    avgWriteLatencyMs,
    avgThroughput,
    daysUntilFull,
}) =>
    (
        <Headline className="card-right-padding card-left-padding">
            <Headline.Section className="medium-11 medium-unstack">
                <h6>Capacity</h6>
                <div className="row align-middle">
                    <div className={daysUntilFull !== null ? 'columns medium-12 medium-unstack' : 'columns medium-13 medium-unstack'}>
                        <DonutChart
                            allocatedBytes={allocatedBytes}
                            usedBytes={usedBytes}
                            freeBytes={freeBytes}
                            unusedReserveBytes={unusedReserveBytes}
                            growthPercent={lastWeekGrowthPercent}
                            warningThreshold={90}
                        />
                    </div>
                    <div className={daysUntilFull !== null ? 'columns medium-7 medium-unstack' : 'columns medium-11 medium-unstack'}>
                        <Savings
                            totalSavings={totalSavingsBytes}
                            cloneSavingsFactor={cloneSavingsFactor}
                            compressionSavingsFactor={compressionSavingsFactor}
                            dedupSavingsFactor={dedupSavingsFactor}
                            totalSavingsFactor={totalSavingsFactor}
                            cloneSavings={savingsCloneBytes}
                            compressionSavings={savingsCompressionBytes}
                            dedupSavings={savingsDedupBytes}
                            usedBytes={usedBytes}
                        />
                        <h6>Total Savings</h6>
                    </div>
                    {daysUntilFull !== null &&
                        <div className="columns medium-5 medium-unstack">
                            <KeyStatistic>
                                <DaysUntilFull className="font-weight-inherit" days={daysUntilFull} />
                            </KeyStatistic>
                            <h6>Until Full</h6>
                        </div>
                    }
                </div>
            </Headline.Section>
            <Headline.Section className="medium-11 medium-unstack">
                <h6 className="stacked-title">Performance<br />
                    <small>(Past Week Avg)</small>
                </h6>
                <div className="row align-middle flex-child-grow">
                    <div className="columns">
                        <KeyStatistic className="nowrap">
                            {formatValue(avgIopsPerWeek, NUMBER_ABBREVIATE_FORMAT)} {!isEmpty(avgIopsPerWeek) ? <small>IO/sec</small> : null}
                        </KeyStatistic>
                        <h6>IOPS</h6>
                    </div>
                    <div className="columns">
                        <KeyStatistic>
                            {numeralPerSec(avgThroughput, '0.0 ib')}
                        </KeyStatistic>
                        <h6>Throughput</h6>
                    </div>
                    <div className="columns">
                        <KeyStatistic>
                            {formatValue(avgReadLatencyMs, '0.0')} {!isEmpty(avgReadLatencyMs) ? <small>ms</small> : null}
                        </KeyStatistic>
                        <h6>R Latency</h6>
                    </div>
                    <div className="columns">
                        <KeyStatistic>
                            {formatValue(avgWriteLatencyMs, '0.0')} {!isEmpty(avgWriteLatencyMs) ? <small>ms</small> : null}
                        </KeyStatistic>
                        <h6>W Latency</h6>
                    </div>
                </div>
            </Headline.Section>
            <Headline.Section className="medium-2 medium-unstack">
                <h6 style={{ marginBottom: '20px' }}>Snapshots</h6>
                <KeyStatistic className="nowrap align-middle">
                    {numeral(snapshotCount).format('0,0')}
                </KeyStatistic>
            </Headline.Section>
        </Headline>
    );

PoolHeadline.propTypes = {
    allocatedBytes: PropTypes.number,
    usedBytes: PropTypes.number,
    unusedReserveBytes: PropTypes.number,
    freeBytes: PropTypes.number,
    lastWeekGrowthPercent: PropTypes.number,
    snapshotCount: PropTypes.number,
    savingsCompressionBytes: PropTypes.number,
    savingsCloneBytes: PropTypes.number,
    savingsDedupBytes: PropTypes.number,
    totalSavingsBytes: PropTypes.number,
    cloneSavingsFactor: PropTypes.number,
    compressionSavingsFactor: PropTypes.number,
    dedupSavingsFactor: PropTypes.number,
    totalSavingsFactor: PropTypes.number,
    avgIopsPerWeek: PropTypes.number,
    avgReadLatencyMs: PropTypes.number,
    avgWriteLatencyMs: PropTypes.number,
    avgThroughput: PropTypes.number,
    daysUntilFull: PropTypes.string,
};

export default PoolHeadline;
