import React from 'react';
import PropTypes from 'prop-types';
import Headline from '@infosight/elmer/dist/components/Headline';
import DonutChart from '../../../../charts/DataUsageDonut';
import MonitorServices from './MonitorServices';
import HardwareStatus from './HardwareStatus';
import getNetworkCardInfo from './NetworkInfo';

const ArrayHeadline = ({
    arrayUid,
    model,
    softwareVersion,
    latestHeartBeat,
    sendingAsups,
    arrayCapacityBytes,
    arrayFreeBytes,
    lastAsupReceivedTime,
    isAllFlash,
    tenGigSfpPortCount,
    tenGigBaseTPortCount,
    gigPortCount,
    fcPortCount,
}) => (
    <Headline>
        <Headline.Section className="medium-4 medium-unstack">
            <div style={{ paddingLeft: 20, paddingRight: 20 }}>
                <div className="row align-center">
                    <h6>Summary</h6>
                </div>
                <div className="row align-justify">
                    Array SN:
                    <strong>{arrayUid}</strong>
                </div>
                <div className="row align-justify">
                    Model:
                    <strong>{model}</strong>
                </div>
                <div className="row align-justify">
                    S/W Version:
                    <strong>{softwareVersion && softwareVersion.split('-')[0]}</strong>
                </div>
                <div className="row align-justify">
                    Host Interface:
                    <strong>
                        <div className="text-right" style={{ maxWidth: 90 }}>
                            { getNetworkCardInfo({ tenGigSfpPortCount, tenGigBaseTPortCount, gigPortCount, fcPortCount }) }
                        </div>
                    </strong>
                </div>
            </div>
        </Headline.Section>
        <Headline.Section className="medium-6 medium-unstack">
            <h6>Capacity</h6>
            <div className="row align-middle align-center">
                <DonutChart
                    allocatedBytes={arrayCapacityBytes}
                    usedBytes={arrayCapacityBytes - arrayFreeBytes}
                    freeBytes={arrayFreeBytes}
                />
            </div>
        </Headline.Section>
        <Headline.Section className="medium-8 medium-unstack">
            <h6>Hardware Status</h6>
            <div className="row align-center" style={{ paddingLeft: 20, paddingRight: 20 }}>
                <HardwareStatus
                    latestHeartbeat={latestHeartBeat}
                    isAllFlash={isAllFlash}
                />
            </div>
        </Headline.Section>
        <Headline.Section className="medium-6 medium-unstack">
            <h6>Monitoring Services</h6>
            <MonitorServices
                latestHeartbeatTime={latestHeartBeat && latestHeartBeat.timestamp}
                sendingAsups={sendingAsups}
                lastAsupReceivedTime={lastAsupReceivedTime}
            />
        </Headline.Section>
    </Headline>
);

ArrayHeadline.propTypes = {
    arrayUid: PropTypes.string,
    model: PropTypes.string,
    softwareVersion: PropTypes.string,
    sendingAsups: PropTypes.string,
    arrayCapacityBytes: PropTypes.number,
    arrayFreeBytes: PropTypes.number,
    isAllFlash: PropTypes.bool,
    latestHeartBeat: PropTypes.shape({
        serialNumber: PropTypes.string,
        timestamp: PropTypes.number,
        controllers: PropTypes.arrayOf(PropTypes.shape({
            name: PropTypes.string,
            state: PropTypes.string,
            components: PropTypes.arrayOf(PropTypes.shape({
                name: PropTypes.oneOf(['psu', 'fan', 'temperature']),
                status: PropTypes.string,
            })),
        })),
        ssdGoodCount: PropTypes.number,
        ssdFailedCount: PropTypes.number,
        hddGoodCount: PropTypes.number,
        hddFailedCount: PropTypes.number,
    }),
    lastAsupReceivedTime: PropTypes.number,
    tenGigSfpPortCount: PropTypes.number,
    tenGigBaseTPortCount: PropTypes.number,
    gigPortCount: PropTypes.number,
    fcPortCount: PropTypes.number,
};

export default ArrayHeadline;
