import React from 'react';
import PropTypes from 'prop-types';
import numeral from 'numeral';
import PercentGrowth from '@infosight/elmer/dist/components/PercentGrowth';
import { isNumber } from 'underscore';
import classnames from 'classnames/bind';
import style from '../../shared.scss';

const cx = classnames.bind(style);

// Exported for testing
export const getFormat = value => (value >= 1024 ? '0.0 ib' : '0 ib');
export const getValueAndUnit = (value) => {
    if (isNumber(value)) {
        return numeral(value).format(getFormat(value)).split(' ');
    }

    return [null, null];
};

function TotalUsage({ totalUsage, growthPctLastWeek, capacity, className, children }) {
    // Bad/Missing data
    if (!isNumber(totalUsage)) {
        return (
            <div className={className}>
                <div className={cx('metric')}>
                    <span className={cx('value')}>N/A</span>
                </div>
            </div>
        );
    }

    const [usedValue, usedUnit] = getValueAndUnit(totalUsage);
    const [capacityValue, capacityUnit] = getValueAndUnit(capacity);

    let growth;
    if (growthPctLastWeek) {
        growth = <span><PercentGrowth percent={growthPctLastWeek} precision={1} isPercent /> past 7 days</span>;
    }

    return (
        <div className={className}>
            <div className={cx('subtitle')}>Total Usage</div>
            <div className={cx('metric')}>
                <span className={cx('value')}>{usedValue}</span>
                <span className={cx('unit')}>{usedUnit}</span>
                <span className={cx('muted')}>{capacityValue ? ' of ' : null}</span>
                <span className={cx('value', 'muted')}>{capacityValue}</span>
                <span className={cx('unit', 'muted')}>{capacityUnit}</span>
            </div>
            {growth}
            {children}
        </div>
    );
}

TotalUsage.propTypes = {
    totalUsage: PropTypes.number,
    capacity: PropTypes.number,
    growthPctLastWeek: PropTypes.number,
    children: PropTypes.node,
};

export default TotalUsage;
