import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { property } from 'underscore';
import numeral from 'numeral';
import moment from 'moment-timezone';
import ReactDimensions from 'react-dimensions';
import NoData from '@infosight/elmer/dist/components/NoData';
import mergeSharedTooltip, { Timestamp } from '@infosight/elmer/dist/charts/mergeSharedTooltip';
import { DAY_MOMENT_FORMAT } from '@infosight/elmer/dist/utils/time';
import { getUnitPrefix, convertBytes, BINARY_BASE } from '@infosight/elmer/dist/utils/bytes';
import unavailableDataPlotBands from './unavailableDataPlotBands';
import { dateTimeAxisSnippet } from '../mixins/dateTimeAxis';
import { xAxisDataRange } from '../mixins/xAxis';
import applyMixins from '../mixins/applyMixins';
import dragZoomMixin from '../mixins/dragZoom';

const BYTE_FORMAT = '0.0 ib';

const Chart = mergeSharedTooltip({
    valueFormatter(point) {
        return numeral(point.y).format(BYTE_FORMAT);
    },
    xValueFormatter(point) {
        return Timestamp(point.x, DAY_MOMENT_FORMAT);
    },
});

export const FORECAST_RANGE_SERIES = 'forecastRangeSeries';

const ForecastChart = mergeSharedTooltip({
    valueFormatter(point) {
        if (point.series.options.id === FORECAST_RANGE_SERIES) {
            return `${numeral(point.point.low).format(BYTE_FORMAT)} - ${numeral(point.point.high).format(BYTE_FORMAT)}`;
        }
        return numeral(point.y).format(BYTE_FORMAT);
    },
    xValueFormatter(point) {
        return Timestamp(point.x, DAY_MOMENT_FORMAT);
    },
});

const preventDefault = e => e.preventDefault();

export class CapacityTimeSeries extends Component {
    shouldComponentUpdate({
        timeSeries,
        chartConfig,
        chartRangeStart,
        chartRangeEnd,
        containerHeight,
        containerWidth,
    }) {
        return !(
            moment(chartRangeStart).isSame(this.props.chartRangeStart) &&
            moment(chartRangeEnd).isSame(this.props.chartRangeEnd) &&
            timeSeries === this.props.timeSeries &&
            chartConfig === this.props.chartConfig &&
            containerHeight === this.props.containerHeight &&
            containerWidth === this.props.containerWidth
        );
    }

    static getUnavailableDataBands(chartRangeStart, chartRangeEnd, timeSeries) {
        return unavailableDataPlotBands({
            chartRangeStart,
            chartRangeEnd,
            timeSeriesStart: timeSeries[0].timestamp,
            timeSeriesEnd: timeSeries.slice(-1)[0].timestamp,
        });
    }

    static getXAxis(chartRangeStart, chartRangeEnd, timeSeries, showUnavailableDataBands) {
        return {
            ...dateTimeAxisSnippet(),
            ...xAxisDataRange(chartRangeStart, chartRangeEnd),
            plotBands: showUnavailableDataBands && CapacityTimeSeries.getUnavailableDataBands(
                chartRangeStart,
                chartRangeEnd,
                timeSeries
            ),
        };
    }

    getMaxTotalCapacity(timeSeries) {
        const { getTotalCapacity } = this.props;
        let sizes = timeSeries.map(getTotalCapacity);
        let maxSize = Math.max.apply(null, sizes);
        if (!maxSize) {
            const { getForecastedCapacity } = this.props;
            sizes = timeSeries.map(getForecastedCapacity);
            maxSize = Math.max.apply(null, sizes);
        }
        return maxSize;
    }

    getYAxis(yAxisTitle, enableYAxisTitle, timeSeries) {
        const maxTotalCapacity = this.getMaxTotalCapacity(timeSeries);
        const storageUnit = getUnitPrefix(maxTotalCapacity, BINARY_BASE);
        const displayUnit = storageUnit ? `${storageUnit}iB` : '';
        return {
            title: {
                text: `${yAxisTitle} (${displayUnit})`,
                enabled: enableYAxisTitle,
            },
            labels: {
                formatter() {
                    return `${numeral(convertBytes(this.value, storageUnit)).format('0.0')} ${displayUnit}`;
                },
            },
        };
    }

    capacityUsageSeries() {
        const { timeSeries, getUsedCapacity, seriesName } = this.props;
        return {
            type: 'area',
            name: seriesName,
            data: timeSeries.map(val => ({
                x: val.timestamp,
                y: getUsedCapacity(val),
            })),
            events: {
                legendItemClick: preventDefault,
            },
            marker: {
                enabled: false,
                symbol: 'circle',
            },
            lineWidth: 2,
        };
    }

    snapshotSeries() {
        const { timeSeries, getSnapshotUsage, snapshotSeriesTitle } = this.props;

        return {
            type: 'area',
            color: '#666',
            fillColor: '#E2E6EA',
            name: snapshotSeriesTitle,
            data: timeSeries.map(val => ({
                x: val.timestamp,
                y: getSnapshotUsage(val),
            })),
            events: {
                legendItemClick: preventDefault,
            },
            marker: {
                enabled: false,
                symbol: 'circle',
            },
            lineWidth: 2,
        };
    }

    totalCapacitySeries() {
        const { timeSeries, getTotalCapacity, totalCapacityColor } = this.props;

        return {
            type: 'line',
            color: totalCapacityColor,
            name: 'Total Capacity',
            data: timeSeries.map(val => ({
                x: val.timestamp,
                y: getTotalCapacity(val),
            })),
            events: {
                legendItemClick: preventDefault,
            },
            marker: {
                enabled: false,
                symbol: 'circle',
            },
            lineWidth: 2,
        };
    }

    config() {
        const {
            chartRangeStart,
            chartRangeEnd,
            timeSeries,
            showUnavailableDataBands,
            dateTimeFormat,
            yAxisTitle,
            includeSnapshotUsageSeries,
            includeTotalCapacityUsageSeries,
            onChartSelect,
            enableYAxisTitle,
        } = this.props;

        const series = [this.capacityUsageSeries()];

        if (includeSnapshotUsageSeries) {
            series.push(this.snapshotSeries());
        }

        if (includeTotalCapacityUsageSeries) {
            series.push(this.totalCapacitySeries());
        }

        return applyMixins(
            dragZoomMixin(onChartSelect),
            {
                series,
                chart: {
                    height: this.props.containerHeight,
                    spacingTop: 20,
                },
                title: false,
                xAxis: CapacityTimeSeries.getXAxis(
                    chartRangeStart,
                    chartRangeEnd,
                    timeSeries,
                    showUnavailableDataBands,
                    dateTimeFormat),
                yAxis: this.getYAxis(yAxisTitle, enableYAxisTitle, timeSeries),
            }
        );
    }

    render() {
        const { timeSeries, forecastChart } = this.props;
        const hasData = timeSeries && timeSeries.length > 0;
        return (
            <NoData hasData={hasData} message="No capacity history data available.">
                {() => (
                    forecastChart ?
                        <ForecastChart config={{ ...this.config(), ...this.props.chartConfig }} {...this.props} /> :
                        <Chart config={{ ...this.config(), ...this.props.chartConfig }} {...this.props} />
                )}
            </NoData>
        );
    }
}

CapacityTimeSeries.propTypes = {

    /**
     * HighChart Config object.
     * This will be shallow merged with default config object.
     * Usage: If the default behavior of the chart has to be changed,
     * construct an object with the key which has to be replaced with
     * the default. The possible key values are 'chart', 'title',
     * 'xAxis', 'yAxis', 'legend', 'series', 'credits'.
     *
     * If a config object with any other key is passed it will be just
     * added to the default config object.
     */
    chartConfig: PropTypes.object,

    timeSeries: PropTypes.arrayOf(PropTypes.shape({
        timestamp: PropTypes.oneOfType([PropTypes.number, PropTypes.object]),
        usedCapacity: PropTypes.number,
        totalCapacity: PropTypes.number,
    })),

    /**
     * If `props.timeSeries.usedCapacity` has a different key, provide a getter to get the value
     */
    getUsedCapacity: PropTypes.func,

    /**
     * If `props.timeSeries.forecastedUsageBytes` has a different key, provide a getter to get the value
     * Only used when usedCapacity is null
     */
    getForecastedCapacity: PropTypes.func,

    /**
     * If `props.timeSeries.totalCapacity` has a different key, provide a getter to get the value
     */
    getTotalCapacity: PropTypes.func,
    totalCapacityColor: PropTypes.string,

    /**
     * If `props.timeSeries.snapshotUsage` has a different key, provide a getter to get the value
     */
    getSnapshotUsage: PropTypes.func,

    /**
     * Starting timestamp where the chart begins
     * moment or number
     */
    chartRangeStart: PropTypes.oneOfType([PropTypes.object, PropTypes.number]),

    /**
     * Last timestamp where the chart ends
     * moment or number
     */
    chartRangeEnd: PropTypes.oneOfType([PropTypes.object, PropTypes.number]),

    /**
     * Chart start/end date may differ from TimeSeries data availability.
     * <p>
     * In such cases to make the data unavailability more obvious,<br/> paint
     * chart areas with different color where the data is unavailabile.
     * </p>
     */
    showUnavailableDataBands: PropTypes.bool,

    /**
     *
     * @ignore
     */
    containerHeight: PropTypes.number.isRequired,

    /**
     *
     * @ignore
     */
    containerWidth: PropTypes.number.isRequired,

    /**
     * Title to be shown on Y-Axis
     */
    yAxisTitle: PropTypes.string,

    /**
     *  Name of the series, used to show as Legend too
     */
    seriesName: PropTypes.string,
    snapshotSeriesTitle: PropTypes.string,

    includeSnapshotUsageSeries: PropTypes.bool,
    includeTotalCapacityUsageSeries: PropTypes.bool,

    /**
     * Date format to be used for plotting X-Axis
     */
    dateTimeFormat: PropTypes.string,

    /**
     * Is this a Capacity Forecast chart?
     */
    forecastChart: PropTypes.bool,

    onChartSelect: PropTypes.func,

    enableYAxisTitle: PropTypes.bool,
};

CapacityTimeSeries.defaultProps = {
    containerHeight: 0,
    showUnavailableDataBands: false,
    dateTimeFormat: 'M/D',
    yAxisTitle: 'Usage',
    seriesName: 'Volume Usage',
    getUsedCapacity: property('usedCapacity'),
    getTotalCapacity: property('totalCapacity'),
    totalCapacityColor: '#F1C232', // warningYellow
    getSnapshotUsage: property('snapshotUsage'),
    snapshotSeriesTitle: 'Snapshot Usage',
    includeSnapshotUsageSeries: false,
    includeTotalCapacityUsageSeries: false,
    forecastChart: false,
    enableYAxisTitle: false,
    onChartSelect: event => event.preventDefault(),
};

export default ReactDimensions({ elementResize: true })(CapacityTimeSeries);
