var Backbone = require('backbone');

module.exports = Backbone.Model.extend(
    /** @lends models/grid/BaseGridModel.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name models/grid/BaseGridModel
         * @constructor
         * @augments Backbone.Model
         */

        defaults: {
            // gridUrl: null,
            // pages: 10,
            showSearch: true,
            showFilters: false,
            showPaging: true,
            filterLabel: 'Filter...',
            emptyLabel: 'No results found',
            columns: [],

            /*
             * Configures grouping rows by a column value.
             * Takes a function(DataTablesApi) -> Integer
             * The function should return the column index by which to group, or null if grouping
             * is to be disabled based on the current table configuration (e.g. the table is ordered
             * by a column with a non-enumerated value).
             *
             * Grouping is only allowed by a scolumn also selected for ordering.
             */
            groupBy: function () {
                return null;
            },
        },

        validate: function (attrs, options) {
            if (!attrs.columns || attrs.columns.length === 0) {
                return '"columns" must be specified in the model.';
            }
        },
    });
