const _ = require('underscore');
var $ = require('jquery');
var jQueryUI = require('jqueryui');
var Backbone = require('backbone');
var Marionette = require('backbone.marionette');
require('datatables.net');
require('progeny/components/GridView/DataTablesRenderer');
var BaseGridModel = require('app/models/grid/BaseGridModel');
var LocalGridModel = require('app/models/grid/LocalGridModel');
var CollectionGridModel = require('app/models/grid/CollectionGridModel');
var BulkLoadGridModel = require('app/models/grid/BulkLoadGridModel');
var RemoteGridModel = require('app/models/grid/RemoteGridModel');
var GridFilterView = require('./GridFilterView');
var GridSortCollection = require('./GridSortCollection');
var tpl = require('raw-loader!./GridView.html');

var RowSelectionMode = Object.freeze({
    NONE: 'none',
    SINGLE: 'single',
    MULTI: 'multi',
});

// TODO move this.  currently doesn't play well outside of this file.
$.fn.dataTableExt.oApi.fnSetFilteringDelay = function (oSettings, iDelay) {
    var _that = this;

    if (iDelay === undefined) {
        iDelay = 250;
    }

    this.each(function (i) {
        $.fn.dataTableExt.iApiIndex = i;
        var
            $this = this,
            oTimerId = null,
            sPreviousSearch = null,
            anControl = $('input', _that.fnSettings().aanFeatures.f);

        anControl.unbind('keyup search input').bind('keyup', function () {
            var $$this = $this;

            if (sPreviousSearch === null || sPreviousSearch != anControl.val()) {
                window.clearTimeout(oTimerId);
                sPreviousSearch = anControl.val();
                oTimerId = window.setTimeout(function () {
                    $.fn.dataTableExt.iApiIndex = i;
                    _that.fnFilter(anControl.val());
                }, iDelay);
            }
        });

        return this;
    });
    return this;
};

/**
 * A sort pre-formatter for NimbleOS version strings of the form "1.2.2.0-17686-opt".
 *
 * Translates the raw version String into a String that, when lexically compared,
 * results in proper sort ordering.
 * Implementation pads each numerical component in the version string to 4 digits.
 * See http://datatables.net/plug-ins/sorting
 */
$.fn.dataTable.ext.type.order['nimbleos-version-pre'] = function (version) {
    var pad = '0000';
    var parts = version.split(/\.|\-/);
    return parts.slice(0, 4).map(function (part) {
        return part.length >= pad.length ? part : (pad + part).slice(-pad.length);
    }).join('.');
};

$.fn.dataTable.ext.search.push(function (settings, searchData, index, row, counter) {
    var api = new $.fn.dataTable.Api(settings);
    var view = api.init().view;
    if (!view) {
        // Some code uses DataTables directly and not via GridView
        return true;
    }

    return view.getOption('_isRowIncluded').call(view, index, row, searchData);
});

module.exports = Marionette.LayoutView.extend(
    /** @lends views/components/grid/GridView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/grid/GridView
         * @constructor
         * @augments Backbone.View
         */

        template: _.template(tpl),

        className: 'grid-view',

        regions: {
            filterRegion: '.filter-region',
        },

        ui: {
            selectAllCheckbox: 'th.row-selection-checkbox input',
        },

        events: {
            'init.dt': 'onDataTableInit',
            'draw.dt': 'onDraw',
            'xhr.dt': 'onAjaxComplete',
            'click tr:not(.child-row)': 'onRowClick',
            'click @ui.selectAllCheckbox': 'onSelectAllClick',
            'click td.details-control': 'toggleChildRow',
        },

        url: null,
        columns: null,
        chooseColumns: false,
        childTemplate: null,
        dataTableOptions: null,
        rowSelectionMode: RowSelectionMode.NONE,
        toolbar: null,
        initialized: false,
        highlightRowOnChange: true,

        /**
         * Overridden Backbone initialize method.
         * @param  {Object} options Parameterized initialization object.
         */
        initialize: function (options) {

            _.bindAll(this, 'selectRow', 'updateSelectionForRowVisibilityChange', 'refreshData', 'getDataTableApi');
            //this.refreshData = _.debounce(this.refreshData, 300, true);

            if (!this.model) {
                // throw new Error('"model" must be provided to the GridView component.');
            } else if (!(this.model instanceof BaseGridModel)) {
                throw new Error('"model" must extend from the BaseGridModel class.');
            } else if (!this.model.isValid()) {
                // throw new Error('Model is not valid: ' + this.model.validationError );
            }

            // ensure a template is provided
            if (!this.options.tableTemplate) {
                throw new Error('"tableTemplate" must be provided to the GridView component.  ' +
                    'It is expected to define the table structure for the grid.');
            }

            if (options.childTemplate) {
                this.ChildView = Marionette.ItemView.extend({
                    template: options.childTemplate,
                });
            } else {
                this.ChildView = options.ChildView;
            }
            this.dataTableOptions = options.dataTableOptions;
            if (options.rowSelectionMode) {
                this.rowSelectionMode = options.rowSelectionMode;
            }
            if (options.toolbar) {
                this.toolbar = options.toolbar;
            }

            /**
             * sort can be specified in 3 ways:
             * 1. A GridSortCollection object.  Useful if parent view needs to manipulate the Sort programmatically.
             * 2. An array of column names:
             *      [ 'Array SN', 'Volume Name ']
             * 3. An array of GridSortModel attributes:
             *      [ { column: 'Array SN', order: 'asc' }, { column: 'Volume Size', order: 'desc' } ]
             */

            if (options.sort instanceof GridSortCollection) {
                this.sort = options.sort;
            } else {
                options.sort = options.sort || [];
                if (options.sort.length && _.isString(options.sort[0])) {
                    this.sort = new GridSortCollection(options.sort.map(function (columnName) {
                        return { column: columnName };
                    }));
                } else {
                    this.sort = new GridSortCollection(options.sort);
                }
            }
            this.listenTo(this.sort, 'update', this.applyOrdering);
            this.listenTo(this.sort, 'reset', this.applyOrdering);

            if (this.model.get('showFilters')) {
                this.filterView = new GridFilterView({
                    grid: this,
                });
            }

        },

        onRender: function () {
            var self = this;

            if (this.options.tableTemplate instanceof $) {
                console.warn('Use of existing DOM element as GridView template is deprecated - a template function (or HTML string) should be used instead');
                this.$table = this.options.tableTemplate;
            } else {
                this.$table = $(_.result(this.options, 'tableTemplate'));
            }

            // datatables doesn't do much validation of the columns, so we'll do some additional
            // checks up front before attempting to create the table.
            if (this.$table.find('THEAD').length === 0) {
                throw new Error('GridView[' + this.id + '] GridView template must specify a THEAD section.');
            }

            // TODO? Use a region for the table element instead of directly appending to the $el
            this.$el.append(this.$table);

            if (this.model.get('showFilters')) {
                this.filterRegion.show(this.filterView);
            }

            var isRemote = this.model instanceof RemoteGridModel;
            var columns = this.model.get('columns');
            var pageLength = this.model.get('pageLength');
            var emptyLabel = this.model.get('emptyLabel');
            var groupBy = this.model.get('groupBy');

            // Synthetic column headers always span the entire header row
            var syntheticHeaderRowspan = this.$table.find('thead tr').length;

            if (this.ChildView) {
                this.$table.find('th:first').before($('<th/>').attr('rowspan', syntheticHeaderRowspan));
                columns = $.merge([
                    {
                        data: null, 'class': 'details-control', orderable: false, defaultContent:
                            '<span class="fa fa-caret-right"/>',
                    },
                ], columns);
            }

            if (this.rowSelectionMode == RowSelectionMode.MULTI) {
                this.$table.find('th:first').before($('<th><input type="checkbox"/></th>').attr('rowspan', syntheticHeaderRowspan));
                columns = $.merge([
                    {
                        data: null, 'class': 'row-selection-checkbox', orderable: false, defaultContent:
                            '<input type="checkbox"/>',
                    },
                ], columns);
            }

            this.model.set('columns', columns);

            this.applyColumnNames();
            this.applyColumnIndices();

            // if ( columns.length != $table.find('THEAD TH').length ) {
            //     throw new Error('GridView[' + this.id + '] Mismatch between the number of columns specified in the ' +
            //         '"columns" parameter (' + columns.length + ') and the table headers ' +
            //         'in the template (' + $table.find('THEAD TH').length + ').');
            // }

            var dataTableConfig = {
                processing: isRemote,
                serverSide: isRemote,
                language: {
                    emptyTable: emptyLabel,
                },
                pageLength: pageLength,

                order: this.sortToDatatablesOrder(),
                columns: columns,

                dom: '<"top"C<"toolbar">f<"clear">lp<"clear"><"custom-filter">>' +
                    'rt' +
                    '<"bottom"p<"clear">>',

                lengthMenu: [
                    [10, 25, 50, 100],
                    [10, 25, 50, 100],
                ],
                drawCallback: function (settings) {
                    var api = this.api();
                    var groupByColumn = groupBy(api);
                    if (groupByColumn !== null) {
                        self.insertGroupingRows(api, groupByColumn);
                    }
                },
                createdRow: this.createdRow.bind(this),
                view: this,
            };

            var errorHandler = function (xhr, textStatus, textThrown) {
                console.log('GridView data load error - ' + textStatus + ': ' + textThrown);
                self.trigger('error', xhr, textStatus, textThrown);
                self.$table.dataTable().api().destroy();
                self.$table.dataTable({
                    "info": false,
                    "language": {
                        "emptyTable": "Error while fetching data"
                    }
                });
            };
            if (this.model instanceof LocalGridModel) {
                dataTableConfig.data = this.model.get('data');
            } else if (this.model instanceof CollectionGridModel) {
                var collection = this.model.get('collection');
                dataTableConfig.data = collection.toJSON();
                this.listenTo(collection, 'add', this.refreshModelData);
                this.listenTo(collection, 'remove', this.refreshModelData);
                this.listenTo(collection, 'change', this.refreshModelData);
                this.listenTo(collection, 'reset', this.refreshCollectionData);
            } else if (this.model instanceof BulkLoadGridModel) {
                var loadedData = this.model.get('data');
                dataTableConfig.ajax = {
                    url: self.model.url(),
                    dataSrc: self.model.get('dataSrc'),
                    error: errorHandler,
                };
            } else {
                dataTableConfig.ajax = this.cachePipeline({
                    url: this.model.get('url'),
                    pages: this.model.get('pages'),
                    error: errorHandler,//,
                    // headers: {
                    //     Accept: 'application/datatables+json'
                    // }
                });
            }

            $.extend(dataTableConfig, this.dataTableOptions);

            this.$table.dataTable(dataTableConfig);

            // this.$table.fnSetFilteringDelay(750);

            return this;
        },

        onDataTableInit: function () {
            var api = this.$table.DataTable();
            var self = this;

            // Show pagination if there is more than one page (> pageLength entries).
            if (this.model instanceof CollectionGridModel) {
                var collection = this.model.get('collection'),
                    $paginationElements = this.$('.dataTables_paginate');

                var togglePaginationControls = function () {
                    var pageLength = self.model.get('pageLength');
                    if (pageLength) {
                        $paginationElements.toggle(collection.length > pageLength);
                    } else {
                        // If pageLength is undefined, use DataTables' default page length,
                        // 10 entries per page.
                        $paginationElements.toggle(collection.length > 10);
                    }
                };

                togglePaginationControls();
                this.listenTo(collection, 'sync', togglePaginationControls);
                this.listenTo(collection, 'add', togglePaginationControls);
                this.listenTo(collection, 'remove', togglePaginationControls);
                this.listenTo(collection, 'reset', togglePaginationControls);
            }

            if (this.toolbar) {
                this.$('.toolbar').html(this.toolbar);
            }

            // add tooltips to the headers
            // Do not override the title if the header has already set it.
            this.$table.find('TH:not(.row-selection-checkbox):not([title])').each(function (index, value) {
                $(this).attr('title', 'Click to sort.  Shift click to sort by multiple columns.');
            });

            this.initialized = true;

            // notify listeners grid is loaded
            this.trigger('init', this);
        },

        applyColumnNames: function () {
            var $table = this.$table;
            this.model.get('columns').forEach(function (column, index) {
                if (!column.name) {
                    var $header = $table.find('thead > tr:first > th:nth-of-type(' + (index + 1) + ')');
                    column.name = $header.text();
                }
            });
        },

        applyColumnIndices: function () {
            this.model.get('columns').forEach(function (column, index) {
                column.index = index;
            });
        },

        /**
         * Ensure clean up of DataTables objects and references when
         * 'destroy' is called on region containing this view.
         */
        onDestroy: function () {
            // Check that this.$table is non-null to prevent case where
            // API user attempts to destroy not-yet-rendered GridView.
            if (this.$table) {
                this.$table.api().destroy();
            }
        },

        /**
         * Creates the filter widget.
         * @param  {DataTableAPI} api   DataTables API instance.
         * @param  {Array} columns      Columns
         * @private
         */

        /**
         * Returns the internal DataTable instance.
         * @return {DataTable} Returns the DataTable instance.
         * @public
         */
        getDataTable: function () {
            return this.$table;
        },

        /**
         * Returns the DataTable API
         * @return {DataTableAPI} Returns a DataTable API instance
         */
        getDataTableApi: function () {
            return this.$table.api();
        },

        onDraw: function () {
            this.bindUIElements();
            if (this.rowSelectionMode != RowSelectionMode.NONE) {
                this.updateSelectionForRowVisibilityChange();
            }
        },

        onRowClick: function (event) {
            this.selectRow($(event.currentTarget));
        },

        selectRow: function ($row) {
            if ($row.find('td').hasClass('dataTables_empty')) {
                return;
            }
            if ($row.parents('tr.child-row').length) {
                // TRs nested within child rows are not selectable
                return;
            }
            if ($row.hasClass('unselectable')) {
                return;
            }
            if (this.rowSelectionMode == RowSelectionMode.NONE) {
                return;
            }

            if ($row.hasClass('selected')) {
                $row.removeClass('selected');
            } else {
                if (this.rowSelectionMode == RowSelectionMode.SINGLE) {
                    this.$table.find('TR.selected').removeClass('selected');
                }
                $row.addClass('selected');
            }

            this.$table.find('TR.selected TD.row-selection-checkbox INPUT').prop('checked', true);
            this.$table.find('TR:not(.selected) TD.row-selection-checkbox INPUT').prop('checked', false);

            this.updateSelectAllCheckbox();

            this.trigger('row-selection:changed', this.$table.find('TR.selected'));
        },

        onSelectAllClick: function (event) {
            var $input = $(event.currentTarget);
            var $rows = this.getDataTableApi().$('TR', { page: 'current' });
            if ($input.is(':checked')) {
                $rows.addClass('selected');
                $rows.find('TD.row-selection-checkbox INPUT').prop('checked', true);
            } else {
                $rows.removeClass('selected');
                $rows.find('TD.row-selection-checkbox INPUT').prop('checked', false);
            }

            this.trigger('row-selection:changed', this.$table.find('TR.selected'));
        },

        updateSelectAllCheckbox: function () {
            if (this.rowSelectionMode != RowSelectionMode.MULTI) {
                return;
            }

            var $rows = this.getDataTableApi().$('TR', { page: 'current' });

            if ($rows.filter('.selected').length === 0) {
                this.ui.selectAllCheckbox.prop('checked', false);
                this.ui.selectAllCheckbox.prop('indeterminate', false);
            } else if ($rows.filter(':not(.selected)').length === 0) {
                this.ui.selectAllCheckbox.prop('checked', true);
                this.ui.selectAllCheckbox.prop('indeterminate', false);
            } else {
                this.ui.selectAllCheckbox.prop('indeterminate', true);
            }
        },

        /**
         * Update the selection state when the set of visible rows is changed via:
         * <ul>
         * <li>Search
         * <li>Filter
         * <li>Page change
         * <li>Page length change
         * </ul>
         */
        updateSelectionForRowVisibilityChange: function () {
            var $allRows = this.getDataTableApi().$('TR');
            var $visibleRows = this.getDataTableApi().$('TR', { page: 'current' });
            var $invisibleRows = $allRows.filter(function () {
                return !$(this).filter($visibleRows).length;
            });

            // Unselect everything no longer visible
            $invisibleRows.removeClass('selected');
            $invisibleRows.find('TD.row-selection-checkbox INPUT').prop('checked', false);

            this.updateSelectAllCheckbox();

            this.trigger('row-selection:changed', this.$table.find('TR.selected'));
        },

        /**
         * Returns the data for the selected row.
         * @return {Object} Returns the data used for the selected row.
         */
        getSelectedRowData: function () {
            console.assert(this.rowSelectionMode == RowSelectionMode.SINGLE);
            return this.getDataTableApi().row('.selected').data();
        },

        /**
         * Returns the data for the selected rows.
         * @return {Array} Returns an array of data objects for the selected rows.
         */
        getSelectedRowsData: function () {
            console.assert(this.rowSelectionMode == RowSelectionMode.MULTI);
            return this.getDataTableApi().rows('.selected').data();
        },

        /**
         * Execute the provided function for each row in the datatable
         */
        eachRow: function (func) {
            var api = this.getDataTableApi();
            api.rows().eq(0).each(function (index) {
                func(api.row(index));
            });
        },

        refreshCollectionData: function () {
            this.refreshData();
        },

        refreshModelData: function (model) {
            if (this.getOption('highlightRowOnChange')) {
                this.highlightRowsOnNextDraw(function (rowData) {
                    return model.id === rowData.id;
                });
            }
            this.refreshData();
        },

        refreshData: function () {
            if (this.model instanceof LocalGridModel) {
                // No-op (?)
            } else if (this.model instanceof CollectionGridModel) {
                this.getDataTableApi()
                    .clear()
                    .rows
                    .add(this.model.get('collection').toJSON())
                    .draw();
                this.trigger('reload', this);
            } else {
                this.getDataTableApi().ajax.reload();
            }
        },

        onAjaxComplete: function () {
            if (!this.initialized) {
                return;
            }

            // Defer the reload event until datatables has actually updated the table from the AJAX response
            var self = this;
            _.defer(function () {
                self.trigger('reload', self);
            });
        },

        highlightRowsOnNextDraw: function (predicate) {
            var self = this;
            this.getDataTableApi().one('draw', function () {
                self.eachRow(function (row) {
                    if (predicate(row.data())) {
                        $(row.node()).find('> td').effect('highlight', { color: '#78c639' }, 3000);
                    }
                });
            });
        },

        /**
         * Helper for building the cache pipeline.
         * @param  {Object} opts Parameterized object.
         * @private
         */
        cachePipeline: function (opts) {
            // Configuration options
            var conf = $.extend({
                pages: 5,     // number of pages to cache
                url: '',      // script url
                data: null,   // function or object with parameters to send to the server
                              // matching how `ajax.data` works in DataTables
                method: 'GET', // Ajax HTTP method
            }, opts);

            // Private variables for storing the cache
            var cacheLower = -1;
            var cacheUpper = null;
            var cacheLastRequest = null;
            var cacheLastJson = null;

            return function (request, drawCallback, settings) {
                var ajax = false;
                var requestStart = request.start;
                var requestLength = request.length;
                var requestEnd = requestStart + requestLength;

                if (settings.clearCache) {
                    // API requested that the cache be cleared
                    ajax = true;
                    settings.clearCache = false;
                } else if (cacheLower < 0 || requestStart < cacheLower || requestEnd > cacheUpper) {
                    // outside cached data - need to make a request
                    ajax = true;
                } else if (JSON.stringify(request.order) !== JSON.stringify(cacheLastRequest.order) ||
                    JSON.stringify(request.columns) !== JSON.stringify(cacheLastRequest.columns) ||
                    JSON.stringify(request.search) !== JSON.stringify(cacheLastRequest.search)
                ) {
                    // properties changed (ordering, columns, searching)
                    ajax = true;
                }

                // Store the request for checking next time around
                cacheLastRequest = $.extend(true, {}, request);

                if (ajax) {
                    // Need data from the server
                    if (requestStart < cacheLower) {
                        requestStart = requestStart - (requestLength * (conf.pages - 1));

                        if (requestStart < 0) {
                            requestStart = 0;
                        }
                    }

                    cacheLower = requestStart;
                    cacheUpper = requestStart + (requestLength * conf.pages);

                    request.start = requestStart;
                    request.length = requestLength * conf.pages;

                    // Provide the same `data` options as DataTables.
                    if ($.isFunction(conf.data)) {
                        // As a function it is executed with the data object as an arg
                        // for manipulation. If an object is returned, it is used as the
                        // data object to submit
                        var d = conf.data(request);
                        if (d) {
                            $.extend(request, d);
                        }
                    } else if ($.isPlainObject(conf.data)) {
                        // As an object, the data given extends the default
                        $.extend(request, conf.data);
                    }

                    settings.jqXHR = $.ajax({
                        'type': conf.method,
                        'url': conf.url,
                        'data': request,
                        'dataType': 'json',
                        'cache': false,
                        'success': function (json) {
                            cacheLastJson = $.extend(true, {}, json);

                            if (cacheLower != requestStart) {
                                json.data.splice(0, requestStart - cacheLower);
                            }
                            json.data.splice(requestLength, json.data.length);

                            drawCallback(json);
                        },
                    });
                } else {
                    var json = $.extend(true, {}, cacheLastJson);
                    json.draw = request.draw; // Update the echo for each response
                    json.data.splice(0, requestStart - cacheLower);
                    json.data.splice(requestLength, json.data.length);

                    drawCallback(json);
                }
            };
        },

        /**
         * Synthesize row grouping by inserting a row with the group name that spans the table.
         * See https://datatables.net/examples/advanced_init/row_grouping.html for an overview of the approach.
         *
         * Note: this starts to look bad if the table is not sorted by the grouping column
         * (or a column correlated to the grouping column).  See comment above about allowing
         * specification of which sort column(s) enable the grouping.
         */
        insertGroupingRows: function (api, groupByColumn) {
            var columns = this.model.get('columns');
            var rows = api.rows({ page: 'current' }).nodes();
            var last = null;

            api.column(groupByColumn, { page: 'current' }).cache('order').each(function (group, i) {
                if (last !== group) {
                    $(rows).eq(i).before(
                        `<tr class="rowgroup-header"><td colspan="${columns.length}">${group}</td></tr>`,
                    );

                    last = group;
                }
            });
        },

        toggleChildRow: function (event) {
            console.assert(this.ChildView, 'Child view expected to be defined');

            var api = this.getDataTableApi(),
                tr = $(event.currentTarget).closest('tr'),
                row = api.row(tr),
                clickedRow = $(row.node()),
                childRow = null,
                rowData = row.data(),
                childView,
                childModel,
                childCollection;

            event.stopPropagation();

            tr.find('SPAN').toggleClass('fa-caret-right fa-caret-down');

            if (row.child.isShown()) {
                childView = row.child().data('view');
                childView.destroy();
                // This row is already open - close it
                row.child.remove();
                childRow = clickedRow.next();
                this.trigger('child-row:hide', clickedRow, rowData);
            } else {
                // Open this row
                if (this.model instanceof CollectionGridModel) {
                    childCollection = this.model.get('collection');
                    var idAttr = childCollection.model.prototype.idAttribute || 'id';
                    childModel = childCollection.get(rowData[idAttr]);
                } else {
                    childModel = new Backbone.Model(rowData);
                }
                childView = new this.ChildView({
                    model: childModel,
                    collection: childCollection,
                    rowData: rowData,
                });
                row.child(childView.render().el).show();
                row.child().data('view', childView);

                childRow = clickedRow.next();
                childRow
                    .addClass('child-row')
                    .css('background-color', 'white');
                // tr.addClass('shown');
                this.trigger('child-row:show', childRow, rowData);
            }
        },

        /**
         * True by default, can be overridden by passing in a 'rowFilter' function to the view
         */
        isRowIncluded: function () {
            return true;
        },

        /**
         * Called by our custom $.fn.dataTable.ext.search function
         * Return true/false if a row should be included in the table.
         * This is called on every draw()/redraw() of the DataTable
         * A row is included if either:
         *   The custom isRowIncluded function passed into GridView returns true
         * OR
         *   The filter state (select dropdowns) represented by GridFilterView matches the row
         */
        _isRowIncluded: function (rowIndex, rowObject, rowSearchData) {
            return this.getOption('isRowIncluded')(rowIndex, rowObject, rowSearchData)
                && (this.filterView ? this.filterView.isRowIncluded(rowIndex, rowObject, rowSearchData) : true);
        },

        redraw: function () {
            this.getDataTableApi().draw();
        },

        rawRows: function () {
            return this.getDataTableApi().rows({ 'search': 'applied' });
        },

        sortToDatatablesOrder: function () {
            var columns = this.model.get('columns');

            return this.sort.map(function (sort) {
                return [
                    _.findIndex(columns, function (column) {
                        return column.name == sort.get('column');
                    }),
                    sort.get('order'),
                ];
            });
        },

        applyOrdering: function () {
            this.getDataTableApi()
                .order(this.sortToDatatablesOrder())
                .draw();
        },

        createdRow: function (row, data, dataIndex) {
            if (data.id) {
                $(row).attr('data-model-id', data.id);
            }
        },
    },
    {
        RowSelectionMode: RowSelectionMode,
    });
