import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { noop } from 'underscore';
import ReactClickOutside from 'react-click-outside';
import style from './style.scss';
import Menu from './Menu';
import Toggle from './Toggle';

const cx = classnames.bind(style);

class Dropdown extends Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = {
            active: false,
        };

        console.warn('Don\'t use anything in here if you can avoid it.' +
            ' I plan to replace this convoluted nonsense that I factored out of capacity report. - Marque');
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.selection !== this.props.selection) {
            this.setState({ active: false });
        }
    }

    onClickToggle(event) {
        event.preventDefault();
        this.setState({ active: !this.state.active });
    }

    onClickMenuItem(option) {
        this.setState({ active: false }, () => this.props.onChange(option));
    }

    // Used by ReactClickOutside
    handleClickOutside() {
        if (this.state.active) {
            this.setState({ active: false });
        }
    }

    render() {
        const { active } = this.state;
        const { disabled, className, value, options } = this.props;
        return (
            <div className={cx(className, 'nmbl-dropdown')}>
                <Toggle active={active} value={value} onClick={this.onClickToggle} disabled={disabled} />
                <Menu active={active} options={options} onClick={this.onClickMenuItem} disabled={disabled} />
            </div>
        );
    }
}

Dropdown.propTypes = {
    value: PropTypes.object,
    onChange: PropTypes.func,
    options: PropTypes.array.isRequired,
    selection: PropTypes.any,
    disabled: PropTypes.bool.isRequired,
};

Dropdown.defaultProps = {
    disabled: false,
    onChange: noop,
};

export default ReactClickOutside(Dropdown);
