import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import ReactClickOutside from 'react-click-outside';
import style from './style.scss';
import ComplexToggle from './ComplexToggle';

const cx = classnames.bind(style);

class ComplexDropdown extends Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = {
            active: false,
        };

        console.warn('Don\'t use anything in here if you can avoid it.' +
            ' I plan to replace this convoluted nonsense that I factored out of capacity report. - Marque');
    }

    componentWillReceiveProps() {
        this.setState({ active: false });
    }

    onClickToggle() {
        this.setState({ active: !this.state.active });
    }

    // Called by react-click-outside HOC
    handleClickOutside() {
        if (this.state.active) {
            this.setState({ active: false });
        }
    }

    render() {
        const { valueRenderer, value, className, menuClassName, children, disabled } = this.props;
        const { active } = this.state;
        return (
            <div className={cx(className, 'nmbl-dropdown')}>
                <ComplexToggle active={active} onClick={this.onClickToggle} value={value} disabled={disabled}>
                    {valueRenderer(value)}
                </ComplexToggle>
                <div className={cx('menu', menuClassName, { hide: !active || disabled })}>
                    {children}
                </div>
            </div>
        );
    }
}

ComplexDropdown.propTypes = {
    value: PropTypes.object,
    menuClassName: PropTypes.string,
    children: PropTypes.node.isRequired,
    valueRenderer: PropTypes.func.isRequired,
    disabled: PropTypes.bool.isRequired,
};

ComplexDropdown.defaultProps = {
    value: null,
    disabled: false,
};

export default ReactClickOutside(ComplexDropdown);
