import { isNumber, findLastIndex } from 'underscore';
import { CHART_COLORS } from '../../../../../../../util/chartColors';

const preventDefault = e => e.preventDefault();

let legendIndex = 0;
const getLegendIndex = () => {
    legendIndex += 1;
    return legendIndex;
};

function totalUsageSeries(timeSeries, isLineChart = false) {
    return {
        type: isLineChart ? 'line' : 'area',
        id: 'totalusage',
        name: 'Total Usage',
        color: CHART_COLORS.blue,
        fillColor: CHART_COLORS.blueLight,
        data: timeSeries.map(val => ({
            x: val.timestamp,
            y: val.usedBytes,
        })),
        legendIndex: getLegendIndex(),
        events: {
            legendItemClick: preventDefault,
        },
    };
}

function savingsSeries(timeSeries) {
    return {
        type: 'area',
        id: 'Saings',
        name: 'Savings',
        color: CHART_COLORS.teal,
        fillColor: CHART_COLORS.tealLight,
        data: timeSeries.map(val => ({
            x: val.timestamp,
            y: val.totalSavingsBytes,
        })),
        legendIndex: getLegendIndex(),
        events: {
            legendItemClick: preventDefault,
        },
    };
}

function allocatedCapacitySeries(timeSeries) {
    // Reverse search the data array for the last actual data point ( data point just before the forecast starts)
    const lastActualDataPoint = timeSeries[findLastIndex(timeSeries, { dataSource: 'Actual' })];
    return {
        type: 'line',
        id: 'Capacity',
        name: 'Capacity',
        color: CHART_COLORS.redOrange,
        lineWidth: 2,
        data: timeSeries.map(val => ({
            x: val.timestamp,
            // Its possible that the data has only forecast data ie., there is not actual data
            y: isNumber(val.allocatedBytes) ? val.allocatedBytes : lastActualDataPoint && lastActualDataPoint.allocatedBytes,
        })),
        step: true,
        legendIndex: getLegendIndex(),
        events: {
            legendItemClick: preventDefault,
        },
    };
}

const getPerformanceDegradationBytes = bytes => (isNumber(bytes) ? bytes * 0.9 : bytes);

function performanceDegradationCapacitySeries(timeSeries) {
    // Reverse search the data array for the last actaul data point ( data point just before the forecast starts)
    const lastActualDataPoint = timeSeries[findLastIndex(timeSeries, { dataSource: 'Actual' })];
    return {
        type: 'line',
        dashStyle: 'dot',
        name: 'Performance Degradation Threshold (90%)',
        color: CHART_COLORS.redOrange,
        lineWidth: 2,
        data: timeSeries.map(val => ({
            x: val.timestamp,
            // Its possible that the data has only forecast data ie., there is not actual data
            y: val.allocatedBytes ? getPerformanceDegradationBytes(val.allocatedBytes) :
                lastActualDataPoint && getPerformanceDegradationBytes(lastActualDataPoint.allocatedBytes),
        })),
        step: true,
        legendIndex: getLegendIndex(),
        events: {
            legendItemClick: preventDefault,
        },
    };
}

function usageForecastSeries(timeSeries) {
    return {
        type: 'line',
        id: 'forecastedUsage',
        name: 'Forecasted Pool Usage',
        color: CHART_COLORS.blue,
        dashStyle: 'shortdash',
        data: timeSeries.map(val => ({
            x: val.timestamp,
            y: val.forecastedUsageBytes,
        })),
        legendIndex: getLegendIndex(),
        events: {
            legendItemClick: preventDefault,
        },
    };
}

function usageRangeForecastSeries(timeSeries, forecastRangeId) {
    return {
        type: 'arearange',
        id: forecastRangeId,
        name: 'Forecasted Range',
        color: CHART_COLORS.blue,
        linkedTo: 'forecastedUsage',
        data: timeSeries.map(val => ({
            x: val.timestamp,
            low: val.forecastedLowerBoundBytes,
            high: val.forecastedUpperBoundBytes,
        })),
        lineWidth: 0,
        fillOpacity: 0.3,
        zIndex: 0,
        events: {
            legendItemClick: preventDefault,
        },
    };
}

export default ({ timeseries, forecastRangeId, includeSavingsSeries = true, isLineChart = false }) => ({
    series: [
        allocatedCapacitySeries(timeseries),
        includeSavingsSeries && savingsSeries(timeseries),
        totalUsageSeries(timeseries, isLineChart),
        performanceDegradationCapacitySeries(timeseries),
        usageRangeForecastSeries(timeseries, forecastRangeId),
        usageForecastSeries(timeseries),
    ],
    plotOptions: {
        area: {
            animation: false,
            stacking: 'normal',
            marker: {
                enabled: false,
                symbol: 'circle',
                states: {
                    hover: {
                        enabled: true,
                    },
                },
            },
        },
        line: {
            marker: {
                enabled: false,
                symbol: 'circle',
                states: {
                    hover: {
                        enabled: true,
                    },
                },
            },
        },
    },
});
