import React from 'react';
import { flatten, compact } from 'underscore';
import Spinner from '@infosight/elmer/dist/components/Spinner';
import PoolContainer from './nodeDecorators/Pool';
import { FolderNodeHeader, FolderNodeIcon } from './nodeDecorators/Folder';
import ArrayGroupContainer from './nodeDecorators/ArrayGroup';
import Container from './nodeDecorators/Container';
import { POOL, FOLDER, ARRAY_GROUP, ROOT } from '../../constants';

const poolNodeDecorators = {
    Loading: () => (<Spinner />),
    Container: PoolContainer,
};

const groupNodeDecorators = {
    Loading: () => (<Spinner />),
    Container: ArrayGroupContainer,
};

const folderNodeDecorators = {
    Loading: () => (<Spinner />),
    Container,
    Toggle: FolderNodeIcon,
    Header: FolderNodeHeader,
};

const clone = tree => JSON.parse(JSON.stringify(tree));

export default function createTree(objectRelationGraph) {
    const getVisitor = parentNode =>
        (node) => {
            const visitor = visitors[node.type]; // eslint-disable-line no-use-before-define
            if (visitor) {
                visitor(node, parentNode);
            } else if (parentNode) {
                parentNode.children[parentNode.children.indexOf(node)] = undefined; // eslint-disable-line
                // no-param-reassign
            }
        };

    const visit = node => node.children.forEach(getVisitor(node));

    const visitors = {
        [ROOT]: (node) => {
            visit(node);

            const children = node.children.map((child) => {
                // If a group has multiple pools, show the group
                if (child.children && child.children.length > 1) {
                    return child;
                }

                // Otherwise replace the node with its lone child node (or no node)
                return child.children[0];
            });

            Object.assign(node, {
                children: compact(flatten(children)),
            });
        },
        [ARRAY_GROUP]: (node) => {
            visit(node);

            const children = compact(node.children);
            Object.assign(node, {
                children,
                decorators: groupNodeDecorators,
                toggled: true,
            });
        },
        [POOL]: (node, parent) => {
            visit(node);

            Object.assign(node, {
                parent,
                decorators: poolNodeDecorators,
                toggled: false,
                children: compact(node.children),
            });
        },
        [FOLDER]: (node, parent) => {
            Object.assign(node, {
                parent,
                decorators: folderNodeDecorators,
            });
        },
    };

    const tree = clone(objectRelationGraph);
    visitors[ROOT](tree);
    return tree.children;
}
