import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { QueryTabs, Tab, TabList, TabPanel } from '@infosight/elmer/dist/components/Tabs';
import VolumeHeadlineContainer from './headline/VolumeHeadlineContainer';
import VolumeCapacityContainer from './capacity/VolumeCapacityContainer';
import VolumePerformanceContainer from './performance/VolumePerformanceContainer';
import { reset } from '../actionCreators';

class VolumeDetailContainer extends Component {
    componentWillUnmount() {
        this.props.reset();
    }

    render() {
        const { scope: { id }, ...props } = this.props;

        return (
            <div>
                <VolumeHeadlineContainer volumeSn={id} />
                <QueryTabs values={['performance', 'capacity']}>
                    <TabList>
                        <Tab>Performance</Tab>
                        <Tab>Capacity</Tab>
                    </TabList>
                    <TabPanel>
                        <VolumePerformanceContainer {...props} volumeSn={id} />
                    </TabPanel>
                    <TabPanel>
                        <VolumeCapacityContainer {...props} volumeSn={id} />
                    </TabPanel>
                </QueryTabs>
            </div>
        );
    }
}

VolumeDetailContainer.propTypes = {
    // Injected by XX_INFRASTRUCTURE_XX
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),

    reset: PropTypes.func,
};

const mapDispatchToProps = dispatch => bindActionCreators({
    reset,
}, dispatch);

export default connect(null, mapDispatchToProps)(VolumeDetailContainer);

