import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment-timezone';
import classnames from 'classnames/bind';

const SupportContractEndDateTooFarInFutureInYears = 10;
const SupportContractExpirationWarningThresholdInDays = 90;

const SupportStatus = Object.freeze({
    NOT_AVAILABLE: 'N/A',
    CURRENT: 'Current',
    EXPIRING: 'Expiring',
    EXPIRED: 'Expired',
});

export const parseSupportStatus = (supportStatus, supportEndDate) => {
    if (supportStatus.toLowerCase() === SupportStatus.EXPIRED.toLowerCase()) {
        return SupportStatus.EXPIRED;
    }

    // pass 'true' to avoid rounding
    if (Math.abs(moment(supportEndDate).diff(moment(), 'years', true)) > SupportContractEndDateTooFarInFutureInYears) {
        return SupportStatus.NOT_AVAILABLE;
    }

    const expiring = Math.abs(moment(supportEndDate).diff(moment.utc(), 'days')) <= SupportContractExpirationWarningThresholdInDays;
    return expiring ? SupportStatus.EXPIRING : SupportStatus.CURRENT;
};

const ArraySupportStatus = ({ parsedSupportStatus }) => (
    <span className={classnames(parsedSupportStatus !== SupportStatus.CURRENT ? 'text-danger' : '')}>
        { parsedSupportStatus }
    </span>
);

ArraySupportStatus.propTypes = {
    parsedSupportStatus: PropTypes.string,
};

export default ArraySupportStatus;
