import React from 'react';
import numeral from 'numeral';
import {
    ContainsStringFacet,
} from '@infosight/elmer/dist/components/FacetedFilter';
import { ProgressWithValue, Progress } from '@infosight/elmer/dist/components/Progress';
import { FieldBuilder, CollectionConfigBuilder } from '@infosight/elmer/dist/lib/CollectionConfig';
import { MultiFieldColumnHeader } from '@infosight/elmer/dist/components/GridView';

const SUMMARY_TAB = 'summary';
const SPACE_TAB = 'space';
const PERFORMANCE_TAB = 'performance';
const PROTECTION_TAB = 'protection';

export default function getConfig() {
    return new CollectionConfigBuilder()
        .setDefaultFacetCategory(SUMMARY_TAB)
        .addCategory(SUMMARY_TAB, 'Summary')
        .addCategory(SPACE_TAB, 'Space')
        .addCategory(PERFORMANCE_TAB, 'Performance')
        .addCategory(PROTECTION_TAB, 'Protection')
        .addField(
            new FieldBuilder()
                .field('groupName')
                .title('Group Name')
                .column(true)
                .facet(<ContainsStringFacet />)
        )
        .addField(
            new FieldBuilder()
                .field('poolCount')
                .title('# pools')
                .category(SUMMARY_TAB)
                .column(true)
        )
        .addField(
            new FieldBuilder()
                .field('dataUsageBytes')
                .title('Data')
                .categories([SUMMARY_TAB, SPACE_TAB])
                .column({
                    header: <MultiFieldColumnHeader leftTitle="data" rightTitle="size" />,
                    render: (dataUsageBytes, { allocatedBytes }) => (
                        <ProgressWithValue
                            leftLabel={numeral(dataUsageBytes).format('0.0 b')}
                            rightLabel={numeral(allocatedBytes).format('0.0 b')}
                        >
                            <Progress
                                max={allocatedBytes}
                                value={dataUsageBytes > allocatedBytes ? allocatedBytes : dataUsageBytes}
                            />
                        </ProgressWithValue>
                    ),
                })
        )
        .addField(
            new FieldBuilder()
                .field('totalUsageBytes')
                .title('Total Usage')
                .category(SPACE_TAB)
                .column({ render: totalUsageBytes => numeral(totalUsageBytes).format('0.00 b') })
        )
        .valueOf();
}
