export const xAxisDataRange = (chartRangeStart, chartRangeEnd) => ({
    min: chartRangeStart.valueOf(),
    max: chartRangeEnd.valueOf(),
});

export default function (chartRangeStart, chartRangeEnd) {
    return () => ({
        xAxis: {
            ...xAxisDataRange(chartRangeStart, chartRangeEnd),
        },
    });
}
