const _ = require('underscore');
var $ = require('jquery');
var Backbone = require('backbone');
var Marionette = require('backbone.marionette');
var numeral = require('numeral');
var AsyncView = require('app/common/AsyncView');
var PerformanceTimeseries = require('./PerformanceTimeseries');
var IopsChartGroup = require('./IopsChartGroup');
var LatencyChart = require('./LatencyChart');
var MbpsChart = require('./MbpsChart');
var EventsChart = require('./EventsChart');
var ArrayEventsCollection = require('app/collections/events/ArrayEventsCollection');
var PoolEventsCollection = require('app/collections/events/PoolEventsCollection');
var ErrorView = require('../../../common/ErrorView');
var moment = require('moment');
var tpl = require('raw-loader!./PerformanceChartView.html');
var tooltipTpl = require('raw-loader!./Tooltip.html');
var arrayEventsTooltipTpl = require('raw-loader!./ArrayEventsTooltip.html');
var OverlaySpinner = require('../../../common/OverlaySpinner');

var tooltipTemplate = _.template(tooltipTpl);
var arrayEventsTooltipTemplate = _.template(arrayEventsTooltipTpl);

var LOCAL_TIMEZONE = moment.tz(moment.tz.guess()).format('z');

var TIMESTAMP_FORMATS = {
    minute: 'L hh:mmA',
    hour: 'L hA',
    day: 'L',
};

/**
 * Some references on stacked/synchronized charts:
 * http://www.highcharts.com/stock/demo/candlestick-and-volume
 * http://www.highcharts.com/demo/synchronized-charts
 */

var View = Marionette.LayoutView.extend({

    template: _.template(tpl),

    templateHelpers() {
        return { numeral };
    },

    className: 'performance-chart-view',

    regions: {
        iops: '[data-region="iops"]',
        latency: '[data-region="latency"]',
        mbps: '[data-region="mbps"]',
        arrayEvents: '[data-region="events"]',
        error: '[data-errors]',
        spinner: '[data-region="spinner"]',
    },

    ui: {
        noData: '[data-no-data]',
        crosshairContainer: '[data-crosshair-container]',
    },

    chartDefaults: function (options) {
        var resolution = moment.duration(1, 'minutes');
        var timestampFormat = TIMESTAMP_FORMATS.minute;
        if (this.collection.length > 1) {
            resolution = moment.duration(this.collection.at(1).get('timestamp') - this.collection.at(0).get('timestamp'));
            if (resolution.asDays() >= 1) {
                timestampFormat = TIMESTAMP_FORMATS.day;
            } else if (resolution.asHours() >= 1) {
                timestampFormat = TIMESTAMP_FORMATS.hour;
            }
        }

        options = _.defaults(options || {}, {
            height: 150,
            type: 'line',
            title: {},
            tooltipTemplate: tooltipTemplate,
        });

        return {
            global: {
                useUTC: false,
            },
            credits: {
                enabled: false,
            },
            rangeSelector: {
                enabled: false,
            },
            navigator: {
                enabled: false,
            },
            scrollbar: {
                enabled: false,
            },
            chart: {
                animation: false,
                backgroundColor: null,
                height: options.height,
                spacingLeft: 0,
                marginLeft: 80,
                marginTop: 20,
                type: options.type,
                panning: false,
                zoomType: 'x',
                events: {
                    selection: this.onChartSelect,
                },
                reflow: false,
            },
            plotOptions: {
                series: {
                    animation: false,
                    enableMouseTracking: false,
                    dataGrouping: {
                        // Must disable dataGrouping for colorByPoint to respect our per-point configured colors
                        enabled: false,
                    },
                    turboThreshold: 0,
                    cursor: 'default',
                    pointRange: resolution.asMilliseconds(),
                    // 'between' is more visually accurate than the default (centered) since the backend rollup is done by timestamp truncation and not rounding
                    pointPlacement: 'between',
                },
                column: {
                    pointPadding: 0,
                    groupPadding: 0.01,
                    borderWidth: 0,
                    minPointLength: 3,
                    colorByPoint: true,
                },
                line: {},
                area: {
                    stacking: 'normal',
                },
                scatter: {
                    //stickyTracking: false
                },
            },
            title: _.defaults(options.title, {
                text: null,
                align: 'left',
                floating: true,
                x: 80,
                y: 0,
                useHTML: true,
                margin: 0,
                style: {
                    fontSize: '13px',
                    padding: 0,
                },
            }),
            tooltip: {
                animation: false,
                hideDelay: 0,
                backgroundColor: 'none',
                shadow: false,
                borderWidth: 0,
                style: {
                    padding: 0,
                },
                shared: true,
                useHTML: true,
                positioner: function () {
                    return {
                        x: this.chart.plotLeft,
                        y: 0,
                    };
                },
                formatter: function () {
                    var template = options.tooltipTemplate;
                    var chart = this.points[0].series.chart;
                    return template({
                        numeral,
                        points: this.points,
                        width: chart.plotWidth + 'px',
                        total: this.total,
                        timestamp: `${moment(this.x).format(timestampFormat)} ${LOCAL_TIMEZONE}`,
                    });
                },
            },
            yAxis: {
                opposite: false,
                showLastLabel: true,
                min: 0,
            },
            xAxis: {
                min: this.collection.first().get('timestamp'),
                max: this.collection.last().get('timestamp'),
                ordinal: false,
            },
        };
    },

    events: {
        'mousemove [data-chart-synchronized]': 'onMouseMove',
        'mouseleave [data-chart-synchronized]': 'onMouseLeave',
    },

    initialize: function (options) {
        _.bindAll(this, 'onDocumentMouseMove', 'onChartSelect', 'onMouseMove');
        this.collection = new PerformanceTimeseries({}, {
            poolUid: options.poolUid,
            arrayModel: options.arrayModel,
            rangeModel: options.rangeModel,
        });

        if (options.arrayModel.get('serialNumber')) {
            this.eventsCollection = ArrayEventsCollection.get(options.arrayModel.get('serialNumber'));
        } else {
            this.eventsCollection = PoolEventsCollection.get(options.poolUid);
        }

        $(document).on('mousemove.nmbl.synchronizedChart', this.onDocumentMouseMove);
        this.listenTo(options.rangeModel, 'change', this.onRangeModelChange);
        $(window).on('resize', this.onWindowResize.bind(this));
        this.listenTo(this.collection, 'sync', () => {
            if (this.collection.length) {
                const startTimestamp = this.collection.first().get('timestamp');
                const endTimestamp = this.collection.last().get('timestamp');
                this.getOption('rangeModel').setActualDataRange(startTimestamp, endTimestamp);
            } else {
                this.getOption('rangeModel').setActualDataRange();
            }
        });
    },

    onRender: function () {
        this.spinner = new OverlaySpinner();
        this.showChildView('spinner', this.spinner);
        this.renderCharts();
        this.rendered = true;
    },

    useCustomCrosshair: function () {
        return !this.eventsChart.isEmpty();
    },

    renderCharts: function () {
        if (!this.collection.length) {
            this.$('.chart-content').addClass('hide');
            this.ui.noData.removeClass('hide');
            this.hasCharts = false;
            return;
        } else {
            this.$('.chart-content').removeClass('hide');
            this.ui.noData.addClass('hide');
            this.hasCharts = true;
        }

        var childViewOptions = {
            collection: this.collection,
            eventsCollection: this.eventsCollection,
            chartDefaults: this.chartDefaults.bind(this),
            allFlash: false, //TODO: fetch allFlash property
        };

        this.showChildView('iops', new IopsChartGroup(childViewOptions));
        this.showChildView('latency', new LatencyChart(childViewOptions));
        this.showChildView('mbps', new MbpsChart(childViewOptions));

        this.eventsChart = new EventsChart(childViewOptions);
        this.getRegion('arrayEvents').empty();
        if (this.eventsChart.isEmpty()) {
            this.$('[data-region="events"]').closest('.chart-group').hide();
        } else {
            this.showChildView('arrayEvents', this.eventsChart);
            this.$('[data-region="events"]').closest('.chart-group').show();
        }

        this.getRegion('spinner').$el.hide();
        this.$charts = this.$('[data-highcharts-chart][data-chart-synchronized]');
    },

    onAttach: function () {
        if (this.collection.length) {
            this.$charts.each(function () {
                $(this).highcharts().reflow();
            });

            if (this.useCustomCrosshair()) {
                this.createCrosshair();
            }
        }
    },

    onRangeModelChange: function (model, { noRefresh }) {
        if (!this.rendered || noRefresh) {
            return;
        }

        this.fetching = true;
        this.showSpinner();
        $.when(
            this.collection.fetch({ reset: true }),
            this.eventsCollection.fetch({ reset: true }),
        )
            .done(this.onCollectionSync.bind(this))
            .fail(this.onError.bind(this));
    },

    showSpinner: function () {
        this.getRegion('spinner').$el.show();
        this.spinner.showSpinner();
    },

    hideSpinner: function () {
        this.spinner.hideSpinner();
    },

    hideCrosshair: function () {
        if (!this.crosshairEl) {
            // Avoid console errors when client moves mouse over [data-chart-synchronized]
            // before the chart is created
            return;
        }

        this.crosshairEl.style.visibility = 'hidden';
    },

    showCrosshairAt: function (location) {
        if (!this.crosshairEl) {
            // Avoid console errors when client moves mouse over [data-chart-synchronized]
            // before the chart is created
            return;
        }

        const offsetLeft = location - this.el.getBoundingClientRect().left;
        this.crosshairEl.style.left = offsetLeft + 'px';
        this.crosshairEl.style.visibility = 'visible';
    },

    showEventsOnCrosshair: function (point) {
        // Remove tooltip for previous point
        if (this.eventFlagsEl.getAttribute('data-x') !== point.x) {
            this.eventFlagsEl.innerHTML = null;
            this.eventFlagsEl.classList.remove('right', 'left');
        }

        // Create tooltip
        if (!this.eventFlagsEl.childElementCount) {
            this.eventFlagsEl.innerHTML = arrayEventsTooltipTemplate({
                points: point.arrayEvents || [point],
                color: point.color,
                date: moment(point.x).format('L'),
            });
            this.crosshairEl.classList.add('highlight');
            $(this.crosshairEl).find('[data-crosshair-segment]').css({
                'border-left-color': point.color,
            });

            var widthOverflows = this.eventFlagsEl.getBoundingClientRect().right > window.innerWidth;
            this.eventFlagsEl.classList.add(widthOverflows ? 'right' : 'left');
        }
    },

    hideCrosshairEvents: function () {
        this.crosshairEl.classList.remove('highlight');
        $(this.crosshairEl).find('[data-crosshair-segment]').css({
            'border-left-color': '',
        });
        this.eventFlagsEl.innerHTML = null;
    },

    createCrosshair: function () {
        if (this.crosshairEl) {
            this.crosshairEl.parentNode.removeChild(this.crosshairEl);
            this.crosshairEl = undefined;
        }

        // Crosshair segments are absolutely positioned relative to the parent view's positioning context
        const viewTop = this.el.getBoundingClientRect().top;

        var segmentOptions = this.$charts.get().map(function (el) {
            var isEventsChart = el.getAttribute('data-chart') === 'events';
            var tooltipHeight = isEventsChart ? 0 : 25;
            const chartTopOffset = el.getBoundingClientRect().top - viewTop;

            // We can't simply use offsetTop here since SVG elements don't have that property
            const xAxisTopOffset = el.querySelector('.highcharts-xaxis-labels').getBoundingClientRect().top - viewTop;

            return {
                top: chartTopOffset + tooltipHeight,
                bottom: xAxisTopOffset,
                linkTooltip: isEventsChart,
            };
        });

        if (!this.crosshairEl) {
            var container = document.createElement('div');
            container.setAttribute('data-crosshair', '');
            container.style.visibility = 'hidden';
            this.ui.crosshairContainer[0].appendChild(container);

            this.crosshairEl = container;
        }

        segmentOptions.forEach(function (bounds) {
            var el = document.createElement('div');
            el.setAttribute('data-crosshair-segment', '');
            el.style.height = bounds.bottom - bounds.top + 'px';
            el.style.top = bounds.top + 'px';

            if (bounds.linkTooltip) {
                this.eventFlagsEl = document.createElement('div');
                this.eventFlagsEl.classList.add('events');
                this.eventFlagsEl.style.bottom = this.crosshairEl.clientHeight - bounds.top + 'px';
                this.crosshairEl.appendChild(this.eventFlagsEl);
            }

            this.crosshairEl.appendChild(el);
            return el;
        }.bind(this));
    },

    onMouseMove: function (event) {
        var self = this;

        if (this.fetching || !this.hasCharts) {
            return;
        }

        // All charts have the same x-axis configuration, so arbitrarily pick a chart and
        // translate the pointer's viewport coordinate to a value on the x-axis.
        // Example: 300px = 1478447377443.6091 = "2016-11-06T10:49:37-05:00"
        var pointerCoordinate = this.$charts.first().highcharts().xAxis[0].toValue(event.offsetX);

        var proximalPoints = this.$charts.get().map(function (el) {
            var chart = $(el).highcharts();
            var normalizedEvent = chart.pointer.normalize(event.originalEvent); // Find coordinates within the chart

            var points = chart.series.map(function (series) {
                return series.searchPoint(normalizedEvent, true);
            });

            points = _.compact(points);

            return {
                chart: chart,
                el: el,
                points: points,
                proximity: points.length ? Math.abs(points[0].x - pointerCoordinate) : Infinity,
                isEventsChart: el.getAttribute('data-chart') === 'events',
            };
        });

        // Use the closest point to the pointer. If a chart's data is truncated compared to other charts, this
        // prevents cases where the currently selected point (showing tooltips etc) is not the same point the
        // crosshair is on for other charts.
        var minProximity = _(proximalPoints).min(_.property('proximity')).proximity;
        var crosshairDrawn = false;

        proximalPoints.forEach(function (point) {
            var points = point.points;
            var chart = point.chart;

            if (minProximity !== point.proximity || !points.length) {
                chart.pointer.reset(); // Deselect any points and remove tooltip
                return;
            }

            points.forEach(x => x.onMouseOver());

            if (chart.tooltip) {
                chart.tooltip.refresh(points);
            }

            if (self.useCustomCrosshair()) {
                if (crosshairDrawn) {
                    return;
                }

                // x-Axis is always before the y-Axis in SVG, but this could change with a highcharts upgrade
                // Highcharts 5 gives each element specific attributes, so it should be trivial to update.
                var seriesBounds = point.el.querySelector('g.highcharts-axis').getBoundingClientRect();
                var leftEdge = seriesBounds.left + window.scrollX;
                var crosshairLocation = points[0].plotX + leftEdge - 1; // Place line over point center

                self.showCrosshairAt(crosshairLocation);

                var eventAtPoint = self.eventsChart.getPointsAt(points[0].x);
                if (eventAtPoint) {
                    self.showEventsOnCrosshair(eventAtPoint);
                } else {
                    self.hideCrosshairEvents();
                }

                crosshairDrawn = true;
            } else {
                chart.xAxis[0].drawCrosshair(event, points[0]);
            }
        });

        event.stopPropagation();
    },

    onMouseLeave: function (event) {
        if (this.fetching || !this.hasCharts) {
            return;
        }

        if (this.useCustomCrosshair()) {
            this.hideCrosshair();
        }

        this.$charts.each(function () {
            var chart = $(this).highcharts();
            // A catch-all to hide point markers, tooltip, and crosshair.
            chart.pointer.reset();
        });

        event.stopPropagation();
    },

    onWindowResize: _.debounce(function () {
        if (this.collection.length) {
            this.$charts.each(function () {
                $(this).highcharts().reflow();
            });

            if (this.useCustomCrosshair()) {
                this.createCrosshair();
            }
        }
    }, 50),

    onDocumentMouseMove: function (event) {
        // Workaround for onMouseLeave not being called in certain scenarios
        // See Highcharts.onDocumentMouseMove and https://github.com/highcharts/highcharts/issues/149
        this.onMouseLeave(event);
    },

    onDestroy: function () {
        $(document).off('mousemove.nmbl.synchronizedChart', this.onDocumentMouseMove);
    },

    onChartSelect: function (event) {
        event.preventDefault();

        this.triggerMethod('dragZoom', {
            min: moment(event.xAxis[0].min).toDate(),
            max: moment(event.xAxis[0].max).toDate(),
        });
    },

    onCollectionSync: function () {
        if (this.rendered && this.fetching) {
            this.renderCharts();
            this.$charts.each(function () {
                $(this).highcharts().reflow();
            });
            this.createCrosshair();
            this.fetching = false;
            this.hideSpinner();
        }
    },

    onError: function (jqXhr) {
        var view = new ErrorView({
            model: new Backbone.Model({
                message: 'Error fetching data',
                error: jqXhr && jqXhr.responseJSON ? jqXhr.responseJSON.message : null,
            }),
        });
        this.showChildView('error', view);
        this.fetching = false;
        this.hideSpinner();
    },
});

module.exports = AsyncView.extend({
    ViewConstructor: View,

    load: function () {
        var deferred = $.Deferred();

        $.when(
            this.view.collection.fetch(),
            this.view.eventsCollection.fetch(),
        )
            .fail(_.bind(deferred.reject, deferred))
            .done(_.bind(deferred.resolve, deferred, this.view));

        return deferred;
    },

});
