import React from 'react';
import PropTypes from 'prop-types';
import { isNumber } from 'underscore';
import style from '../../shared.scss';

function RecentIssues({ className, recentIssuesCount }) {
    if (!isNumber(recentIssuesCount)) {
        return null;
    }

    return (
        <div className={className}>
            <div className={style.subtitle}>Recent Issues</div>
            <div className={style.metric}>
                <span className={style.value}>{recentIssuesCount || 'N/A'}</span>
            </div>
        </div>
    );
}

RecentIssues.propTypes = {
    recentIssuesCount: PropTypes.number.isRequired,
};

export default RecentIssues;
