import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import numeral from 'numeral';
import style from './FolderUsageBarChart.scss';
import { SNAPSHOT_USAGE_COLOR, VOLUME_USAGE_COLOR, LIMIT_COLOR, FREE_SPACE_COLOR } from '../../util';

const cx = classnames.bind(style);

// TODO Might be extracted at some point. Array GUI doesn't use highcharts legends; they are custom.
const LegendItem = ({ swatchClassName, color, name, value }) => {
    const [displayValue, unit] = numeral(value).format('0.00 ib').split(' ');

    return (
        <div className={classnames(cx('legend'))}>
            <span className={cx('legend-name')}>
                <i className={classnames(swatchClassName, cx('swatch'))} style={{ backgroundColor: color }} />
                {name}
            </span>
            <span className={cx('legend-value')}>{displayValue}</span>
            <span className={cx('legend-unit')}>{unit}</span>
        </div>
    );
};

LegendItem.propTypes = {
    color: PropTypes.string,
    name: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    swatchClassName: PropTypes.string,
};

// TODO convert to a common component BarChart that doesn't use Highcharts
const FolderUsageBarChart = ({ className, volumeUsage, snapshotUsage, unusedReserve, limit }) => {
    let includeLimitCaret = true;
    let total = (volumeUsage || 0) + (snapshotUsage || 0) + (unusedReserve || 0);
    let freeSpace = limit - total;

    if (freeSpace > 0) {
        total = limit;
        includeLimitCaret = false;
    } else {
        freeSpace = 0;
    }

    const percentage = value => numeral(value / total).format('0%');

    const limitPercent = percentage(limit);
    const volumePercent = percentage(volumeUsage);
    const snapshotPercent = percentage(snapshotUsage);
    const unusedReservePercent = percentage(unusedReserve);

    return (
        <div className={className}>
            <div className={cx('bar-chart')}>
                <div
                    className={cx('bar', { 'is-zero': volumePercent === '0%' })}
                    style={{ width: volumePercent, backgroundColor: VOLUME_USAGE_COLOR }}
                />
                <div
                    className={cx('bar', { 'is-zero': snapshotPercent === '0%' })}
                    style={{ width: snapshotPercent, backgroundColor: SNAPSHOT_USAGE_COLOR }}
                />
                {!!unusedReserve && (
                    <div
                        className={cx('striped', 'bar', { 'is-zero': unusedReservePercent === '0%' })}
                        style={{ width: unusedReservePercent }}
                    />
                )}
                {includeLimitCaret && (
                    <div className={cx('caret')} style={{ left: limitPercent }}>
                        <i className="fa fa-caret-up" style={{ position: 'relative', left: '-0.4ch' }} />
                    </div>
                )}
            </div>
            <div className={cx('legend-container')}>
                <LegendItem name="Volume Usage" value={volumeUsage} color={VOLUME_USAGE_COLOR} />
                <LegendItem name="Snapshot Usage" value={snapshotUsage} color={SNAPSHOT_USAGE_COLOR} />
                {!!unusedReserve && (
                    <LegendItem
                        name="Unused Reserve"
                        value={unusedReserve}
                        swatchClassName={cx('striped')}
                        color="white"
                    />
                )}
                <LegendItem name="Limit" value={limit} color={LIMIT_COLOR} />
                {!!freeSpace && <LegendItem name="Free Space" value={freeSpace} color={FREE_SPACE_COLOR} />}
            </div>
        </div>
    );
};

FolderUsageBarChart.propTypes = {
    volumeUsage: PropTypes.number,
    snapshotUsage: PropTypes.number,
    unusedReserve: PropTypes.number,
    limit: PropTypes.number,
};

export default FolderUsageBarChart;
