import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import { findWhere } from 'underscore';
import {
    ARRAY_FETCH,
    ARRAY_HARDWARE_UPGRADE_RECOMMENDATIONS_FETCH,
    RESET,
} from './constants';
import Request from '../../../../services/Request';
import { firstOrNullData } from '../../../../util/genericQuery';

function fetchArrayDetail(scope) {
    return new Request().pod().genericQuery('array', ['detail'])
        .post({ scope });
}

function fetchHeartbeat(scope) {
    return new Request().pod()
        .array(scope.id)
        .urlSegment('heartbeat')
        .get();
}

function fetchAsupTime(scope) {
    return new Request().pod()
        .array(scope.id)
        .urlSegment('lastAsupReceivedTimestamp')
        .get();
}

function compareShelfNames(a, b) {
    if (a.assetType === b.assetType) {
        return (a.shelfSn > b.shelfSn);
    }
    return a.assetType > b.assetType;
}

export const getArrayDetail = arraySn => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(ARRAY_FETCH, dispatch);
    dispatchStart();
    let scope;
    if (arraySn) {
        scope = { type: 'array', id: arraySn };
    }

    new Request().all([
        fetchArrayDetail(scope),
        fetchHeartbeat(scope),
        fetchAsupTime(scope),
    ])
        .then(([arrays, heartbeats, asupTime]) => {
            const arraysData = (arrays && arrays.data && arrays.data.data) || [];

            // get head shelf
            const arrayWithShelves = findWhere(arraysData, { assetType: 'Array' }) || {};

            // get all shelves sorted by head shelf first, then shelfSn
            arrayWithShelves.shelves = arraysData.sort(compareShelfNames);

            const heartbeatsData = (heartbeats && heartbeats.data) || {};
            const asupTimeObj = (asupTime && asupTime.data) || {};
            return { ...arrayWithShelves, latestHeartBeat: heartbeatsData[arraySn], lastAsupReceivedTime: asupTimeObj[arraySn] };
        })
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const getArrayHardwareUpgradeRecommendations = arraySn => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(ARRAY_HARDWARE_UPGRADE_RECOMMENDATIONS_FETCH, dispatch);
    dispatchStart();
    let scope;
    if (arraySn) {
        scope = { type: 'array', id: arraySn };
    }

    new Request().pod()
        .genericQuery('array', ['hardwareUpgradeRecommendations'])
        .post({ scope })
        .then(firstOrNullData)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const reset = () => ({ type: RESET });
