import { createReducersForAction, createSelector, getInitialState } from '@infosight/elmer/dist/utils/redux';
import rootState from '../rootStateSelector';
import {
    FETCH_ALL_ARRAYS,
    RESET,
} from './constants';

const initialState = {
    ...getInitialState('accounts'),
    ...getInitialState('accountsArrays'),
};

const ACTION_HANDLERS = {
    [RESET]: () => (initialState),
    ...createReducersForAction({ type: FETCH_ALL_ARRAYS, stateKey: 'accountsArrays' }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

const baseSelector = state => rootState(state).resellerArrays;
export const accountsCollectionSelector = createSelector(baseSelector, 'accountsArrays');
