export const folderContextSelector = state => state.capacityReport.folderContext;

export const isLoadingSelector = (state) => {
    const { loadingTimeSeries, loadingApplications, loadingSummary } = folderContextSelector(state);
    return loadingTimeSeries || loadingApplications || loadingSummary;
};

export default {
    folderContextSelector,
    isLoadingSelector,
};
