import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { isNumber } from 'underscore';
import Bar from './Bar';

const StackedProgress = ({ className, values, min, max, colors, context }) => {
    if (!isNumber(min) || !isNumber(max) || values.length !== colors.length) {
        return null;
    }

    return (
        <div
            className={classnames(className, 'progress', context)}
            aria-valuemax={max}
            aria-valuemin={min}
            role="progressbar"
        >
            {values.map((value, index) => (
                <Bar key={colors[index]} value={value} color={colors[index]} min={min} max={max} />
            ))}
        </div>
    );
};

// Either percent | value & number are required. percent is primary
StackedProgress.propTypes = {
    min: PropTypes.number,
    max: PropTypes.number,
    values: PropTypes.arrayOf(PropTypes.number),
    colors: PropTypes.arrayOf(PropTypes.string),
    context: PropTypes.oneOf(['chart', 'thin']),
};

StackedProgress.defaultProps = {
    min: 0,
    max: 100,
    context: 'thin',
};

export default StackedProgress;
