import { findIndex, findWhere } from 'underscore';
import { createReducersForAction, getInitialState } from '@infosight/elmer/dist/utils/redux';

import {
    LOAD_PREFERENCES,
    DISABLE_DIALOG,
    SET_NOTIFICATION,
    ACCEPT_EULA,
} from './constants';

const initialState = {
    ...getInitialState('preferences'),
    dialogs: [],
    acceptedEulas: [],
    notifications: {},
};

const upsertById = (arr, obj) => {
    const idx = findIndex(arr, obj.id);
    if (idx > -1) {
        return [...arr.slice(0, idx), obj, arr.slice(idx + 1)];
    }
    return [...arr, obj];
};

const ACTION_HANDLERS = {
    ...createReducersForAction({ type: LOAD_PREFERENCES, stateKey: 'preferences' }),
    [DISABLE_DIALOG]: (state, { payload: id }) => ({
        ...state,
        dialogs: upsertById(state.dialogs, { id, visible: false }),
    }),
    [SET_NOTIFICATION]: (state, { payload: { type, subtype, value } }) => ({
        ...state,
        notifications: {
            ...state.notifications,
            [type]: !subtype ? value : {
                ...state.notifications[type],
                [subtype]: value,
            },
        },
    }),
    [ACCEPT_EULA]: (state, { payload: eula }) => ({
        ...state,
        acceptedEulas: [...state.acceptedEulas, eula],
    }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

export const preferencesSelector = state => state.preferences;
export const alreadyFetchedPreferences = state => preferencesSelector(state).loadingPreferences || preferencesSelector(state).loadedPreferences;

export const getNotification = (state, { type, subtype }) => {
    const { notifications } = preferencesSelector(state);
    if (!notifications) {
        return undefined;
    }

    return subtype ? (notifications[type] || {})[subtype] : notifications[type];
};

export const isDialogEnabled = (state, id) => {
    const pref = findWhere(preferencesSelector(state).dialogs, { id });
    return preferencesSelector(state).loadedPreferences && (!pref || pref.visible);
};

export const isEulaAccepted = (state, key) => {
    const { acceptedEulas } = preferencesSelector(state);
    return acceptedEulas && acceptedEulas.includes(key);
};
