import React from 'react';
import PropTypes from 'prop-types';
import Trends from './Trends';
import { daysUntilFullText } from './DaysUntilFull';

function FolderTrends({ className, fullCapacityReachDays }) {
    const trends = [];

    const dayText = daysUntilFullText(fullCapacityReachDays);
    if (dayText) {
        trends.push({ type: 'warning', message: `Full capacity in ${dayText}` });
    } else {
        trends.push({ type: 'success', message: 'No capacity risk' });
    }

    return <Trends className={className} trends={trends} />;
}

FolderTrends.propTypes = {
    fullCapacityReachDays: PropTypes.number,
};

export default FolderTrends;
