import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { toTimeRangeSelection } from '@infosight/elmer/dist/components/TimeRangeSelector';
import ChartCard from '@infosight/elmer/dist/components/Card';
import Async from '@infosight/elmer/dist/components/Async';
import NoData from '@infosight/elmer/dist/components/NoData';
import { property } from 'underscore';
import { getPoolOverviewData } from '../../actionCreators';
import { poolOverviewSelector, poolSelector } from '../../reducer';
import { OBJECTS } from '../../../../inventory/constants';
import { SynchronizedChartMediator, SynchronizedChart } from '../../../../../../modules/charts/SyncChart';
import { computeUnionedTimeRange } from './util';
import OverviewCapacityChart from './OverviewCapacityChart';
import PoolSaturationChart from './PoolSaturationChart';
import PoolEventsChart from './EventsChart';

export class OverviewContainer extends Component {
    componentDidMount() {
        const { loadingPoolOverview, loadedPoolOverview, scope, config } = this.props;

        // Fetch the data only if its not loaded already
        if (!loadingPoolOverview && !loadedPoolOverview) {
            const poolArrayList = this.getPoolChildArrays();
            const eventFilters = [
                {
                    field: 'category',
                    $in: ['SOFTWARE', 'CAPACITY', 'HARDWARE'],
                }, {
                    field: 'verbosity',
                    $lte: 2,
                },
            ];
            const timeRange = toTimeRangeSelection({ duration: 'P1Y' });
            this.props.getPoolOverviewData({ scope: config.convertToGenericQueryScope(scope), timeRange, eventFilters, poolArrayList });
        }
    }

    getPoolChildArrays() {
        const { scope, config } = this.props;
        const poolUid = scope && scope.id;
        let childArraySerialNumbers;
        /*
            Ideally we should be able to fetch this data with just the poolid.
            But for now we are still using the legacy api as this will be soon replaced
            and the legacy api's source table doesn't support using pool uid
        */
        if (poolUid) {
            const topology = config.getObjectTopology(OBJECTS.POOL, poolUid);
            childArraySerialNumbers = topology && topology.children[OBJECTS.ARRAY];
        }
        return childArraySerialNumbers;
    }

    render() {
        const { loadingPoolOverview, poolOverview, pool, loadedPoolOverview } = this.props;
        return (
            <Async loading={loadingPoolOverview}>
                <NoData hasData={loadedPoolOverview && !!poolOverview}>
                    {() => {
                        const { poolCpuCacheUsage, poolCapacity, poolEvents } = poolOverview;
                        const showCacheChart = (!pool || (pool && !pool.allFlash));
                        const { start: chartRangeStart, end: chartRangeEnd } = computeUnionedTimeRange({ poolEvents, poolCpuCacheUsage, poolCapacity });

                        return (
                            <SynchronizedChartMediator>
                                <ChartCard
                                    title="Events"
                                    className="card-section-no-top-padding card-section-no-bottom-padding card-section-no-right-padding"
                                    style={{ marginBottom: 2, marginTop: 4 }}
                                >
                                    <div style={{ height: 80 }}>
                                        <SynchronizedChart>
                                            <PoolEventsChart
                                                poolEvents={poolEvents}
                                                spacingLeft={62}
                                                chartRangeStart={chartRangeStart}
                                                chartRangeEnd={chartRangeEnd}
                                            />
                                        </SynchronizedChart>
                                    </div>
                                </ChartCard>
                                <ChartCard
                                    title="CPU Saturation"
                                    className="card-section-no-top-padding card-section-no-bottom-padding card-section-no-right-padding"
                                    style={{ marginBottom: 2, marginTop: 2 }}
                                >
                                    <div className="chart-container--small">
                                        <SynchronizedChart>
                                            <PoolSaturationChart
                                                arrayRecommendations={poolCpuCacheUsage}
                                                spacingLeft={20}
                                                getDataKey={property('cpu')}
                                                chartRangeStart={chartRangeStart}
                                                chartRangeEnd={chartRangeEnd}
                                            />
                                        </SynchronizedChart>
                                    </div>
                                </ChartCard>

                                { showCacheChart &&
                                <ChartCard
                                    title="Cache Saturation"
                                    className="card-section-no-top-padding card-section-no-bottom-padding card-section-no-right-padding"
                                    style={{ marginBottom: 2, marginTop: 2 }}
                                >
                                    <div className="chart-container--small">
                                        <SynchronizedChart>
                                            <PoolSaturationChart
                                                arrayRecommendations={poolCpuCacheUsage}
                                                spacingLeft={20}
                                                getDataKey={property('cache')}
                                                chartRangeStart={chartRangeStart}
                                                chartRangeEnd={chartRangeEnd}
                                            />
                                        </SynchronizedChart>
                                    </div>
                                </ChartCard>
                                }

                                <ChartCard
                                    title="Capacity Usage"
                                    className="card-section-no-top-padding card-section-no-bottom-padding card-section-no-right-padding"
                                    style={{ marginBottom: 2, marginTop: 2 }}
                                >
                                    <div className="chart-container--medium">
                                        <SynchronizedChart>
                                            <OverviewCapacityChart
                                                poolCapacity={poolCapacity}
                                                includeSavingsSeries={false}
                                                isLineChart
                                                chartRangeStart={chartRangeStart}
                                                chartRangeEnd={chartRangeEnd}
                                            />
                                        </SynchronizedChart>
                                    </div>
                                </ChartCard>
                            </SynchronizedChartMediator>);
                    }}
                </NoData>
            </Async>
        );
    }
}

OverviewContainer.propTypes = {
    loadingPoolOverview: PropTypes.bool,
    loadedPoolOverview: PropTypes.bool,
    getPoolOverviewData: PropTypes.func.isRequired,
    poolOverview: PropTypes.shape({
        poolCapacity: PropTypes.shape({
            data: PropTypes.shape({
                timeseries: PropTypes.arrayOf(PropTypes.shape({
                    timestamp: PropTypes.number,
                    usedBytes: PropTypes.number,
                    allocatedBytes: PropTypes.number,
                    totalSavingsBytes: PropTypes.number,
                })).isRequired,
            }).isRequired,
            meta: PropTypes.shape({
                time: PropTypes.shape({
                    start: PropTypes.number.isRequired,
                    end: PropTypes.number.isRequired,
                }).isRequired,
            }).isRequired,
        }),
        poolEvents: PropTypes.shape({
            timestamp: PropTypes.number,
            eventCollection: PropTypes.arrayOf(PropTypes.shape({
                timestamp: PropTypes.number,
                arraySn: PropTypes.string,
                category: PropTypes.string,
                verbosity: PropTypes.string,
                eventDetails: PropTypes.string,
                change: PropTypes.string,
                previousValue: PropTypes.string,
                currentValue: PropTypes.string,
            })),
        }),
        poolCpuCacheUsage: PropTypes.arrayOf(PropTypes.shape({
            serialNumber: PropTypes.string,
            hostname: PropTypes.string,
            model: PropTypes.string,
            cache: PropTypes.arrayOf(PropTypes.shape({
                ts: PropTypes.number,
                usage: PropTypes.number,
            })),
            cpu: PropTypes.arrayOf(PropTypes.shape({
                ts: PropTypes.number,
                usage: PropTypes.number,
            })),
        })),
    }),
    pool: PropTypes.shape({
        allFlash: PropTypes.bool,
    }),
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),
    config: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    ...poolOverviewSelector(state),
    ...poolSelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    getPoolOverviewData,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(OverviewContainer);
