/* eslint-disable react/no-unused-state */

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import numeral from 'numeral';
import chroma from 'chroma-js';
import { pick, pluck, compact, flatten } from 'underscore';
import Legend from '@infosight/elmer/dist/components/Legend';
import { MINUTE_MOMENT_FORMAT } from '@infosight/elmer/dist/utils/time';
import { minWidthPixelsSelector } from 'progeny/modules/user/reducer';
import { getStore } from 'progeny/util/storeProvider';
import { CHART_COLORS } from '../../../util/chartColors';
import { findPoints } from '../../charts/util/synchronization';
import HighchartsWrapper, { POINT_BORDER_WIDTH_PIXELS } from './HighchartsWrapper';

// Give each series a fixed height to align the legend/axis labels
const SERIES_HEIGHT_PIXELS = 24;

// Fix a cell width to acheive optimal data resolution
const POINT_WIDTH_PIXELS = 6;

// Total horizontal space used by each cell
const POINT_WIDTH = POINT_WIDTH_PIXELS + POINT_BORDER_WIDTH_PIXELS;

export const formatValue = (value) => {
    if (value === undefined || value === null) {
        return 'N/A';
    }

    return numeral(value).format('0,0.00');
};

export const buildDefaultPointValues = ({ baseTimeSeries, field, min, max, minColor, maxColor }) => {
    if (!baseTimeSeries) {
        return {};
    }

    const aggregatedValues = pluck(flatten(pluck(baseTimeSeries, 'timeseries')), field);
    if (typeof min !== 'number') {
        min = Math.min(...aggregatedValues);
    }

    if (typeof max !== 'number') {
        max = Math.max(...aggregatedValues);
    }

    if (max === 0) {
        maxColor = minColor;
    }

    const chromaScale = chroma.scale([minColor, maxColor]).domain([min, max]);

    return baseTimeSeries.reduce((memo, set) => {
        const value = set[field];
        const color = chromaScale(value);
        memo[set.id] = { value, color };
        return memo;
    }, {});
};

// TODO Test this
export default class CorrelationHeatmapTimeSeries extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        const defaultPointValues = buildDefaultPointValues(this.props);
        this.state = {
            defaultPointValues,
            pointValues: defaultPointValues,
            pointTimestamp: null,
            isPointHovered: false,
        };
    }

    componentDidMount() {
        if (this.props.chartSync) {
            this.props.chartSync.monitorChart(this, { syncPoints: this.syncPoints, resetPoints: this.resetPoints });
        }
    }

    componentWillReceiveProps(newProps) {
        const { baseTimeSeries, field } = newProps;
        if (this.props.baseTimeSeries !== baseTimeSeries || this.props.field !== field) {
            this.setState({ defaultPointValues: buildDefaultPointValues(newProps) });
        }
    }

    componentDidUpdate(prevProps, prevState) {
        const prevPointTimestamp = prevState.pointTimestamp;
        const { pointTimestamp } = this.state;
        if (this.chart && pointTimestamp !== prevPointTimestamp) {
            // we need to do this by modifying the chart rather than with props
            // because ReactHighcharts is not truly functional and breaks
            // if you just modify the props to have a different config
            const plotLineId = 'fake-crosshair-plotline';
            this.chart.xAxis[0].removePlotLine(plotLineId);
            if (pointTimestamp) {
                this.chart.xAxis[0].addPlotLine({
                    id: plotLineId,
                    value: pointTimestamp,
                    color: CHART_COLORS.crosshairs,
                    width: 2,
                    zIndex: 10,
                });
            }
        }
    }

    componentWillUnmount() {
        if (this.props.chartSync) {
            this.props.chartSync.removeChart(this);
        }
    }

    onMouseOverPoint(opts) {
        if (!opts.point) {
            return;
        }
        if (this.props.chartSync) {
            this.props.chartSync.syncPoints(opts);
        } else {
            this.syncPoints(opts.point);
        }
    }

    onMouseOut(event) {
        if (this.props.chartSync) {
            this.props.chartSync.syncPoints({ event });
        } else {
            this.resetPoints();
        }
    }

    onAfterChartRendered(chart) {
        this.chart = chart;
        const { requestDataResolution } = this.props;

        if (requestDataResolution) {
            requestDataResolution({ maxSamples: Math.floor(chart.plotWidth / POINT_WIDTH) });
        }

        this.forceUpdate();
    }

    resetPoints() {
        this.setState(({ defaultPointValues }) => ({
            pointValues: defaultPointValues,
            pointTimestamp: null,
            isPointHovered: false,
        }));
    }

    syncPoints({ index, x }) {
        this.setState({
            pointValues: findPoints(this.chart, index, x, (point, series) => (point ? pick(point, 'value', 'color') : { color: series.color, value: null })),
            pointTimestamp: x,
            isPointHovered: true,
        });
    }

    // TODO do this only when chart size has changed
    calculateChartHeight() {
        const { baseTimeSeries } = this.props;
        let plotVerticalMargin;

        if (this.chart) {
            const { chartHeight, plotHeight } = this.chart;
            plotVerticalMargin = chartHeight - plotHeight;
        } else {
            plotVerticalMargin = 101;
        }
        return (baseTimeSeries.length * SERIES_HEIGHT_PIXELS) + plotVerticalMargin;
    }

    buildLegendItems() {
        const { unit, getObjectLinkProps, valueFormatter } = this.props;
        const { series } = this.chart;
        const { pointValues } = this.state;
        return compact(series).map((highchartsSeries) => {
            const { options: { id } } = highchartsSeries;
            return {
                id,
                unit,
                label: highchartsSeries.name,
                icon: true,
                color: !pointValues[id] || pointValues[id].value === null ? 'transparent' : pointValues[id].color,
                value: pointValues[id] && valueFormatter(pointValues[id].value),
                linkProps: getObjectLinkProps(highchartsSeries),
            };
        });
    }

    render() {
        const {
            baseTimeSeries,
            chartRangeStart,
            chartRangeEnd,
            field,
            minColor,
            maxColor,
            granularity,
            spacingLeft,
            timestampFormat,
            valueTitle,
            onChartSelect,
        } = this.props;
        let {
            min,
            max,
        } = this.props;
        const { chart } = this;
        const { pointTimestamp, isPointHovered } = this.state;

        const aggregatedValues = pluck(flatten(pluck(baseTimeSeries, 'timeseries')), field);
        if (typeof min !== 'number') {
            min = Math.min(...aggregatedValues);
        }

        if (typeof max !== 'number') {
            max = Math.max(...aggregatedValues);
        }

        return (
            <div className="row expanded" style={{ height: `${this.calculateChartHeight()}px` }}>
                <div className="column large-19">
                    <HighchartsWrapper
                        baseTimeSeries={baseTimeSeries}
                        chartRangeStart={chartRangeStart}
                        chartRangeEnd={chartRangeEnd}
                        onAfterChartRendered={this.onAfterChartRendered}
                        onMouseOverPoint={this.onMouseOverPoint}
                        onMouseOut={this.onMouseOut}
                        onChartSelect={onChartSelect}
                        granularity={granularity}
                        field={field}
                        min={min}
                        max={max}
                        minColor={minColor}
                        maxColor={max === 0 ? minColor : maxColor}
                        spacingLeft={spacingLeft}
                        containerHeight={this.calculateChartHeight()}
                    />
                </div>
                {chart && chart.series && (
                    <Legend
                        className="column large-5"
                        timestampFormat={timestampFormat}
                        items={this.buildLegendItems()}
                        timestamp={(isPointHovered && pointTimestamp) || null}
                        reserveTimestampSpace
                        height={SERIES_HEIGHT_PIXELS}
                        valueTitle={valueTitle}
                    />
                )}
            </div>
        );
    }
}

CorrelationHeatmapTimeSeries.propTypes = {
    /**
     * Each element in `props.baseTimeSeries[].` should have a `props.field` stanza, which is the aggregated value
     * Each element in `props.baseTimeSeries[].timeseries[]` should have a `props.field` stanza which is the value
     * at that point.
     */
    baseTimeSeries: PropTypes.arrayOf(PropTypes.shape({
        id: PropTypes.string,
        name: PropTypes.string.isRequired,
        timeseries: PropTypes.arrayOf(PropTypes.shape({
            timestamp: PropTypes.number.isRequired,
        })).isRequired,
    })),

    chartRangeStart: PropTypes.oneOfType([
        /**
         * Moment
         */
        PropTypes.object,
        PropTypes.number,
    ]),

    chartRangeEnd: PropTypes.oneOfType([
        /**
         * Moment
         */
        PropTypes.object,
        PropTypes.number,
    ]),

    /**
     * Callback used to request data at a specific resolution
     * @param {{maxSamples: Number}} options
     */
    requestDataResolution: PropTypes.func,
    field: PropTypes.string.isRequired,
    unit: PropTypes.string.isRequired,

    /**
     * Moment.Duration
     */
    granularity: PropTypes.object.isRequired,

    /**
     * The minimum value of the axis in terms of map point values.
     * If null, the min value is automatically calculated.
     */
    min: PropTypes.number,

    /**
     * The minimum value of the axis in terms of map point values.
     * If null, the min value is automatically calculated.
     */
    max: PropTypes.number,

    /**
     * The color to represent the minimum of the color axis.
     */
    minColor: PropTypes.string.isRequired,

    /**
     * The color to represent the maximum of the color axis.
     */
    maxColor: PropTypes.string.isRequired,

    /**
     * Get the props for a elmer.QueryLink component
     * @param highchartsSeries
     * @return {Object}
     */
    getObjectLinkProps: PropTypes.func.isRequired,

    /**
     * Format the value displayed in the point
     * @param value
     * @return {String}
     */
    valueFormatter: PropTypes.func.isRequired,

    /**
     * Some optional padding/spacing to put on the left side of the chart.
     */
    spacingLeft: PropTypes.number,

    timestampFormat: PropTypes.string,
    valueTitle: PropTypes.node,
    chartSync: PropTypes.shape({
        monitorChart: PropTypes.func.isRequired,
        removeChart: PropTypes.func.isRequired,
        syncPoints: PropTypes.func.isRequired,
    }),

    onChartSelect: PropTypes.func,
};

CorrelationHeatmapTimeSeries.defaultProps = {
    min: 0,
    minColor: CHART_COLORS.gradientMinGrey,
    maxColor: CHART_COLORS.blue,
    valueFormatter: formatValue,
    timestampFormat: MINUTE_MOMENT_FORMAT,
};

/**
 * Notice the column widths above which are based on foundation's flex grid?
 * @type {number}
 */
const CHART_AREA_GRID_WIDTH_RATIO = 19 / 24;

/**
 * Guess the sample rate used to determine the granularity of the chart. Useful for the first render.
 * @return {number}
 */
export const guessMaxSampleCount = () => {
    const width = Math.max((window && window.innerWidth) || 0, minWidthPixelsSelector(getStore().getState()));
    return Math.floor((width * CHART_AREA_GRID_WIDTH_RATIO) / POINT_WIDTH);
};
