import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment-timezone';
import NimbleIcon from '../../../components/NimbleIcon';
import { isHeartbeatDelayed } from '../../collection/components/list/ArrayHealth';
import { FORMAT_MOMENT_DATE_TIME_TIMEZONE_FROM_DESC } from '../../../../../util/formatter/constants';
import { formatDateTime, isEmpty } from '../../../../../util/formatter';

const MonitorServices = ({ latestHeartbeatTime, lastAsupReceivedTime }) => {
    const heartBeatIconName = `Array Heartbeat ${isHeartbeatDelayed(latestHeartbeatTime) ? 'Down' : 'Up'}`;
    const asupIconName = `Array Asup ${isEmpty(lastAsupReceivedTime) ? 'Down' : 'Up'}`;
    return (
        <div className="row">
            <div className="column">
                <NimbleIcon name={heartBeatIconName} height="35px" width="42px" />
                <div>{latestHeartbeatTime ? moment(latestHeartbeatTime).fromNow() : 'Not Received'}</div>
                <div>{latestHeartbeatTime && formatDateTime(latestHeartbeatTime, FORMAT_MOMENT_DATE_TIME_TIMEZONE_FROM_DESC)}</div>
            </div>
            <div className="column flex-dir-column">
                <NimbleIcon name={asupIconName} height="35px" width="42px" />
                <div>{lastAsupReceivedTime ? moment(lastAsupReceivedTime).fromNow() : 'Not Received'}</div>
                <div>{lastAsupReceivedTime ? formatDateTime(lastAsupReceivedTime, FORMAT_MOMENT_DATE_TIME_TIMEZONE_FROM_DESC) : '(in past 2 weeks)'}</div>
            </div>
        </div>
    );
};

MonitorServices.propTypes = {
    latestHeartbeatTime: PropTypes.number,
    lastAsupReceivedTime: PropTypes.number,
};

export default MonitorServices;
