/**
 * Wraps a view with a loading spinner.
 *
 * See BulletinsHelpContent.js for example usage.
 *
 * The default 'load' function calls fetch() on the target view's model or collection.
 *
 * Subclasses may override the 'load' function.
 * The 'load' function must return a jQuery deferred which:
 *  - When resolve()d, yields the target view when resolved.
 *  - When reject()ed, yields an optional error message
 *
 * Subclass can optionally define a 'loadingMessage' property to customize the text adjacent to the spinner.
 */
const $ = require('jquery');
var _ = require('underscore');
var Backbone = require('backbone');
var Marionette = require('backbone.marionette');
var spinnerTpl = require('raw-loader!./Spinner.html');
var ErrorView = require('./ErrorView');

var SpinnerView = Marionette.ItemView.extend({
    template: _.template(spinnerTpl),
});

var AsyncView = Marionette.LayoutView.extend({
    animateOnLoad: true,
    template: _.constant('<div class="region"></div>'),

    regions: {
        region: '.region',
    },

    initialize: function () {
        _.bindAll(this, 'onLoad', 'onError');

        this.loadingMessage = this.getOption('loadingMessage');
        this.view = this.getOption('view');
        this.ViewConstructor = this.getOption('ViewConstructor');

        if (this.ViewConstructor) {
            this.view = new this.ViewConstructor(_(this.options).omit('el'));
        }
        this.listenTo(this.view, 'all', this.triggerMethodFilter);
    },

    triggerMethodFilter: function (eventName, payload) {
        switch (eventName) {
        case 'render':
            break;
        default:
            this.triggerMethod(eventName, payload);
        }
    },

    load: function () {
        console.assert(this.view, 'A "view" or "ViewConstructor" option must be defined to use the default load function');

        var deferred = $.Deferred();

        // NOTE: If view has both a model and collection, we arbitrarily choose the collection to fetch,
        // unless fetchModel:true flag is set in view
        // In case of fetchModel:true is set, will choose model to fetch
        var modelOrCollection;
        if (this.view.fetchModel === true) {
            modelOrCollection = this.view.model || this.view.collection;
        } else {
            modelOrCollection = this.view.collection || this.view.model;
        }
        console.assert(modelOrCollection, 'The target view must have a model or a collection to be fetched');

        modelOrCollection.fetch({ reset: true })
            .done(_.bind(deferred.resolve, deferred, this.view))
            .fail(_.bind(deferred.reject, deferred));

        return deferred;
    },

    onRender: function () {
        if (this.useSpinner !== false) {
            this.region.show(new SpinnerView());
        }

        this.load()
            .done(this.onLoad)
            .fail(this.onError);
    },

    onLoad: function (view) {
        // Handle case when the view is no longer needed
        if (!this.region) {
            return;
        }

        if (this.getOption('animateOnLoad') == 'true') {
            this.region.$el.addClass('animated fadeIn');
        }

        this.region.show(view);

        // TODO consider proxying events on the view to listeners on ourself
    },

    onError: function (jqXhr) {
        // View destroyed or XHR aborted.
        // https://developer.mozilla.org/en-US/docs/Web/API/XMLHttpRequest/abort
        if (this.isDestroyed || (jqXhr.readyState === 0 && jqXhr.status === 0)) {
            return;
        }

        this.region.$el.addClass('animated fadeIn');
        this.region.show(new ErrorView({
            model: new Backbone.Model({
                error: jqXhr.responseJSON ? jqXhr.responseJSON.message : null,
                message: 'Unable to load content.',
            }),
        }));
    },

}, {

    /**
     * Wrap the provided view in a new AsyncView.
     * Shortcut for calling: new AsyncView({ view: view })
     */
    wrap: function (view) {
        return new AsyncView({ view: view });
    },

});

module.exports = AsyncView;
