import chroma from 'chroma-js';
import numeral from 'numeral';
import { cartesianProductOfKeys } from '../.././../../../../util/cartesianProductOf';

export const buildPerspectiveKey = ({ unit, io, access }) => `${unit}_${io}_${access}`;

export const buildPerspectives = () => {
    const UNITS = {
        iops: {
            title: 'IO/sec',
            fieldSegment: 'iops',
        },
        mbps: {
            title: 'MiB/sec',
            fieldSegment: 'mbps',
            valueFormatter(value) {
                if (value === undefined || value === null) {
                    return 'N/A';
                }

                return numeral(value / (2 ** 20)).format('0,0.00');
            },
        },
    };

    const LATENCY_UNIT = {
        ms: { title: 'ms', fieldSegment: 'latency' },
    };

    const IO_TYPES = {
        readWrite: { title: 'Reads & Writes', fieldSegment: 'ReadWrite' },
        read: { title: 'Reads', fieldSegment: 'Read' },
        write: { title: 'Writes', fieldSegment: 'Write' },
    };

    const ACCESS_TYPES = {
        randomSequential: { title: 'Random & Sequential', fieldSegment: 'RandomSequential' },
        random: { title: 'Random', fieldSegment: 'Random' },
        seq: { title: 'Sequential', fieldSegment: 'Sequential' },
    };

    const perspectives = {};

    cartesianProductOfKeys(UNITS, IO_TYPES, ACCESS_TYPES)
        .reduce((memo, [unit, io, access]) => {
            memo[buildPerspectiveKey({ unit, io, access })] = {
                field: `${UNITS[unit].fieldSegment}${ACCESS_TYPES[access].fieldSegment}${IO_TYPES[io].fieldSegment}`,
                title: 'Throughput',
                valueTitle: (`${UNITS[unit].fieldSegment}` === 'iops') ? 'Avg. IOPS' : 'Avg. Throughput',
                chartTitle: `${ACCESS_TYPES[access].title} ${IO_TYPES[io].title}`,
                unit: `${UNITS[unit].title}`,
                latencyPerspectiveKey: buildPerspectiveKey({ io, unit: 'ms', access: '' }),
                min: 0,
                minColor: '#E2E6EA',
                maxColor: chroma('#002e6d').hex(),
                keys: { unit, io, access },
                valueFormatter: UNITS[unit].valueFormatter,
            };

            return memo;
        }, perspectives);

    cartesianProductOfKeys(LATENCY_UNIT, IO_TYPES)
        .reduce((memo, [unit, io]) => {
            memo[buildPerspectiveKey({ unit, io, access: '' })] = {
                field: `latency${IO_TYPES[io].fieldSegment}`,
                title: 'Latency',
                valueTitle: 'Avg. Latency',
                chartTitle: `${IO_TYPES[io].title}`,
                unit: `${LATENCY_UNIT[unit].title}`,
                min: 0,
                max: 10, // TODO This depends on the underlying hardware. It should really depend on the pain score.
                minColor: '#E2E6EA',
                maxColor: chroma('#d3524d').hex(),
                keys: { unit, io, access: '' },
            };

            return memo;
        }, perspectives);

    return perspectives;
};
