                    accessor: 'growthLast30Days',
                    filterable: false,
                    className: 'text-right',
                    minWidth: COLUMN_WIDTHS.XSMALL,
                    Cell: ({ original: { growthLast30Days } }) => (growthLast30Days === null ? '' : <PercentGrowth value={growthLast30Days} isPercent />),
