import { FillPattern } from '@infosight/elmer/dist/charts/highcharts';
import { CHART_COLORS } from '../../../util/chartColors';

const unusedReservePattern = new FillPattern('unusedReservePattern')
    .svgPath(FillPattern.patterns[0])
    .strokeWidth(1)
    .strokeColor(CHART_COLORS.ununsedReservedColor)
    .dimensions(5)
    .fillColor(CHART_COLORS.plotBandGrey);

export default ({ height, width, data }) => ({
    chart: {
        type: 'pie',
        spacingTop: 0,
        spacingBottom: 0,
        spacingLeft: 0,
        spacingRight: 0,
        marginTop: -10,
        marginRight: 0,
        marginBottom: -10,
        marginLeft: -20,
        height,
        width,
    },
    defs: {
        patterns: [unusedReservePattern],
    },
    title: false,
    plotOptions: {
        pie: {
            shadow: false,
            dataLabels: {
                enabled: false,
            },
            center: [height / 2, '50%'],
        },
    },
    tooltip: {
        enabled: false,
    },
    series: [{
        name: 'Series',
        data,
        showInLegend: true,
        size: '100%',
        innerSize: '85%',
        borderWidth: 0,
        states: {
            hover: {
                enabled: false,
            },
        },
    }],
});
