import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Async from '@infosight/elmer/dist/components/Async';
import NoData from '@infosight/elmer/dist/components/NoData';
import {
    QueryTimeRangeSelector,
    TimeRangeSelection,
    deserialize,
    makeChartSelector,
    makeRangeSelector,
} from '@infosight/elmer/dist/components/TimeRangeSelector';
import { getVolumeCapacity } from '../../actionCreators';
import { capacitySelector } from '../../reducer';
import VolumeCapacityChart from './VolumeCapacityChart';
import { capacityTimeRangeParam } from '../../queryParams';

class CapacityChartContainer extends Component {
    componentDidMount() {
        const {
            loadingVolumeCapacity,
            loadedVolumeCapacity,
        } = this.props;

        if (!loadingVolumeCapacity && !loadedVolumeCapacity) {
            this.fetchData(this.props);
        }
    }

    componentWillReceiveProps(nextProps) {
        const { volumeSn, timeRange } = nextProps;

        if (volumeSn !== this.props.volumeSn || !timeRange.equals(this.props.timeRange)) {
            this.fetchData(nextProps);
        }
    }

    fetchData(props) {
        let { timeRange } = props;

        if (timeRange.type === 'DURATION') {
            // Avoid fetching only forecast data
            const { from } = timeRange.asFixedRange();
            timeRange = {
                time: {
                    range: {
                        start: from.valueOf(),
                    },
                },
            };
        } else {
            timeRange = timeRange.toGenericQueryTime();
        }

        this.props.getVolumeCapacity(props.volumeSn, timeRange);
    }

    render() {
        const { volumeCapacity, timeRange, loadingVolumeCapacity, history, location } = this.props;
        let displayTimeRange;
        const hasCapacityData = !!(volumeCapacity && volumeCapacity.data.timeseries);
        if (hasCapacityData) {
            displayTimeRange = TimeRangeSelection.toTimeRangeSelection(volumeCapacity.meta.time);
        }

        const onSelectRange = makeRangeSelector(location, capacityTimeRangeParam(), false, history);
        const onChartSelect = makeChartSelector(onSelectRange);

        return (
            <div>
                <QueryTimeRangeSelector
                    parameterPrefix={capacityTimeRangeParam()}
                    value={timeRange}
                    presets={QueryTimeRangeSelector.PRESETS.NONVOLATILE}
                    displayValue={displayTimeRange}
                    card
                />
                <Async loading={loadingVolumeCapacity}>
                    <NoData hasData={hasCapacityData}>
                        {() => <VolumeCapacityChart volumeCapacity={volumeCapacity} onChartSelect={onChartSelect} />}
                    </NoData>
                </Async>
            </div>
        );
    }
}

CapacityChartContainer.propTypes = {
    volumeSn: PropTypes.string.isRequired,
    loadingVolumeCapacity: PropTypes.bool,
    loadedVolumeCapacity: PropTypes.bool,
    getVolumeCapacity: PropTypes.func,
    volumeCapacity: PropTypes.shape({
        data: PropTypes.shape({
            timeseries: PropTypes.arrayOf(PropTypes.shape({
                dataUsageBytes: PropTypes.number,
                sizeBytes: PropTypes.number,
                snapshotUsageBytes: PropTypes.number,
                timestamp: PropTypes.number,
            })).isRequired,
        }).isRequired,
        meta: PropTypes.shape({
            time: PropTypes.shape({
                start: PropTypes.number.isRequired,
                end: PropTypes.number.isRequired,
            }).isRequired,
        }).isRequired,
    }),
    timeRange: PropTypes.instanceOf(TimeRangeSelection).isRequired,
    history: PropTypes.object.isRequired,
    location: PropTypes.object,
};

const mapUrlToProps = ({ match: { params } }) => ({
    timeRange: deserialize(params, capacityTimeRangeParam('')) || QueryTimeRangeSelector.DEFAULTS.NONVOLATILE,
});

const mapStateToProps = (state, props) => ({
    ...capacitySelector(state),
    ...mapUrlToProps(props),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    getVolumeCapacity,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(CapacityChartContainer);
