import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import FacetedCollectionPage from '../../../../../../layouts/FacetedCollectionPage/index';
import { getGroups } from '../../actionCreators';
import { listSelector } from '../../reducer';
import style from './style.scss';
import getConfig from './getConfig';

export class ListContainer extends Component {
    componentDidMount() {
        this.props.getGroups();
    }

    render() {
        const { loadingGroups, loadedGroups, groups, config } = this.props;
        if (!loadingGroups && !loadedGroups && !groups) {
            return null;
        }

        return (
            <FacetedCollectionPage
                config={getConfig(config)}
                collection={groups}
                loadingCollection={loadingGroups}
                tableClassName={style.table}
                keyField="groupUid"
                defaultSort={[{ colname: 'groupName', order: 'asc' }]}
            />
        );
    }
}

ListContainer.propTypes = {
    getGroups: PropTypes.func.isRequired,
    groups: PropTypes.arrayOf(PropTypes.object),
    loadingGroups: PropTypes.bool.isRequired,
    loadedGroups: PropTypes.bool.isRequired,

    // Injected by XX_INFRASTRUCTURE_XX
    config: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    ...listSelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({ getGroups }, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(ListContainer);
