import numeral from 'numeral';

export const byteToTibibyte = bytes => bytes / (2 ** 40);

export const getByteFormat = maxCapacityBytes => (byteToTibibyte(maxCapacityBytes) < 25 ? '0.000' : '0');

export const formatBytes = (bytes, maxCapacityBytes) =>
    numeral(byteToTibibyte(bytes)).format(getByteFormat(maxCapacityBytes));

export const VOLUME_USAGE_COLOR = '#2d94cf';
export const SNAPSHOT_USAGE_COLOR = '#66CCCC';
export const TOTAL_USAGE_COLOR = VOLUME_USAGE_COLOR;
export const LIMIT_COLOR = '#f1c232';
export const FREE_SPACE_COLOR = '#EEF0F2';
