import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import ReactDimensions from 'react-dimensions';
import { max, uniqueId } from 'underscore';
import moment from 'moment-timezone';
import NoData from '@infosight/elmer/dist/components/NoData';
import mergeSharedTooltip, { Timestamp } from '@infosight/elmer/dist/charts/mergeSharedTooltip';
import { DAY_MOMENT_FORMAT } from '@infosight/elmer/dist/utils/time';
import { formatValue, isEmpty } from '../../../../../../util/formatter';
import { moveCrosshair } from '../../../../../../modules/charts/util/synchronization';
import { FORMAT_NUMBER_NO_PRECISION } from '../../../../../../util/formatter/constants';
import { xAxisDataRange } from '../../../../../charts/mixins/xAxis';
import { dateTimeAxisSnippet } from '../../../../../charts/mixins/dateTimeAxis';
import { truncateTime, getMilliSecs, defaultGetCorrespondingPoints } from './util';
import { CHART_COLORS } from '../../../../../../util/chartColors';

const Chart = mergeSharedTooltip({
    valueFormatter(point) {
        return formatValue(point.y, FORMAT_NUMBER_NO_PRECISION, '%');
    },

    xValueFormatter(point) {
        return `Week of : ${Timestamp(point.x, DAY_MOMENT_FORMAT)}`;
    },
});

const findMaxYaxisValue = ({ arraysRecommendations, getDataKey }) => {
    const maxValueInData = Math.max(...arraysRecommendations.map(arrayRec =>
        max(getDataKey(arrayRec), dataPoint => (!isEmpty(dataPoint.usage) ? dataPoint.usage : 0)).usage || 0));
    // Adding 20 randomly just to add some padding on y-axis
    return Math.max(maxValueInData + 20, 100);
};

class PoolSaturationChart extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.containerElementId = uniqueId(this.constructor.name);
    }

    componentDidMount() {
        if (this.props.chartSync) {
            this.props.chartSync.monitorChart(this, { syncPoints: this.syncPoints, resetPoints: this.resetPoints });
        }
    }

    componentWillUnmount() {
        const container = document.getElementById(this.containerElementId);
        if (!container) {
            return;
        }

        container.removeEventListener('mouseleave', this.onMouseOut);
    }

    onAfterChartRendered(chart) {
        this.chart = chart;
        const container = document.getElementById(this.containerElementId);
        if (!container) {
            return;
        }

        container.addEventListener('mouseleave', this.onMouseOut);
    }

    onMouseOut(event) {
        if (this.props.chartSync) {
            this.props.chartSync.syncPoints({ event });
        } else {
            this.resetPoints();
        }
    }

    onMouseOverPoint(opts) {
        if (!opts.point) {
            return;
        }
        if (this.props.chartSync) {
            this.props.chartSync.syncPoints(opts);
        } else {
            this.syncPoints(opts.point);
        }
    }

    getChartConfig() {
        if (this.config) {
            return this.config;
        }

        const { arrayRecommendations, getDataKey, spacingLeft, chartRangeStart, chartRangeEnd } = this.props;
        const series = arrayRecommendations.map(array => this.usageSeries({
            arraySerialNumber: array.serialNumber,
            arrayHostName: array.hostname,
            timeSeries: getDataKey(array),
        }));
        const yAxisMax = findMaxYaxisValue({ arraysRecommendations: arrayRecommendations, getDataKey });
        const showNotAvailableDataBands = truncateTime(moment()).isBefore(chartRangeEnd);
        this.config = {
            chart: {
                height: this.props.containerHeight,
                spacingTop: 20,
                spacingLeft,
                zoomType: null,
            },
            title: false,
            xAxis: {
                ...dateTimeAxisSnippet(),
                ...xAxisDataRange(chartRangeStart, chartRangeEnd),
                plotBands: showNotAvailableDataBands && {
                    color: CHART_COLORS.plotBandGrey,
                    /* Ideally the plot band should start when the pool capacity forecast starts
                    But I chose to add it from current day for the below reasons
                    1. In most scenarios the current day and pool capacity forecast start day will be the same
                    2. To avoid unnecessary computing
                    3. This is going to be temporary
                    */
                    from: getMilliSecs(moment()),
                    to: getMilliSecs(chartRangeEnd),
                    label: {
                        text: 'Forecast Data Not Available',
                        align: 'center',
                        y: 50,
                    },
                },
            },
            yAxis: {
                title: {
                    enabled: false,
                },
                labels: {
                    formatter() {
                        return `${formatValue(this.value, FORMAT_NUMBER_NO_PRECISION, '%')}`;
                    },
                },
                min: 0,
                max: yAxisMax,
            },
            series,
            plotOptions: {
                series: {
                    connectNulls: true,
                    zones: [{
                        value: 100,
                    }, {
                        color: CHART_COLORS.redOrange,
                    }],
                },
            },
        };

        return this.config;
    }

    usageSeries({ arraySerialNumber, arrayHostName, timeSeries }) {
        const thisComponent = this;

        return {
            name: `${!isEmpty(arrayHostName) ? `${arrayHostName} (${arraySerialNumber})` : arraySerialNumber} `,
            data: timeSeries.map(val => ({
                x: truncateTime(val.ts),
                y: val.usage,
            })),
            events: {
                legendItemClick: e => e.preventDefault(),
            },
            marker: {
                enabled: false,
            },
            lineWidth: 2,
            point: {
                events: {
                    mouseOver(event) {
                        if (thisComponent.lastPointHovered !== this) {
                            thisComponent.lastPointHovered = this;
                            thisComponent.onMouseOverPoint({ point: this, event });
                        }
                    },
                },
            },
        };
    }

    syncPoints(point) {
        const hoverPoints = this.props.getCorrespondingPoints(point, this.chart);
        if (this.chart.legend) {
            this.chart.legend.group.hide();
            this.chart.legend.display = false;
        }
        moveCrosshair(hoverPoints, this.chart);
    }

    resetPoints() {
        if (this.chart) {
            // Remove markers from any active points
            if (this.chart.hoverPoints) {
                this.chart.hoverPoints.forEach(point => point.setState());
            }

            if (this.chart.xAxis) {
                this.chart.xAxis[0].hideCrosshair();
                this.chart.tooltip.hide();
            }

            if (this.chart.legend) {
                this.chart.legend.group.show();
                this.chart.legend.display = true;
            }
        }
    }

    render() {
        const { arrayRecommendations, getDataKey } = this.props;
        const hasPoolCpuCacheData = arrayRecommendations && arrayRecommendations.length > 0 &&
        getDataKey(arrayRecommendations[0]) && getDataKey(arrayRecommendations[0]).length > 0;
        return (
            <NoData hasData={hasPoolCpuCacheData}>
                { () => <Chart config={this.getChartConfig()} callback={this.onAfterChartRendered} domProps={{ id: this.containerElementId }} />}
            </NoData>);
    }
}

PoolSaturationChart.propTypes = {
    arrayRecommendations: PropTypes.arrayOf(PropTypes.shape({
        serialNumber: PropTypes.string,
        hostname: PropTypes.string,
        model: PropTypes.string,
        cpu: PropTypes.arrayOf(PropTypes.shape({
            ts: PropTypes.number,
            usage: PropTypes.number,
        })),
        cache: PropTypes.arrayOf(PropTypes.shape({
            ts: PropTypes.number,
            usage: PropTypes.number,
        })),
    })),

    /**
     * Key to access the data in context from the given data array
     */
    getDataKey: PropTypes.func,

    /**
     *
     * @ignore
     */
    containerHeight: PropTypes.number.isRequired,

    /**
     * This event chart can be used in different places.
     * And Y-Axis labels doesn't make sense on this charts.
     * To align properly with other charts on the page we might need to add padding on the left (assuming Y-Axis is on left which usually is)
     */
    spacingLeft: PropTypes.number,

    /**
     * Get all the values at a timestamp that should be displayed in the legend when the user hovers over a point.
     * This function is allowed to have synchronous side effects, such as updating crosshairs.
     * @param point {Highcharts.Point}
     * @param chart {Highcharts.Chart}
     * @param event {Event}
     * @return {pointValues, hoverPoints}
     *      {Object} pointValues Each key should be the series id. The value is {color, value}
     *      {Array<Highcharts.Point>} hoverPoints
     */
    getCorrespondingPoints: PropTypes.func,

    /**
     * Wrap this element in a `<SynchronizedChart>` element to inject this prop.
     */
    chartSync: PropTypes.shape({
        monitorChart: PropTypes.func.isRequired,
        removeChart: PropTypes.func.isRequired,
        syncPoints: PropTypes.func.isRequired,
    }),

    /**
     * Starting timestamp where the chart begins
     * moment or number
     */
    chartRangeStart: PropTypes.oneOfType([PropTypes.object, PropTypes.number]),

    /**
     * Last timestamp where the chart ends
     * moment or number
     */
    chartRangeEnd: PropTypes.oneOfType([PropTypes.object, PropTypes.number]),
};

PoolSaturationChart.defaultProps = {
    containerHeight: 0,
    getCorrespondingPoints: defaultGetCorrespondingPoints,
    showThresholdColors: false,
};

export default ReactDimensions({ elementResize: true })(PoolSaturationChart);
