import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Treebeard } from 'react-treebeard';
import autobind from 'react-autobind';
import NoData from '@infosight/elmer/dist/components/NoData';
import treebeardStyle from './treebeardStyle.json';

export default class TreeView extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.state = {
            treeData: [{ loading: true }],
            cursor: undefined,
        };
    }

    componentWillMount() {
        const { data, createTree } = this.props;
        this.setState({
            treeData: createTree(data),
        });
    }

    componentWillReceiveProps({ data, createTree }) {
        if (this.props.data !== data) {
            const treeData = createTree(data);
            this.setState({ treeData });
        }
    }

    onToggle(node) {
        const { onSelect } = this.props;

        if (node === this.state.cursor) {
            onSelect(node.value);
            return;
        }

        if (this.state.cursor) {
            this.state.cursor.active = false;
        }

        node.active = true; // eslint-disable-line no-param-reassign

        this.setState({ cursor: node }, () => onSelect(node.value, node));
    }

    onClickIcon(node, toggled) {
        node.toggled = toggled; // eslint-disable-line no-param-reassign
        this.forceUpdate();
    }

    render() {
        const { treeData } = this.state;

        return (
            <div className={this.props.className}>
                <NoData hasData={treeData} message={this.props.noDataMessage}>
                    {() => (
                        <Treebeard
                            data={treeData}
                            onToggle={this.onToggle}
                            onClickIcon={this.onClickIcon}
                            style={treebeardStyle}
                        />
                    )}
                </NoData>
            </div>
        );
    }
}

TreeView.propTypes = {
    /**
     * Callback that takes two parameters,`TreeNode.value` and `TreeNode`
     */
    onSelect: PropTypes.func,

    /**
     * Function to create the tree. It takes one parameter, `props.data`.
     * We generate the tree as needed so it can be mutated internally.
     * If this function doesn't care about the parameter, make sure to trigger a render as needed. This component
     * will call createTree each time the reference to `props.data` is changed.
     */
    createTree: PropTypes.func.isRequired,

    /**
     * Source used to generate data backing the tree view.
     * If needed you could create tree, accouting for mutations and have `props.createTree` simply return its
     * input.
     */
    data: PropTypes.any.isRequired,

    /**
     * Message to display when `props.createTree()` returns falsey value
     */
    noDataMessage: PropTypes.string,
};
