import { compact } from 'underscore';
import { CHART_COLORS } from '../../../util/chartColors';

const initialPlotBand = (chartRangeStart, timeSeriesStart) => {
    if (chartRangeStart.valueOf() < timeSeriesStart) {
        return {
            color: CHART_COLORS.plotBandGrey,
            from: chartRangeStart.valueOf(),
            to: timeSeriesStart,
        };
    }

    return undefined;
};

const finalPlotBand = (chartRangeEnd, timeSeriesEnd) => {
    if (chartRangeEnd.valueOf() > timeSeriesEnd) {
        return {
            color: CHART_COLORS.plotBandGrey,
            from: timeSeriesEnd,
            to: chartRangeEnd.valueOf(),
        };
    }

    return undefined;
};

export default ({ chartRangeStart, chartRangeEnd, timeSeriesStart, timeSeriesEnd }) =>
    compact([initialPlotBand(chartRangeStart, timeSeriesStart), finalPlotBand(chartRangeEnd, timeSeriesEnd)]);
