import moment from 'moment-timezone';
import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';

import NoData from '@infosight/elmer/dist/components/NoData';
import ReactDimensions from 'react-dimensions';
import { mergeSharedTooltipConfig } from '@infosight/elmer/dist/charts/mergeSharedTooltip';
import { ReactHighcharts } from '@infosight/elmer/dist/components/ReactHighcharts';

import { Highstock } from 'progeny/modules/nimble/highchartsProxy';
import { COLOR_SETS } from '../../../util/chartColors';

import { toLatency, toThroughput, toPercent, toIops } from '../util/formatterLibrary';

import applyMixins from '../mixins/applyMixins';
import legendConfigMixin from '../mixins/legend';
import chartDimensionsMixin from '../mixins/dimensions';
import plotOptionsMixin from '../mixins/plotOptions';
import titleMixin from '../mixins/title';
import xAxisMixin from '../mixins/xAxis';
import dragZoomMixin from '../mixins/dragZoom';


const LOCAL_TIMEZONE = moment.tz.guess();
class PerfChart extends PureComponent {
    data() {
        const { timeSeries, titles, timeSeriesKeys, colorList, invisibleSeries } = this.props;

        if (timeSeriesKeys.length !== titles.length) {
            throw new Error('PerfChart: props timeseriesKeys and titles must have the same length');
        }

        const data = timeSeriesKeys.map((key, i) => {
            const color = colorList[i % colorList.length];
            return {
                name: titles[i],
                data: timeSeries.map(val => ({
                    x: val.timestamp,
                    y: val[key],
                })),
                color: color.stroke,
                fillColor: color.fill,
                lineWidth: 2,
                states: {
                    hover: {
                        lineWidth: 2,
                    },
                },
                visible: !invisibleSeries.includes(i),
            };
        });

        return data;
    }

    config() {
        const {
            chartRangeStart,
            chartRangeEnd,
            title,
            legendDisabled,
            type,
            yAxisMax,
            customDimensions,
            onChartSelect,
        } = this.props;

        const data = this.data();

        const chartConfig = Highstock.merge(
            customDimensions || chartDimensionsMixin(),
            {
                chart: {
                    type,
                    height: this.props.containerHeight,
                    spacingRight: 10,
                },
            }
        );

        const formatByUnit = (chartPt) => {
            switch (this.props.yAxisLabel) {
            case 'MiB/sec':
                return toThroughput(chartPt * 1024 * 1024);
            case 'ms':
                return toLatency(chartPt);
            case '%':
                return toPercent(chartPt);
            case 'IO/sec':
                return toIops(chartPt);
            default:
                return undefined;
            }
        };

        const xopt = applyMixins(
            () => chartConfig,
            legendConfigMixin(legendDisabled),
            plotOptionsMixin(type),
            titleMixin(title),
            xAxisMixin(chartRangeStart, chartRangeEnd),
            dragZoomMixin(onChartSelect),
            {
                yAxis: {
                    title: {
                        text: '',
                    },
                    max: yAxisMax || null,
                    min: 0,
                    labels: {
                        formatter() {
                            const val = formatByUnit(this.value);
                            return val && val.toValue().split(' ')[0];
                        },
                    },
                },
            }
        );

        xopt.series = data;

        return mergeSharedTooltipConfig(xopt, {
            pointFormatter: chartPt => (
                `<i class="fa fa-square" style="color: ${chartPt.point.color};"></i>\
                ${chartPt.series.name}:\
                ${formatByUnit(chartPt.y)}\
                &nbsp;&nbsp;&nbsp;&nbsp;`
            ),
            xValueFormatter: chartPt => (
                `${moment(chartPt.x).tz(LOCAL_TIMEZONE).format('L hh:mm a z')}`
            ),
        });
    }

    render() {
        const config = this.config();

        return (
            <NoData hasData={!!config}>
                {() => <ReactHighcharts config={config} />}
            </NoData>
        );
    }
}

PerfChart.propTypes = {
    /**
     * Moment
     */
    chartRangeStart: PropTypes.object,

    /**
     * Moment
     */
    chartRangeEnd: PropTypes.object,

    // From ReactDimensions
    containerHeight: PropTypes.number.isRequired,
    legendDisabled: PropTypes.bool,
    timeSeries: PropTypes.array,
    timeSeriesKeys: PropTypes.arrayOf(PropTypes.string),
    title: PropTypes.string,
    titles: PropTypes.arrayOf(PropTypes.string),
    // tooltipFormatter: PropTypes.function,
    type: PropTypes.string,
    yAxisLabel: PropTypes.string,
    yAxisMax: PropTypes.number,
    customDimensions: PropTypes.object,
    colorList: PropTypes.arrayOf(PropTypes.object),
    invisibleSeries: PropTypes.arrayOf(PropTypes.number),
    onChartSelect: PropTypes.func,
};

PerfChart.defaultProps = {
    containerHeight: 0,
    legendDisabled: false,
    title: null,
    colorList: [COLOR_SETS.teal, COLOR_SETS.purple, COLOR_SETS.green, COLOR_SETS.blue],
    invisibleSeries: [],
    onChartSelect: event => event.preventDefault(),
};

export default ReactDimensions({ elementResize: true })(PerfChart);
