const _ = require('underscore');
var Backbone = require('backbone');
var Marionette = require('backbone.marionette');
var FactorsHistogram = require('./FactorsHistogram');
var tpl = require('raw-loader!./FactorsChart.html');

var FACTORS = [
    {
        id: 'factorCache',
        name: 'Cache Underprovisioning',
        abbreviation: 'Cache',
        color: '#7cb5ec',
    },
    {
        id: 'factorCpu',
        name: 'CPU Saturation',
        abbreviation: 'CPU',
        color: '#434348',
    },
    {
        id: 'factorDisk',
        name: 'Sequential I/O Contention',
        abbreviation: 'Seq I/O',
        color: '#90ed7d',
    },
    {
        id: 'factorHostNetwork',
        name: 'Host Network',
        abbreviation: 'Host/Net',
        color: '#f7a35c',
    },
    {
        id: 'factorUnalignFixable',
        name: 'Unaligned I/O',
        color: '#8085e9',
    },
    {
        id: 'factorOther',
        name: 'Other',
        color: '#f15c80',
    },
];

var Model = Backbone.Model.extend({
    defaults: {
        threshold: 5,
    },
});

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    regions: {
        histogram: '[data-region="histogram"]',
    },

    bindings: {
        'input[name="threshold"]': {
            observe: 'threshold',
            onSet: function (val) {
                return Number.parseFloat(val);
            },
        },
    },

    modelEvents: {
        'change:threshold': 'onChangeThreshold',
    },

    initialize: function (options) {
        this.chartDefaults = options.chartDefaults;
        this.model = new Model({
            threshold: options.allFlash ? 1 : 5,
        });
    },

    onRender: function () {
        this.stickit();
        this.renderTimeseriesChart();
        this.renderHistogram();
    },

    renderTimeseriesChart: function () {
        var collection = this.collection;
        this.$('[data-chart="timeseries"]').highcharts('StockChart', _(this.chartDefaults({
            title: { text: 'Factors' },
        })).extend({
            chart: _(this.chartDefaults(this).chart).extend({
                type: 'area',
            }),
            series: FACTORS.map(this.getFactorSeries.bind(this)),
            yAxis: _(this.chartDefaults(this).yAxis).extend({
                title: {
                    text: 'ms',
                },
                min: 0,
                minRange: 1,
            }),
        }));
    },

    renderHistogram: function () {
        this.showChildView('histogram', new FactorsHistogram({
            collection: this.collection,
            threshold: this.model.get('threshold'),
            factors: FACTORS,
        }));
    },

    getFactorSeries: function (factor) {
        var threshold = this.model.get('threshold');

        // Would be more correct and visually accurate to use 'null' instead of 0
        // However this causes points without neighbors to not render properly
        // pointRange: 1 should fix this but it doesn't seem to work
        var belowThresholdValue = 0;

        return {
            name: factor.abbreviation || factor.name,
            color: factor.color,
            data: this.collection
                .map(function (point) {
                    return {
                        x: point.get('timestamp'),
                        y: point.get('latencyTotal') > threshold ? point.get(factor.id) * point.get('latencyTotal') : 0,
                        color: factor.color,
                    };
                }),
        };
    },

    onChangeThreshold: _.debounce(function () {
        this.renderTimeseriesChart();
        this.renderHistogram();
    }, 500),

});
