import { FETCH_INVENTORY, OBJECTS, RESET } from './constants';
import { defaultFetchInventoryCreator } from '../../infrastructure/factories';
import NimbleUserTenantHelpers from '../tenantSelection/NimbleUserTenantHelpers';

export const fetchInventory = (...args) => (
    defaultFetchInventoryCreator({
        FETCH_INVENTORY,
        OBJECTS,
        view: 'nimble/inventory',
        productFamily: 'NIMBLE',
        enableClientSideSearch: true,
        excludedTypesFromClientSideSearch: NimbleUserTenantHelpers.canAccessMultipleTenants() ? [OBJECTS.ARRAY] : null,
    })(...args)
);

export const reset = () => ({ type: RESET });
