import React from 'react';
import { ProgressWithValue, Progress } from '@infosight/elmer/dist/components/Progress';
import { ObjectDetailLink } from '@infosight/elmer/dist/infrastructure';
import StatusIndicator from '@infosight/elmer/dist/components/StatusIndicator';
import PercentGrowth from '@infosight/elmer/dist/components/PercentGrowth';
import { COLUMN_WIDTHS } from '@infosight/elmer/dist/components/Table';
import NamedNimbleIcon from '../../../../components/NamedNimbleIcon';
import { OBJECTS } from '../../../../inventory';
import { DataProtection, DaysUntilFull, categorizeDaysUntilFull, VolumeStatus, VolumeDataUsageInfo } from '../../../common';
import {
    BYTE_FORMAT_PRECISION,
    NUMBER_FORMAT_PRECISION,
    NUMBER_ABBREVIATE_FORMAT,
    LATENCY_UNIT_ABBR,
    IOPS_UNIT_ABBR,
    PERCENT_ABBR,
    listValueFormatter,
    formatValue,
    isEmpty,
} from '../../../../../constants';
import { formatDateTime } from '../../../../../../util/formatter';
import { FORMAT_MOMENT_DATE_TIME } from '../../../../../../util/formatter/constants';
import { DEFAULT_TAB, SPACE_TAB, PERFORMANCE_TAB, PROTECTION_TAB } from '../../constants';

const percentGrowthRenderer = value => (isEmpty(value) ? null : <PercentGrowth value={value} isPercent />);

/* eslint-disable react/prop-types */

export default infrastructureConfig =>
    ({
        categories: [DEFAULT_TAB, SPACE_TAB, PERFORMANCE_TAB, PROTECTION_TAB],
        columns: [
            {
                Header: 'Volume Name',
                accessor: 'name',
                minWidth: COLUMN_WIDTHS.XLARGE,
                Cell: ({ original: { name, sn } }) =>
                    <ObjectDetailLink objectType={OBJECTS.VOLUME} id={sn} productInterface={infrastructureConfig} name={name} />,
                filterable: true,
            },
            {
                Header: 'Attributes',
                headerClassName: 'text-center',
                accessor: 'volumeStatusSort',
                categories: [DEFAULT_TAB],
                minWidth: COLUMN_WIDTHS.SMALL,
                Cell: ({ original: { volumePinned, volumeEncrypted } }) => (
                    <VolumeStatus
                        volumePinned={volumePinned}
                        volumeEncrypted={volumeEncrypted}
                    />),
            },
            {
                Header: 'Application',
                accessor: 'appCategory',
                categories: [DEFAULT_TAB],
                minWidth: COLUMN_WIDTHS.LARGE,
                Cell: ({ original: { appCategory } }) =>
                    <NamedNimbleIcon iconName={appCategory} inline />,
                filterable: true,
            },
            {
                Header: 'Array',
                accessor: 'arraySearchableName',
                categories: [DEFAULT_TAB],
                minWidth: COLUMN_WIDTHS.LARGE,
                Cell: ({ original: { hostName, arraySn } }) => {
                    if (arraySn === null) {
                        return null;
                    }
                    if (hostName === null) {
                        hostName = '';
                    }
                    const name = `${hostName} (${arraySn})`;
                    return <ObjectDetailLink objectType={OBJECTS.ARRAY} id={arraySn} config={infrastructureConfig} name={name} includeIcon={false} />;
                },
                filterable: true,
            },
            {
                Header: 'Pool',
                accessor: 'poolName',
                categories: [DEFAULT_TAB],
                minWidth: COLUMN_WIDTHS.MEDIUM,
                Cell: ({ original: { poolName, poolUid } }) =>
                    <ObjectDetailLink objectType={OBJECTS.POOL} id={poolUid} config={infrastructureConfig} name={poolName} includeIcon={false} />,
                filterable: true,
            },
            {
                Header: 'Folder',
                accessor: 'folderName',
                categories: [DEFAULT_TAB],
                minWidth: COLUMN_WIDTHS.MEDIUM,
                filterable: true,
            },
            {
                Header: 'Total Usage',
                headerClassName: 'text-right',
                accessor: 'totalUsageBytes',
                categories: [SPACE_TAB],
                className: 'text-right',
                minWidth: COLUMN_WIDTHS.SMALL,
                Cell: ({ original: { totalUsageBytes } }) => listValueFormatter(totalUsageBytes, BYTE_FORMAT_PRECISION),
            },
            {
                Header: <VolumeDataUsageInfo title="% Data Usage" />,
                accessor: 'percentFull',

                categories: [DEFAULT_TAB, SPACE_TAB],
                minWidth: COLUMN_WIDTHS.XLARGE,
                style: { marginTop: '8px' },
                Cell: ({ original: { percentFull, dataUsageBytes, allocatedBytes } }) => (
                    <ProgressWithValue
                        leftLabel={`${listValueFormatter(percentFull, PERCENT_ABBR)}
                        \u00A0(${listValueFormatter(dataUsageBytes, BYTE_FORMAT_PRECISION)})`}
                    >
                        <Progress
                            className={percentFull >= 0.9 ? 'danger' : null}
                            max={allocatedBytes}
                            value={dataUsageBytes > allocatedBytes ? allocatedBytes : dataUsageBytes}
                        />
                    </ProgressWithValue>),
                sortable: true,
            },
            {
                Header: 'Size',
                accessor: 'allocatedBytes',
                headerClassName: 'text-right',
                className: 'text-right',
                minWidth: COLUMN_WIDTHS.XSMALL,
                Cell: ({ original: { allocatedBytes } }) => listValueFormatter(allocatedBytes, BYTE_FORMAT_PRECISION),
                categories: [DEFAULT_TAB, SPACE_TAB],
            },
            {
                Header: 'Days Until Full',
                headerClassName: 'text-right',
                accessor: 'daysUntilFull',
                filterable: true,
                categories: [SPACE_TAB],
                className: 'text-right',
                minWidth: COLUMN_WIDTHS.MEDIUM,
                Cell: ({ original: { daysUntilFull } }) => <DaysUntilFull days={daysUntilFull} inline />,
                filterMethod: (filter, row) => {
                    const { id, value: filterValue } = filter;
                    const rowValue = row[id];
                    if (!isEmpty(rowValue)) {
                        /* Why is this switch added: IS-18362 */
                        switch (filterValue) {
                        case '<= 1 Week': return rowValue <= 7;
                        case '>1 Week and <=1 Month': return rowValue > 7 && rowValue <= 28;
                        case '>1 Month': return rowValue > 29;
                        default:
                            return String(categorizeDaysUntilFull(rowValue).category)
                                .toLowerCase().includes(String(filterValue).toLowerCase());
                        }
                    }
                    return false;
                },
            },
            {
                Header: 'SNAPSHOT',
                headerClassName: 'text-center',
                categories: [SPACE_TAB],
                columns: [
                    {
                        Header: 'Count',
                        headerClassName: 'text-right',
                        accessor: 'snapshotCount',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                    },
                    {
                        Header: 'Usage',
                        headerClassName: 'text-right',
                        accessor: 'snapshotBytes',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.LARGE,
                        Cell: ({ original: { snapshotBytes } }) => listValueFormatter(snapshotBytes, BYTE_FORMAT_PRECISION),
                    },
                ],
            },
            {
                Header: 'Compression',
                headerClassName: 'text-right',
                accessor: 'volumeCompression',

                className: 'text-right',
                categories: [SPACE_TAB],
                minWidth: COLUMN_WIDTHS.LARGE,
                Cell: ({ original: { volumeCompression } }) => (isEmpty(volumeCompression) ? null :
                    `${formatValue(volumeCompression, NUMBER_FORMAT_PRECISION)}X`),
            },
            {
                Header: 'Dedupe',
                headerClassName: 'text-right',
                accessor: 'dedupeEnabled',

                categories: [SPACE_TAB],
                minWidth: COLUMN_WIDTHS.SMALL,
                Cell: ({ original: { dedupeEnabled } }) => { // eslint-disable-line arrow-body-style
                    return isEmpty(dedupeEnabled) ? null :
                        (<StatusIndicator
                            status={dedupeEnabled ? 'success' : 'unavailable'}
                            tooltip={dedupeEnabled ? 'Enabled' : 'Disabled'}
                            size={17}
                        />);
                },
            },
            {
                Header: 'Past Growth (days)',
                headerClassName: 'text-center',
                categories: [SPACE_TAB],
                columns: [
                    {
                        Header: '90',
                        headerClassName: 'text-right',
                        accessor: 'last90daysGrowth',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.XSMALL,
                        Cell: ({ original: { last90daysGrowth } }) => percentGrowthRenderer(last90daysGrowth),
                    },
                    {
                        Header: '60',
                        headerClassName: 'text-right',
                        accessor: 'last60daysGrowth',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.XSMALL,
                        Cell: ({ original: { last60daysGrowth } }) => percentGrowthRenderer(last60daysGrowth),
                    },
                    {
                        Header: '30',
                        headerClassName: 'text-right',
                        accessor: 'last30daysGrowth',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.XSMALL,
                        Cell: ({ original: { last30daysGrowth } }) => percentGrowthRenderer(last30daysGrowth),
                    },
                ],
            },
            {
                Header: 'IO/Week',
                headerClassName: 'text-right',
                accessor: 'ioPerWeek',

                categories: [DEFAULT_TAB],
                className: 'text-right',
                minWidth: COLUMN_WIDTHS.MEDIUM,
                Cell: ({ original: { perWeekIopsRead, perWeekIopsWrite } }) =>
                    listValueFormatter(perWeekIopsRead + perWeekIopsWrite, NUMBER_ABBREVIATE_FORMAT),
            },
            {
                Header: 'Performance Policy',
                accessor: 'performancePolicy',
                categories: [PERFORMANCE_TAB],
                minWidth: COLUMN_WIDTHS.LARGE,
                filterable: true,
            },
            {
                Header: 'Avg IOPS (past week)',
                headerClassName: 'text-center',
                categories: [PERFORMANCE_TAB],
                columns: [
                    {
                        Header: 'Read',
                        headerClassName: 'text-right',
                        accessor: 'avgReadsPerWeek',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { avgReadsPerWeek } }) => listValueFormatter(avgReadsPerWeek, NUMBER_ABBREVIATE_FORMAT, IOPS_UNIT_ABBR),
                    },
                    {
                        Header: 'Write',
                        headerClassName: 'text-right',
                        accessor: 'avgWritesPerWeek',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { avgWritesPerWeek } }) => listValueFormatter(avgWritesPerWeek, NUMBER_ABBREVIATE_FORMAT, IOPS_UNIT_ABBR),
                    },
                    {
                        Header: 'Overall',
                        headerClassName: 'text-right',
                        accessor: 'avgIopsPerWeek',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { avgIopsPerWeek } }) => listValueFormatter(avgIopsPerWeek, NUMBER_ABBREVIATE_FORMAT, IOPS_UNIT_ABBR),
                    },
                ],
            },
            {
                Header: 'Avg Latency (past week)',
                headerClassName: 'text-center',
                categories: [PERFORMANCE_TAB],
                columns: [
                    {
                        Header: 'Read',
                        headerClassName: 'text-right',
                        accessor: 'avgReadLatencyMs',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { avgReadLatencyMs } }) => listValueFormatter(avgReadLatencyMs, NUMBER_ABBREVIATE_FORMAT, LATENCY_UNIT_ABBR),
                    },
                    {
                        Header: 'Write',
                        headerClassName: 'text-right',
                        accessor: 'avgWriteLatencyMs',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { avgWriteLatencyMs } }) => listValueFormatter(avgWriteLatencyMs, NUMBER_ABBREVIATE_FORMAT, LATENCY_UNIT_ABBR),
                    },
                    {
                        Header: 'Overall',
                        headerClassName: 'text-right',
                        accessor: 'avgLatencyMs',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { avgLatencyMs } }) => listValueFormatter(avgLatencyMs, NUMBER_ABBREVIATE_FORMAT, LATENCY_UNIT_ABBR),
                    },
                ],
            },
            {
                Header: 'Avg Throughput (past week)',
                headerClassName: 'text-center',
                categories: [PERFORMANCE_TAB],
                columns: [
                    {
                        Header: 'Read',
                        headerClassName: 'text-right',
                        accessor: 'avgReadThroughput',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { avgReadThroughput } }) => listValueFormatter(avgReadThroughput, BYTE_FORMAT_PRECISION),
                    },
                    {
                        Header: 'Write',
                        headerClassName: 'text-right',
                        accessor: 'avgWriteThroughput',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { avgWriteThroughput } }) => listValueFormatter(avgWriteThroughput, BYTE_FORMAT_PRECISION),
                    },
                    {
                        Header: 'Overall',
                        headerClassName: 'text-right',
                        accessor: 'avgThroughput',

                        className: 'text-right',
                        minWidth: COLUMN_WIDTHS.SMALL,
                        Cell: ({ original: { avgThroughput } }) => listValueFormatter(avgThroughput, BYTE_FORMAT_PRECISION),
                    },
                ],
            },
            {
                Header: 'Cache Hit Rate',
                headerClassName: 'text-right',
                accessor: 'cacheHit',

                categories: [PERFORMANCE_TAB],
                className: 'text-right',
                minWidth: COLUMN_WIDTHS.SMALL,
                Cell: ({ original: { cacheHit } }) => listValueFormatter(cacheHit, NUMBER_FORMAT_PRECISION, PERCENT_ABBR),
            },
            {
                Header: 'Volume Collection',
                accessor: 'volumeCollection',
                categories: [PROTECTION_TAB],
                minWidth: COLUMN_WIDTHS.LARGE,
                filterable: true,
            },
            {
                Header: 'Protection Level',
                accessor: 'protectionLevel',
                categories: [DEFAULT_TAB, PROTECTION_TAB],
                minWidth: COLUMN_WIDTHS.LARGE,
                Cell: ({ original: { protectionLevel } }) =>
                    <DataProtection protectionLevel={protectionLevel} inline />,
                filterable: true,
            },
            {
                Header: 'Replication Partner',
                accessor: 'replicationPartner',
                categories: [PROTECTION_TAB],
                minWidth: COLUMN_WIDTHS.LARGE,
                filterable: true,
            },
            {
                Header: 'Last Snapped',
                headerClassName: 'text-right',
                accessor: 'lastSnapped',

                className: 'text-right',
                categories: [PROTECTION_TAB],
                minWidth: COLUMN_WIDTHS.MEDIUM,
                Cell: ({ original: { lastSnapped } }) => (
                    <div
                        className="ellipsis-on-overflow"
                        title={formatDateTime(lastSnapped, FORMAT_MOMENT_DATE_TIME)}
                    >
                        {formatDateTime(lastSnapped, FORMAT_MOMENT_DATE_TIME)}
                    </div>),
            },
            {
                Header: 'Last Replicated',
                headerClassName: 'text-right',
                accessor: 'lastReplicated',

                className: 'text-right',
                categories: [PROTECTION_TAB],
                minWidth: COLUMN_WIDTHS.MEDIUM,
                Cell: ({ original: { lastReplicated } }) => (
                    <div
                        className="ellipsis-on-overflow"
                        title={formatDateTime(lastReplicated, FORMAT_MOMENT_DATE_TIME)}
                    >
                        {formatDateTime(lastReplicated, FORMAT_MOMENT_DATE_TIME)}
                    </div>),
            },
            {
                Header: 'Replication Lag (days)',
                headerClassName: 'text-right',
                accessor: 'replicationBehindDays',

                className: 'text-right',
                categories: [PROTECTION_TAB],
                minWidth: COLUMN_WIDTHS.LARGE,
            },
        ],
    });
