// Adapted from http://eddmann.com/posts/cartesian-product-in-javascript/
export default function cartesianProductOf(...sets) {
    return sets.reduce((acc, set) =>
        [].concat(...acc.map(x => set.map(y => [...x, y]))),
    [[]]);
}

/**
 * Cartesian product of the keys in objects
 * @param sets
 * @return {*}
 */
export function cartesianProductOfKeys(...sets) {
    return cartesianProductOf(...sets.map(Object.keys));
}

