import React from 'react';
import numeral from 'numeral';
import { max } from 'underscore';
import PercentGrowth from '@infosight/elmer/dist/components/PercentGrowth';
import { Progress, StackedProgress, ProgressWithValue } from '@infosight/elmer/dist/components/Progress';
import { COLUMN_WIDTHS } from '@infosight/elmer/dist/components/Table';
import { SNAPSHOT_USAGE_COLOR, VOLUME_USAGE_COLOR } from '../../util';
import { DEFAULT_FOLDER } from '../../constants';
import style from './style.scss';

/* eslint-disable react/prop-types */

export const foldersConfig = () => ({
    categories: ['space'],
    columns: [
        {
            Header: 'Folder',
            accessor: 'folderName',
            filterable: true,
            minWidth: COLUMN_WIDTHS.LARGE,
            Cell: ({ original: { poolUID, folderName } }) => (
                <a href={`#capacity-report?pool=${poolUID}&folder=${folderName}`}>
                    {folderName || DEFAULT_FOLDER}
                </a>
            ),
        },
        {
            Header: 'Volumes',
            accessor: 'volumeCount',
            minWidth: COLUMN_WIDTHS.SMALL,
            Cell: ({ original: { volumeCount } }) => <span>{ volumeCount }</span>,
        },
        {
            Header: 'Volume Usage',
            accessor: 'volumeUsed',
            minWidth: COLUMN_WIDTHS.XLARGE,
            style: { marginTop: '5px' },
            Cell: ({ original: { volumeUsed, folderLimit, totalUsed } }) => (
                <ProgressWithValue leftLabel={numeral(volumeUsed).format('0.0 ib')}>
                    <Progress
                        color={VOLUME_USAGE_COLOR}
                        // IS-19371:Added 0 to handle undefined values, leading 0 was required because we use old version of underscore
                        // Math.max returns NaN for Math.max(null, undefined) and Math.max(0, undefined)
                        max={max([0, folderLimit, totalUsed])}
                        value={volumeUsed}
                    />
                </ProgressWithValue>
            ),
        },
        {
            Header: 'Snapshot Usage',
            accessor: 'snapshotUsed',
            minWidth: COLUMN_WIDTHS.XLARGE,
            style: { marginTop: '5px' },
            Cell: ({ original: { snapshotUsed, folderLimit, totalUsed } }) => (
                <ProgressWithValue leftLabel={numeral(snapshotUsed).format('0.0 ib')}>
                    <Progress
                        color={SNAPSHOT_USAGE_COLOR}
                        // IS-19371:Added 0 to handle undefined values, leading 0 was required because we use old version of underscore
                        // Math.max returns NaN for Math.max(null, undefined) and Math.max(0, undefined)
                        max={max([0, folderLimit, totalUsed])}
                        value={snapshotUsed}
                    />
                </ProgressWithValue>
            ),
        },
        {
            Header: 'Total Usage',
            accessor: 'totalUsed',
            minWidth: COLUMN_WIDTHS.XLARGE,
            style: { marginTop: '5px' },
            Cell: ({ original: { volumeUsed, snapshotUsed, folderLimit, totalUsed } }) => {
                let rightValue = totalUsed;
                let rightLabel = 'No limit';
                if (folderLimit > 0) {
                    const isOverLimit = totalUsed >= folderLimit;
                    rightValue = isOverLimit ? totalUsed : folderLimit;
                    rightLabel = isOverLimit ?
                        <span className={style.warning}>Exceeds limit</span> :
                        numeral(folderLimit).format('0.0 ib');
                }
                return (
                    <ProgressWithValue leftLabel={numeral(totalUsed).format('0.00 ib')} rightLabel={rightLabel}>
                        <StackedProgress
                            values={[volumeUsed, snapshotUsed]}
                            colors={[VOLUME_USAGE_COLOR, SNAPSHOT_USAGE_COLOR]}
                            max={rightValue}
                            context="thin"
                        />
                    </ProgressWithValue>
                );
            },
        },
        {
            Header: 'Limit',
            accessor: 'folderLimit',
            minWidth: COLUMN_WIDTHS.SMALL,
            Cell: ({ original: { folderLimit } }) => (folderLimit ? numeral(folderLimit).format('0.0 ib') : 'No limit'),
        },
        {
            Header: '% Free',
            accessor: 'percentFree',
            minWidth: COLUMN_WIDTHS.SMALL,
            Cell: ({ original: { percentFree } }) => {
                if (percentFree > 1) {
                    return null;
                } else if (percentFree <= 0.1) {
                    return <span className={style.warning}>{numeral(percentFree < 0 ? 0 : percentFree).format('0%')}</span>;
                }
                return numeral(percentFree).format('0%');
            },
        },
        {
            Header: 'Past Growth (days)',
            headerClassName: 'text-center',
            columns: [
                {
                    Header: '90',
                    headerClassName: 'text-right',
                    accessor: 'growthPct90day',
                    filterable: false,
                    className: 'text-right',
                    minWidth: COLUMN_WIDTHS.XSMALL,
                    Cell: ({ original: { growthPct90day } }) => (growthPct90day === null ? '' : <PercentGrowth value={growthPct90day} isPercent />),
                },
                {
                    Header: '60',
                    headerClassName: 'text-right',
                    accessor: 'growthPct60day',
                    filterable: false,
                    className: 'text-right',
                    minWidth: COLUMN_WIDTHS.XSMALL,
                    Cell: ({ original: { growthPct60day } }) => (growthPct60day === null ? '' : <PercentGrowth value={growthPct60day} isPercent />),
                },
                {
                    Header: '30',
                    headerClassName: 'text-right',
                    accessor: 'growthPct30day',
                    filterable: false,
                    className: 'text-right',
                    minWidth: COLUMN_WIDTHS.XSMALL,
                    Cell: ({ original: { growthPct30day } }) => (growthPct30day === null ? '' : <PercentGrowth value={growthPct30day} isPercent />),
                },
            ],
        },
    ],
});

export { foldersConfig as default };
