import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { OBJECTS } from '../../../nimble/inventory';
import VolumesList from '../../../nimble/volumes/collection/components/list';
import { SPACE_TAB } from '../../../nimble/volumes/collection/constants';
import { DEFAULT_FOLDER } from '../../constants';
import style from './style.scss';

const cx = classnames.bind(style);

function VolumesTable({ poolUid, folderId, config, userName }) {
    let scope = null;
    let filters = null;
    if (poolUid || folderId) {
        if (folderId === DEFAULT_FOLDER) {
            scope = {
                type: OBJECTS.POOL,
                id: poolUid,
            };
            filters = [{
                field: '_scope_folderUid',
                $eq: null,
            }];
        } else {
            scope = {
                id: folderId || poolUid,
                type: folderId ? OBJECTS.FOLDER : OBJECTS.POOL,
            };
        }
    }

    return (
        <div className={cx('container')}>
            <VolumesList
                tableId={`${userName}_NIMBLE_VOLUMES_CAPACITY`}
                scope={scope}
                filters={filters}
                config={config}
                enabledFieldSets={[SPACE_TAB]}
                syncUrl={false}
                disableDefaultSort
            />
        </div>);
}

VolumesTable.propTypes = {
    poolUid: PropTypes.string,
    folderId: PropTypes.string,
    config: PropTypes.object,
    userName: PropTypes.string,
};

export default VolumesTable;
