import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import classnames from 'classnames/bind';

import { withQueryParams } from '@infosight/elmer/dist/components/Router';

import Card from '@infosight/elmer/dist/components/Card';
import { QueryToggle } from '@infosight/elmer/dist/components/Toggle';
import { TimeRangeSelection, TIME_RANGE_DEFAULTS } from '@infosight/elmer/dist/components/TimeRangeSelector';
import NoData from '@infosight/elmer/dist/components/NoData';
import { OBJECTS } from '../../../../inventory/constants';
import ArrayPerformanceContainer from './ArrayPerformanceContainer';

import style from './style.scss';

const cx = classnames.bind(style);

const arrayToggleParam = 'perf.array';

/* eslint-disable jsx-a11y/label-has-for */
const PoolPerformanceContainer = ({
    poolUid,
    arrays,
    selectedArray = '',
    timeRange,
}) => {
    if (!arrays) {
        return <NoData message="Invalid pool" />;
    }
    return (
        <Card>
            <div className={cx('toggle-container', { hide: arrays.length === 1 })}>
                <label>Array</label>
                <QueryToggle
                    parameter={arrayToggleParam}
                    value={selectedArray}
                >
                    <button key={poolUid} value="">All Arrays</button>
                    {arrays.map(array => (
                        <button key={array.id} value={array.id}>{array.name}</button>
                    ))}
                </QueryToggle>
            </div>
            <div>
                {/*
                  * Setting the key to the selectedArray SN forces the component to be recreated instead of re-rendered.
                  * Changing ArrayPerformanceContainer to recreate the legacy Backbone view on prop change might be better, but much more complicated
                  */}
                <ArrayPerformanceContainer key={selectedArray} poolUid={poolUid} array={selectedArray} timeRange={timeRange} />
            </div>
        </Card>
    );
};

PoolPerformanceContainer.propTypes = {
    poolUid: PropTypes.string,
    arrays: PropTypes.array.isRequired,
    timeRange: PropTypes.instanceOf(TimeRangeSelection).isRequired,
    selectedArray: PropTypes.string,
};

PoolPerformanceContainer.defaultProps = {
    timeRange: TIME_RANGE_DEFAULTS.VOLATILE,
};

export default withQueryParams(connect(
    (state,
        {
            config,
            poolUid,
            match: { params },
        }) => {
        const topology = config.getObjectTopology(OBJECTS.POOL, poolUid);

        // TODO encapsulate this madness in a DecoratedConfig method
        const arrays = topology && topology.children[OBJECTS.ARRAY] && topology.children[OBJECTS.ARRAY]
            .map(arrayId => config.findObject(OBJECTS.ARRAY, arrayId));

        return ({
            poolUid,
            arrays,
            selectedArray: params[arrayToggleParam],
        });
    }
)(PoolPerformanceContainer));
