import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { ClientControlledTable } from '@infosight/elmer/dist/components/Table';
import { userSelector } from '../../../../../user/reducer';
import { getArrays, reset, getArraysCsv } from '../../actionCreators';
import { listSelector, csvSelector } from '../../reducer';
import getConfig from './getConfig';

export class ListContainer extends Component {
    componentDidMount() {
        const { scope, config } = this.props;
        this.props.getArrays(config.convertToGenericQueryScope(scope));
    }

    componentWillUnmount() {
        this.props.resetArrayListStore();
    }

    render() {
        const {
            loadingArrays,
            arrays,
            config,
            scope,
            loadingArraysCsv,
            userName,
        } = this.props;

        const { columns, categories } = getConfig(config);

        return (
            <ClientControlledTable
                tableId={`${userName}_NIMBLE_ARRAYS`}
                data={arrays}
                columns={columns}
                categories={categories}
                loading={loadingArrays}
                defaultSorted={[{ id: 'arraySearchableName', desc: false }]}
                downloadCsv={() => this.props.getArraysCsv(config.convertToGenericQueryScope(scope))}
                downloadingCsv={loadingArraysCsv}
            />
        );
    }
}

ListContainer.propTypes = {
    arrays: PropTypes.arrayOf(PropTypes.object),
    getArrays: PropTypes.func.isRequired,
    resetArrayListStore: PropTypes.func.isRequired,
    loadingArrays: PropTypes.bool.isRequired,
    getArraysCsv: PropTypes.func.isRequired,
    loadingArraysCsv: PropTypes.bool.isRequired,
    userName: PropTypes.string,

    // Injected by XX_INFRASTRUCTURE_XX
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),
    config: PropTypes.object.isRequired,
};

ListContainer.defaultProps = {
    arrays: [],
    loadingArrays: false,
    loadingArraysCsv: false,
};

const mapStateToProps = state => ({
    ...listSelector(state),
    ...csvSelector(state),
    userName: userSelector(state).username,
});

const mapDispatchToProps = dispatch => bindActionCreators({ getArrays, resetArrayListStore: reset, getArraysCsv }, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(ListContainer);
