import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Async from '@infosight/elmer/dist/components/Async';
import { getVolume, reset } from '../../actionCreators';
import { volumeSelector } from '../../reducer';
import VolumeHeadline from './VolumeHeadline';

class VolumeHeadlineContainer extends PureComponent {
    componentWillMount() {
        if (!this.props.loadingVolume && !this.props.loadedVolume) {
            this.props.getVolume(this.props.volumeSn);
        }
    }

    render() {
        const { loadingVolume, volume } = this.props;

        return (
            <Async loading={loadingVolume}>
                <VolumeHeadline {...(volume || {})} />
            </Async>
        );
    }
}

VolumeHeadlineContainer.propTypes = {
    volumeSn: PropTypes.string,
    volume: PropTypes.shape({
        applicationCategory: PropTypes.string,
        dataUsageBytes: PropTypes.number,
        snapshotBytes: PropTypes.number,
        allocatedBytes: PropTypes.number,
        perWeekIops: PropTypes.number,
        perWeekMibs: PropTypes.number,
        perWeekLatencyRead: PropTypes.number,
        perWeekLatencyWrite: PropTypes.number,
        localRecoveryPoint: PropTypes.bool,
        remoteRecoveryPoint: PropTypes.bool,
        last30daysGrowth: PropTypes.number,
        last60daysGrowth: PropTypes.number,
        last90daysGrowth: PropTypes.number,
        avgIopsPerWeek: PropTypes.number,
        avgReadLatencyMs: PropTypes.number,
        avgWriteLatencyMs: PropTypes.number,
        avgThroughput: PropTypes.number,
    }),
    loadingVolume: PropTypes.bool,
    loadedVolume: PropTypes.bool,
    getVolume: PropTypes.func,
};

VolumeHeadlineContainer.defaultProps = {
    loadingVolume: false,
};

const mapStateToProps = state => ({
    ...volumeSelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    getVolume,
    reset,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(VolumeHeadlineContainer);
