import _ from 'underscore';
import chroma from 'chroma-js';

/**
 * The percentile which is mapped to a "10 / 10" score.
 * This sets the shape of the logarithmic function that converts percentile to score.
 * Increasing this value makes the score less sensitive and vice versa
 */
var MAX_SCORE_PERCENTILE = 0.99; // Formerly from infosight0.properties -> 'performance.impactScoreMaxPercentile'
var MAX_SCORE = 10;
var CLASSIFICATIONS = [
    {
        name: 'Low',
        max: 3,
        hideScore: true,
    },
    {
        name: 'Medium',
        max: 6,
    },
    {
        name: 'High',
        max: MAX_SCORE + 1,
    },
];

// See http://gka.github.io/chroma.js/#chroma-scale for options
var COLOR_SCALE = chroma
    .scale([chroma.hsv(0, 0, 0.86), chroma.hsv(0, 1, 0.86)])
    .mode('hsv')
    .domain([3, 10]);

function percentileToScore(percentile) {
    // Note: IE doesn't support Math.log10. Use Math.log instead.
    function log10(n) {
        return Math.log(n) / Math.LN10;
    }

    var score = (MAX_SCORE / log10(1 - MAX_SCORE_PERCENTILE)) * log10(1 - percentile);
    return Math.min(score, MAX_SCORE);
}

function scoreToClassification(score) {
    return _(CLASSIFICATIONS).find(function (classification) {
        return score < classification.max;
    });
}

function scoreToColor(score) {
    return COLOR_SCALE(Math.max(CLASSIFICATIONS[0].max, score));
}

export default {
    COLOR_SCALE,
    percentileToScore,
    scoreToClassification,
    scoreToColor,
};
