import React from 'react';
import PropTypes from 'prop-types';
import numeral from 'numeral';
import { isNumber } from 'underscore';
import style from '../../shared.scss';

function Volumes({ className, volumeCount, totalAllocated }) {
    let allocated;
    if (isNumber(totalAllocated)) {
        allocated = <span>{numeral(totalAllocated).format('0.0 ib')} allocated</span>;
    }

    const count = volumeCount || volumeCount === 0 ? numeral(volumeCount).format('0,0') : 'N/A';
    return (
        <div className={className}>
            <div className={style.subtitle}>Volumes</div>
            <div className={style.metric}>
                <span className={style.value}>{count}</span>
            </div>
            {allocated}
        </div>
    );
}

Volumes.propTypes = {
    volumeCount: PropTypes.number,
    totalAllocated: PropTypes.number, // TODO currently missing from API response
};

export default Volumes;
