/* eslint-disable import/prefer-default-export */
/**
 * Find points in all series in a chart that align on the same timestamp
 * @param chart {object} Highcharts chart object
 * @param index {number} Potential index where you'd expect this point to be located in the source data
 * @param xValue {number} Timestamp/xValue-axis value of this expected point
 * @param pointMapper {function(point, series)} transform Highhcharts.Point to some object. This function should handle falsy point values.
 * @return {{}}
 */
export const findPoints = (chart, index, xValue, pointMapper) => {
    if (!chart || !chart.series || !chart.series.length) {
        return {};
    }

    return chart.series.reduce((memo, series) => {
        // Do we have a point at the same index as the other charts?
        let point = series.points[index];

        // Does the point have the same timestamp? If not, find the right point
        if (!point || point.x !== xValue) {
            point = series.points.find(pt => pt.x === xValue);
        }

        memo[series.options.id] = point && point.x === xValue ? pointMapper(point, series) : pointMapper(null, series);

        return memo;
    }, {});
};

/**
 * Instead of re-implementing Highcharts, exploit the tooltip to highlight points and draw crosshairs.
 * Also handles unhighlighting and removal of crosshairs.
 * @param hoverPoints {Array<Highcharts.Point>}
 * @param chart {Highcharts.Chart}
 * @param event {Event} mousemove event that triggered this sync
 */
export const moveCrosshair = (hoverPoints, chart, event) => {
    if (hoverPoints.length) {
        // Refreshing the tooltip is more reliable than using Point.setState('hover') directly
        chart.tooltip.refresh(hoverPoints, event);
        chart.xAxis[0].drawCrosshair(event, hoverPoints[0]);
    } else if (chart.hoverPoints && chart.hoverPoints.length) {
        // If there is no active point for this chart, remove the markers and crosshair
        chart.hoverPoints.forEach(point => point.setState());
        chart.xAxis[0].hideCrosshair();
    }
};
