import moment from 'moment-timezone';
import { first, last } from 'underscore';

/**
 * This helps to remove time from the timestamp and therefore help in synchronizing charts
 * on the overview page, as all the charts will have the same timestamp for a given day
 */
export const truncateTime = timestamp => moment.utc(timestamp).startOf('day');
export const getMilliSecs = timestamp => truncateTime(timestamp).valueOf();

/**
 * Finds the minimum start data and maximum end date out of all the data given
 * @param {poolEvents, poolCpuCacheUsage, poolCapacityData}
 */
export const computeUnionedTimeRange = ({ poolEvents, poolCpuCacheUsage, poolCapacity }) => {
    const startTimeStamps = [];
    const endTimeStamps = [];

    if (poolEvents && poolEvents.length > 0) {
        startTimeStamps.push(truncateTime(first(poolEvents).timestamp));
        endTimeStamps.push(truncateTime(last(poolEvents).timestamp));
    }

    if (poolCpuCacheUsage && poolCpuCacheUsage.length > 0) {
        startTimeStamps.push(truncateTime(first(poolCpuCacheUsage[0].cpu).ts));
        endTimeStamps.push(truncateTime(last(poolCpuCacheUsage[0].cpu).ts));
    }

    if (poolCapacity && poolCapacity.meta) {
        startTimeStamps.push(truncateTime(poolCapacity.meta.time.dataStart));
        endTimeStamps.push(truncateTime(poolCapacity.meta.time.dataEnd));
    }

    return {
        start: moment.min(...startTimeStamps),
        end: moment.max(...endTimeStamps),
    };
};

/**
 * Find the point in all the series in the given chart which have exactly same given x-axis value
 * @param {moment} Highchart's point objects x value which is {moment} in all the Pool overview charts
 * @param {Object} Highchart object
 */
export const defaultGetCorrespondingPoints = ({ x }, chart) => {
    const hoverPoints = [];
    if (!chart || !chart.series || !chart.series.length) {
        return hoverPoints;
    }

    // Find points in all series in the chart that align on the same timestamp
    chart.series.forEach((series) => {
        // Do we have a point at the same xvalue as the other charts?
        const point = series.points.find(pt => pt.x.isSame(x));
        if (point) {
            hoverPoints.push(point);
        }
    });

    return hoverPoints;
};
