import React, { Component } from 'react';
import autobind from 'react-autobind';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {
    QueryTimeRangeSelector,
    TimeRangeSelection,
    deserialize,
    makeChartSelector,
    makeRangeSelector,
} from '@infosight/elmer/dist/components/TimeRangeSelector';
import { guessMaxSampleCount } from '../../../../../charts/CorrelationHeatmapTimeSeries';
import { determineGranularityFromSampleCount } from '../../../../../../util/genericQuery';
import { SynchronizedChartMediator, SynchronizedChart } from '../../../../../charts/SyncChart';
import { throughputTimeseriesSelector, latencyTimeseriesSelector, poolCpuCacheTimeseriesSelector } from '../../reducer';
import { getTopVolumesThroughputTimeSeries, getTopVolumesLatencyTimeSeries, getPoolCpuCacheUsageSeries, reset } from '../../actionCreators';
import { timeParam, perspectiveParam } from './queryParams';
import { buildPerspectives, buildPerspectiveKey } from './buildPerspectives';
import VolumePerformance from './VolumePerformance';
import PoolPerformanceChart from './PoolPerformanceChart';

const PERSPECTIVES = buildPerspectives();

class PerformanceContainer extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.maxSamples = guessMaxSampleCount();
    }

    componentDidMount() {
        this.fetchCpuCacheUsageSeries();
        this.fetchThroughputTimeSeries();
        this.fetchLatencyTimeSeries();
    }

    componentDidUpdate({ timeRange, throughputPerspective, latencyPerspective }) {
        if (!this.props.timeRange.equals(timeRange)) {
            this.fetchThroughputTimeSeries();
            this.fetchLatencyTimeSeries();
            this.fetchCpuCacheUsageSeries();
            return;
        }

        if (this.props.throughputPerspective !== throughputPerspective) {
            this.fetchThroughputTimeSeries();
        }

        if (this.props.latencyPerspective !== latencyPerspective) {
            this.fetchLatencyTimeSeries();
        }
    }

    componentWillUnmount() {
        this.props.resetPerformanceStore();
    }

    getRequestGranularity() {
        const { from, to } = this.props.timeRange.asFixedRange();
        return determineGranularityFromSampleCount(from, to, this.maxSamples);
    }

    getUnionedTimeRange(...responses) {
        let start = Infinity;
        let end = -Infinity;

        // Deep object checks because this can take any GQ response var and check if there is a value then get the value
        // That way the consumers don't have to replicate the checks.
        responses.forEach((response) => {
            if (response && response.meta && response.meta.time) {
                start = Math.min(start, response.meta.time.dataStart || start);
                end = Math.max(end, response.meta.time.dataEnd || end);
            }
        });

        return start < Infinity ? TimeRangeSelection.toTimeRangeSelection({ start, end }) : this.props.timeRange;
    }

    buildFetchOptions(perspective, addGranularity) {
        const { scope, config, timeRange } = this.props;
        const granularity = addGranularity && this.getRequestGranularity();

        return {
            scope: config.convertToGenericQueryScope(scope),
            timeRange: {
                time: {
                    ...timeRange.toGenericQueryTime().time,
                    granularity: granularity ? granularity.toJSON() : undefined,
                    gapFill: true,
                },
            },
            rankBy: perspective && perspective.field,
        };
    }

    fetchCpuCacheUsageSeries() {
        // TODO Ask Ramesh why the addGranularity flag was added.
        this.props.getPoolCpuCacheUsageSeries(this.buildFetchOptions(null, true));
    }

    fetchThroughputTimeSeries() {
        const { throughputPerspective } = this.props;
        this.props.getTopVolumesThroughputTimeSeries(this.buildFetchOptions(throughputPerspective, true));
    }

    fetchLatencyTimeSeries() {
        const { latencyPerspective } = this.props;
        this.props.getTopVolumesLatencyTimeSeries(this.buildFetchOptions(latencyPerspective, true));
    }

    render() {
        const {
            loadingPoolCpuCacheTimeseries,
            throughputTimeseries,
            latencyTimeseries,
            timeRange,
            poolCpuCacheTimeseries,
            history,
            location,
        } = this.props;

        const hasPoolPerformanceData = !!(poolCpuCacheTimeseries && poolCpuCacheTimeseries.data && poolCpuCacheTimeseries.data.timeseries.length > 0);
        const displayTimeRange = this.getUnionedTimeRange(poolCpuCacheTimeseries, throughputTimeseries, latencyTimeseries).asFixedRange();
        const parameterPrefix = timeParam('');
        const onSelectRange = makeRangeSelector(location, parameterPrefix, false, history);
        const onChartSelect = makeChartSelector(onSelectRange);

        return (
            <div>
                <QueryTimeRangeSelector
                    presets={QueryTimeRangeSelector.PRESETS.VOLATILE}
                    valueClassName="hide"
                    parameterPrefix={parameterPrefix}
                    value={timeRange}
                    displayValue={displayTimeRange}
                    card
                />
                <SynchronizedChartMediator>
                    <SynchronizedChart>
                        <PoolPerformanceChart
                            loading={loadingPoolCpuCacheTimeseries}
                            loaded={hasPoolPerformanceData}
                            isAllFlashPool={hasPoolPerformanceData ? poolCpuCacheTimeseries.data.isAllFlashPool : null}
                            cpuCacheTimeseries={hasPoolPerformanceData ? poolCpuCacheTimeseries.data.timeseries : []}
                            cpuThresholdPercent={hasPoolPerformanceData ? poolCpuCacheTimeseries.data.cpuThresholdPercent : 0}
                            cacheThresholdPercent={hasPoolPerformanceData ? poolCpuCacheTimeseries.data.cacheThresholdPercent : 0}
                            chartRangeStart={displayTimeRange.from}
                            chartRangeEnd={displayTimeRange.to}
                            onChartSelect={onChartSelect}
                        />
                    </SynchronizedChart>
                    <VolumePerformance
                        {...this.props}
                        chartRangeStart={displayTimeRange.from}
                        chartRangeEnd={displayTimeRange.to}
                        onChartSelect={onChartSelect}
                    />
                </SynchronizedChartMediator>
            </div>
        );
    }
}

PerformanceContainer.propTypes = {
    // Injected by mapDispatchToProps
    getTopVolumesThroughputTimeSeries: PropTypes.func,
    getTopVolumesLatencyTimeSeries: PropTypes.func,
    resetPerformanceStore: PropTypes.func,

    // Injected by mapStateToProps
    loadingThroughputTimeseries: PropTypes.bool,
    loadedThroughputTimeseries: PropTypes.bool,
    throughputTimeseries: PropTypes.shape({
        data: PropTypes.arrayOf(PropTypes.object),
        meta: PropTypes.shape({
            time: PropTypes.shape({
                start: PropTypes.number.isRequired,
                end: PropTypes.number.isRequired,
                granularity: PropTypes.string.isRequired,
            }),
        }),
    }),
    loadingLatencyTimeseries: PropTypes.bool,
    loadedLatencyTimeseries: PropTypes.bool,
    latencyTimeseries: PropTypes.shape({
        data: PropTypes.arrayOf(PropTypes.object),
        meta: PropTypes.shape({
            time: PropTypes.shape({
                start: PropTypes.number.isRequired,
                end: PropTypes.number.isRequired,
                granularity: PropTypes.string.isRequired,
            }),
        }),
    }),
    timeRange: PropTypes.oneOfType([
        PropTypes.instanceOf(TimeRangeSelection),
        PropTypes.string,
    ]).isRequired,
    throughputPerspective: PropTypes.object,
    latencyPerspective: PropTypes.object,

    loadingPoolCpuCacheTimeseries: PropTypes.bool,
    loadedPoolCpuCacheTimeseries: PropTypes.bool,
    getPoolCpuCacheUsageSeries: PropTypes.func.isRequired,
    poolCpuCacheTimeseries: PropTypes.shape({
        data: PropTypes.shape({
            isAllFlashPool: PropTypes.string,
            cpuThresholdPercent: PropTypes.number,
            cacheThresholdPercent: PropTypes.number,
            timeseries: PropTypes.arrayOf(PropTypes.shape({
                cpu50thPercentile: PropTypes.number,
                cache50thPercentile: PropTypes.number,
                timestamp: PropTypes.number,
            })).isRequired,
        }).isRequired,
        meta: PropTypes.shape({
            time: PropTypes.shape({
                start: PropTypes.number.isRequired,
                end: PropTypes.number.isRequired,
            }).isRequired,
        }).isRequired,
    }),
    // Injected by XX_INFRASTRUCTURE_XX
    scope: PropTypes.shape({
        type: PropTypes.string.isRequired,
        id: PropTypes.string.isRequired,
    }),
    config: PropTypes.object.isRequired,
    history: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
};

const mapUrlToProps = ({ match: { params } }) => {
    const perspectiveKey = buildPerspectiveKey({
        unit: params[perspectiveParam('unit')] || 'iops',
        io: params[perspectiveParam('io')] || 'readWrite',
        access: params[perspectiveParam('access')] || 'randomSequential',
    });

    const throughputPerspective = PERSPECTIVES[perspectiveKey];
    return {
        throughputPerspective,
        latencyPerspective: PERSPECTIVES[throughputPerspective.latencyPerspectiveKey],
        timeRange: deserialize(params, timeParam('')) || QueryTimeRangeSelector.DEFAULTS.VOLATILE,
    };
};

const mapStateToProps = (state, props) => ({
    ...throughputTimeseriesSelector(state),
    ...latencyTimeseriesSelector(state),
    ...poolCpuCacheTimeseriesSelector(state),
    ...mapUrlToProps(props),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    getTopVolumesThroughputTimeSeries,
    getTopVolumesLatencyTimeSeries,
    getPoolCpuCacheUsageSeries,
    resetPerformanceStore: reset,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(PerformanceContainer);
