var Backbone = require('backbone');
var NimbleOsReleaseModel = require('./NimbleOsReleaseModel');

function toComparableVersion(version) {
    var pad = '0000';
    var parts = version.split(/\.|\-/);
    return parts.slice(0, 4).map(function (part) {
        return part.length >= pad.length ? part : (pad + part).slice(-pad.length);
    }).join('.');
}

module.exports = Backbone.Collection.extend({

    model: NimbleOsReleaseModel,

    comparator: function (a, b) {
        return -toComparableVersion(a.get('version')).localeCompare(toComparableVersion(b.get('version')));
    },

});
