import { createReducersForAction, createSelector, getInitialState } from '@infosight/elmer/dist/utils/redux';
import rootState from '../../rootStateSelector';
import { USER_CONTEXT_CHANGED } from '../../../user/constants';
import {
    ARRAYS_LIST_FETCH,
    ARRAYS_CSV_FETCH,
    RESET,
} from './constants';

const initialState = {
    ...getInitialState('arrays'),
    ...getInitialState('arraysCsv'),
};

const ACTION_HANDLERS = {
    [USER_CONTEXT_CHANGED]: () => (initialState),
    [RESET]: () => (initialState),
    ...createReducersForAction({ type: ARRAYS_LIST_FETCH, stateKey: 'arrays' }),
    ...createReducersForAction({ type: ARRAYS_CSV_FETCH, stateKey: 'arraysCsv' }),
};

export default function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

const baseSelector = state => rootState(state).arrayCollection;
export const listSelector = createSelector(baseSelector, 'arrays');
export const csvSelector = createSelector(baseSelector, 'arraysCsv');
