import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';

const contextTypes = {
    chartSyncProvider: PropTypes.shape({
        monitorChart: PropTypes.func.isRequired,
        removeChart: PropTypes.func.isRequired,
        syncPoints: PropTypes.func.isRequired,
    }),
};

/**
 * Wraps an element to add a chartSync prop that contains the callbacks needed to work with ChartSyncProvider
 * @param children
 * @param chartSyncProvider
 * @constructor
 */
export const SynchronizedChart = ({ children }, { chartSyncProvider }) => React.cloneElement(React.Children.only(children), { chartSync: chartSyncProvider });
SynchronizedChart.contextTypes = contextTypes;

/**
 * When there are complex interactions required for one chart to be synced with another, such as deconstructed and recomposed charts like the Shower Tile
 * view, SyncChart breaks down. This is a mediator entity that responds to a request to sync a point from one chart by triggering all charts to sync to that
 * point. Each component registers itself with two callbacks which are used to let each chart handle moving crosshairs and activating marker.
 */
export class SynchronizedChartMediator extends Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.charts = [];
    }

    getChildContext() {
        return { chartSyncProvider: this };
    }

    monitorChart(chart, { syncPoints, resetPoints }) {
        if (!chart) {
            return;
        }

        if (!syncPoints || !resetPoints) {
            console.assert(syncPoints, 'Expected a callback syncPoints(point)');
            console.assert(resetPoints, 'Expected a callback resetPoints()');
            return;
        }

        this.charts.push({ chart, syncPoints, resetPoints });
    }

    removeChart(chart) {
        for (let i = 0; i < this.charts.length; i += 1) {
            if (this.charts[i].chart === chart) {
                this.charts.splice(i, 1);
            }
        }
    }

    syncPoints({ point }) {
        if (point) {
            this.charts.forEach(chart => chart.syncPoints(point));
        } else {
            this.charts.forEach(chart => chart.resetPoints(point));
        }
    }

    render() {
        return this.props.children;
    }
}

SynchronizedChartMediator.propTypes = {
    children: PropTypes.node,
};

SynchronizedChartMediator.childContextTypes = contextTypes;
