import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import {
    VOLUME_FETCH,
    VOLUME_DETAIL_PROTECTION_FETCH,
    VOLUME_DETAIL_CAPACITY_FETCH,
    VOLUME_DETAIL_PERFORMANCE_FETCH,
    RESET,
} from './constants';
import Request from '../../../../services/Request';
import { firstOrNullData, firstOrNull } from '../../../../util/genericQuery';

export const getVolume = volumeSn => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(VOLUME_FETCH, dispatch);
    dispatchStart();

    new Request().pod().genericQuery('volumes')
        .post({
            scope: {
                type: 'volume',
                id: volumeSn,
            },
        })
        .then(firstOrNullData)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const getVolumeCapacity = (volumeSn, timeRange) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(VOLUME_DETAIL_CAPACITY_FETCH, dispatch);
    dispatchStart();

    new Request().pod().genericQuery('volume', ['capacity'])
        .post({
            ...timeRange,
            scope: {
                type: 'volume',
                id: volumeSn,
            },
        })
        .then(firstOrNull)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const getVolumeProtection = volumeId => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(VOLUME_DETAIL_PROTECTION_FETCH, dispatch);
    dispatchStart();

    new Request().pod().genericQuery('volume', ['protection'])
        .post({
            scope: {
                type: 'volume',
                id: `${volumeId}`,
            },
        })
        .then(x => x.data)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const getVolumePerformance = (volumeSn, time) => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(VOLUME_DETAIL_PERFORMANCE_FETCH, dispatch);
    dispatchStart();

    // TODO get severity score
    return new Request().pod().genericQuery('volume', ['performance'])
        .post({
            scope: {
                type: 'volume',
                id: volumeSn,
            },
            ...time,
        })
        .then(x => x.data)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const reset = () => ({ type: RESET });
