import React from 'react';
import PropTypes from 'prop-types';
import PercentGrowth from '@infosight/elmer/dist/components/PercentGrowth';
import classnames from 'classnames/bind';
import styles from './styles.scss';

const cx = classnames.bind(styles);

const isEmpty = value => !!((value === null || value === undefined || value === ''));

const percentGrowthRenderer = (growthValue, dayCount) => (
    isEmpty(growthValue)
        ? null
        :
        <div className="column flex-dir-column shrink">
            <PercentGrowth value={growthValue} isPercent />
            <div className={cx('dayCount')}>
                <small>{dayCount} Days</small>
            </div>
        </div>
);

const PastGrowthList = ({ last90daysGrowth, last60daysGrowth, last30daysGrowth }) => {
    if ([last30daysGrowth, last60daysGrowth, last90daysGrowth].every(isEmpty)) {
        return null;
    }

    return (
        <div className="flex-container row expanded align-center">
            {[last90daysGrowth, last60daysGrowth, last30daysGrowth].map((growth, index) =>
                (percentGrowthRenderer(growth, 90 - (index * 30))))}
        </div>
    );
};

PastGrowthList.propTypes = {
    last90daysGrowth: PropTypes.number,
    last60daysGrowth: PropTypes.number,
    last30daysGrowth: PropTypes.number,
};

export default PastGrowthList;
