const _ = require('underscore');
const $ = require('jquery');
var Marionette = require('backbone.marionette');
var tpl = require('raw-loader!./SoftwareUpgradesView.html');
var upgradePathsTpl = require('raw-loader!./SoftwareUpgradePaths.html');
var blacklistTooltipTpl = require('raw-loader!./BlacklistTooltip.html');
var GridView = require('app/views/components/grid/GridView');
var BulkLoadGridModel = require('app/models/grid/BulkLoadGridModel');

var upgradePathsTemplate = _.template(upgradePathsTpl);
var blacklistTooltipTemplate = _.template(blacklistTooltipTpl);

var truncateRelease = function (release) {
    return release.split(/\-/)[0];
};

function renderBlackListEntry(blackListEntries) {
    var result = '';
    _.each(blackListEntries, function (entry) {
        var reason = entry.blackListReasons;
        if (reason) {
            var title = reason.publicTitle === 'null' ? '' : ' - ' + reason.publicTitle;
            result += '<table class="blacklisted-reason"> <tbody><tr><th title=""> AS-' + entry.bugNumber + title + '</th></tr>';
            result += (reason.publicDescription === 'null') ? '' : '<tr><td style="padding-left: 20px">' + reason.publicDescription + '</td></tr>';
            result += (reason.publicWorkaround === 'null') ? '' : '<tr><td style="padding-left: 20px">' + reason.publicWorkaround + '</td></tr>';
            result += '</tbody></table>';
        }
    });
    return result;
}

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    className: 'software-upgrades-dialog',

    regions: {
        gridRegion: '.grid-region',
    },

    blacklistedReleases: null,

    templateHelpers() {
        return { _, displayAsModal: this.getOption('displayAsModal') !== false };
    },

    columns: function () {
        var self = this;
        return [
            {
                data: function (row) {
                    return row.targetRelease.split('.')[0] + '.x';
                },
                visible: false,
                filter: true,
            },
            {
                data: 'targetRelease',
                type: 'nimbleos-version',
                /*
                 * Order the 'Major Release' and 'Release' columns in tandem
                 * so that our row grouping works properly
                 */
                orderData: [0, 1],
                render: {
                    display: function (data, type, row) {
                        var result = '';
                        if (row.blacklistEntries.length > 0) {
                            result += '<span class="blacklisted-release">';
                            result += '<span class="blacklisted-icon fa fa-circle"></span>';
                        } else {
                            result += '<span>';
                        }
                        result += truncateRelease(data);
                        result += '</span>';
                        result += renderBlackListEntry(row.blacklistEntries);
                        return result;
                    },
                },
            },
            {
                data: 'releaseStatus.description',
                orderable: false,
            },
            {
                data: function (row) {
                    return upgradePathsTemplate({
                        upgradePaths: row.shortestUpgradePaths,
                        targetRelease: row.targetRelease,
                        blacklistedReleases: self.blacklistedReleases,
                        truncateRelease: truncateRelease,
                    });
                },
                orderable: false,
            },
        ];
    },

    onRender: function () {
        var self = this;

        this.$el.tooltip({
            html: true,
            selector: '.blacklisted-icon',
            title: _.partial(this.renderTooltip, this),
        });

        this.grid = new GridView({
            model: new BulkLoadGridModel({
                url: this.api()
                    .array(this.model.get('serialNumber'))
                    .segment('releaseManagement/upgrades')
                    .addQuery({ allowedReleaseStatus: ['GA', 'LTSR'] }),
                columns: this.columns(),
                showFilters: false,
                emptyLabel: 'No software upgrades found',
                pageLength: 10,
                groupBy: function (api) {
                    var releaseColumn = 1;
                    var majorReleaseColumn = 0;
                    var order = api.order();
                    // Only group if table is ordered by 'Release' column
                    return (order.length > 0 && order[0][0] == releaseColumn) ? majorReleaseColumn : null;
                },
                dataSrc: function (response) {
                    self.blacklistedReleases = _.reduce(response.data, function (memo, upgrade) {
                        if (upgrade.blacklistEntries.length > 0) {
                            memo[upgrade.targetRelease] = true;
                        }

                        return memo;
                    }, {});

                    return response.data;
                },
            }),
            id: 'upgrades-grid',
            tableTemplate: this.$('.grid-content').html(),
            dataTableOptions: {
                dom: '<"H">t<"F">',
                columnDefs: [
                    { width: '50%', targets: 1 },
                ],
            },
            sort: ['Release'],
        });
        this.listenTo(this.grid, 'init', this.onGridLoaded);

        this.gridRegion.show(this.grid);
    },

    onGridLoaded: function () {
        var model = this.model;

        if (!_.isEmpty(this.blacklistedReleases)) {
            this.$('.software-upgrades-legend').show();
        }

        this.$('ul.upgrade-paths').each(function () {
            var $list = $(this);
            var $morePaths = $list.children('li:gt(0)').hide();

            if ($morePaths.length > 0) {
                $('<a href="#">More</a>').click(function (event) {
                    event.preventDefault();
                    var $link = $(this);

                    $morePaths.slideToggle();
                    $link.text($link.text() == 'More' ? 'Less' : 'More');
                })
                    .appendTo($('<li></li>').appendTo($list));
            }
        });
    },

    renderTooltip: function (self) {
        return blacklistTooltipTemplate({
            array: self.model.get('hostname'),
            release: $(this).closest('.blacklisted-release').text(),
        });
    },
});
