const getPayload = response => response.data;
/**
 * The shape of the axios response object combined with the GQ response can get confusing:
 * response
 *  data
 *      data
 *      meta
 *  headers
 *  ...
 *
 *  This immutably replaces the response.data.data with the the data param
 * @param response
 * @param data
 */
const replacePayloadData = (response, data) => ({
    ...response,
    data: {
        ...response.data, data,
    },
});

/**
 * Replace response.data.data with null if response.data.data is empty
 * @param response
 */
const firstOrNullTransform = (response) => {
    const payload = getPayload(response);
    const newData = payload.data.length ? payload.data[0] : null;
    return replacePayloadData(response, newData);
};

/**
 * Return response.data.data[0] OR null when response.data.data is empty
 * @param response
 */
export function firstOrNullData(response) {
    return firstOrNullTransform(response).data.data;
}

/**
 * Return response.data OR null when response.data.data is empty
 * @param response
 */
export function firstOrNull(response) {
    const { data } = firstOrNullTransform(response);
    return data.data ? data : null;
}
