import _ from 'underscore';

export const ADD_GLOBAL_NOTIFICATION = 'ADD_GLOBAL_NOTIFICATION';
export const REMOVE_GLOBAL_NOTIFICATION = 'REMOVE_GLOBAL_NOTIFICATION';
export const DISMISS_GLOBAL_NOTIFICATION = 'DISMISS_GLOBAL_NOTIFICATION';

export const addNotification = (severity = 'info', { content, id }) => ({
    type: ADD_GLOBAL_NOTIFICATION,
    id: id || _.uniqueId(),
    severity,
    content,
});

export const dismissNotification = id => ({
    type: DISMISS_GLOBAL_NOTIFICATION,
    id,
});
