import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { getStore } from './storeProvider';
import { appBaseUrlSelector } from '../modules/user/reducer';

// TODO Move these functions somewhere else, such as user module

export function getLoginPath(postLoginTarget = '') {
    if (postLoginTarget !== '') {
        return `/app/login?logout&redirectTo=${encodeURIComponent(postLoginTarget)}`;
    }

    return '/app/login?logout';
}

export function redirectToLogin() {
    let targetPath = '';
    if (window.location.pathname) {
        targetPath = window.location.search ?
            `${window.location.pathname}${window.location.search}` :
            window.location.pathname;
    }

    const redirectUrl = getLoginPath(targetPath);

    window.location.replace(redirectUrl);
}

/**
 * Use for legacy non-React-Router links that need to link to use HTML5 PushState
 * @param args
 */
export function buildUrlFromBase(...args) {
    return buildUrl(appBaseUrlSelector(getStore().getState()), ...args);
}
