const _ = require('underscore');
var Marionette = require('backbone.marionette');
var tpl = require('raw-loader!./EventsChart.html');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    initialize: function () {
        this.data = this.getSeriesData();
    },

    onRender: function () {
        this.$('[data-chart]').highcharts('StockChart', _(this.options.chartDefaults({
            height: 50,
            type: 'scatter',
        })).extend({
            series: [
                {
                    marker: {
                        enabled: true,
                        //symbol: 'text:\uf05a', // fa-info-circle
                        symbol: 'text:\uf0ad', // fa-wrench
                        states: {
                            hover: {
                                enabled: false,
                            },
                        },
                    },
                    data: this.data.toArray(),
                },
            ],
            tooltip: false,
            yAxis: {
                visible: false,
                max: 1,
                min: -1,
            },
        }));
    },

    getSeriesData: function () {
        var self = this;
        var minTimestamp = this.collection.first().get('timestamp');
        var maxTimestamp = this.collection.last().get('timestamp');

        // Sort events to correctly work out closest date
        this.options.eventsCollection.comparator = function (event) {
            return event.get('date');
        };
        this.options.eventsCollection.sort();

        // We get events at per-day granularity, but performance timeseries data varies with the range.
        // Since the perf charts take precedence when snapping to the crosshairs to a point,
        // set the event's x-coordinate to the closes perf point within 1-day threshold.
        //
        // Assumes the source data is sorted
        var THRESHOLD = 24 * 3600 * 1000;
        var indexOfLastPoint = 0;
        var points = self.collection.pluck('timestamp');
        var closest = function (searchVal) {
            var val;
            var i = indexOfLastPoint;
            var memo = points[i];
            var memoDistance = Number.MAX_SAFE_INTEGER;
            var distance;

            while (i < points.length) {
                val = points[i];
                distance = Math.abs(val - searchVal);

                // Found the closest point and distances are now increasing; bail
                if (distance >= memoDistance) {
                    break;
                }

                // Found a closer point within threshold
                if (distance < THRESHOLD && distance < memoDistance) {
                    memo = val;
                    memoDistance = Math.abs(memo - searchVal);
                    indexOfLastPoint = i;
                }

                i++;
            }

            return memo;
        };

        return this.options.eventsCollection
            .filter(function (event) {
                return event.get('date') >= minTimestamp && event.get('date') <= maxTimestamp;
            })
            .reduce(function (memo, event) {
                var point = _({
                    x: closest(event.get('date')),
                    y: 0,
                }).defaults(event.getFlagConfig());

                // Collapse multiple points
                var overlap = memo.points[point.x];
                if (overlap) {
                    memo.points[point.x] = {
                        x: overlap.x,
                        y: 0,
                        color: '#235789',
                        arrayEvents: (overlap.arrayEvents || [overlap]).concat(point),
                    };
                } else {
                    memo.points[point.x] = point;
                    memo.length++;
                }

                return memo;
            }, {
                points: {},
                toArray: function () {
                    var numericComparer = function (a, b) {
                        return a - b;
                    };

                    return Object.keys(this.points)
                        .sort(numericComparer)
                        .map(_.propertyOf(this.points));
                },
                length: 0,
            });
    },

    isEmpty: function () {
        return this.data.length === 0;
    },

    getPointsAt: function (xCoordinate) {
        return this.data.points[xCoordinate];
    },
});
